import VacationList from "../views/myPage/VacationList";
import FamilyEventList from "../views/myPage/FamilyEventList";
import ProfileManage from "../views/myPage/ProfileManage";
import BuyRequestList from "../views/myPage/BuyRequestList";
import TeamLeaderVacationManage from "../views/system/VacationAdminList";
import VacationStateList from "../views/system/VacationStateList";

export default [
  {
    path: "/mypage/vacation",
    name: "vacation",
    component: VacationList,
  },
  {
    path: "/mypage/family-event",
    name: "FamilyEventList",
    component: FamilyEventList,
    props: { menuName: "경조금신청현황" },
  },
  {
    //프로필관리 - 임직원
    path: "/mypage/ProfileManage",
    name: "ProfileManage",
    component: ProfileManage,
  },

  {
    path: "/mypage/buy-request",
    name: "BuyRequestList",
    component: BuyRequestList,
    props: { menuName: "구매신청", upcodes: ["11900", "12000"] },
  },
  {
    path: "/mypage/teamLeaderVacationManage",
    name: "teamLeaderVacationManage",
    component: TeamLeaderVacationManage,
    props: { upMenuPath: "mypage" },
  },
  {
    path: "/mypage/vacation-state-list",
    name: "MypageVacationStateList",
    component: VacationStateList,
    props: { upMenuPath: "mypage" },
  },
];
