<template>
  <ModalPopup
    title="경조사 마스터 등록"
    :visible.sync="modalVisible"
    width="960px"
  >
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="tbl-row write-mode">
      <table class="padding-15-9">
        <colgroup>
          <col class="col-row-title" />
          <col style="width: 100%" />
          <col class="col-row-title" />
          <col style="width: 100%" />
        </colgroup>
        <tbody>
          <tr>
            <th scope="row" class="required">경조구분</th>
            <td>
              <vSelect
                :options="getGubunExceptAll"
                label="text"
                v-model="form.famEvnGubun"
                :reduce="(getGubunExceptAll) => getGubunExceptAll.value"
                :clearable="false"
                :searchable="false"
                @input="onChangeFamEvnGubun"
                :disabled="famEvnGubunDisabled"
              ></vSelect>
            </td>
            <th scope="row" class="required">사용여부</th>
            <td>
              <vSelect
                :options="getUseYnExceptAll"
                label="text"
                v-model="form.useYn"
                :reduce="(getUseYnExceptAll) => getUseYnExceptAll.value"
                :clearable="false"
                :searchable="false"
              ></vSelect>
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">본인과의 관계</th>
            <td>
              <vSelect
                :options="getRelationExceptAll"
                label="text"
                v-model="form.famRelationCd"
                :reduce="(getRelationExceptAll) => getRelationExceptAll.value"
                :clearable="false"
                :searchable="false"
                :disabled="famRelationCdDisabled"
              ></vSelect>
            </td>
            <th scope="row" class="required">경조종류</th>
            <td>
              <vSelect
                :options="getFamEvnExceptAll"
                label="text"
                v-model="form.famEvnCd"
                :reduce="(getFamEvnExceptAll) => getFamEvnExceptAll.value"
                :clearable="false"
                :searchable="false"
                :disabled="famEvnCdDisabled"
              ></vSelect>
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">경조금액</th>
            <td>
              <InputBox
                ref="famEvnAmt"
                type="text"
                :value.sync="form.famEvnAmt"
              />
            </td>
            <th scope="row" class="required">경조물품</th>
            <td>
              <InputBox
                ref="famEvnProduct"
                type="text"
                :value.sync="form.famEvnProduct"
                :disabled="getFamEvnProductDisabled"
              />
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">정렬순서</th>
            <td>
              <InputBox ref="sortSeq" type="text" :value.sync="form.sortSeq" />
            </td>
            <th scope="row" class="required">증빙서류타입</th>
            <td>
              <vSelect
                :options="evidencialTypeOption"
                label="text"
                v-model="form.evidencialType"
                :reduce="(evidencialType) => evidencialType.value"
                :clearable="false"
                :searchable="false"
                @input="onChangeEvidencialType"
              ></vSelect>
            </td>
          </tr>
          <tr v-if="form.evidencialType !== '1'">
            <th scope="row" class="required">증빙서류 추가</th>
            <td colspan="3">
              <button type="button" class="btn" @click="addEvidencial">
                추가
              </button>
            </td>
          </tr>
          <tr v-for="(e, index) in form.evidencialCds" :key="index">
            <th scope="row" class="required">증빙서류{{ index + 1 }}</th>
            <td colspan="3">
              <vSelect
                :options="getEvidencialExceptAll"
                label="text"
                v-model="form.evidencialCds[index]"
                :reduce="
                  (getEvidencialExceptAll) => getEvidencialExceptAll.value
                "
                :clearable="false"
                :searchable="false"
              ></vSelect>
              <button
                type="button"
                class="btn margin-left-10"
                @click="() => removeEvidencial(index)"
              >
                삭제
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="modal-popup-btn-box">
      <button type="button" class="btn btn-primary" @click="save">저장</button>
      <button type="button" class="btn" @click="cancel">취소</button>
    </div>
  </ModalPopup>
</template>

<script>
import Vue from "vue";
import axios from "axios";
import "@toast-ui/editor/dist/toastui-editor.css";

import InputBox from "@/components/InputBox";
import vSelect from "vue-select";
import ModalPopup from "@/components/ModalPopup";

import { Empty } from "@/mixins/Empty";
import Loading from "vue-loading-overlay";

export default {
  name: "FamilyEventMasterPop",
  props: ["options", "search"],
  mixins: [Empty],
  components: {
    InputBox,
    vSelect,
    ModalPopup,
    Loading,
  },
  data() {
    return {
      modalVisible: false,
      evidencialTypeOption: [
        { text: "단일 서류", value: "1" },
        { text: "AND (여러 개)", value: "2" },
        { text: "OR (여러 개 중 하나)", value: "3" },
      ],
      form: {
        useYn: "Y",
        famEvnGubun: "C",
        famEvnCd: "11510",
        famRelationCd: "11610",
        evidencialCds: ["11710"],
        evidencialType: "1",
        famEvnAmt: "",
        famEvnProduct: "",
        sortSeq: "",
      },
      famEvnDetail: {},
      famEvnCdDisabled: false,
      famEvnGubunDisabled: false,
      famRelationCdDisabled: false,

      isNew: true,
      isLoading: false,
      fullPage: true,
    };
  },
  watch: {
    modalVisible(val, oldVal) {
      if (!val) {
        this.resetForm();
        this.isNew = true;
        this.famEvnCdDisabled = false;
        this.famEvnGubunDisabled = false;
        this.famRelationCdDisabled = false;
      }
    },
  },
  computed: {
    getGubunExceptAll() {
      const exceptAll = [...this.options.famEvnGubun];
      exceptAll.shift();
      return exceptAll;
    },
    getUseYnExceptAll() {
      const exceptAll = [...this.options.useYn];
      exceptAll.shift();
      return exceptAll;
    },
    getRelationExceptAll() {
      const exceptAll = [...this.options.famRelationCds];
      exceptAll.shift();
      return exceptAll;
    },
    getFamEvnExceptAll() {
      const exceptAll = [...this.options.famEvnCds];
      exceptAll.shift();
      return exceptAll;
    },
    getEvidencialExceptAll() {
      const exceptAll = [...this.options.evidencialCds];
      exceptAll.shift();
      return [...exceptAll];
    },
    getFamEvnProductDisabled() {
      return !(
        this.form.famEvnGubun === "C" &&
        this.form.famRelationCd === "11616" &&
        this.form.famEvnCd === "11512"
      );
    },
  },
  methods: {
    // 팝업 열기
    openInsertPop() {
      this.modalVisible = true;
    },
    openUpdatePop(eventRow) {
      this.callFamilyEventDetail(eventRow);
      this.isNew = false;
      this.famEvnCdDisabled = true;
      this.famEvnGubunDisabled = true;
      this.famRelationCdDisabled = true;
      this.modalVisible = true;
    },
    // 팝업 닫기
    cancel() {
      this.modalVisible = false;
    },
    // 저장시 validation
    save() {
      const saveData = this.form;

      if (this.isEmpty(saveData.famEvnGubun)) {
        alert("경조구분을 선택하세요.");
        return false;
      }
      if (this.isEmpty(saveData.useYn)) {
        alert("사용여부를 선택하세요.");
        return false;
      }
      if (this.isEmpty(saveData.famRelationCd)) {
        alert("본인과의 관계를 선택하세요.");
        return false;
      }
      if (this.isEmpty(saveData.famEvnCd)) {
        alert("경조종류를 선택하세요.");
        return false;
      }
      if (this.isEmpty(saveData.famEvnAmt)) {
        alert("경조금액을 입력하세요.");
        this.$refs.famEvnAmt.focus();
        return false;
      }
      if (saveData.famEvnAmt % 10 > 0) {
        alert("경조금액은 10원 단위로 입력하세요.");
        this.$refs.famEvnAmt.focus();
        return false;
      }
      if (!Number(saveData.famEvnAmt)) {
        alert("경조금액을 확인하세요.");
        this.$refs.famEvnAmt.focus();
        return false;
      }
      if (
        !this.getFamEvnProductDisabled &&
        this.isEmpty(saveData.famEvnProduct)
      ) {
        alert("경조물품을 입력하세요.");
        this.$refs.famEvnProduct.focus();
        return false;
      }
      if (this.isEmpty(saveData.sortSeq)) {
        alert("정렬순서를 입력하세요.");
        this.$refs.sortSeq.focus();
        return false;
      }
      if (!Number(saveData.sortSeq)) {
        alert("정렬순서를 확인하세요.");
        this.$refs.sortSeq.focus();
        return false;
      }
      if (this.isEmpty(saveData.evidencialType)) {
        alert("증빙서류를 선택하세요.");
        return false;
      }
      if (saveData.evidencialCds.length < 1) {
        alert("증빙서류는 최소 1개 이상입니다.");
        return false;
      }

      const set = new Set(saveData.evidencialCds);
      if (set.size !== saveData.evidencialCds.length) {
        alert("증빙서류는 중복으로 등록 할 수 없습니다.");
        return false;
      }

      saveData.evidencialCd = saveData.evidencialCds.join(":");

      this.callFamilyEventMasterSave(saveData);
    },
    // 저장 API 호출
    async callFamilyEventMasterSave(saveData) {
      this.isLoading = true;

      const apiUrl = `/api/system/family-event-master/${
        this.isNew ? "create" : "update"
      }`;

      const save = await this.$http
        .post(apiUrl, saveData)
        .then((solve) => {
          if (solve.data.statusCode === 200) {
            this.modalVisible = false;
            this.search();
          } else {
            if (solve.data.message !== null) alert(solve.data.message);
            throw new Error("게시물을 저장하는데 실패하였습니다.");
          }

          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });

      return save;
    },
    removeEvidencial(index) {
      if (this.form.evidencialCds.length < 2) {
        alert("증빙서류는 최소 1개 이상입니다.");
        return false;
      }
      this.form.evidencialCds.splice(index, 1);
    },
    addEvidencial() {
      this.form.evidencialCds.push("11710");
    },
    // 경조사 마스터 상세 조회
    async callFamilyEventDetail(eventRow) {
      this.isLoading = true;

      const apiUrl = `/api/system/family-event-master/get-detail/${eventRow.famEvnCd}/${eventRow.famRelationCd}`;
      const data = await this.$http
        .get(apiUrl)
        .then((solve) => {
          if (solve.data.statusCode === 200) {
            this.famEvnDetail = solve.data.data;
            this.famEvnDetail.evidencialCds = solve.data.data.evidencialCd.split(
              ":"
            );

            this.form.useYn = this.famEvnDetail.useYn;
            this.form.famEvnGubun = this.famEvnDetail.famEvnGubun;
            this.form.famEvnCd = this.famEvnDetail.famEvnCd;
            this.form.famRelationCd = this.famEvnDetail.famRelationCd;
            this.form.evidencialCds = this.famEvnDetail.evidencialCds;
            this.form.evidencialType = this.famEvnDetail.evidencialType;
            this.form.famEvnAmt = this.famEvnDetail.famEvnAmt;
            this.form.famEvnProduct = this.famEvnDetail.famEvnProduct;
            this.form.sortSeq = this.famEvnDetail.sortSeq;
          } else {
            throw new Error("게시물 상세정보를 가져오는데 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    resetForm() {
      this.form = {
        useYn: "Y",
        famEvnGubun: "C",
        famEvnCd: "11510",
        famRelationCd: "11610",
        evidencialCds: ["11710"],
        evidencialType: "1",
        famEvnAmt: "",
        famEvnProduct: "",
        sortSeq: "",
      };
    },
    onChangeFamEvnGubun(value) {
      if (value === "E") {
        this.form.famRelationCd = "11617";
        this.form.famEvnCd = "11515";
        this.famRelationCdDisabled = true;
        this.famEvnCdDisabled = true;
      } else {
        this.famRelationCdDisabled = false;
        this.famEvnCdDisabled = false;
      }
    },
    onChangeEvidencialType(value) {
      if (value === "1") {
        this.form.evidencialCds = [this.form.evidencialCds[0]];
      }
    },
  },
};
</script>
<style scoped lang="scss">
.padding-15-9 > tbody > tr > th {
  padding: 15px 9px !important;
}
.margin-left-10 {
  margin-left: 10px;
}
</style>