<template>
  <span class="file-btn">
    <!-- DESC : input id / label for 동일하게 맞춰주세요 -->
    <input
      type="file"
      :id="id"
      :title="label + ' 추가'"
      @change="onFileChange"
      :multiple="multiple"
    />
    <label :for="id" class="btn">{{ label }}</label>
  </span>
</template>
<script>
export default {
  props: {
    label: {
      type: String,
      require: true,
    },
    multiple: {
      type: Boolean,
      require: false,
    },
    idx: {
      type: Number,
      require: false,
    },
  },
  data() {
    return {
      uid: null,
    };
  },
  computed: {
    id() {
      return "inputFile" + this.uid;
    },
  },
  created() {
    this.uid = this._uid;
  },
  methods: {
    onFileChange(event) {
      this.$emit("change", { event, idx: this.idx });
    },
  },
};
</script>
