<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="page-title-box">
      <h1 class="page-title">경조사 마스터 관리</h1>
      <ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
        <li class="breadcrumb">홈</li>
        <li class="breadcrumb">인트라넷관리</li>
        <li class="breadcrumb">경조사마스터관리</li>
      </ol>
    </div>
    <div class="search-input-box">
      <div class="search-input-row">
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="gubun" class="padding-right-44">경조 구분</label>
          <div class="search-value">
            <vSelect
              id="gubun"
              :options="options.famEvnGubun"
              v-model="searchData.famEvnGubun"
              :reduce="(famEvnGubun) => famEvnGubun.value"
              :clearable="false"
              :searchable="false"
              label="text"
            ></vSelect>
          </div>
        </div>
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="evidencialCd">증빙 서류</label>
          <div class="search-value">
            <vSelect
              id="evidencialCd"
              :options="options.evidencialCds"
              v-model="searchData.evidencialCd"
              :reduce="(evidencialCds) => evidencialCds.value"
              :clearable="false"
              :searchable="false"
              label="text"
            ></vSelect>
          </div>
        </div>
      </div>
      <div class="search-input-row">
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="famRelationCd">본인과의 관계</label>
          <div class="search-value">
            <vSelect
              id="famRelationCd"
              :options="options.famRelationCds"
              v-model="searchData.famRelationCd"
              :reduce="(famRelationCds) => famRelationCds.value"
              :clearable="false"
              :searchable="false"
              label="text"
            ></vSelect>
          </div>
        </div>
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="famEvnCd">경조 종류</label>
          <div class="search-value">
            <vSelect
              id="famEvnCd"
              :options="options.famEvnCds"
              v-model="searchData.famEvnCd"
              :reduce="(famEvnCds) => famEvnCds.value"
              :clearable="false"
              :searchable="false"
              label="text"
            ></vSelect>
          </div>
        </div>
      </div>

      <div class="search-input-row">
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="useYn" class="padding-right-44">사용 여부</label>
          <div class="search-value">
            <vSelect
              id="useYn"
              :options="options.useYn"
              v-model="searchData.useYn"
              :reduce="(useYn) => useYn.value"
              :clearable="false"
              :searchable="false"
              label="text"
            ></vSelect>
          </div>
        </div>
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="regDateFrom">등록일</label>
          <div class="search-value date-range-box">
            <DatePicker ref="fromDate" />
            <span class="tilde-text">~</span>
            <DatePicker ref="toDate" />
          </div>
        </div>
        <div class="search-item confirm-box">
          <button type="button" class="btn" @click="resetSearch">초기화</button>
          <button type="button" class="btn btn-primary" @click="search">
            검색
          </button>
        </div>
      </div>
    </div>
    <div class="grid-box">
      <div class="grid-tool-box">
        <div>
          <button type="button" class="btn btn-primary" @click="openInsertPop">
            등록
          </button>
        </div>
      </div>
      <Grid
        class="tui-grid"
        ref="tuiGrid"
        :options="gridProps.options"
        :data="gridProps.data"
        :columns="gridProps.columns"
        @click="onGridClick"
      />
      <div class="per-page-box" v-if="!this.$store.state.isMobile">
        <span class="total-count-text">
          총
          <span class="count">{{ gridProps.data.length }}</span
          >개
        </span>
        <div class="select-box">
          <vSelect
            ref="perPage"
            :options="pageData.data"
            :clearable="false"
            :searchable="false"
            v-model="perPage"
            :reduce="(label) => label.value"
            @input="onPerPageInput"
          ></vSelect>
        </div>
      </div>
    </div>

    <FamilyEventMasterPop ref="detailPop" :options="options" :search="search" />
  </div>
</template>
<script>
import "codemirror/lib/codemirror.css";
import { mapState } from "vuex";
import { Grid } from "@toast-ui/vue-grid";
import { CheckboxRenderer } from "@/components/renderer/GridRenderer";
import { Empty } from "@/mixins/Empty";
import { DateFunc } from "@/mixins/DateFunc";
import InputBox from "@/components/InputBox";
import vSelect from "vue-select";
import DatePicker from "@/components/Datepicker";
import Loading from "vue-loading-overlay";
import FamilyEventMasterPop from "@/views/system/pop/FamilyEventMasterPop";

export default {
  name: "FamilyEventMaster",
  props: ["typeCode"],
  mixins: [Empty, DateFunc],
  components: {
    Grid,
    InputBox,
    vSelect,
    DatePicker,
    Loading,
    FamilyEventMasterPop,
  },
  data() {
    return {
      perPage: 10,
      famEvnCd: "11500",
      famRelationCd: "11600",
      evidencialCd: "11700",
      searchData: {
        famEvnCd: "",
        evidencialCd: "",
        famRelationCd: "",
        famEvnGubun: "",
        useYn: "",
      },
      options: {
        famEvnCds: [],
        evidencialCds: [],
        famRelationCds: [],
        famEvnGubun: [
          { text: "전체", value: "" },
          { text: "축의금", value: "C" },
          { text: "조의금", value: "D" },
          { text: "기타", value: "E" },
        ],
        useYn: [
          { text: "전체", value: "" },
          { text: "사용", value: "Y" },
          { text: "미사용", value: "N" },
        ],
      },
      pageData: {
        data: [
          { label: "10", value: 10 },
          { label: "20", value: 20 },
          { label: "50", value: 50 },
          { label: "100", value: 100 },
        ],
        selected: "10",
      },
      famEvnList: [],
      isLoading: false,
      fullPage: true,
    };
  },
  computed: {
    ...mapState(["code"]),
    isMobile() {
      return this.$store.state.isMobile;
    },
    gridProps() {
      return {
        options: {
          rowHeight: 48,
          scrollX: false,
          scrollY: true,
          bodyHeight: 480,
          showDummyRows: false,
          selectionUnit: "row",
          pageOptions: {
            useClient: true,
            perPage: this.perPage,
            visiblePages: 5,
          },
        },
        columns: [
          {
            header: "경조번호",
            name: "famEvnCd",
            width: 0,
            align: "center",
            hidden: true,
          },
          {
            header: "가족관계번호",
            name: "famRelationCd",
            width: 0,
            align: "center",
            hidden: true,
          },
          {
            header: "경조구분",
            name: "famEvnGubunNm",
            width: 80,
            align: "center",
            sortable: true,
          },
          {
            header: "경조종류",
            name: "famEvnNm",
            formatter(data) {
              return `
                <div class="board-title-box">
                  <a class="board-title clickable-text">${data.value}</a>
                  ${
                    Math.floor(
                      (new Date() - new Date(data.row.regDt)) /
                        (1000 * 60 * 60 * 24)
                    ) <= 5
                      ? '<span class="ico-new">새 게시물</span>'
                      : ""
                  }
                </div>
              `;
            },
            width: 180,
            align: "center",
            sortable: true,
          },
          {
            header: "경조금액",
            name: "famEvnItem",
            width: 130,
            align: "center",
            sortable: true,
          },
          {
            header: "증빙서류",
            name: "evidencialNm",
            align: "center",
            sortable: true,
          },
          {
            header: "사용여부",
            name: "useYn",
            formatter(data) {
              return data.value === "Y" ? "사용" : "미사용";
            },
            width: 80,
            align: "center",
            sortable: true,
          },
          {
            header: "등록일",
            name: "regDt",
            width: 100,
            align: "center",
            sortable: true,
          },
          {
            header: "등록자",
            name: "regEmpNm",
            width: 100,
            align: "center",
            sortable: true,
          },
          {
            header: "수정일",
            name: "modDt",
            width: 100,
            align: "center",
            sortable: true,
          },
          {
            header: "수정자",
            name: "modEmpNm",
            width: 100,
            align: "center",
            sortable: true,
          },
        ],
        data: this.famEvnList,
      };
    },
  },
  created() {
    this.setFamilyEventMasterInfo();
  },
  watch: {
    // 그리드 내용 변경시
    gridProps(val, oldVal) {
      this.$refs.tuiGrid.invoke("resetData", val.data);
    },
  },
  methods: {
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data);
    },
    // 그리드 클릭
    onGridClick(event) {
      if (
        event.targetType !== "columnHeader" &&
        event.columnName === "famEvnNm"
      ) {
        const eventRow = this.$refs.tuiGrid.invoke("getRow", event.rowKey);
        this.openUpdatePop(eventRow);
      }
    },
    // 신규 팝업 오픈
    openInsertPop() {
      this.$refs.detailPop.openInsertPop();
    },
    // 수정 팝업 오픈
    openUpdatePop(eventRow) {
      this.$refs.detailPop.openUpdatePop(eventRow);
    },
    // 공통코드 세팅
    setFamilyEventMasterInfo() {
      this.options.famEvnCds = [
        {
          text: "전체",
          value: "",
        },
        ...this.code.codeInfo.filter((x) => x.value === this.famEvnCd).shift()
          .list,
      ];
      this.options.famRelationCds = [
        {
          text: "전체",
          value: "",
        },
        ...this.code.codeInfo
          .filter((x) => x.value === this.famRelationCd)
          .shift().list,
      ];
      this.options.evidencialCds = [
        {
          text: "전체",
          value: "",
        },
        ...this.code.codeInfo
          .filter((x) => x.value === this.evidencialCd)
          .shift().list,
      ];
      this.callFamilyEventMasterList();
    },
    // 경조사 마스터 목록 조회
    async callFamilyEventMasterList(params) {
      this.isLoading = true;

      if (params === void 0)
        params = {
          // upcode: this.typeCode,
        };

      const apiUrl = "/api/system/family-event-master/get-list";
      const data = await this.$http
        .post(apiUrl, params)
        .then((solve) => {
          if (solve.data.result) {
            this.famEvnList = solve.data.data;
          } else {
            throw new Error("게시물 목록을 가져오는데 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    // 검색
    search() {
      const param = this.searchData;

      const startDt = this.$refs.fromDate.instance.getDate();
      const endDt = this.$refs.toDate.instance.getDate();
      if (this.isNotEmpty(startDt)) {
        param.startDt = this.formatDate(startDt, "-");
      }
      if (this.isNotEmpty(endDt)) {
        param.endDt = this.formatDate(endDt, "-");
      }

      this.callFamilyEventMasterList(param);
    },
    // 경조사 마스터 상세 조회
    async callFamilyEventDetail(eventRow) {
      this.isLoading = true;

      const apiUrl = `/api/system/family-event-master/get-detail/${eventRow.famEvnCd}/${eventRow.famRelationCd}`;
      const data = await this.$http
        .get(apiUrl)
        .then((solve) => {
          if (solve.data.statusCode === 200) {
            this.famEvnDetail = solve.data.data;
            this.famEvnDetail.evidencialCds = solve.data.data.evidencialCd.split(
              ":"
            );
          } else {
            throw new Error("게시물 상세정보를 가져오는데 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });

      this.openUpdatePop();
    },
    // 검색조건 초기화
    resetSearch() {
      this.searchData = {
        famEvnCd: "",
        evidencialCd: "",
        famRelationCd: "",
        famEvnGubun: "",
        useYn: "",
      };
      this.$refs.fromDate.instance.setDate(null);
      this.$refs.toDate.instance.setDate(null);
    },
  },
};
</script>
<style scoped lang="scss">
.padding-right-44 {
  padding-right: 44px !important;
}
</style>