<template>
  <ModalPopup :title="'회원조회'" :visible.sync="modalVisible" width="772px">
    <loading :active.sync="isLoading" :is-full-page="true"></loading>
    <div class="search-input-box">
      <div class="search-input-row">
        <div class="search-item">
            <label for="companyPos">임직원명</label>
            <div class="search-value">
                <InputBox type="text" :value.sync="gridSearchParams.empNm"/>
            </div>
        </div>
        <div class="search-item">
            <label for="companyPos">직급</label>
            <div class="search-value">
                <vSelect id="companyPos" v-model="gridSearchParams.gradeCd" :reduce="label => label.value"
                    :options="gradeCodeList.options" :clearable="false" :searchable="false"/>
            </div>
        </div>
        <div class="search-item confirm-box">
            <button type="button" @click.passive.stop="search" class="btn btn-primary">검색</button>
        </div>
      </div>
    </div>
    <div class="grid-box">
      <div class="grid-tool-box">
        <div class="left-box">
          <button type="button" class="btn btn-control" @click.prevent.stop="addMember" >추가</button>
        </div>
      </div>
      <grid class="tui-grid" ref="tuiGrid" :data="gridProps.data" :columns="gridProps.columns" :options="gridProps.options" />
    </div>
    <div class="modal-popup-btn-box">
      <button type="button" class="btn" @click.prevent.stop="closed">취소</button>
    </div>
  </ModalPopup>
</template>

<script>
import { mapState, mapActions } from "vuex";
import "tui-grid/dist/tui-grid.css";
import Loading from "vue-loading-overlay";
import vSelect from 'vue-select'
import { Grid } from "@toast-ui/vue-grid";
import { Grids } from "@/mixins/Grids";
import { Empty } from "@/mixins/Empty";
import { Comm } from "@/mixins/Comm";
import {CheckboxRenderer} from '@/components/renderer/GridRenderer';
import ModalPopup from "@/components/ModalPopup";
import InputBox from "@/components/InputBox";

import TuiGrid from 'tui-grid';
TuiGrid.setLanguage('ko');

export default {
  name: "MemberSearchPop",
  mixins: [Empty,Comm],
  components: {
    vSelect,
    grid: Grid,
    loading: Loading,
    ModalPopup,
    InputBox
  },
  data() {
    return {
      modalVisible: false,
      isLoading: false,
      gridSearchParams: {empNm:'', projectNo:'', type:'', gradeCd: ''},
      gradeCodeList : {options: []},
      gridProps : {
        options: {
          rowHeight: 48,
          rowHeaders: [{
            type: 'checkbox',
            header: `
              <span class="ui-chk">
                <input id="chkAll1" type="checkbox" name="_checked"><label for="chkAll1"></label>
              </span>`,
            renderer: {
              type: CheckboxRenderer,
            },
            }],
          scrollX: false,
          scrollY: true,
          bodyHeight: 480,
					showDummyRows: false,
          selectionUnit: 'row',
          display: { noData: 'No Data' },
        },
        columns: [
          {header: "empNo", name: "empNo", hidden: true},
          {header: "이름", name: "empNm",align: "center", sortable: true },
          {header: "부서", name: "dptNm",align: "center", sortable: true },
          {header: "직급", name: "gradeNm",align: "center", sortable: true},
        ],
        data: []
      }
    };
  },
  computed: {
  },
  created() {
    this.gradeCodeList.options = this.commSelect("10100", "선택하세요");
  },
  watch: {
    modalVisible(val, oldVal) {
      if (!val) {
        this.resetData();
      }
    },
  },
  methods: {
   open(info) {
      this.gridSearchParams.type = info.type;
      this.gridSearchParams.projectNo = info.projectNo
      this.search();
      this.modalVisible = true;
    },
    closed (obj) {
      this.modalVisible = false;
      //부모 함수 콜백
      this.$emit('closeMemberSearchPop', obj);
    },
    search(){
      this.isLoading = true;
      this.$http.post("/api/system/team-manage/getNotProjectMemberList", this.gridSearchParams,)
          .then((response) => {
            if( this.isEmpty(response.data)){
              alert('임직원 조회중 오류가 발생했습니다.');
              return false;
            }
            this.$refs.tuiGrid.invoke('resetData', response.data.data, {});
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    addMember(){
      const gridRows = this.$refs.tuiGrid.invoke("getCheckedRows");
      if( this.isEmpty(gridRows)){
        alert("선택항목이 없습니다.");
        return false;
      }

      let rtnObj = {};
      rtnObj.type = this.gridSearchParams.type;
      //관리자
      if(this.gridSearchParams.type == 'manager'){
        if( gridRows.length > 1 ){ alert('1명만 선택 가능 합니다.'); return false; }
        rtnObj.empNo = gridRows[0].empNo;
        rtnObj.empNm = gridRows[0].empNm;
        this.closed(rtnObj);
      } else {
        if( this.isNotEmpty(this.gridSearchParams.projectNo) ){
          let params = {};
          params.projectNo = this.gridSearchParams.projectNo;
          params.teamList = gridRows;

          this.isLoading = true;
          this.$http.post("/api/system/team-manage/insertProjectTeamMember", params)
              .then((response) => {
                alert(response.data.message);
                this.closed(rtnObj);
              })
              .catch((e) => {
                console.log(e);
                this.isLoading = false;
              })
              .finally(() => {
                //this.isLoading = false;
              });
        } else {
          rtnObj.list = gridRows;
          this.closed(rtnObj);
        }
      }
    },
    resetData(){
      this.gridSearchParams.projectNo = '';
      this.gridSearchParams.empNm = '';
      this.gridSearchParams.positionCd = '';
      this.gridSearchParams.type = '';
    },
  }
}
</script>

<style lang="css" scoped>

</style>
