<template>
  <modal-popup
    :title="'구매신청'"
    :visible="seen"
    @update:visible="close"
    width="100%"
    height="100%"
  >
    <div class="form-table">
      <table>
        <colgroup>
          <col class="col-form-title" />
          <col style="width: 100%;" />
          <col class="col-form-title" />
          <col style="width: 100%;" />
        </colgroup>
        <tbody>
          <tr>
            <th scope="row">신청자</th>
            <td>
              <input-box type="text" :value.sync="getUserNm" disabled="true" />
            </td>
            <th scope="row">소속</th>
            <td>
              <input-box type="text" :value="'쓰리탑'" disabled="true" />
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="form-table">
      <table>
        <colgroup>
          <col class="col-form-title" />
        </colgroup>
        <tbody>
          <tr>
            <th scope="row">신청사항</th>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="grid-box" style="margin-top:20px;">
      <div class="grid-tool-box">
        <div v-if="isEditable">
          <button type="button" class="btn" @click="appendGrid1Row">
            추가
          </button>
          <button type="button" class="btn" @click="deleteRow(1)">삭제</button>
        </div>
        <div></div>
      </div>
      <grid
        class="tui-grid"
        ref="grid1"
        :data="grid1.gridProps.data"
        :columns="grid1.gridProps.columns"
        :options="grid1.gridProps.options"
      />
      <div class="per-page-box"></div>
    </div>
    <div class="form-table">
      <table>
        <colgroup>
          <col class="col-form-title" />
        </colgroup>
        <tbody>
          <tr>
            <th scope="row">장비현황 (교체일 경우)</th>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="grid-box" style="margin-top:20px;">
      <div class="grid-tool-box">
        <div v-if="isEditable">
          <button type="button" class="btn" @click="appendGrid2Row">
            추가
          </button>
          <button type="button" class="btn" @click="deleteRow(2)">삭제</button>
        </div>
        <div></div>
      </div>
      <grid
        class="tui-grid"
        ref="grid2"
        :data="grid2.gridProps.data"
        :columns="grid2.gridProps.columns"
        :options="grid2.gridProps.options"
      />
      <div class="per-page-box"></div>
    </div>
    <div class="form-table">
      <table>
        <colgroup>
          <col class="col-form-title" />
        </colgroup>
        <tbody>
          <tr>
            <th scope="row">지급 대상자명 (신규일 경우)</th>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="grid-box" style="margin-top:20px;">
      <div class="grid-tool-box">
        <div v-if="isEditable">
          <button type="button" class="btn" @click="appendGrid3Row">
            추가
          </button>
          <button type="button" class="btn" @click="deleteRow(3)">삭제</button>
        </div>
        <div></div>
      </div>
      <grid
        class="tui-grid"
        ref="grid3"
        :data="grid3.gridProps.data"
        :columns="grid3.gridProps.columns"
        :options="grid3.gridProps.options"
      />
      <div class="per-page-box"></div>
    </div>
    <div class="modal-popup-btn-box">
      <button
        v-if="isDetail && isEditable"
        type="button"
        class="btn btn-primary"
        @click="modify"
      >
        수정
      </button>
      <button
        v-else-if="!isDetail"
        type="button"
        class="btn btn-primary"
        @click="submit"
      >
        신청
      </button>
      <button type="button" class="btn" @click="close">취소</button>
    </div>
  </modal-popup>
</template>

<script>
import "tui-grid/dist/tui-grid.css";

import { mapState, mapActions } from "vuex";
import ModalPopup from "@/components/ModalPopup";
import { Grid } from "@toast-ui/vue-grid";
import { Grids } from "@/mixins/Grids";
import InputBox from "@/components/InputBox";
import DatePicker from "@/components/Datepicker";
import { CheckboxRenderer } from "@/components/renderer/GridRenderer";
import { DateFunc } from "@/mixins/DateFunc";
import { Comm } from "@/mixins/Comm";

export default {
  name: "BuyRequestPop",
  components: {
    ModalPopup,
    InputBox,
    DatePicker,
    grid: Grid,
  },
  props: ["isDetail", "upcodes"],
  mixins: [DateFunc, Grids, Comm],
  data() {
    return {
      buyReqNo: "",
      seen: false,
      form: {
        empNo: "",
        empNm: "",
        mgrApprovalYn: "",
      },
      options: { "11900": [], "12000": [] },
      grid1: {
        gridProps: {},
      },
      grid2: {
        gridProps: {},
      },
      grid3: {
        gridProps: {},
      },
      isInProcess: false,
    };
  },
  created() {
    this.formatterInstall();
    for (let upcode of this.upcodes) {
      this.options[upcode] = this.commSelect(upcode);
    }

    let vm = this;
    let itemCodeList = this.commSelect("11900", "선택", true);
    itemCodeList[0].value = "";
    let reqTypeCodeList = this.commSelect("12000", "선택", true);
    reqTypeCodeList[0].value = "";

    this.grid1.gridProps = {
      options: {
        editingEvent: "click",
        rowHeight: 48,
        rowHeaders: [
          {
            type: "checkbox",
            header: `
							<span class="ui-chk">
								<input id="chkAll1" type="checkbox" name="_checked"><label for="chkAll1"></label>
							</span>`,
            renderer: {
              type: CheckboxRenderer,
            },
          },
          {
            type: "rowNum",
            header: "번호",
          },
        ],
        scrollY: true,
        scrollX: false,
        bodyHeight: 144,
        showDummyRows: false,
        selectionUnit: "row",
        display: { noData: "No Data" },
        onGridMounted(ev) {
          ev.instance.eventBus.on("editingFinish", vm.checkAppend);
        },
      },
      columns: [
        {
          header: "품명",
          name: "itemCode",
          sortable: true,
          formatter: "listItemText",
          align: "center",
          editor: {
            type: "select",
            options: {
              listItems: itemCodeList,
            },
          },
          validation: { required: true },
        },
        {
          header: "수량",
          name: "itemQty",
          sortable: true,
          editor: "text",
          align: "center",
          validation: { dataType: "number", required: true, max: 99, min: 1 },
        },
        {
          header: "요청구분",
          name: "reqTypeCode",
          sortable: true,
          formatter: "listItemText",
          align: "center",
          editor: {
            type: "select",
            options: {
              listItems: reqTypeCodeList,
            },
          },
          validation: { required: true },
        },
        {
          header: "사유",
          name: "reqReason",
          editor: "text",
          align: "center",
          validation: { required: true },
        },
        {
          header: "희망일",
          name: "hopeDt",
          sortable: true,
          editor: {
            type: "datePicker",
            options: {
              format: "yyyy-MM-dd",
              language: "ko",
              calendar: {
                showToday: false,
              },
            },
            validation: { required: true },
          },
          align: "center",
        },
        { header: "비고", name: "remarks", editor: "text", align: "center" },
      ],
      data: [],
    };

    this.grid2.gridProps = {
      options: {
        editingEvent: "click",
        rowHeight: 48,
        rowHeaders: [
          {
            type: "checkbox",
            header: `
							<span class="ui-chk">
								<input id="chkAll2" type="checkbox" name="_checked"><label for="chkAll2"></label>
							</span>`,
            renderer: {
              type: CheckboxRenderer,
            },
          },
          {
            type: "rowNum",
            header: "번호",
          },
        ],
        scrollX: false,
        scrollY: true,
        bodyHeight: 192,
        showDummyRows: false,
        selectionUnit: "row",
        display: { noData: "No Data" },
      },
      columns: [
        {
          header: "요청구분",
          name: "reqTypeCode",
          hidden: true,
        },
        {
          header: "사용자",
          name: "reqUserNm",
          sortable: true,
          align: "center",
          editor: { type: "text" },
          validation: { required: true },
        },
        {
          header: "품명",
          name: "itemCode",
          sortable: true,
          formatter: "listItemText",
          align: "center",
          editor: {
            type: "select",
            options: {
              listItems: itemCodeList,
            },
          },
          validation: { required: true },
        },
        {
          header: "장비번호",
          name: "itemMgrNo",
          align: "center",
          editor: "text",
        },
        { header: "장비현황", name: "itemTx", align: "center", editor: "text" },
        { header: "모니터", name: "spec1", align: "center", editor: "text" },
        { header: "RAM", name: "spec2", align: "center", editor: "text" },
        { header: "CPU", name: "spec3", align: "center", editor: "text" },
        { header: "HDD", name: "spec4", align: "center", editor: "text" },
        { header: "SSD", name: "spec5", align: "center", editor: "text" },
      ],
      data: [],
    };

    this.grid3.gridProps = {
      options: {
        editingEvent: "click",
        rowHeight: 48,
        rowHeaders: [
          {
            type: "checkbox",
            header: `
							<span class="ui-chk">
								<input id="chkAll3" type="checkbox" name="_checked"><label for="chkAll3"></label>
							</span>`,
            renderer: {
              type: CheckboxRenderer,
            },
          },
          {
            type: "rowNum",
            header: "번호",
          },
        ],
        scrollX: false,
        scrollY: true,
        bodyHeight: 192,
        showDummyRows: false,
        selectionUnit: "row",
        display: { noData: "No Data" },
      },
      columns: [
        {
          header: "요청구분",
          name: "reqTypeCode",
          hidden: true,
        },
        {
          header: "품명",
          name: "itemCode",
          sortable: true,
          formatter: "listItemText",
          align: "center",
          editor: {
            type: "select",
            options: {
              listItems: itemCodeList,
            },
          },
          validation: { required: true },
        },
        {
          header: "지급대상자",
          name: "reqUserNm",
          align: "center",
          editor: { type: "text" },
          validation: { required: true },
        },
        {
          header: "비고",
          name: "remarks",
          align: "center",
          editor: { type: "text" },
        },
      ],
      data: [],
    };
  },
  computed: {
    ...mapState({
      loginInfo: (state) => state.userInfo,
      codeInfo: (state) => state.codeInfo,
    }),
    isEditable() {
      return (
        !this.isDetail ||
        (this.isDetail &&
          !(this.form.pmApprovalYn == "Y" || this.form.mgrApprovalYn == "Y"))
      );
    },
    getUserNm() {
      return this.isDetail ? this.form.empNm : this.loginInfo.userNm;
    },
  },
  methods: {
    open() {
      this.seen = true;
    },
    openDetail(data) {
      this.buyReqNo = data.master.buyReqNo;
      this.form.empNo = data.master.empNo;
      this.form.empNm = data.master.empNm;
      this.form.pmApprovalYn = data.master.pmApprovalYn;
      this.form.mgrApprovalYn = data.master.mgrApprovalYn;

      this.seen = true;
      this.$nextTick(() => {
        this.$refs.grid1.invoke("resetData", data.detailList);
        this.$refs.grid2.invoke("resetData", data.chgItemList);
        this.$refs.grid3.invoke("resetData", data.newItemList);

        if (!this.isEditable) {
          this.$refs.grid1.invoke("disable");
          this.$refs.grid2.invoke("disable");
          this.$refs.grid3.invoke("disable");
        }
      });
    },
    close() {
      this.resetForm();
      this.seen = false;
    },
    resetForm() {
      this.$set(this.$data, "buyReqNo", "");
      Object.assign(this.$data.form, this.$options.data().form);
      this.$refs.grid3.invoke("resetData", []);
      this.$refs.grid2.invoke("resetData", []);
      this.$refs.grid1.invoke("resetData", []);
    },
    validate(param) {
      if (!param.detailData || param.detailData.length == 0) {
        alert("신청사항을 하나 이상 입력해주세요.");
        return false;
      } else if (
        !param.itemData ||
        (param.detailData.length > 0 && param.itemData.length == 0)
      ) {
        alert("신청사항은 존재하나 상세 항목이 없습니다.");
        return false;
      }

      return (
        this.validateGrid("1") &&
        this.validateGrid("2") &&
        this.validateGrid("3")
      );
    },
    submit() {
      let param = {};
      param.detailData = this.$refs.grid1.invoke("getData");
      param.itemData = [
        ...this.$refs.grid2.invoke("getData"),
        ...this.$refs.grid3.invoke("getData"),
      ];

      if (this.isInProcess) {
        alert("처리중입니다. 잠시만 기다려주세요.");
        return false;
      }

      if (this.validate(param)) {
        this.isInProcess = true;
        this.$http
          .post("/api/system/buy-request-manage/create", param)
          .then((response) => {
            if (response.data.data) {
              alert(response.data.data.msg);
              this.seen = false;
              this.$emit("created");
            }
          })
          .catch((e) => {
            console.error(e);
          })
          .finally(() => {
            this.isInProcess = false;
          });
      }
    },
    modify() {
      let param = {};
      param.buyReqNo = this.buyReqNo;
      param.detailData = this.$refs.grid1.invoke("getData");
      param.itemData = [
        ...this.$refs.grid2.invoke("getData"),
        ...this.$refs.grid3.invoke("getData"),
      ];

      if (this.isInProcess) {
        alert("처리중입니다. 잠시만 기다려주세요.");
        return false;
      }

      if (this.buyReqNo && this.validate(param)) {
        this.isInProcess = true;

        this.$http
          .post("/api/system/buy-request-manage/update", param)
          .then((response) => {
            if (response.data.data) {
              alert(response.data.data.msg);
              this.seen = false;
              this.$emit("modified");
            }
          })
          .catch((e) => {
            console.error(e);
          })
          .finally(() => {
            this.isInProcess = false;
          });
      }
    },
    appendGrid1Row() {
      this.$refs.grid1.invoke("appendRow", { itemCode: "", reqTypeCode: "" });
    },
    appendGrid2Row() {
      this.$refs.grid2.invoke("appendRow", {
        itemCode: "",
        reqTypeCode: "12011",
      });
    },
    appendGrid3Row() {
      this.$refs.grid3.invoke("appendRow", {
        itemCode: "",
        reqTypeCode: "12010",
      });
    },
    deleteRow(idx) {
      let checked = this.$refs["grid" + idx].invoke("getCheckedRows");
      if (!checked || checked.length == 0) {
        alert("삭제할 항목이 없습니다.");
        return false;
      }
      for (let row of checked) {
        this.$refs["grid" + idx].invoke("removeRow", row.rowKey);
      }
    },
    validateGrid(idx, chkKey) {
      let validated = this.$refs["grid" + idx].invoke("validate");

      if (validated.length > 0) {
        if (chkKey && validated[0].rowKey != chkKey) {
          return false;
        }
        let row = this.$refs["grid" + idx].invoke(
          "getRow",
          validated[0].rowKey
        );
        let colIdx = this.$refs["grid" + idx].invoke(
          "getIndexOfColumn",
          validated[0].errors[0].columnName
        );
        let cols = this.$refs["grid" + idx].invoke("getColumns", 0);
        let errCode = validated[0].errors[0].errorCode[0];
        let errInfo = validated[0].errors[0].errorInfo[0];
        let alertInfo = {
          gridNum: idx,
          col: cols[colIdx],
          rowNum: row._attributes.rowNum,
        };

        this.validateAlert(alertInfo, errCode, errInfo);
      }
      return validated.length == 0;
    },
    validateAlert(alertInfo, errCode, errInfo) {
      let location =
        " [" +
        alertInfo.gridNum +
        "번 그리드 " +
        alertInfo.rowNum +
        "번째 줄 " +
        alertInfo.col.header +
        "]";
      if (errCode == "TYPE_NUMBER") {
        alert("숫자만 입력 가능합니다. " + location);
      } else if (errCode == "MAX") {
        alert(`최대 ${errInfo.max} 이하로 입력해주세요.` + location);
      } else if (errCode == "MIN") {
        alert(`최소 ${errInfo.min} 이상으로 입력해주세요.` + location);
      } else if (errCode == "REQUIRED") {
        alert("필수 입력 입니다." + location);
      }
    },
    checkAppend(prop) {
      let rowData = this.$refs.grid1.invoke("getRow", prop.rowKey);
      if (
        rowData.itemCode &&
        rowData.itemQty &&
        rowData.reqTypeCode &&
        rowData.reqReason &&
        rowData.hopeDt
      ) {
        if (!this.validateGrid("1", prop.rowKey)) {
          return false;
        }
        let grid1Data = this.$refs.grid1.invoke("getData");
        if (grid1Data.length > 1) {
          for (let data of grid1Data) {
            if (
              data.itemCode == rowData.itemCode &&
              data.reqTypeCode == rowData.reqTypeCode
            ) {
              alert(
                "품명, 요청구분이 같은 항목을 1개 이상 추가할 수 없습니다."
              );
              return false;
            }
          }
        }

        let itemCount = 0;
        let gridName = rowData.reqTypeCode == "12011" ? "grid2" : "grid3";
        let subGridData = this.$refs[gridName].invoke("getData");
        for (let data of subGridData) {
          if (data.itemCode == rowData.itemCode) {
            itemCount++;
          }
        }

        if (itemCount < rowData.itemQty) {
          const addCount = rowData.itemQty - itemCount;
          for (let i = 0; i < addCount; i++) {
            this.$refs[gridName].invoke("appendRow", {
              itemCode: rowData.itemCode,
              reqTypeCode: rowData.reqTypeCode,
            });
          }
        }
      }
    },
  },
};
</script>
