<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="page-title-box">
      <h1 class="page-title">교육/세미나 보고서</h1>
      <ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
        <li class="breadcrumb">홈</li>
        <li class="breadcrumb">게시판</li>
        <li class="breadcrumb">교육/세미나 보고서</li>
      </ol>
    </div>
    <div class="search-input-box">
      <div class="search-input-row">
        <div class="search-item w100">
          <label for="eduTxt">교육 강좌명</label>
          <div class="search-value">
            <InputBox
              id="eduTxt"
              type="text"
              inputWidth="100%"
              :value.sync="searchData.eduTxt"
              :targetMethod="search"
            />
          </div>
        </div>
      </div>
      <div class="search-input-row">
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="regDateFrom">교육 기간</label>
          <div class="search-value date-range-box">
            <DatePicker ref="fromDate" />
            <span class="tilde-text">~</span>
            <DatePicker ref="toDate" />
          </div>
        </div>
        <div class="search-item w100" v-if="!this.$store.state.isMobile">
          <label for="eduAgency">교육 기관</label>
          <div class="search-value">
            <InputBox
              id="eduAgency"
              type="text"
              inputWidth="100%"
              :value.sync="searchData.eduAgency"
              :targetMethod="search"
            />
          </div>
        </div>
        <div class="search-item confirm-box">
          <button type="button" class="btn" @click="resetSearch">초기화</button>
          <button type="button" class="btn btn-primary" @click="search">
            검색
          </button>
        </div>
      </div>
    </div>
    <EduReportList
      ref="eduReportList"
      :gridHeader="gridHeader"
      :eduReportList="eduReportList"
      :openDetailPop="openDetailPop"
      :onTitleGridClick="callEduReportDetail"
      :openInsertPop="openInsertPop"
    />
    <EduReportDetailPop
      ref="detailPop"
      :eduReportDetail="eduReportDetail"
      :openUpdatePop="openUpdatePop"
      :resetDetail="resetDetail"
      :isHtmlText="isHtmlText"
    />
    <EduReportWritePop
      ref="writePop"
      :eduReportDetail="eduReportDetail"
      :resetDetail="resetDetail"
      :search="search"
    />
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";
import InputBox from "@/components/InputBox";
import DatePicker from "@/components/Datepicker";
import ModalPopup from "@/components/ModalPopup";
import EduReportList from "@/views/board/list/EduReportList";
import EduReportDetailPop from "@/views/board/pop/EduReportDetailPop";
import EduReportWritePop from "@/views/board/pop/EduReportWritePop";
import { Empty } from "@/mixins/Empty";
import { DateFunc } from "@/mixins/DateFunc";
import { Comm } from "@/mixins/Comm";
import Loading from "vue-loading-overlay";

export default {
  name: "EduReport",
  mixins: [Empty, DateFunc, Comm],
  components: {
    Grid,
    vSelect,
    InputBox,
    DatePicker,
    ModalPopup,
    EduReportList,
    EduReportDetailPop,
    EduReportWritePop,
    Loading,
  },
  data() {
    return {
      gridHeader: [
        {
          header: "게시글번호",
          name: "eduNo",
          width: 0,
          align: "center",
          hidden: true,
        },
        {
          header: "교육 강좌명",
          name: "eduTxt",
          formatter(data) {
            return `
                <div class="board-title-box">
                  <a class="board-title clickable-text">${data.value}</a>
                  ${
                    Math.floor(
                      (new Date() - new Date(data.row.regDt)) /
                        (1000 * 60 * 60 * 24)
                    ) <= 5
                      ? '<span class="ico-new">새 게시물</span>'
                      : ""
                  }
                </div>
              `;
          },
          sortable: true,
        },
        {
          header: "참석자",
          name: "eduAttendeeNm",
          width: 130,
          align: "center",
          sortable: true,
        },
        {
          header: "교육 기관",
          name: "eduAgency",
          width: 130,
          align: "center",
          sortable: true,
        },
        {
          header: "교육 장소",
          name: "eduAddr",
          width: 130,
          align: "center",
          sortable: true,
        },
        {
          header: "교육 기간",
          name: "eduDt",
          width: 200,
          align: "center",
          sortable: true,
        },
        {
          header: "작성자",
          name: "regEmpNm",
          width: 80,
          align: "center",
          sortable: true,
        },
        {
          header: "등록일",
          name: "regDt",
          width: 100,
          align: "center",
          sortable: true,
        },
      ],
      resourceType: {
        options: [],
      },
      searchData: {
        eduTxt: "",
        eduAgency: "",
      },
      eduReportList: [],
      eduReportDetail: {},

      isLoading: false,
      fullPage: true,
    };
  },
  computed: {
    mobileHidden() {
      return this.$store.getters.isMobile;
    },
    isHtmlText() {
      return (
        this.formatDate(this.eduReportDetail.regDt, "") <
        this.getUsedMarkdownFromDt()
      );
    },
  },
  watch: {
    mobileHidden(val, oldVal) {},
  },
  created() {
    this.callEduReportList();
  },
  methods: {
    ...mapGetters(["getUsedMarkdownFromDt"]),
    // 상세 팝업
    openDetailPop() {
      this.$refs.detailPop.openPop();
    },
    // 보고서 작성 팝업
    openInsertPop() {
      this.$refs.writePop.openPop();
    },
    // 보고서 수정 팝업
    openUpdatePop() {
      this.$refs.detailPop.closePop();
      this.$refs.detailPop.setToUpdatePop(true);
      this.$refs.writePop.openUpdatePop();
    },
    // 검색
    search() {
      const param = {
        eduTxt: this.searchData.eduTxt,
        eduAgency: this.searchData.eduAgency,
      };

      const fromDate = this.$refs.fromDate.instance.getDate();
      const toDate = this.$refs.toDate.instance.getDate();
      if (!this.isEmpty(fromDate)) {
        param.eduStartDt = this.formatDate(fromDate, "-");
      }
      if (!this.isEmpty(toDate)) {
        param.eduEndDt = this.formatDate(toDate, "-");
      }

      this.callEduReportList(param);
    },
    // 교육/세미나 보고서 목록 API 조회
    async callEduReportList(params) {
      this.isLoading = true;

      if (params === void 0) params = {};

      const apiUrl = "/api/board/get-edu-report-list";
      const data = await this.$http
        .post(apiUrl, params)
        .then((solve) => {
          if (solve.data.result) {
            this.eduReportList = solve.data.data;
          } else {
            throw new Error("게시물 목록을 가져오는데 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    // 상세 데이터 조회
    async callEduReportDetail(eventRow) {
      this.isLoading = true;

      const apiUrl = "/api/board/get-edu-report-detail/" + eventRow.eduNo;
      const data = await this.$http
        .get(apiUrl)
        .then((solve) => {
          if (solve.data.statusCode === 200) {
            this.eduReportDetail = solve.data.data;

            const date = this.formatDate(solve.data.data.regDt, "");

            if (date < this.getUsedMarkdownFromDt()) {
              this.eduReportDetail.eduContents = this.replaceLucyFilter(
                solve.data.data.eduContents
              );
              this.eduReportDetail.eduApplication = this.replaceLucyFilter(
                solve.data.data.eduApplication
              );
              this.eduReportDetail.eduOpinion = this.replaceLucyFilter(
                solve.data.data.eduOpinion
              );
            }
          } else {
            throw new Error("게시물 목록을 가져오는데 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });

      this.openDetailPop();
    },
    // 검색조건 초기화
    resetSearch() {
      this.searchData.eduTxt = "";
      this.searchData.eduAgency = "";
      this.searchData.uploadfile1 = "";
      this.$refs.fromDate.instance.setDate(null);
      this.$refs.toDate.instance.setDate(null);
    },
    // 상세정보 초기화
    resetDetail() {
      this.$refs.detailPop.setToUpdatePop(false);
      this.eduReportDetail = {
        eduNo: "",
        eduTxt: "",
        eduAgency: "",
        eduAddr: "",
        eduStartDt: "",
        eduEndDt: "",
        eduContents: " ",
        eduApplication: " ",
        eduOpinion: " ",
      };
    },
  },
};
</script>
