<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="true" />
    <p class="p-title">프로젝트팀 관리</p>
        <div>
      <div class="page-title-box">
        <h1 class="page-title"></h1>
        <ol class="breadcrumb-list">
          <li class="breadcrumb">홈</li>
          <li class="breadcrumb">인트라넷 관리</li>
          <li class="breadcrumb">프로젝트팀 관리</li>
        </ol>
      </div>
    </div>
    <div class="search-input-box" v-if="!this.$store.state.isMobile">
      <div class="search-input-row">
        <div class="search-item">
          <label for="empNm">프로젝트명</label>
          <div class="search-value">
            <input-box type="text" :value.sync="searchParam.projectNm" />
          </div>
        </div>
        <div class="search-item">
          <label for="confirmYn">상태</label>
          <div class="search-value">
            <v-select v-model="searchParam.state" :reduce="label => label.value" :options="options.stateList"
              :clearable="false" :searchable="false"></v-select>
          </div>
        </div>
        <div class="search-item confirm-box">
          <button type="button" @click.passive.stop="popUpRequest" class="btn">등록</button>
          <button type="button" @click.passive.stop="search" class="btn btn-primary">검색</button>
        </div>
      </div>        
    </div>
    <div class="grid-box">
      <div class="grid-tool-box"></div>
      <grid class="tui-grid" ref="tuiGrid" :data="gridProps.data" :columns="gridProps.columns" :options="gridProps.options"
        @click="onClick"/>
      <div class="per-page-box">
        <span class="total-count-text">
          총
          <span class="count">{{ totalCnt }}</span>개</span>
        <div class="select-box">
          <v-select v-model="perPage" :options="options.pageList" :clearable="false"
            :searchable="false" @input="onPerPageInput"></v-select>
        </div>
      </div>
    </div>
    <ProjectTeamDetailPop ref="detailPop" @closedPopup="closedPopup"/>
  </div>
</template>

<script>
import "tui-grid/dist/tui-grid.css";
import "vue-select/dist/vue-select.css";
import { mapState } from "vuex";
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";
import Loading from "vue-loading-overlay";
import { Check } from "@/mixins/Check";
import { Grids } from "@/mixins/Grids";
import { Comm } from "@/mixins/Comm";
import { Empty } from "@/mixins/Empty";
import InputBox from "@/components/InputBox";
import DatePicker from "@/components/Datepicker";
import ProjectTeamDetailPop from "@/views/system/pop/ProjectTeamDetailPop";

import TuiGrid from "tui-grid";
TuiGrid.setLanguage("ko");

export default {
  name: "ProjectTeamManage",
  mixins: [Check, Grids, Comm, Empty],
  components: {
    grid: Grid,
    vSelect,
    InputBox,
    DatePicker,
    loading: Loading,
    ProjectTeamDetailPop,
  },
  data() {
    return {
      perPage: 10,
      totalCnt: 0,
      isLoading: false,
      gridProps: {},
      searchParam: { projectNm: "", state: "" },
      options: {
        stateList: [
          { label: "전체", value: "" },
          { label: "진행중", value: "i" },
          { label: "종료", value: "e" },
          { label: "예정", value: "d" },
        ],
        pageList: [ { label: "10", value: 10 }, { label: "20", value: 20 }, { label: "50", value: 50 }, { label: "100", value: 100 }, ],
      }
    };
  },
  computed: {
    ...mapState({
      
    }),
  },
  created() {
    this.gridProps = {
      options: {
        rowHeight: 48,
        scrollX: false,
        scrollY: true,
        bodyHeight: 480,
        showDummyRows: false,
        selectionUnit: "row",
        display: { noData: "No Data" },
        pageOptions: {
          useClient: true,
          perPage: this.perPage,
          visiblePages: 5,
        },
      },
      columns: [
        { header: "projectNo", name: "projectNo", hidden: true },
        { header: "프로젝트팀", name: "projectNm", align: "left",sortable: true, formatter: this.gDetail,},
        { header: "팀관리자", name: "teamManagerNm", align: "center",sortable: true },
        { header: "시작일", name: "startDt", align: "center",sortable: true },
        { header: "종료일", name: "endDt", align: "center",sortable: true },
        { header: "상태", name: "state", align: "center",sortable: true },
      ],
      data: [],
    };
    
    this.search();
  },
  methods: {
    onClick(ev) {
      if( this.isEmpty(ev.rowKey)) return false;
      if (ev.columnName == "projectNm") {
        const rowData = this.$refs.tuiGrid.invoke("getRow", ev.rowKey);
        this.$refs.detailPop.open(rowData.projectNo);
      }
    },
    search() {
      let params = {};
      params.projectNm = this.searchParam.projectNm;
      params.state = this.searchParam.state;

      this.isLoading = true;
      this.$http.post("/api/system/team-manage/getProjectTeamList", params)
          .then((response) => {
            if (response.data.data) {
              this.totalCnt = response.data.data.length;
              let contentsList = response.data.data;
              this.$refs.tuiGrid.invoke('resetData', contentsList, {});
            } 
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data.value);
    },
    popUpRequest() {
      this.$refs.detailPop.open('');
    },
    closedPopup (rtnObj) {
      if( !this.isEmpty(rtnObj)){
        if(rtnObj == 'success'){this.search();}
      }
    },
  },
};
</script>
<style scoped lang="scss">
.p-title {
  margin-top: 25px;
  font-size: 24px;
  font-weight: 700;

  ~ div {
    margin-top: 15px;
  }
}
</style>
