<template>
  <div class="contents-wrap">
    <p class="p-title">{{ menuName }}</p>
    <div>
      <div class="page-title-box">
        <h1 class="page-title"></h1>
        <ol class="breadcrumb-list">
          <li class="breadcrumb">홈</li>
          <li class="breadcrumb">인트라넷 관리</li>
          <li class="breadcrumb">{{ menuName }}</li>
        </ol>
      </div>
    </div>
    <div class="search-input-box" v-if="!this.$store.state.isMobile">
      <div class="search-input-row">
        <div class="search-item">
          <label for="dptNm">부서/팀</label>
          <div class="search-value">
            <v-select
              v-model="searchParam.dptNo"
              :reduce="(no) => no.value"
              :options="options[10000]"
              :clearable="false"
              :searchable="true"
            >
            </v-select>
          </div>
        </div>
        <div class="search-item">
          <label for="empNm">임직원</label>
          <div class="search-value">
            <input-box
              type="text"
              :value.sync="searchParam.empNm"
              :targetMethod="search"
            />
          </div>
        </div>
      </div>
      <div class="search-input-row">
        <div class="search-item">
          <label for="confirmYn">상태</label>
          <div class="search-value">
            <v-select
              v-model="searchParam.confirmYn"
              :reduce="(yn) => yn.value"
              :options="options.confirmYnList"
              :clearable="false"
              :searchable="true"
            ></v-select>
          </div>
        </div>
        <div class="search-item">
          <label for="dptNm">신청일</label>
          <div class="search-value">
            <div class="search-value date-range-box">
              <date-picker ref="startDt" :date.sync="searchParam.startDt" />
              <span class="tilde-text">~</span>
              <date-picker ref="endDt" :date.sync="searchParam.endDt" />
            </div>
          </div>
        </div>
        <div class="search-item confirm-box">
          <button
            type="button"
            @click.passive.stop="resetSearchForm"
            class="btn"
          >
            초기화
          </button>
          <button
            type="button"
            @click.passive.stop="search"
            class="btn btn-primary"
          >
            검색
          </button>
        </div>
      </div>
    </div>
    <div class="grid-box">
      <div class="grid-tool-box">
        <div>
          <button type="button" class="btn btn-primary" @click="popUpRequest">
            경조금 신청
          </button>
          <button type="button" class="btn" @click="popUpCancel">
            취소 신청
          </button>
        </div>
        <div>
          <button type="button" class="btn btn-primary" @click="approve">
            승인
          </button>
          <button type="button" class="btn" @click="reject">반려</button>
          <button type="button" class="btn" @click="searchToday">
            금일 신청자
          </button>
        </div>
      </div>
      <grid
        class="tui-grid"
        ref="tuiGrid"
        :data="gridProps.data"
        :columns="gridProps.columns"
        :options="gridProps.options"
        @click="onClick"
      />
      <div class="per-page-box" v-if="!this.$store.state.isMobile">
        <span class="total-count-text">
          총
          <span class="count">{{ totalCnt }}</span
          >개
        </span>
        <div class="select-box">
          <v-select
            v-model="perPage"
            :reduce="(opt) => opt.value"
            :options="options.pageList"
            :clearable="false"
            :searchable="false"
            @input="onPerPageInput"
          ></v-select>
        </div>
      </div>
    </div>
    <family-event-pop
      ref="requestPop"
      :isDetail="false"
      :descs="pop.descs"
      :upcodes="pop.upcodes"
      :empNoList="pop.empNoList"
      @created="search"
    ></family-event-pop>
    <family-event-pop
      ref="detailPop"
      :isDetail="true"
      :descs="pop.descs"
      :upcodes="pop.upcodes"
      @modified="search"
    ></family-event-pop>
    <family-event-cancel-pop
      ref="cancelPop"
      @canceled="search"
    ></family-event-cancel-pop>
    <loading
      :active.sync="isLoading"
      :on-cancel="loadingCancel"
      :is-full-page="fullPage"
    ></loading>
  </div>
</template>

<script>
import "tui-grid/dist/tui-grid.css";
import "vue-select/dist/vue-select.css";

import { mapState } from "vuex";
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";
import { Check } from "@/mixins/Check";
import { Grids } from "@/mixins/Grids";
import { DateFunc } from "@/mixins/DateFunc";
import { Comm } from "@/mixins/Comm";
import InputBox from "@/components/InputBox";
import DatePicker from "@/components/Datepicker";
import { CheckboxRenderer } from "@/components/renderer/GridRenderer";
import FamilyEventPop from "@/views/system/pop/FamilyEventManagePop";
import FamilyEventCancelPop from "@/views/system/pop/FamilyEventManageCancelPop";
import Loading from "vue-loading-overlay";

import TuiGrid from "tui-grid";
TuiGrid.setLanguage("ko");

export default {
  name: "FamilyEventManage",
  mixins: [Check, DateFunc, Grids, Comm],
  props: ["menuName", "upcodes"],
  components: {
    grid: Grid,
    vSelect,
    InputBox,
    DatePicker,
    FamilyEventPop,
    FamilyEventCancelPop,
    loading: Loading,
  },
  data() {
    return {
      perPage: 10,
      totalCnt: 0,
      isLoading: false,
      fullPage: true,
      gridProps: {},
      searchParam: {
        dptNo: null,
        confirmYn: "",
        empNm: "",
        startDt: "",
        endDt: "",
      },
      options: {
        "10000": [],
        confirmYnList: [
          { label: "전체", value: "" },
          { label: "경조금 승인", value: "Y" },
          { label: "경조금 신청", value: "N" },
          { label: "취소신청", value: "CR" },
          { label: "취소승인", value: "CA" },
          { label: "반려", value: "RJ" },
        ],
        pageList: [
          { label: "10", value: 10 },
          { label: "20", value: 20 },
          { label: "50", value: 50 },
          { label: "100", value: 100 },
        ],
      },
      pop: {
        upcodes: ["11500", "11600"],
        descs: [],
        empNoList: [],
      },
    };
  },
  computed: {
    ...mapState({
      codeInfo: (state) => state.codeInfo,
    }),
  },
  created() {
    this.token();
    this.formatterInstall();
    this.initDates();
    for (let upcode of this.upcodes) {
      this.options[upcode] = this.commSelect(upcode, "전체");
    }

    this.gridProps = {
      options: {
        rowHeight: 48,
        rowHeaders: [
          {
            type: "checkbox",
            header: `
							<span class="ui-chk">
								<input id="chkAll" type="checkbox" name="_checked"><label for="chkAll"></label>
							</span>`,
            renderer: {
              type: CheckboxRenderer,
            },
          },
        ],
        scrollX: false,
        scrollY: true,
        bodyHeight: this.$store.state.isMobile ? "auto" : 480,
        showDummyRows: false,
        selectionUnit: "row",
        display: { noData: "No Data" },
        pageOptions: {
          useClient: true,
          perPage: this.perPage,
          visiblePages: 5,
        },
      },
      columns: [
        { header: "순번", name: "famEvnNo", hidden: true },
        { header: "확정상태", name: "confirmYn", hidden: true },
        { header: "신청일", name: "requestDt", align: "center" },
        { header: "이름", name: "empNm", align: "center" },
        { header: "소속", name: "dptNm", align: "center" },
        { header: "경조일자", name: "famEvnDt", align: "center" },
        {
          header: "경조구분",
          name: "famEvnNm",
          align: "center",
          formatter: this.gDetail,
        },
        { header: "확정여부", name: "confirmState", align: "center" },
        { header: "확정일", name: "approveDt", align: "center" },
        { header: "확정자", name: "approveNm", align: "center" },
        {
          header: "PDF 다운",
          name: "pdfDownload",
          align: "center",
          formatter: this.gLink,
        },
      ],
      data: [],
    };

    this.$http
      .get("/api/system/family-event-manage/pop", {})
      .then((response) => {
        if (response.data) {
          this.pop.descs = response.data.data.descs;
          this.pop.empNoList = response.data.data.empNoList;
        }
      })
      .catch((e) => {
        console.error(e);
      });

    this.search();
  },
  methods: {
    initDates() {
      let today = new Date();
      let startDate = new Date(today.getFullYear(), today.getMonth(), 1);
      let endDate = new Date(
        new Date(today.getFullYear(), today.getMonth() + 1, 1) -
          60 * 24 * 24 * 1000
      );

      this.searchParam.startDt = startDate;
      this.searchParam.endDt = endDate;

      this.$nextTick(() => {
        this.$refs.startDt.setDate(startDate);
        this.$refs.endDt.setDate(endDate);
      });
    },
    resetSearchForm() {
      Object.assign(this.$data.searchParam, this.$options.data().searchParam);
      this.initDates();
    },
    onClick(ev) {
      console.log(ev);
      if (ev.columnName == "famEvnNm") {
        const rowData = this.$refs.tuiGrid.invoke("getRow", ev.rowKey);

        this.$http
          .post("/api/system/family-event-manage/detail/" + rowData.famEvnNo)
          .then((response) => {
            if (response.data.data) {
              this.$refs.detailPop.openDetail(response.data.data);
            }
          })
          .catch((e) => {
            console.error(e);
          });
      } else if (ev.columnName == "pdfDownload") {
        const rowData = this.$refs.tuiGrid.invoke("getRow", ev.rowKey);
        this.downloadPdf(rowData);
      }
    },
    searchToday(ev) {
      this.search(ev, true);
    },
    search(ev, today) {
      let params = {
        dptNo: this.searchParam.dptNo,
        empNm: this.searchParam.empNm,
        confirmYn: this.searchParam.confirmYn,
        startDt: this.searchParam.startDt.format("yyyyMMdd"),
        endDt: this.searchParam.endDt.format("yyyyMMdd"),
      };
      if (today) {
        params.today = true;
      }

      this.isLoading = true;
      this.$http
        .post("/api/system/family-event-manage/list", params)
        .then((response) => {
          if (response.data.data) {
            this.totalCnt = response.data.data.contents.length;
            this.$refs.tuiGrid.invoke(
              "resetData",
              response.data.data.contents,
              {}
            );
          } else {
            console.log("no data");
          }
        })
        .catch((e) => {
          console.error(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data);
    },
    loadingCancel() {},
    popUpRequest() {
      this.$refs.requestPop.open();
    },
    popUpCancel() {
      this.$refs.cancelPop.open();
    },
    approve() {
      let checked = this.$refs.tuiGrid.invoke("getCheckedRows");
      if (!checked || checked.length == 0) {
        alert("선택된 항목이 없습니다.");
        return false;
      }
      this.isLoading = true;
      this.$http
        .post("/api/system/family-event-manage/approve", checked)
        .then((response) => {
          if (response.data.data) {
            alert(response.data.data.msg);
            this.search();
          } else {
            console.log("no data");
          }
        })
        .catch((e) => {
          console.error(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
      console.log(checked);
    },
    reject() {
      let checked = this.$refs.tuiGrid.invoke("getCheckedRows");
      if (!checked || checked.length == 0) {
        alert("선택된 항목이 없습니다.");
        return false;
      }
      this.isLoading = true;
      this.$http
        .post("/api/system/family-event-manage/reject", checked)
        .then((response) => {
          if (response.data.data) {
            alert(response.data.data.msg);
            this.search();
          } else {
            console.log("no data");
          }
        })
        .catch((e) => {
          console.error(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
      console.log(checked);
    },
    downloadPdf(rowData) {
      this.isLoading = true;
      let param = {
        famEvnNo: rowData.famEvnNo,
        requestDt: rowData.requestDt,
        empNm: rowData.empNm,
      };
      const profileDownload = this.$http
        .post("/api/utils/report/getFamReport", param, {
          responseType: "arraybuffer",
        })
        .then((solve) => {
          if (solve.status === 200) {
            let fileName =
              rowData.empNm +
              "_경조금신청서_" +
              solve.headers.requestdt +
              ".pdf";
            let blob = new Blob([solve.data], {
              type: solve.headers["content-type"],
            });
            let link = document.createElement("a");
            link.href = window.URL.createObjectURL(blob);
            link.target = "_self";
            link.download = fileName;
            link.click();
          } else {
            alert(2);
            throw new Error("경조금신청서 다운로드를 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
          throw new Error("경조금신청서 다운로드를 실패하였습니다.");
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
  },
};
</script>
<style scoped lang="scss">
.p-title {
  margin-top: 25px;
  font-size: 24px;
  font-weight: 700;

  ~ div {
    margin-top: 15px;
  }
}
</style>
