<template>
  <ModalPopup :title="'MenuAuthPop'" :visible.sync="modalVisible" width="772px">
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="grid-box">
      <div class="grid-tool-box">
        <div class="left-box">
          <button type="button" class="btn btn-control" @click.prevent.stop="addMenu" >추가</button>
        </div>
      </div>
      <grid class="tui-grid" ref="tuiGrid" :data="gridProps.data" :columns="gridProps.columns" :options="gridProps.options" />
    </div>
    <div class="modal-popup-btn-box">
      <button type="button" class="btn" @click.prevent.stop="closed">취소</button>
      <button type="button" class="btn btn-primary" @click.passive.stop="addMenu" >추가</button>
    </div>
  </ModalPopup>
</template>

<script>
import { mapState, mapActions } from "vuex";
import "tui-grid/dist/tui-grid.css";
import { Grid } from "@toast-ui/vue-grid";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import ModalPopup from "@/components/ModalPopup";
import InputBox from '@/components/InputBox';
import {CheckboxRenderer} from '@/components/renderer/GridRenderer';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import TuiGrid from 'tui-grid';
TuiGrid.setLanguage('ko');

export default {
  name: "MenuAuthPop",
  mixins: [Check,Empty],
  components: {
    grid: Grid,
    ModalPopup,
    Loading,
    InputBox
  },
  props:{
  },
  data() {
    return {
      modalVisible : false,
      menuId: '',
      isLoading: false,
      fullPage: true,
      gridProps : {
        options: {
          rowHeight: 48,
          rowHeaders: [{
						type: 'checkbox',
						header: `
							<span class="ui-chk">
								<input id="chkAll" type="checkbox" name="_checked"><label for="chkAll"></label>
							</span>`,
						renderer: {
							type: CheckboxRenderer,
						},
					}],
          scrollX: false,
          scrollY: true,
          bodyHeight: 480,
					showDummyRows: false,
					selectionUnit: 'row',
          display: { noData: 'No Data' },
				},
        columns: [
          {header: "그룹ID", name: "groupauthId", align: "center"},
          {header: "그룹명", name: "groupauthNm", align: "center"},
          {header: "설명", name: "commentTxt", align: "center"},
          {header: "menuId",name: "menuId", hidden: true,},
          {header: "empty",name: "empty", hidden: true,}
        ],
        data: []
      }
    };
  },
  computed: {
  },
  created() {
  },
  mounted() {
  },
  methods: {
    open(id) {
      this.menuId = id;
      this.search();
      this.modalVisible = true;
    },
    closed (obj) {
      this.modalVisible = false;
      this.resetInfo();
      //부모 함수 콜백
      this.$emit('closedMenuAuthPop', obj);
    },
    search(){
      let apiUrl = "/api/system/menu/getSearchGroup";
      let searchparams = {};
      searchparams.menuId = this.menuId;

      this.isLoading = true;
      this.$http.post(apiUrl, searchparams)
          .then((response) => {
            this.isLoading = false;
            if (response.data.data) {
              this.$refs.tuiGrid.invoke('resetData', response.data.data, {});
            }
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    addMenu(){
      const gridRows = this.$refs.tuiGrid.invoke("getCheckedRows");
      if( this.isEmpty(gridRows)){
        alert("선택항목이 없습니다.");
        return false;
      }
      
      let params = {};
      params.authList = gridRows;
      this.isLoading = true;
      this.$http.post("/api/auth/insertGridGroupMenu", params)
          .then((response) => {
            if(response.data.statusCode != 200){
              alert(response.data.message);
              return false;
            }else{
              alert(response.data.message);
              this.closed('success');
            }
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    resetInfo(){
      this.menuId = '';
    },
  },
};
</script>