<template>
	<div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="true"/>
		<div class="page-title-box">
			<h1 class="page-title">공통코드관리</h1>
			<ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
				<li class="breadcrumb">홈</li>
				<li class="breadcrumb">시스템</li>
				<li class="breadcrumb">공통코드관리</li>
			</ol>
		</div>
		<div class="code-manage-box">
			<div class="code-fraction-box">
				<p class="code-fraction-title" @click="reSetCurrentCode">공통코드</p>
				<ul class="code-fraction-list" ref="codeFractionList">
					<li v-for="item in this.codeFractionListData" class="code-fraction-item">
						<button type="button" class="btn-code-fraction" :class="{'selected': item.code === currentCode}" :data-code="item.code" @click="onCodeFractionClick">{{ item.label }}</button>
					</li>
				</ul>
			</div>
			<div class="code-fraction-sub-box">
				<div class="tbl-tool-box">
					<div>
						<p class="tbl-info-text">* 표시는 필수 입력사항 입니다.</p>
					</div>
					<div>
						<button type="button" class="btn btn-primary" @click.passive.stop="updateDetail">수정</button>
					</div>
				</div>
				<div class="tbl-row write-mode">
					<table>
						<colgroup>
							<col style="width: 180px;">
							<col>
							<col style="width: 180px;">
							<col>
						</colgroup>
						<tbody>
							<tr>
								<th scope="row" class="requried">상위코드명</th>
								<td>
									<InputBox type="text" :value.sync="detail.upcodeNm" disabled="true" />
								</td>
								<th scope="row" class="requried">상위코드ID</th>
								<td>
									<InputBox type="text" :value.sync="detail.upcodeCd" disabled="true" />
								</td>
							</tr>
							<tr>
								<th scope="row" class="requried">코드명</th>
								<td>
									<InputBox type="text" v-model="detail.codeNm" :value.sync="detail.codeNm" />
								</td>
								<th scope="row" >코드ID</th>
								<td>
									<InputBox type="text" v-model="detail.codeCd" :value.sync="detail.codeCd" disabled="true" />
								</td>
							</tr>
							<tr>
								<th scope="row">사용여부</th>
								<td>
									<vSelect id="companyPos" v-model="detail.useYn" :reduce="label => label.value" :options="useYnList.options" :clearable="false" :searchable="false"></vSelect>
								</td>
								<th scope="row">정렬순서</th>
								<td>
									<InputBox type="text" v-model="detail.sortSeq" :value.sync="detail.sortSeq" />
								</td>
							</tr>
							<tr>
								<th scope="row">임시필드1</th>
								<td>
									<InputBox type="text" v-model="detail.tmpfield1Txt" :value.sync="detail.tmpfield1Txt" />
								</td>
								<th scope="row">임시필드2</th>
								<td>
									<InputBox type="text" :v-model="detail.tmpfield2Txt" :value.sync="detail.tmpfield2Txt" />
								</td>
							</tr>
							<tr>
								<th scope="row">임시필드3</th>
								<td>
									<InputBox type="text" v-model="detail.tmpfield3Txt" :value.sync="detail.tmpfield3Txt" />
								</td>
								<th scope="row">내부연동코드</th>
								<td>
									<InputBox type="text" v-model="detail.insideTxt" :value.sync="detail.insideTxt" />
								</td>
							</tr>
							<tr>
								<th scope="row">비고</th>
								<td colspan="3">
									<div class="text-area-box">
										<textarea rows="3" v-model="detail.commentTxt"></textarea>
									</div>
								</td>
							</tr>
						</tbody>
					</table>
				</div>

        <div class="grid-box">
          <div class="grid-tool-box">
            <div>
              <button type="button" class="btn btn-control" @click.passive.stop="updateGrid">수정</button>
            </div>
            <div>
              <button type="button" class="btn btn-primary" @click.passive.stop="handleCodeDetail('reg')" >등록</button>
            </div>
          </div>
          <grid class="tui-grid" ref="tuiGrid" :options="gridProps.options" :data="gridProps.data" :columns="gridProps.columns" 
                @click ="onClick" @editingFinish="onGridEditingFinish"/>
        </div>
        <CodeDetailPop ref="detailPop" :upcodeCd="currentCode" :upcodeNm="currentCodeNm" @closedDetailPopup="closedDetailPopup"/>
			</div>
		</div>
	</div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import "tui-grid/dist/tui-grid.css";
import { Grid } from "@toast-ui/vue-grid";
import 'vue-select/dist/vue-select.css';
import vSelect from 'vue-select'
import Loading from "vue-loading-overlay";
import InputBox from '@/components/InputBox';
import { Grids } from "@/mixins/Grids";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import {CheckboxRenderer} from '@/components/renderer/GridRenderer';
import CodeDetailPop from "@/views/system/pop/CodeDetailPop";

export default {
  name: "Code",
  mixins: [Check,Empty,Grids],
  components: {
    vSelect,
    InputBox,
    grid: Grid,
    loading: Loading,
    CodeDetailPop
  },
  data() {
    return {
      codeFractionListData: [],
      currentCode: '00000',
      currentCodeNm: '공통코드',
      isLoading: false,
      useYnList: {options:[{label: '선택하세요', value: '' },{label: '사용', value: 'Y' },{label: '미사용', value: 'N'}]},
      detail: { upcodeNm: '', upcodeCd: '', codeNm: '', codeCd: '', useYn: '', sortSeq: '',
                tmpfield1Txt: '', tmpfield2Txt: '', tmpfield3Txt: '', insideTxt: '', commentTxt: ''},
      gridProps : {
        options: {
          rowHeight: 48,
          rowHeaders: [{
						type: 'checkbox',
						header: `
							<span class="ui-chk">
								<input id="chkAll" type="checkbox" name="_checked"><label for="chkAll"></label>
							</span>`,
						renderer: {
							type: CheckboxRenderer,
						},
					}],
          scrollX: false,
          scrollY: true,
          bodyHeight: 480,
					showDummyRows: false,
					selectionUnit: 'row',
          display: { noData: 'No Data' },
				},
        columns: [
          {header: "코드", name: "codeCd", formatter: this.gDetail, align: "center"},
          {header: "코드명", name: "codeNm", align: "left"},
          {header: "정렬순서", name: "sortSeq", align: "center",
           validation: {dataType: 'number'},
           defaultValue: 99,
           onBeforeChange(ev) {
             if(Number(ev.nextValue)){
               ev.value = Number(ev.nextValue);
               return ev;
             } else {
               ev.stop();
             }
           },
           editor: { type: 'text'}
          },
          {header: "사용여부",name: "useYn",align: "center",
           formatter: 'listItemText',
           editor: {
              type:'select',
              options: {
                listItems: [
                  { text: '사용', value: 'Y' },
                  { text: '미사용', value: 'N' }
                ]
              }
            }
          },
        ],
        data: []
      }
    }
  },
  created() {
    this.token();
    this.search();
    this.getCodeInfo(this.currentCode);
    this.getGridData(this.currentCode);
  },
  computed: {
      ...mapState({
        main: (state) => state.main,
      }),
    },
  methods: {
    search(){
      let searchparms = {codeCd:'00000'};
      this.isLoading = true;
      this.$http.post("/api/code/getSubCodeListByLabel", searchparms)
          .then((response) => {
            if(response.data.statusCode != 200){
                alert('오류가 발생했습니다.');
                return false;
            }
            this.codeFractionListData= response.data.data.contents;
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    onCodeFractionClick(event) {
      if( this.isNotEmpty()){
        this.$refs.codeFractionList.querySelector('.btn-code-fraction.selected').classList.remove('selected');
      }
      
      this.currentCode = event.target.dataset.code;
      event.currentTarget.classList.add('selected');
      this.getCodeInfo(this.currentCode);
      this.getGridData(this.currentCode);
		},
    getCodeInfo(codeCd){
      let searchparms = {};
      searchparms.codeCd = codeCd;
      this.isLoading = true;
      this.$http.post("/api/code/getCodeInfo",searchparms)
          .then((response) => {
            if(response.data.statusCode != 200){
                alert('오류가 발생했습니다.');
                return false;
            }
            let rtnObj = response.data.data[0];
            this.detail = rtnObj;
            this.currentCodeNm = rtnObj.codeNm;
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    getGridData(){
      let searchparms = {codeCd:this.currentCode};
      this.isLoading = true;
      this.$http.post("/api/code/getSubCodeList",searchparms)
          .then((response) => {
            if(response.data.statusCode != 200){
                alert('오류가 발생했습니다.');
                return false;
            }
            this.$refs.tuiGrid.invoke('resetData', response.data.data.contents, {});
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    updateDetail(){
      if(!this.validation()){return false;}
      this.isLoading = true;
      this.$http.post("/api/code/systemCodeModifyEnd", this.detail)
          .then((response) => {
            if(response.data.statusCode != 200){
                alert('오류가 발생했습니다.');
                return false;
            }else{
              alert(response.data.message);
              this.search();
            }
          })
          .catch((e) => {
            console.log(e);
            this.isLoading = false;
          })
          .finally(() => {
            //this.isLoading = false;
          });
    },
    validation(){
      if( this.isEmpty(this.detail.codeCd) ){
        alert("선택한 코드가 없습니다.");
        return false;
      }

      if( this.isEmpty(this.detail.codeNm) ){
        alert("코드명은 필수값입니다.");
        return false;
      }

      let bytes = this.maxByteCheck(this.detail.codeNm);
      if( bytes > 50 ){
        this.detail.codeNm = this.detail.codeNm.substring(0, 100);
        alert("코드명은 100 바이트 이하로 입력하세요.");
        return false;
      } 

      if( this.isEmpty(this.detail.useYn) ){
        alert('사용여부를 선택해 주세요!!!');
        return false;
      }

      bytes = this.maxByteCheck(this.detail.tmpfield1Txt);
      if( bytes > 20 ){
        alert("임시필드1은 20 바이트 이하로 입력해 주세요.");
        return false;
      }

      bytes = this.maxByteCheck(this.detail.tmpfield2Txt);
      if( bytes > 20 ){
        alert("임시필드2는 20 바이트 이하로 입력해 주세요.");
        return false;
      }

      bytes = this.maxByteCheck(this.detail.tmpfield3Txt);
      if( bytes > 20 ){
        alert("임시필드3는 20 바이트 이하로 입력해 주세요");
        return false;
      }

      bytes = this.maxByteCheck(this.detail.insideTxt);
      if( bytes > 20 ){
        alert("내부연동코드는 20 바이트 이하로 입력해 주세요");
        return false;
      }

      bytes = this.maxByteCheck(this.detail.commentTxt);
      if( bytes > 4000 ){
        alert("비고는 4000 바이트 이하로 입력해 주세요");
        this.detail.commentTxt = this.detail.commentTxt.substring(0, 4000);
        return false;
      }

      return true;
    },
    updateGrid(){
      const gridRows = this.$refs.tuiGrid.invoke("getCheckedRows");
      if( this.isEmpty(gridRows)){
        alert("선택항목이 없습니다.");
        return false;
      }

      let params = {};
      params.codeList = gridRows;
      this.isLoading = true;
      this.$http.post("/api/code/updateSystemCodeGrid", params)
          .then((response) => {
            if(response.data.statusCode != 200){
              alert(response.data.message);
              return false;
            }else{
              alert(response.data.message);
              this.search();
            }
          })
          .catch((e) => {
            console.log(e);
            this.isLoading = false;
          })
          .finally(() => {
            //this.isLoading = false;
          });
    },
    onClick(ev) {
      if( ev.columnName == "codeCd" ){
        const rowData = this.$refs.tuiGrid.invoke("getRow",ev.rowKey);
        this.handleCodeDetail(rowData.codeCd);
      }
    },
    onGridEditingFinish(ev){
      this.$refs.tuiGrid.invoke("check", ev.rowKey);
    },
    handleCodeDetail(inParam) {
      let codeCd = '';
      if(inParam != 'reg'){
        codeCd = inParam;
      }
      this.$refs.detailPop.open(codeCd);
    },
    closedDetailPopup (rtnObj) {
      if( !this.isEmpty(rtnObj)){
        if(rtnObj == 'success'){this.getGridData();}
      }
    },
    reSetCurrentCode(){
      this.currentCode = '00000';
      this.detail.upcodeNm= '';
      this.detail.upcodeCd= '';
      this.detail.codeNm= '';
      this.detail.codeCd= '';
      this.detail.useYn= '';
      this.detail.sortSeq= '';
      this.detail.tmpfield1Txt= '';
      this.detail.tmpfield2Txt= '';
      this.detail.tmpfield3Txt= '';
      this.detail.insideTxt= '';
      this.detail.commentTxt= '';
      this.$refs.tuiGrid.invoke('resetData', [], {});
    }
  }
}
</script>

<style scoped lang="scss">
.p-title {
	margin-top: 25px;
	font-size: 24px;
	font-weight: 700;

	~ div {
		margin-top: 15px;
	}
}
</style>
