<template>
	<div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="true"/>
		<div class="page-title-box">
			<h1 class="page-title">권한메뉴관리</h1>
			<ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
				<li class="breadcrumb">홈</li>
				<li class="breadcrumb">시스템</li>
				<li class="breadcrumb">권한메뉴관리</li>
			</ol>
		</div>
		<div class="code-manage-box">
			<div class="code-fraction-box">
				<p class="code-fraction-title" @click="resetInfo">권한페이지리스트</p>
				<ul class="code-fraction-list" ref="menuFractionList">
					<li v-for="item in this.menuFractionListData" class="code-fraction-item">
						<button type="button" class="btn-code-fraction" :class="{'selected': item.menuId === currentMenuId}" :data-code="item.menuId" :data-label="item.menuNm" @click="onMenuFractionClick">{{ item.menuNm }}</button>
					</li>
				</ul>
			</div>
			<div class="code-fraction-sub-box">
				<div class="grid-box">
          <div class="grid-tool-box">
            <div>
              <button type="button" class="btn btn-control" @click="openDetailPop">등록</button>
              <button type="button" class="btn btn-control" @click.passive.stop="updateMenu">수정</button>
            </div>
          </div>
          <grid class="tui-grid" ref="menuGrid" :data="menuGrid.data" :columns="menuGrid.columns" :options="menuGrid.options"
              @click ="menuGridClick" @editingFinish="onMenuGridEditingFinish" />
        </div>

        <p class="p-title" style="font-size: 13px;">* 위의 그리드에서 [메뉴명]을 선택하시면 메뉴에 해당하는 그룹을 확인할 수 있습니다.</p>

        <div class="grid-box">
          <div class="grid-tool-box">
            <div>
              <button type="button" class="btn btn-control" @click.passive.stop="addMenu">추가</button>
              <button type="button" class="btn btn-control" @click.passive.stop="deleteMenu">삭제</button>
            </div>
          </div>
          <grid class="tui-grid" ref="authGrid" :data="authGrid.data" :columns="authGrid.columns" :options="authGrid.options" />
        </div>

        <MenuAuthPop ref="menuAuthPop"  @closedMenuAuthPop="closedMenuAuthPop" />
        <MenuDetailPop ref="detailPop"  @closedMenuDetailPop="closedMenuDetailPop" />
			</div>
		</div>
	</div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import "tui-grid/dist/tui-grid.css";
import { Grid } from "@toast-ui/vue-grid";
import Loading from "vue-loading-overlay";
import 'vue-select/dist/vue-select.css';
import { Grids } from "@/mixins/Grids";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import {CheckboxRenderer} from '@/components/renderer/GridRenderer';
import MenuAuthPop from '@/views/system/pop/MenuAuthPop';
import MenuDetailPop from '@/views/system/pop/MenuDetailPop';

export default {
  name: "Menu",
  mixins: [Check, Empty, Grids],
  components: {
    grid: Grid,
    loading: Loading,
    MenuAuthPop,
    MenuDetailPop
  },
  data() {
    return {
      menuFractionListData: [],
      currentMenuId: '00000',
      currentMenuIdNm: '최상위',
      authGrid: {},
      menuGrid: {},
      isLoading: false,
    }
  },
  created() {
    this.menuGrid = {
      options: { rowHeight: 48,
                 rowHeaders: [{type: 'checkbox',
                               header: `<span class="ui-chk"><input id="menuChkAll" type="checkbox" name="_checked"><label for="menuChkAll"></label></span>`,
                               renderer: {type: CheckboxRenderer,},}],
                scrollX: false,
                scrollY: true,
                bodyHeight: 240,
                showDummyRows: false,
                selectionUnit: 'row',
                display: { noData: 'No Data' },
      },
      columns: [
        {header: "메뉴ID",      formatter: this.gLink,  name: "menuId", width: 150, minWidth: 100, align: "center", sortable: true},
        {header: "메뉴명",      formatter: this.gDetail,name: "menuNm", width: 200, minWidth: 150, align: "center", sortable: true},
        {header: "프로그램URL", name: "menuurlTxt",   align: "center", sortable: true, width: 300, editor:{ type: 'text'} },
        {header: "정렬순서",    name: "sortSeq",   align: "center", sortable: true,
         validation: {dataType: 'number'},
         defaultValue: 99,
         onBeforeChange(ev) {
           if(Number(ev.nextValue)){
             ev.value = Number(ev.nextValue);
             return ev;
           } else {
             ev.stop();
           }
          },
          editor:{ type: 'text'}
        },
        {header: "사용여부", name: "useYn", align: "center",sortable: true, formatter: 'listItemText',
                  editor: {type:'select', options: { listItems: [{ text: '사용',   value: 'Y' }, { text: '미사용', value: 'N' } ] } }
        },
        {header: "upmenuId",    name: "upmenuId", hidden: true, },
        {header: "upmenuNm",    name: "upmenuNm", hidden: true, }
      ],
      data: [],
    };

    this.authGrid = {
      options: { rowHeight: 48,
                 rowHeaders: [{type: 'checkbox',
                               header: `<span class="ui-chk"><input id="groupChkAll" type="checkbox" name="_checked"><label for="groupChkAll"></label></span>`,
                               renderer: {type: CheckboxRenderer,},}],
                scrollX: false,
                scrollY: true,
                bodyHeight: 240,
                showDummyRows: false,
                selectionUnit: 'row',
                pageOptions: {
                  						useClient: true,
                  						perPage: 5,
                              visiblePages: 5,
                  					},
                display: { noData: 'No Data' },
      },
      columns: [
        {header: "그룹ID",  name: "groupauthId", width: 300, minWidth: 150,align: "center", sortable: true },
        {header: "그룹명",  name: "groupauthNm", width: 300, minWidth: 150,align: "center", sortable: true },
        {header: "설명",    name: "commentTxt", align: "center", sortable: true, },
      ],
      data: [],
    };

    this.search();
    this.menuSearch();
  },
  computed: {
      ...mapState({
        main: (state) => state.main,
      }),
    },
  methods: {
    search(){
      let params = {};
      params.lvl = '1';
      this.isLoading = true;
      this.$http.post("/api/system/menu/getMenuTree", params)
          .then((response) => {
            if(response.data.statusCode != 200){
                alert('오류가 발생했습니다.');
                return false;
            }
            this.menuFractionListData= response.data.data;
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    onMenuFractionClick(event) {
      if( this.isNotEmpty()){
        this.$refs.menuFractionList.querySelector('.btn-code-fraction.selected').classList.remove('selected');
      }
      this.currentMenuId = event.target.dataset.code;
      this.currentMenuIdNm = event.target.dataset.label;
      event.currentTarget.classList.add('selected');
      this.menuSearch();
	  this.$refs.authGrid.invoke('resetData', [], {});

    },
    menuSearch() {
      let params = {};
      params.upmenuId = this.currentMenuId;
      params.lvl = '1';
      this.isLoading = true;
      this.$http.post( "/api/system/menu/getMenuTree", params)
          .then((response) => {
            this.$refs.menuGrid.invoke('resetData', response.data.data )
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    menuGridClick(ev){
      this.selectId = this.$refs.menuGrid.invoke('getValue', ev.rowKey, 'menuId');

      if(ev.columnName === 'menuId') {
        this.authSearch();
      } else if(ev.columnName === 'menuNm') {
        let info = {};
        info.menuId = this.selectId;
        info.type = 'mod';
        this.$refs.detailPop.open(info);
      }
    },
    onMenuGridEditingFinish(ev){
      this.$refs.menuGrid.invoke("check",ev.rowKey);
    },
    authSearch(){
      let params = {};
      params.menuId = this.selectId;
      this.isLoading = true;
      this.$http.post( "/api/system/menu/getAuthList", params)
          .then((response) => {
            this.$refs.authGrid.invoke('resetData', response.data.data)
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    openDetailPop(){
      let info = {};
      info.upmenuId = this.currentMenuId;
      info.upmenuNm = this.currentMenuIdNm;
      info.type = 'reg';
      this.$refs.detailPop.open(info);
    },
    updateMenu(ev){
      const gridRows = this.$refs.groupGrid.invoke("getCheckedRows");
      if( this.isEmpty(gridRows)){
        alert("선택항목이 없습니다.");return false;
      }

      let params = {};
      params.menuList = gridRows;
      this.isLoading = true;
      this.$http.post("/api/system/menu/updateMenuGrid", params)
          .then((response) => {
            this.isLoading = false;
            if(response.data.statusCode != 200){
              alert(response.data.message);
              return false;
            }else{
              alert(response.data.message);
              this.menuSearch();
            }
          })
          .catch((e) => {
            console.log(e);
            this.isLoading = false;
          })
          .finally(() => {
            //this.isLoading = false;
          });
    },
    closedMenuAuthPop(rtnObj) {
      if( !this.isEmpty(rtnObj)){
        this.authSearch();
      }
    },
    closedMenuDetailPop(rtnObj) {
      if( !this.isEmpty(rtnObj)){
        this.resetInfo();
      }
    },
    addMenu() {
      if( this.isEmpty(this.selectId) ){
        alert('메뉴ID를 선택해주세요.');
        return false;
      }
      this.$refs.menuAuthPop.open(this.selectId);
    },
    deleteMenu(){
      const gridRows = this.$refs.authGrid.invoke("getCheckedRows");
      if( this.isEmpty(gridRows)){
        alert("선택항목이 없습니다.");return false;
      }

      let params = {};
      params.menuList = gridRows;
      this.isLoading = true;
      this.$http.post("/api/system/menu/deleteGroupMenu", params)
          .then((response) => {
            this.isLoading = false;
            if(response.data.statusCode != 200){
              alert(response.data.message);
              return false;
            }else{
              alert(response.data.message);
              this.authSearch();
            }
          })
          .catch((e) => {
            console.log(e);
            this.isLoading = false;
          })
          .finally(() => {
            //this.isLoading = false;
          });
    },
    resetInfo(){
      this.currentMenuId = '00000';
      this.$refs.menuGrid.invoke('resetData', [], {});
      this.$refs.authGrid.invoke('resetData', [], {});
      this.search();
      this.menuSearch();
    }
  }
}
</script>

<style scoped lang="scss">
.p-title {
	margin-top: 25px;
	font-size: 24px;
	font-weight: 700;

	~ div {
		margin-top: 15px;
	}
}
</style>
