<template>
  <div class="contents-wrap main">
		<div class="main-column-box" v-if="!this.$store.state.isMobile">
			<div class="main-card card-profile">
				<div class="profile-img-box">
					<span class="profile-img" :style="{backgroundImage: 'url('+ this.$store.state.userInfo.profileImg +')'}"></span>
				</div>
				<div class="profile-info">
					<p class="profile-name">{{this.$store.state.userInfo.userNm}}</p>
					<p class="profile-detail">{{this.$store.state.userInfo.dptNm}} / {{this.$store.state.userInfo.gradeNm}}</p>
				</div>
				<p class="profile-email">{{this.$store.state.userInfo.emailTxt}}</p>
				<p class="profile-phone">{{this.$store.state.userInfo.mobilTxt}}</p>
				<button type="button" class="btn btn-round" @click.prevent.stop="profilePop()">프로필 변경</button>
			</div>
			<div class="main-card card-vacation">
				<div class="card-title-box">
					<p class="card-title">연차관리</p>
				</div>
				<div class="vacation-info-box">
					<div class="vacation-info-cell">
						<span class="vacation-info-label">총 연차</span><span class="vacation-info-value">{{mainVacationInfo.vacCalTotal}}</span>
					</div>
					<div class="vacation-info-cell">
						<span class="vacation-info-label">사용 연차</span><span class="vacation-info-value">{{mainVacationInfo.vacUsedThisYear}}</span>
					</div>
					<div class="vacation-info-cell">
						<span class="vacation-info-label">잔여 연차</span><span class="vacation-info-value remain-value" :class="{ 'is-minus': mainVacationInfo.vacExtra < 0 }">{{mainVacationInfo.vacExtra}}</span>
					</div>
				</div>
				<button type="button" class="btn btn-round" @click.prevent.stop="vacationPop()">휴가 신청</button>
			</div>
		</div>
		<div class="main-column-box">
			<div class="main-card card-notice">
				<div class="card-title-box">
					<p class="card-title">Notice</p>
					<div class="right-box">
						<router-link to="/board/notice" class="btn-more-router">더보기</router-link>
					</div>
				</div>
				<a href="javascript:void(0);" v-on:click="noticePop(item.bbsNo)" class="notice-importance" v-for="item in mainBoardImpInfo" :key="item.bbsNo" >{{item.titleTxt}}</a>
				<div class="main-notice-box">
<!--
	10612	경조사	Y      	 	a
	10615	신규 직원 소개	Y	b
	10610	공지사항	Y      c
	10611	팀게시판	Y      d
	10614	쓰리탑소식	Y      e
	10618	개발자 가동율	Y  f
	10619	인사발령	Y      g
-->
					<div class="notice-box-item" v-for="item in mainBoardInfo" :key="item.bbsNo" >
						<div class="notice-type type-a" v-if="!item.isMobile && item.typCd === '10612'">{{item.typNm}}</div>
						<div class="notice-type type-b" v-else-if="!item.isMobile && item.typCd === '10615'">{{item.typNm}}</div>
						<div class="notice-type type-c" v-else-if="!item.isMobile && item.typCd === '10610'">{{item.typNm}}</div>
						<div class="notice-type type-d" v-else-if="!item.isMobile && item.typCd === '10611'">{{item.typNm}}</div>
						<div class="notice-type type-e" v-else-if="!item.isMobile && item.typCd === '10614'">{{item.typNm}}</div>
						<div class="notice-type type-f" v-else-if="!item.isMobile && item.typCd === '10618'">{{item.typNm}}</div>
						<div class="notice-type type-g" v-else-if="!item.isMobile && item.typCd === '10619'">{{item.typNm}}</div>
						<div class="notice-title"><a href="javascript:void(0);" v-on:click="noticePop(item.bbsNo)">{{item.titleTxt}}</a></div>
						<div class="notice-reg-date" >{{item.regDt}}</div>
					</div>
				</div>
			</div>
			<div class="main-card card-pds" v-if="!this.$store.state.isMobile">
				<div class="card-title-box">
					<p class="card-title">자료실</p>
					<div class="right-box">
						<router-link to="/board/resource" class="btn-more-router">더보기</router-link>
					</div>
				</div>
				<div class="main-pds-box">
					<div class="pds-box-item" v-for="item in mainResourceInfo" :key="item.resNo">
						<span class="pds-title">{{'['+item.resTypeNm+']'}} {{item.titleTxt}}</span>
						<a href="javascript:void(0);" v-on:click="downLoadPop(item.uploadfile1, item.originalFileName1)" class="pds-attach-file">{{item.originalFileName1}}</a>
					</div>
				</div>
			</div>
			<a href="javascript:void(0);" v-on:click="vacationPop()" class="btn-main-primary has-arrow" v-if="this.$store.state.isMobile">휴가신청</a>
			<!-- <router-link to="/m/my/vacationApply" class="btn-main-primary has-arrow" v-if="this.$store.state.isMobile">휴가신청</router-link> -->
		</div>
		<div class="main-column-box" v-if="!this.$store.state.isMobile">
			<div class="main-card card-schedule-month">
				<div class="card-title-month">
					<div class="today-info">
						<p>Today</p>
						<p class="current-date">{{mainToday}}</p>
					</div>
					<div>
						<button type="button" class="btn btn-line-white" @click="onShowCalendar">달력보기</button>
					</div>
					<CalendarlPop ref="calendarPop" @monthScheduleSearch="monthScheduleSearch" />
				</div>
				<div class="card-title-box">
					<p class="card-title">월간 일정</p>
					<div class="right-box">
						<!-- <button type="button" class="btn btn-inner" @click="onClickSchedule">일정등록</button> -->
					</div>
				</div>
				<ul class="schedule-month-list">
					<li class="schedule-month-item no-data" v-if="!this.mainScheduleMonthVisible">일정이 없습니다.</li>
					<li class="schedule-month-item" v-for="item in mainScheduleMonthInfo" :key="item.scheNo">
						<!-- 개인 / 전체에 따라 달력의 일정 컬러 동일하게 글꼴 색상을 적용하려 했으나, 이연기 차장이 전체만 나오게 요청하여 색은 블랙으로 통일
						<a href="javascript:void(0);" v-if="item.entireSche == 'Y'" style="color: #fc1a40;">{{item.titleTxt}}</a>
						<a href="javascript:void(0);" v-if="item.entireSche == 'N'" style="color: #a1b56c;">{{item.titleTxt}}</a>
						-->
						<a href="javascript:void(0);" >{{item.titleTxt}}</a>
						<p class="schedule-date" v-if="item.startDt != item.endDt">{{item.startDt}} ~ {{item.endDt}}</p>
						<p class="schedule-date" v-if="item.startDt == item.endDt">{{item.startDt}}</p>
					</li>
				</ul>
			</div>
			<div class="main-card card-birthday">
				<div class="card-title-box">
					<p class="card-title">Happy Birthday</p>
				</div>
				<div class="birthday-box no-data" v-if="!this.mainBirthVisible">
					생일자가 없습니다.
				</div>
				<!-- v-for start -->
				<div class="birthday-box" v-for="item in mainBirthInfo" :key="item.birthdayTxt">
					<p class="birth-date">{{item.birthdayTxt}}</p>
					<ul class="birthday-list">
						<li class="birthday-item" v-for="subItem in item.birthMemberList" :key="subItem.empNm"><span class="name">{{subItem.empNm}}</span>님 생일축하합니다.</li>
					</ul>
				</div>
			</div>
		</div>
		<MemberInfoPopup ref="memInfoPop" />
		<BoardDetailPop ref="datailPop" />
		<VacationPopup ref="vacPop" />
	</div>
</template>

<script>
import { mapState, mapActions } from "vuex";
//import { EventBus } from "@/modules/event-bus";
//import { DateFunc } from "@/mixins/DateFunc";
import { Check } from "@/mixins/Check";
import { Comm } from "@/mixins/Comm";

//import vaciotionPopup from "@/views/VaciotionPopup";
//import addressPopup from "@/views/myPage/pop/AddressPop";
import CalendarlPop from "@/views/main/pop/CalendarlPop";
import InputBox from '@/components/InputBox';
import MemberInfoPopup from "@/views/member/pop/MemberInfoPopup";
import BoardDetailPop from "@/views/board/pop/BoardDetailPop";
import VacationPopup from "@/views/myPage/pop/VacationPopup";

export default {
  name: "Main",
  mixins: [Check, Comm],
  components: {
    //vaciotionPopup,
    //addressPopup,
	InputBox,
	CalendarlPop,
	MemberInfoPopup,
	BoardDetailPop,
	VacationPopup,
  },
  data() {
    return {
	  mainVacationInfo: {vacCalTotal:'', vacUsedThisYear:'', vacExtra:''},
	  mainBoardImpInfo: [{bbsNo:'', typCd:'', typNm:'', titleTxt:'', importantYn:'', regDt:''}],
	  mainBoardInfo: [{bbsNo:'', typCd:'', typNm:'', titleTxt:'', importantYn:'', regDt:''}],
	  mainResourceInfo: [{resNo:'', resTypeNm:'', titleTxt:'', uploadfile1:'', originalFileName1:'' }],
	  mainToday: '',
	  mainScheduleMonthInfo: [{scheNo:'', empNo:'', recurYear:'', recurMonth:'', entireSche:'', startDt:'', endDt:'', titleTxt:'', contents:''}],
	  mainScheduleMonthVisible: false,
	  mainBirthInfo: [],
	  mainBirthVisible: false,
    };
  },
  computed: {
	// empty
  },
  created() {
    // this.$router.push("/common/login");
    this.token();
	this.search();
	this.monthScheduleSearch();
  },
  mounted() {
	// empty
  },
  methods: {
    ...mapActions({
      setContactPhone: "setContactPhone",
    }),
    search() {
      let searchparams = {};

      this.$http
          .post(
            "/api/main",
            searchparams,
          )
          .then((response) => {
            //console.log(response);
            //this.totalCnt = response.data.data.contents.length;
            //debugger;
            if (response.data) {
			  // 휴가
			  var vacationInfo = response.data.vacationInfo[0];
			  //console.log(vacationInfo);
			  this.mainVacationInfo.vacCalTotal = vacationInfo.vacCalTotal;
			  this.mainVacationInfo.vacUsedThisYear = vacationInfo.vacUsedThisYear;
			  this.mainVacationInfo.vacExtra = vacationInfo.vacExtra;

			  // 게시판
			  var boardImpInfo =  response.data.boardImpInfo;
			  this.mainBoardImpInfo = boardImpInfo;
			  var boardInfo =  response.data.boardInfo;
			  var isMobile = this.$store.state.isMobile;
			  var newBoardInfo = [];
			  if (boardImpInfo.length > 0 && boardImpInfo[0].bbsNo != '') {
				for (let i in boardInfo) {
				  if (boardInfo[i].bbsNo != boardImpInfo[0].bbsNo) {
                      if(boardInfo[i].typCd !== '10618') {
                          boardInfo[i].isMobile = isMobile;
      					  newBoardInfo.push(boardInfo[i]);
                      }
				  }
				}
			  } else {
				for (let i in boardInfo) {
                    let cnt = 0;
                    if(boardInfo[i].typCd !== '10618') {
                        if (cnt < 7) {
          					boardInfo[i].isMobile = isMobile;
          				    newBoardInfo.push(boardInfo[i]);
          				}
                        cnt++;
                    }
				}
			  }
			  //console.log(newBoardInfo);
			  this.mainBoardInfo = newBoardInfo;

			  // 자료실
			  this.mainResourceInfo = response.data.resourceInfo;

			  // 달력
			  this.mainToday = response.data.today;

			  // 생일자
			  var birthInfo = response.data.birthInfo;
			  if (birthInfo.length > 0) {
				  this.mainBirthVisible = true;
			  }
			  //debugger;
			  this.mainBirthInfo = birthInfo;

            } else {
			  console.log(response);
            }
		  });
	},
	monthScheduleSearch() {
	  let searchparams = {};

      this.$http
          .post(
            "/api/main/getScheduleMonthList",
            searchparams,
          )
          .then((response) => {
			if (response.data) {
			  // 달력 - 월간일정
			  var scheduleMonthInfo = response.data.scheduleMonthInfo;
			  if (scheduleMonthInfo.length > 0) {
				this.mainScheduleMonthVisible = true;
			  }
			  this.mainScheduleMonthInfo = scheduleMonthInfo;
            } else {
              console.log(response);
            }
		  });
	},
	profilePop() {
		this.memberInfoPopup();
		return;
	},
	vacationPop() {
	  this.$refs.vacPop.open('');
	  return;
	},
	// 게시판 팝업 - openDetailPop
    noticePop(bbsNo) {
      this.$refs.datailPop.openDetailPop(bbsNo);
    },
	downLoadPop(uploadfile1, originalFileName1) {
	  this.$store.dispatch("fileDownload", {
        fileName: uploadfile1,
        filePath: uploadfile1,
        oFileName: originalFileName1,
      });
	},
	onShowCalendar() {
		this.$refs.calendarPop.onShowCalendar();
	},
  },
};

</script>
