import Menu from "../views/system/Menu";
import Group from "../views/system/Group";
import BoardManage from "../views/system/BoardManage";
import ResourceManage from "../views/system/ResourceManage";
import Code from "../views/system/Code";
import FamilyEventManage from "../views/system/FamilyEventManage";
import FamilyEventMailManage from "../views/system/FamilyEventMailManage";
import ProfileManage from "../views/myPage/ProfileManage";
import FamilyEventMaster from "../views/system/FamilyEventMaster";
import VacationAdminList from "../views/system/VacationAdminList";
import BuyRequestManage from "../views/system/BuyRequestManage";
import ProjectTeamManage from "../views/system/ProjectTeamManage";
import VacationStateList from "../views/system/VacationStateList";


export default [
  {
    path: "/system/group",
    name: "Group",
    component: Group,
  },
  {
    //프로필관리 - 관리자
    path: "/system/ProfileManage",
    name: "ProfileManageAdmin",
    component: ProfileManage,
    props: { flag: "admin" },
  },
  {
    //프로필관리(퇴직자) - 관리자
    path: "/system/ProfileManage/retire",
    name: "ProfileManageAdminRetire",
    component: ProfileManage,
    props: { flag: "adminRetire", stateCd: "10203" },
  },
  {
    path: "/system/menu",
    name: "Menu",
    component: Menu,
  },
  {
    path: "/system/family-event-mail",
    name: "FamilyEventMailManage",
    component: FamilyEventMailManage,
    props: { menuName: "경조메일 수신자관리", upcodes: ["10000"] },
  },
  {
    path: "/system/board-manage",
    name: "BoardManage",
    component: BoardManage,
    props: { typeCode: "10600" },
  },
  {
    path: "/system/resource-manage",
    name: "ResourceManage",
    component: ResourceManage,
    props: { typeCode: "11100" },
  },
  {
    path: "/system/code",
    name: "Code",
    component: Code,
  },
  {
    path: "/system/family-event-manage",
    name: "FamilyEventManage",
    component: FamilyEventManage,
    props: { menuName: "경조금 신청관리", upcodes: ["10000"] },
  },
  {
    path: "/system/family-event-master",
    name: "FamilyEventMaster",
    component: FamilyEventMaster,
  },
  {
    path: "/system/vacationAdminList",
    name: "VacationAdminList",
    component: VacationAdminList,
    props: { menuType: "system" },
  },
  {
    path: "/system/buy-request-manage",
    name: "BuyRequestManage",
    component: BuyRequestManage,
    props: { menuName: "구매신청관리", upcodes: ["11900", "12000"] },
  },
  {
    path: "/system/project-team-manage",
    name: "ProjectTeamManage",
    component: ProjectTeamManage,
  },
  {
    path: "/system/vacation-state-list",
    name: "SystemVacationStateList",
    component: VacationStateList,
    props: { upMenuPath: "system" },
  },
];
