<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>   
    <div class="page-title-box">
      <h1 class="page-title" v-if="this.flag == 'admin'">프로필 관리(임직원)</h1>
      <h1 class="page-title" v-if="this.flag == 'adminRetire'">프로필 관리(퇴직자)</h1>
      <h1 class="page-title" v-if="this.flag === undefined">프로필 관리</h1>
      <ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
        <li class="breadcrumb">홈</li>
        <li class="breadcrumb" v-if="this.flag === undefined">마이페이지</li>
        <li class="breadcrumb" v-if="this.flag !== undefined">인트라넷관리</li>
        <li class="breadcrumb">프로필 관리</li>
      </ol>
    </div>
    <div class="search-input-box">
      <div class="search-input-row">
        <div class="search-item">
          <label for="boardAuthor">임직원명</label>
          <div class="search-value" v-if="this.$store.state.userInfo.groupauthId == '10001'">
            <InputBox id="regstaffId" type="text" :value.sync="searchData.empNm" @keyup="search" :targetMethod="search"/>
          </div>
          <div class="search-value" v-if="this.$store.state.userInfo.groupauthId != '10001'">
            <InputBox id="regstaffId" type="text" :value="this.$store.state.userInfo.userNm" disabled="true" />
          </div>          
        </div>
        <div class="search-item confirm-box">
          <button type="button" class="btn btn-primary" @click="callProfileManageDetail(searchData.empNm,'new')" >새로만들기/불러오기</button>          
          <button type="button" class="btn" @click="searchReset" v-if="this.$store.state.userInfo.groupauthId == '10001'">초기화</button>
          <button type="button" class="btn btn-primary" @click="search" v-if="this.$store.state.userInfo.groupauthId == '10001'">검색</button>
        </div>
      </div>
    </div>
    <ProfileManageList
      ref="profileManageGridList"
      :gridHeader="gridHeader"
      :profileManageList="profileManageList"
      :mobileHidden="mobileHidden"
      :callProfileManageDetail="callProfileManageDetail"
      :openDetailPop="openDetailPop"
      :flag="flag"
    />
    <ProfileManageDetailPop ref="datailPop" :profileManageDetail="profileManageDetail" :gradeCd="gradeCd" :techGradeCd="techGradeCd" :workType="workType" :workTypeGrid="workTypeGrid" />
  </div>
</template>
<script>
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";
import { Check } from "@/mixins/Check";
import InputBox from "@/components/InputBox";
import DatePicker from "@/components/Datepicker";
import ModalPopup from "@/components/ModalPopup";
import ProfileManageList from "@/views/myPage/ProfileManageList";
import ProfileManageDetailPop from "@/views/myPage/pop/ProfileManageDetailPop";
import { Empty } from "@/mixins/Empty";
import Loading from "vue-loading-overlay";

// import { BoardGridHeader } from "@/views/board/BoardGridHeader";

export default { 
  name: "ProfileManage",
  mixins: [Empty,Check],
  props: ["flag","stateCd"],
  components: {
    Grid,
    vSelect,
    InputBox,
    DatePicker,
    ModalPopup,
    ProfileManageList,
    ProfileManageDetailPop,
    Empty,
    Loading,
  },
  data() {
    return {
      isLoading: false,
      fullPage: true,
      gridHeader: [
        {
          header: "임직원 번호",
          name: "empNo",
          width: 0,
          align: "center",
          hidden: true,
        },        
        {
          header: "임직원명",
          name: "empNm",
          formatter(data) {
            return `
                <div class="board-title-box">
                  <a class="board-title clickable-text">${data.value}</a>
                </div>
              `;
          },          
          width: 150,
          align: "center",
          hidden: this.mobileHidden,
          sortable: true,
        },
        {
          header: "소속",
          name: "dptCodeNm",
          align: "center",
          sortable: true,
        },
        {
          header: "직급",
          name: "gradeCodeNm",
          align: "center",
          hidden: this.mobileHidden,
          sortable: true,
        },
        {
          header: "작성일",
          name: "regDt",
          align: "center",
          sortable: true,
        },        
        {
          header: "수정일",
          name: "modDt",
          align: "center",
          sortable: true,
        }    
      ],
      searchData: {
        empNm: this.$store.state.userInfo.groupauthId == '10001'? '' : this.$store.state.userInfo.userNm
        ,stateCd : this.stateCd
      },
      profileManageList: [],
      profileManageDetail: {},
      gradeCd: {
        options: [],
      },
      techGradeCd: {
        options: [],
      },
      workType: {
        options: [],
      },
      workTypeGrid: {
        options: [],
      },                             
    };
  },
  created() { 
      this.search();
  },
  computed: {
    mobileHidden() {
      return this.$store.getters.isMobile;
    },
  },
  watch: {
    mobileHidden(val, oldVal) {

    },
    flag(val, oldVal) {
      this.$refs.profileManageGridList.$refs.perPage.select({label : '10',value:10})
      this.$refs.profileManageGridList.$refs.perPage.open=false;
      this.search(); 
    },
  },
  methods: {
    openDetailPop() {
      this.$refs.datailPop.openDetailPop();
    },
    callProfileManageList(params) {
      this.isLoading = true;

      if (params.empNm == "" || params.empNm === undefined) {params.empNm = null};
      if (params.stateCd == "" || params.stateCd === undefined){params.stateCd = null};

      //params.typCd = this.boardTypeCode;

      const apiUrl = "/api/myPage/profile-manage/list";
      const data = this.$http
        .post(apiUrl, params)
        .then((solve) => {
            this.profileManageList = solve.data.data.contents;          
        })
        .catch((e) => {
          // throw new Error("게시물을 가져오는데 실패하였습니다.");
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });                
      return data;
    },
    async callProfileManageDetail(empNo,flag) {

      var param ={};
      if(flag == 'new' && this.searchData.empNm == ""){
        alert('임직원명을 입력해주세요.');
        return false;
      }else{
        if(flag == 'new'){
          param.empNm = empNo;
        }else{
          param.empNo = empNo;
        }
      }

      this.isLoading = true;

      const apiUrl = "/api/myPage/profile-manage/detail";
      const data = await this.$http
        .post(apiUrl, param)
        .then((solve) => {
          if (solve.status === 200 && solve.data.profileManage != null) {
            this.profileManageDetail = solve.data.profileManage;
            this.gradeCd.options = [
              "전체",
              ...solve.data.codes["10100"],
            ];

            this.techGradeCd.options = [
              "전체",
              ...solve.data.codes["11400"],
            ];

            this.workType.options = [
              "전체",
              ...solve.data.codes["11300"],
            ];

            if(this.workTypeGrid.options.length == 0){
              for(let i=0; i < solve.data.codes["11300"].length; i++){
                this.workTypeGrid.options.push({'text' : solve.data.codes["11300"][i].label , 'value' : solve.data.codes["11300"][i].code})
              }         
            }

            if(this.profileManageDetail.techGradeCd == undefined){
              this.profileManageDetail.techGradeCd = '11410';
            }
            if(this.profileManageDetail.workType == undefined){
              this.profileManageDetail.workType = '11310';
            }            
            
            this.openDetailPop();            
          } else {
            alert("해당 임직원이 존재하지 않습니다.");
            throw new Error("해당 임직원이 존재하지 않습니다.");
          }
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });        
      return data;
    },
    search() {
      const param = {
        empNm : this.searchData.empNm
        ,stateCd : this.stateCd
      };
      this.callProfileManageList(param);
    },
    searchReset() {
      this.searchData.empNm ="";
    },
    formatDate(date) {
      let d = new Date(date),
        month = "" + (d.getMonth() + 1),
        day = "" + d.getDate(),
        year = d.getFullYear();
      if (month.length < 2) month = "0" + month;
      if (day.length < 2) day = "0" + day;

      return [year, month, day].join("-");
    },
  },
};
</script>
