<template>
  <div class="datepicker-box">
    <div class="tui-datepicker-input tui-datetime-input">
      <input ref="input" type="text" :id="targetId" aria-label="Date-Time" :disabled="isDisabled" :readonly="isReadonly"/>
      <span class="tui-ico-date"></span>
    </div>
    <div :id="containerId" class="tui-datepicker-container" style=""></div>
  </div>
</template>

<script>
import "tui-date-picker/dist/tui-date-picker.css";

import DatePicker from "tui-date-picker";

let container;
let target;

export default {
  name: "Datepicker",
  props: {
    date: {
      //초기 세팅 날짜
      default: null,
    },
    type: {
      default: "date",
    },
    language: {
      default: "ko",
    },
    timePicker: {
      default: false,
    },
    format: {
      default: "yyyy-MM-dd",
    },
    selectableRanges: {
      default: function () {
        return [[new Date("1900", "0", "1"), new Date("2999", "12", "31")]];
      },
    },
    openers: {
      default: function () {
        return [];
      },
    },
    showAlways: {
      default: false,
    },
    autoClose: {
      default: true,
    },
    usageStatistics: {
      default: true,
    },
    isDisabled: {
      default: false,
    },
    isReadonly: {
        default: false,
    }
  },
  data() {
    return {
      uid: null,
      containerId: null,
      targetId: null,
      instance: null,
    };
  },
  watch: {},
  components: {
    DatePicker,
  },
  created() {
    this.uid = this._uid;
    this.containerId = "tui-date-picker-container-" + this.uid;
    this.targetId = "tui-date-picker-target-" + this.uid;
  },
  mounted() {
    container = document.getElementById(this.containerId);
    target = document.getElementById(this.targetId);

    let instance = new DatePicker(container, {
      calendar: {
        showToday: false,
      },
      date: this.date === null ? null : this.date,
      type: this.type,
      language: this.language,
      timePicker: this.timePicker,
      selectableRanges: this.selectableRanges,
      openers: this.openers,
      showAlways: this.showAlways,
      autoClose: this.autoClose,
      usageStatistics: this.usageStatistics,
      input: {
        element: target,
        format: this.format,
      },
    });

    let vm = this;
    instance.on("change", function () {
      vm.$emit("update:date", instance.getDate());
    });

    this.instance = instance;
  },
  computed: {
    getDate() {
      return new Date(this.instance.getDate());
    },
  },
  methods: {
    focus() {
      this.$refs.input.focus();
    },
    click() {
      this.$refs.input.click();
    },
    setDate(date) {
     this.instance.setDate(date);
    }
  },
};
</script>
