<template>
  <modal-popup
    :title="'경조금 신청'"
    :visible="seen"
    @update:visible="close"
    width="900px"
  >
    <div class="tbl-row write-mode">
      <table>
        <colgroup>
          <col class="col-form-title" />
          <col />
          <col class="col-form-title" />
          <col />
        </colgroup>
        <tbody>
          <tr v-if="isDetail">
            <th scope="row" class="required">임직원</th>
            <td colspan="3">{{ this.form.empNm }}</td>
          </tr>
          <tr v-else>
            <th scope="row" class="required">임직원</th>
            <td colspan="3">
              <v-select
                v-model="form.empNo"
                :reduce="(emp) => emp.value"
                :options="empNoList"
                :clearable="false"
                :searchable="false"
              />
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">본인과의 관계</th>
            <td>
              <v-select
                v-model="form.famRelationCd"
                :reduce="(cd) => cd.value"
                :required="!form.famRelationCd"
                :options="options[11600]"
                :clearable="false"
                :searchable="false"
                :disabled="!isEditable"
              />
            </td>
            <th scope="row" class="required">경조 구분</th>
            <td>
              <v-select
                v-model="form.famEvnCd"
                :reduce="(cd) => cd.value"
                :required="!form.famEvnCd"
                :options="options[11500]"
                :clearable="false"
                :searchable="false"
                :disabled="!isEditable"
              />
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">경조일자</th>
            <td v-if="isEditable" colspan="3">
              <date-picker ref="famEvnDt" :date.sync="form.famEvnDt" />
            </td>
            <td v-else colspan="3">
              <p>{{ formatFamEvnDt }}</p>
            </td>
          </tr>
          <tr>
            <th scope="row">경조금</th>
            <td colspan="3">
              <p v-if="form.famEvnCd && form.famRelationCd">
                {{ getFamEvnAmt }}
              </p>
              <p v-else>'경조 구분' 또는 '본인과의 관계'를 선택해 주세요.</p>
            </td>
          </tr>
          <tr>
            <th scope="row">증빙서류</th>
            <td colspan="3">
              <p v-if="form.famEvnCd && form.famRelationCd">
                {{ getEvidencialNm }}
              </p>
              <p v-else>'경조 구분' 또는 '본인과의 관계'를 선택해 주세요.</p>
            </td>
          </tr>
          <tr>
            <th scope="row">비고</th>
            <td v-show="isEditable" colspan="3">
              <editor
                :initialValue="' '"
                ref="editor"
                :previewStyle="'tab'"
                :usageStatistics="false"
              />
            </td>
            <td v-show="!isEditable" colspan="3">
              <div
                class="board-content"
                v-if="isHtmlText"
                v-html="form.contentsTxt"
              ></div>
              <viewer v-else :initialValue="form.contentsTxt" ref="viewer" />
            </td>
          </tr>
          <tr>
            <th scope="row">첨부파일 #1</th>
            <td colspan="3">
              <div
                class="input-flex-box"
                v-if="isEditable && !form.uploadFiles[0].name"
              >
                <input-box type="text" placeholder="첨부파일1" readonly />
                <file-button label="파일선택" :idx="0" @change="onFileChange" />
              </div>
              <div v-else-if="!isEditable && !form.uploadFiles[0].name"></div>
              <div class="attach-file-box" v-else>
                <ul class="attached-file-list">
                  <li class="file-item">
                    <div class="file-item-info">
                      <span class="file-name" @click="downloadTrigger(0)">{{
                        form.uploadFiles[0].name
                      }}</span>
                      <button
                        type="button"
                        class="btn-remove-file"
                        @click="onFileDelete(0)"
                      >
                        파일삭제
                      </button>
                    </div>
                  </li>
                </ul>
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row">첨부파일 #2</th>
            <td colspan="3">
              <div
                class="input-flex-box"
                v-if="isEditable && !form.uploadFiles[1].name"
              >
                <input-box type="text" placeholder="첨부파일2" readonly />
                <file-button label="파일선택" :idx="1" @change="onFileChange" />
              </div>
              <div v-else-if="!isEditable && !form.uploadFiles[1].name"></div>
              <div class="attach-file-box" v-else>
                <ul class="attached-file-list">
                  <li class="file-item">
                    <div class="file-item-info">
                      <span class="file-name" @click="downloadTrigger(1)">{{
                        form.uploadFiles[1].name
                      }}</span>
                      <button
                        type="button"
                        class="btn-remove-file"
                        @click="onFileDelete(1)"
                      >
                        파일삭제
                      </button>
                    </div>
                  </li>
                </ul>
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row">첨부파일 #3</th>
            <td colspan="3">
              <div
                class="input-flex-box"
                v-if="isEditable && !form.uploadFiles[2].name"
              >
                <input-box type="text" placeholder="첨부파일3" readonly />
                <file-button label="파일선택" :idx="2" @change="onFileChange" />
              </div>
              <div v-else-if="!isEditable && !form.uploadFiles[2].name"></div>
              <div class="attach-file-box" v-else>
                <ul class="attached-file-list">
                  <li class="file-item">
                    <div class="file-item-info">
                      <span class="file-name" @click="downloadTrigger(2)">{{
                        form.uploadFiles[2].name
                      }}</span>
                      <button
                        type="button"
                        class="btn-remove-file"
                        @click="onFileDelete(2)"
                      >
                        파일삭제
                      </button>
                    </div>
                  </li>
                </ul>
              </div>
            </td>
          </tr>
          <tr v-if="isDetail">
            <th scope="row">내역</th>
            <td colspan="3">
              <p v-if="isDetail && form.requestDt">
                {{ form.requestDt }} 경조금신청
              </p>
              <p v-if="isDetail && form.confirmDt">
                {{ form.confirmDt }} 경조금승인
              </p>
              <p v-if="isDetail && form.cancelReqDt">
                {{ form.cancelReqDt }} 취소신청
              </p>
              <p v-if="isDetail && form.cancelApprovDt">
                {{ form.cancelApprovDt }} 취소승인
              </p>
              <p v-if="isDetail && form.rejectDt">{{ form.rejectDt }} 반려</p>
            </td>
          </tr>
          <tr v-if="isDetail && form.confirmYn !== 'N'">
            <td colspan="4" style="text-align:center;">
              이 경조금 신청건은 확정이 되었습니다. 확정이 된 건은 수정할 수
              없습니다.
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="modal-popup-btn-box">
      <button
        v-if="isDetail && isEditable"
        type="button"
        class="btn btn-primary"
        @click="modify"
      >
        수정
      </button>
      <button
        v-else-if="!isDetail"
        type="button"
        class="btn btn-primary"
        @click="submit"
      >
        신청
      </button>
      <button type="button" class="btn" @click="close">취소</button>
    </div>
    <div style="margin-top: 10px" v-if="!isDetail">
      <p>경조금 지급 관련 확인 사항</p>
      <div class="tbl-row">
        <table>
          <colgroup>
            <col class="col-row-title" />
            <col class="col-row-title" />
            <col class="col-row-title" />
            <col class="col-row-title" />
          </colgroup>
          <tbody>
            <tr>
              <th>구분</th>
              <th>경조 종류</th>
              <th>경조금</th>
              <th>증빙 서류</th>
            </tr>
            <tr v-for="desc in descs" :key="desc.id">
              <td v-if="desc.sortSeq == 1" :rowspan="desc.gubunCnt">
                {{ desc.famEvnGubunNm }}
              </td>
              <td>{{ desc.famEvnNm }}</td>
              <td>{{ desc.famEvnItem }}</td>
              <td>{{ desc.evidencialNm }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </modal-popup>
</template>

<script>
import "vue-select/dist/vue-select.css";
import "codemirror/lib/codemirror.css";
import "@toast-ui/editor/dist/toastui-editor.css";
import "@toast-ui/editor/dist/toastui-editor-viewer.css";

import { mapState, mapActions, mapGetters } from "vuex";
import ModalPopup from "@/components/ModalPopup";
import vSelect from "vue-select";
import { Editor, Viewer } from "@toast-ui/vue-editor";
import InputBox from "@/components/InputBox";
import FileButton from "@/components/FileButton";
import DatePicker from "@/components/Datepicker";
import { DateFunc } from "@/mixins/DateFunc";
import { Comm } from "@/mixins/Comm";
import { Empty } from "@/mixins/Empty";

export default {
  name: "FamilyEventManagePop",
  components: {
    ModalPopup,
    InputBox,
    vSelect,
    FileButton,
    DatePicker,
    editor: Editor,
    viewer: Viewer,
  },
  props: ["isDetail", "descs", "upcodes", "empNoList"],
  mixins: [DateFunc, Comm, Empty],
  data() {
    return {
      famEvnNo: "",
      seen: false,
      uploadFileCount: 3,
      form: {
        empNo: "",
        empNm: "",
        dptNm: "",
        gradeNm: "",
        famRelationCd: null,
        famEvnCd: null,
        famEvnAmt: "",
        famEvnDt: new Date(),
        contentsTxt: "",
        uploadFiles: [
          { name: "", path: "" },
          { name: "", path: "" },
          { name: "", path: "" },
        ],
        confirmYn: "",
        requestDt: "",
        confirmDt: "",
        cancelReqDt: "",
        cancelApprovDt: "",
        rejectDt: "",
        regDt: "",
      },
      options: { "11500": [], "11600": [] },
    };
  },
  created() {
    this.formatterInstall();
    for (let upcode of this.upcodes) {
      this.options[upcode] = this.commSelect(upcode, "선택");
    }
  },
  computed: {
    ...mapGetters(["getUsedMarkdownFromDt"]),
    ...mapState({
      loginInfo: (state) => state.userInfo,
      codeInfo: (state) => state.codeInfo,
    }),
    isHtmlText() {
      return (
        this.isDetail &&
        this.form.regDt.replaceAll("-", "") < this.getUsedMarkdownFromDt
      );
    },
    formatFamEvnDt() {
      return this.form.famEvnDt.format("yyyy-MM-dd");
    },
    isEditable() {
      return !this.isDetail || (this.isDetail && this.form.confirmYn == "N");
    },
    getUserNm() {
      return this.isDetail ? this.form.empNm : this.loginInfo.userNm;
    },
    getDptNm() {
      return this.isDetail ? this.form.dptNm : this.loginInfo.dptNm;
    },
    getGradeNm() {
      return this.isDetail ? this.form.gradeNm : this.loginInfo.gradeNm;
    },
    findDesc() {
      if (this.descs) {
        return this.descs.find(
          (d) => d.id == this.form.famEvnCd + "-" + this.form.famRelationCd
        );
      }
    },
    getFamEvnAmt() {
      var item = this.findDesc;
      return item && item.famEvnItem ? item.famEvnItem : "해당사항 없음";
    },
    getEvidencialNm() {
      var item = this.findDesc;
      return item && item.evidencialNm ? item.evidencialNm : "해당사항 없음";
    },
  },
  methods: {
    ...mapActions({
      fileDownload: "fileDownload",
    }),
    open() {
      this.seen = true;
    },
    close() {
      this.resetForm();
      this.seen = false;
    },
    resetForm() {
      Object.assign(this.$data.form, this.$options.data().form);
    },
    onFileChange(obj) {
      //단건 업로드
      let formData = new FormData();
      formData.append("uploadFiles", obj.event.target.files[0]);
      formData.append("editorYn", "N");

      this.$http
        .post("/api/common/fileUploadRename", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          this.form.uploadFiles[obj.idx].name =
            response.data.data[0].originalFileName;
          this.form.uploadFiles[obj.idx].path = response.data.data[0].filePath;
        })
        .catch((e) => {
          console.error("uploadFile error", e);
        });
    },
    onFileDelete(idx) {
      let formData = new FormData();
      formData.append("filePath", this.form.uploadFiles[idx].path);

      this.$http
        .post("/api/common/removeFile", formData)
        .then((response) => {
          if (response.data.statusCode != 200) {
            alert("파일 삭제중 오류가 발생했습니다.");
            return false;
          }
          alert(response.data.data.message);
          this.form.uploadFiles[obj.idx].name = "";
          this.form.uploadFiles[obj.idx].path = "";
        })
        .catch((e) => {
          console.error("removeFile error", e);
        });
    },
    downloadTrigger(idx) {
      let fileObj = {
        oFileName: this.form.uploadFiles[idx].name,
        filePath: this.form.uploadFiles[idx].path,
      };
      this.fileDownload(fileObj);
    },
    openDetail(detail) {
      this.resetForm();
      this.famEvnNo = detail.famEvnNo;

      this.form.confirmYn = detail.confirmYn;
      this.form.empNo = detail.empNo;
      this.form.empNm = detail.empNm;
      this.form.dptNm = detail.dptNm;
      this.form.gradeNm = detail.gradeNm;
      this.form.famRelationCd = detail.famRelationCd;
      this.form.famEvnCd = detail.famEvnCd;
      this.form.famEvnDt = new Date(detail.famEvnDt);
      this.form.contentsTxt = this.replaceLucyFilter(detail.contentsTxt);
      this.form.regDt = detail.regDt;

      for (let i = 0; i < 3; i++) {
        this.form.uploadFiles[i].name = detail["originalFileName" + (i + 1)];
        this.form.uploadFiles[i].path = detail["uploadFile" + (i + 1)];
      }

      this.form.requestDt = detail.requestDt;
      this.form.confirmDt = detail.confirmDt;
      this.form.cancelReqDt = detail.cancelReqDt;
      this.form.cancelApprovDt = detail.cancelApprovDt;
      this.form.rejectDt = detail.rejectDt;

      this.seen = true;

      // $refs는 reactive 하지않고 컴포넌트 렌더링이 끝난 후에 생성된다
      // nextTick은 다음 update 주기 후에 콜백을 실행시켜준다!
      this.$nextTick(() => {
        if (this.isEditable) {
          //콜백의 this가 자동으로 해당 vue인스턴스로 바인딩된다.
          this.$refs.editor.invoke("setMarkdown", this.form.contentsTxt);
          this.$refs.famEvnDt.setDate(this.form.famEvnDt);
        } else if (!this.isHtmlText) {
          this.$refs.viewer.invoke("setMarkdown", this.form.contentsTxt);
        }
      });
    },
    validate() {
      if (this.isEmpty(this.form.famRelationCd)) {
        alert("본인과의 관계를 선택해주세요.");
        return false;
      }
      if (this.isEmpty(this.form.famEvnCd)) {
        alert("경조 구분을 선택해주세요.");
        return false;
      }
      if (this.isEmpty(this.form.famEvnDt)) {
        alert("경조 일자를 입력해주세요.");
        return false;
      }
      return true;
    },
    submit() {
      if (this.validate()) {
        let param = {};
        param.empNo = this.form.empNo;
        param.adminYn = "Y";
        param.famRelationCd = this.form.famRelationCd;
        param.famEvnCd = this.form.famEvnCd;
        param.contentsTxt = this.$refs.editor.invoke("getMarkdown");
        param.famEvnDt = this.form.famEvnDt.format("yyyyMMdd");

        this.form.uploadFiles.forEach(function(f, idx) {
          if (f.name && f.path) {
            param["originalFileName" + (idx + 1)] = f.name;
            param["uploadFile" + (idx + 1)] = f.path;
          }
        });

        this.$http
          .post("/api/system/family-event-manage/create", param)
          .then((response) => {
            if (response.data.data) {
              alert(response.data.data.msg);
              if (response.status == 200) {
                this.seen = false;
                this.$emit("created");
              }
            }
          })
          .catch((e) => {
            console.error(e);
          });
      }
    },
    modify() {
      if (this.validate()) {
        let param = {};
        param.empNo = this.form.empNo;
        param.famEvnNo = this.famEvnNo;
        param.famRelationCd = this.form.famRelationCd;
        param.famEvnCd = this.form.famEvnCd;
        param.contentsTxt = this.$refs.editor.invoke("getMarkdown");
        param.famEvnDt = this.form.famEvnDt.format("yyyyMMdd");

        this.form.uploadFiles.forEach(function(f, idx) {
          if (f.name && f.path) {
            param["originalFileName" + (idx + 1)] = f.name;
            param["uploadFile" + (idx + 1)] = f.path;
          }
        });

        this.$http
          .post("/api/system/family-event-manage/update", param)
          .then((response) => {
            if (response.data.data) {
              alert(response.data.data.msg);
              if (response.status == 200) {
                this.seen = false;
              }
              this.$emit("modified");
            }
          })
          .catch((e) => {
            console.error(e);
          });
      }
    },
  },
};
</script>
