<template>
  <modal-popup
    :title="'반려사유'"
    :visible="seen"
    @update:visible="close"
    width="900px"
    height="400px"
  >
    <div class="tbl-row write-mode">
      <div class="text-area-box">
        <textarea
          v-model="rejectReason"
          rows="4"
          maxlength="150"
          disabled
        ></textarea>
      </div>
    </div>
    <div class="modal-popup-btn-box">
      <button type="button" class="btn" @click.prevent.stop="close">
        취소
      </button>
    </div>
  </modal-popup>
</template>

<script>
import { mapState } from "vuex";
import ModalPopup from "@/components/ModalPopup";

export default {
  name: "RejectReasonPop",
  components: {
    ModalPopup,
  },
  data() {
    return {
      seen: false,
      rejectReason: "",
    };
  },
  methods: {
    openDetail(text) {
      this.rejectReason = text;
      this.seen = true;
    },
    close() {
      this.rejectReason = "";
      this.seen = false;
    },
  },
};
</script>
