<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="page-title-box">
      <h1 class="page-title">임직원 관리</h1>
      <ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
        <li class="breadcrumb">인트라넷관리</li>
        <li class="breadcrumb">임직원관리</li>
      </ol>
    </div>
    <div class="search-input-box">
      <div class="search-input-row">
        <div class="search-item">
          <label>임직원명</label>
          <div class="search-value">
            <InputBox type="text" :value.sync="form.empNm" :targetMethod="buttonSearch"/>
          </div>
        </div>
        <div class="search-item">
          <label>로그인ID</label>
          <div class="search-value">
            <InputBox type="text" :value.sync="form.loginId" :targetMethod="buttonSearch"/>
          </div>
        </div>
        <div class="search-item">
          <label>휴대폰번호</label>
          <div class="search-value">
            <InputBox type="text" :value.sync="form.mobilTxt" :targetMethod="buttonSearch"/>
          </div>
        </div>
      </div>
      <div class="search-input-row">
        <div class="search-item">
          <label>직급</label>
          <div class="search-value">
            <vSelect
              v-model="form.gradeCd"
              :options="code.gradeCds"
              :clearable="false"
              :searchable="false"
            />
          </div>
        </div>
        <div class="search-item">
          <label>상태</label>
          <div class="search-value">
            <vSelect
              v-model="form.stateCd"
              :options="code.stateCds"
              :clearable="false"
              :searchable="false"
            />
          </div>
        </div>
        <div class="search-item confirm-box">
          <button type="button" class="btn" @click="memberRegiPop">
            임직원등록
          </button>
          <button
            type="button"
            class="btn btn-primary"
            @click.passive.stop="gridHandlers().search()"
          >
            검색
          </button>
        </div>
      </div>
    </div>
    <div class="contact-grid-box">
      <div class="contact-tree-box" v-if="!this.mobileHidden">
        <div class="btn-box">
          <button type="button" class="btn btn-inner" @click="treeOpenAll">
            확장
          </button>
          <button type="button" class="btn btn-inner" @click="treeCloseAll">
            축소
          </button>
        </div>
        <Tree
          ref="memberTree"
          :data="this.treeData"
          @selectLeaf="onTreeSelect"
        />
      </div>
      <grid
        class="tui-grid"
        ref="memberGrid"
        :data="memberGrid.data"
        :columns="memberGrid.columns"
        :options="memberGrid.options"
        @click="cellClick"
      />
    </div>
    <MemberRegiPopup
      ref="memRegiPop"
      @closedMemberRegiPopup="closedMemberRegiPopup"
    />
    <MemberInfoPopup
      ref="memInfoPop"
      :masterYn="this.masterYn"
      @closedMemberInfoPopup="closedMemberInfoPopup"
    />
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import "tui-grid/dist/tui-grid.css";
import { Grids } from "@/mixins/Grids";
import { Grid } from "@toast-ui/vue-grid";
import "vue-select/dist/vue-select.css";
import vSelect from "vue-select";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";

import InputBox from "@/components/InputBox";
import Tree from "@/components/Tree";
import {
  CellButtonRenderer,
  ClickableTextRenderer,
} from "@/components/renderer/GridRenderer";

import MemberRegiPopup from "@/views/member/pop/MemberRegiPopup";
import MemberInfoPopup from "@/views/member/pop/MemberInfoPopup";
import ModalPopup from "@/components/ModalPopup";
import Loading from "vue-loading-overlay";

import TuiGrid from "tui-grid";
TuiGrid.setLanguage("ko");

export default {
  name: "Member",
  mixins: [Check, Empty, Grids],
  components: {
    grid: Grid,
    vSelect,
    InputBox,
    Tree,
    ModalPopup,
    MemberRegiPopup,
    MemberInfoPopup,
    Loading,
  },
  data() {
    return {
      form: {
        empNm: "",
        loginId: "",
        mobilTxt: "",
        searchGubun: "",
        gradeCd: "",
        stateCd: "",
      },
      code: {
        gradeCds: [],
        stateCds: [],
      },
      treeData: [],
      memberGrid: {},
      modalDetailVisible: false,
      treeSelect: {
        backgroundColor: "yellow",
      },
      codeSearchData: ["10100", "10200"],
      isLoading: false,
      fullPage: true,
      //ascending: false,
      masterYn: "Y",
    };
  },
  created() {
    this.codeSearch(),
    this.memberGrid = {
      options: {
        rowHeight: 48,
        scrollX: true,
        scrollY: true,
        bodyHeight: 540,
        showDummyRows: false,
        selectionUnit: "row",
        display: { noData: "No Data" },
      },
      columns: [
        {
          header: "임직원번호",
          name: "empNo",
          width: 100,
          minWidth: 100,
          align: "center",
          sortable: true,
        },
        {
          header: "성명",
          name: "empNm",
          width: 80,
          minWidth: 80,
          align: "center",
          sortable: true,
          formatter: this.gDetail,
        },
        {
          header: "직급",
          name: "gradeNm",
          width: 80,
          align: "center",
          sortable: true,
        },
        //{header: "직급코드", name: "gradeCd", width: 80, align: "center", sortable: true, hidden: false, },
        {
          header: "휴대전화번호",
          name: "mobilTxt",
          width: 180,
          align: "center",
          sortable: true,
        },
        {
          header: "E-Mail",
          name: "emailTxt",
          width: 180,
          align: "center",
          sortable: true,
        },
        {
          header: "상태",
          name: "stateNm",
          width: 100,
          align: "center",
          sortable: true,
        },
        { header: "주소", name: "zipaddrTxt", width: 500, sortable: true },
        { header: "상세주소", name: "addressTxt", width: 300, sortable: true },
      ],
      data: [],
    };

    //this.gridHandlers().search();
    this.callMemberInfo();
    this.treeSearch();
    //this.gridHandlers().search();
  },
  computed: {
    gridProps() {
      return {
        options: {
          rowHeight: 48,
          scrollX: false,
          scrollY: true,
          bodyHeight: "fitToParent",
          showDummyRows: true,
          selectionUnit: "row",
        },
        columns: [],
        data: [],
      };
    },
    mobileHidden() {
      return this.$store.getters.isMobile;
    },
  },
  watch: {
    gridProps(val, oldVal) {
      this.$refs.memberGrid.invoke("setColumns", val.columns);
    },
    mobileHidden(val, oldVal) {
      this.$refs.memberGrid.invoke("setBodyHeight", val ? this.gridProps.data.length * this.gridProps.options.rowHeight : "fitToParent");
    },
  },
  methods: {
    memberRegiPop(dptNo) {
      this.$refs.memRegiPop.open(dptNo);
    },
    codeSearch() {

      let searchparms = {};
      searchparms.codes = this.codeSearchData;

      this.$http
        .post("/api/code/getCodeSearch", searchparms)
        .then((response) => {

          //코드 세팅
          this.code.gradeCds = response.data.data[this.codeSearchData[0]];
          this.code.gradeCds.unshift({ label: "전체", code: "" });
          this.form.gradeCd = this.code.gradeCds[0];

          this.code.stateCds = response.data.data[this.codeSearchData[1]];
          this.form.stateCd = response.data.data[this.codeSearchData[1]][0];

          //this.gridHandlers().search();
        })
        .catch((e) => {
          console.log("공통코드 조회 error : ", e);
        });
    },
    treeSearch() {
      let searchparms = {};
      searchparms.stateCd = "10201";
      //searchparms.stateCd = this.form.stateCd.code;

      this.$http
        .post("/api/member/getMemberListView", searchparms)
        .then((response) => {

          if (response.data.statusCode != 200) {
            alert("오류가 발생했습니다.");
            return false;
          }
          //Tree 초기화
          this.$refs.memberTree.clearTreeData();
          this.treeData = response.data.data.contents;
          this.$refs.memberTree.instance.resetAllData(this.treeData, {});

          let memberTree = this.$refs.memberTree.instance;
          memberTree.eachAll(
              function(node, nodeId) {
                  if(memberTree.getNodeData(nodeId).value === "" ) memberTree.open(nodeId);
          });
        });
    },
    onGridClick(event) {
      if (event.columnName === "name") {
        if (this.$store.getters.isMobile) {
          this.$router.push("/m/contactView");
        } else {
          this.modalBoardView.visible = true;
        }
      }
    },
    onInputSearch(value) {},
    onDetailInfoBtnClick(event, item) {
      this.modalDetailVisible = true;
    },
    onTreeSelect(event) {

      let treeDepth = this.$refs.memberTree.instance.getDepth(event.nodeId);

      // if (treeDepth == "1") {
        let dptNo = this.$refs.memberTree.instance.getNodeData(event.nodeId)
          .value;
        this.memberRegiPop(dptNo);
      // } else {
      //
      //   let empNo = this.$refs.memberTree.instance.getNodeData(event.nodeId)
      //     .value;
      //   this.$refs.memInfoPop.open(empNo, this.masterYn);
      // }

    },
    treeOpenAll() {
      let memberTree = this.$refs.memberTree.instance;
      memberTree.eachAll(function(node, nodeId) {
        memberTree.open(nodeId);
      });
    },
    treeCloseAll() {
      let memberTree = this.$refs.memberTree.instance;
      memberTree.eachAll(function(node, nodeId) {
        memberTree.close(nodeId);
      });
    },
    async callMemberInfo() {
      this.isLoading = true;
      let searchparms = {};

      searchparms.empNm = this.form.empNm;
      searchparms.loginId = this.form.loginId;
      searchparms.gradeCd = this.form.gradeCd.code;
      searchparms.stateCd = this.form.stateCd.code;

      await this.$http
        .post("/api/member/list", searchparms)
        .then((response) => {

          this.$refs.memberGrid.invoke("resetData", response.data.contents);
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    gridHandlers: function() {

      const _self = this;
      return {
        search: function() {
          this.isLoading = true;

          let searchparms = {};

          searchparms.empNm = _self.form.empNm;
          searchparms.loginId = _self.form.loginId;
          searchparms.mobilTxt = _self.form.mobilTxt;
          searchparms.gradeCd = _self.form.gradeCd.code;
          searchparms.stateCd = _self.form.stateCd.code;

          _self.$http
            .post("/api/member/list", searchparms)
            .then((response) => {

              _self.$refs.memberGrid.invoke(
                "resetData",
                response.data.contents
              );
            })
            .catch((e) => {
              console.log(e);
            })
            .finally(() => {
              this.isLoading = false;
            });
        },
      };
    },
    buttonSearch(){
        this.gridHandlers().search();
    },
    cellClick(ev) {
      const clickValue = this.$refs.memberGrid.invoke("getValue", ev.rowKey, "empNo");

      if (ev.columnName === "empNm" && this.isNotEmpty(ev.rowKey)) {
        this.$refs.memInfoPop.open(clickValue, this.masterYn);
      }
    },
    closedMemberRegiPopup(rtnObj) {
      if (!this.isEmpty(rtnObj)) {
        if (rtnObj == "success") {
          this.gridHandlers().search();
          this.treeSearch();
        }
      }
    },
    closedMemberInfoPopup(rtnObj) {
      if (!this.isEmpty(rtnObj)) {
        if (rtnObj == "success") {
          this.gridHandlers().search();
          this.treeSearch();
        }
      }
    },
  },
};
</script>
