<template>
  <div class="grid-box">
    <Grid
      class="tui-grid"
      ref="tuiGrid"
      :options="gridProps.options"
      :data="gridProps.data"
      :columns="gridProps.columns"
      @click="onGridClick"
    />
    <div class="per-page-box" v-if="!this.$store.state.isMobile">
      <span class="total-count-text">
        총
        <span class="count">{{ gridProps.data.length }}</span>개
      </span>
      <div class="select-box">
        <vSelect
          ref="perPage"
          :options="pageData.data"
          :clearable="false"
          :searchable="false"
          v-model="perPage"
          @input="onPerPageInput"
        ></vSelect>
      </div>
    </div>
  </div>
</template>

<script>
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";

export default {
  name: "ProfileManageList",
  props: [
    "gridHeader",
    "profileManageList",
    "mobileHidden",
    "callProfileManageDetail",
    "openDetailPop",
  ],
  components: {
    Grid,
    vSelect,
  },
  data() {
    return {
      perPage : 10,
      pageData: {
        data: [
          { label: "10", value: 10 },
          { label: "20", value: 20 },
          { label: "50", value: 50 },
          { label: "100", value: 100 }, 
        ],
        //selected: "10", 
      },
    };
  },
  computed: {
    gridProps() {
      return {
        options: {
          rowHeight: 48,
          bodyHeight: "auto",
          showDummyRows: false,
          selectionUnit: "row",
					scrollX: false,
          scrollY: true,
          bodyHeight: this.$store.state.isMobile ? 'auto' : 480,          
          pageOptions: {
            useClient: true,
            perPage: this.perPage,
            visiblePages: 5,
          },
          rowHeaders: ['rowNum']
        },
        columns: [...this.gridHeader],
        data: this.profileManageList,
      };
    },
  },
  watch: {
    // profileManageList(val,oldVal){
    //   this.perPage='10'; 
    //   this.onPerPageInput({value:'10'}); 
    // },
    gridProps(val, oldVal) {
      this.$refs.tuiGrid.invoke("resetData", val.data);
    }
  },
  methods: {
    onGridClick(event) {
      if (
        event.targetType !== "columnHeader" &&
        event.columnName === "empNm"
      ) {
        const eventRow = this.$refs.tuiGrid.invoke("getRow", event.rowKey);

        if (this.$store.getters.isMobile) {
          this.$router.push("/m/board/noticeBoardView");
        } else {
          this.callProfileManageDetail(eventRow.empNo,'detail');
        }
      }
    },
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data.value);
    },
  },
};
</script>