<template lang="html">
  <div>
    <ModalPopup :title="'권한그룹상세'" :visible.sync="modalVisible" width="700px">
      <loading :active.sync="isLoading" :is-full-page="true"/>
      <div class="form-table">
        <table>
        <colgroup>
          <col class="col-form-title">
          <col style="width: 100%;">
          <col class="col-form-title">
          <col style="width: 100%;">
        </colgroup>
        <tbody>
          <tr>
            <th scope="row">상위그룹명</th>
            <td>
              <InputBox type="text" v-model="detail.upgroupauthNm" :value.sync="detail.upgroupauthNm" disabled="true" />
            </td>
            <th scope="row">상위그룹ID</th>
            <td>
              <InputBox type="text" v-model="detail.upgroupauthId" :value.sync="detail.upgroupauthId" disabled="true"/>
            </td>
          </tr>

          <tr>
            <th scope="row" class="required">그룹명</th>
            <td>
              <InputBox type="text" ref="groupauthNm" v-model="detail.groupauthNm" :value.sync="detail.groupauthNm" />
            </td>
            <th scope="row">그룹ID</th>
            <td>
              <InputBox type="text" v-model="detail.groupauthId" :value.sync="detail.groupauthId" disabled="true" />
            </td>
          </tr>

          <tr>
            <th scope="row" class="required">사용여부</th>
            <td colspan="3">
              <vSelect v-model="detail.useYn" :options="useYnList.options" :reduce="label => label.value" :clearable="false" :searchable="false"></vSelect>
            </td>
          </tr>
          <tr>
            <th scope="row">사유</th>
            <td colspan="3">
              <textarea style="width:460px; height:70px" v-model="detail.commentTxt"/>
            </td>
        </tr>
        </tbody>
      </table>
      </div>
      <div class="modal-popup-btn-box">
        <button type="button" class="btn btn-primary" @click.prevent.stop="save">신청</button>
        <button type="button" class="btn" @click.prevent.stop="closed">취소</button>
      </div>
    </ModalPopup>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import Loading from "vue-loading-overlay";
import 'vue-select/dist/vue-select.css';
import vSelect from 'vue-select'
import InputBox from '@/components/InputBox';
import ModalPopup from "@/components/ModalPopup";

export default {
  name: "GroupDetailPop",
  mixins: [Check,Empty],
  components: {
    InputBox,
    vSelect,
    loading: Loading,
    ModalPopup
  },
  data() {
    return {
      modalVisible: false,
      useYnList: {options:[{label: '선택하세요', value: '' },{label: '사용', value: 'Y' },{label: '미사용', value: 'N'}]},
      detail: { groupauthId: '', groupauthNm: '', upgroupauthId: '', upgroupauthNm: '', useYn: '', commentTxt: '' },
      isLoading: false,
    };
  },
  computed: {
    // ...mapState(["main"]),
    ...mapState({
      main: (state) => state.main,
    }),
  },
  created() {
  },
  methods: {
    open: function(info) {
      if( info.type == 'mod' ){
        let searchparms = {};
        searchparms.groupauthId = info.groupauthId;
        this.isLoading = true;
        this.$http.post( "/api/auth/getAuthGroupList",searchparms)
            .then((response) => {
              this.detail = response.data.contents[0];
            })
            .catch((e) => {
              console.log(e);
            })
            .finally(() => {
              this.isLoading = false;
            });
      } else {
        this.resetInfo();
        this.detail.upgroupauthId = info.upgroupauthId;
        this.detail.upgroupauthNm = info.upgroupauthNm;
      }
      this.modalVisible = true;
    },
    closed (obj) {
      this.modalVisible = false;
      this.resetInfo();
      //부모 함수 콜백
      this.$emit('closedPop', obj);
    },
    save(){
      if(!this.validation()){return false;}

      let apiUrl = "/api/auth/insertGroupAuth";
      if( this.isNotEmpty(this.detail.groupauthId) ){
        apiUrl = "/api/auth/updateGroupAuth";
      }

      this.isLoading = true;
      this.$http.post( apiUrl, this.detail)
          .then((response) => {
            if(response.data.statusCode != 200){
              alert('오류가 발생하였습니다.!!');
              return false;
            } else {
              alert(response.data.message);
              this.closed('success');
            }
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    validation(){
      if( this.isEmpty(this.detail.groupauthNm) ){
        alert('그룹명을 입력해 주세요.');
        this.$refs.groupauthNm.focus();
        return false;
      }

      let bytes = this.maxByteCheck(this.detail.groupauthNm);
      if( bytes > 50 ){
        this.detail.groupauthNm = this.detail.groupauthNm.substring(0, 50);
        alert('그룹명을 50 바이트 이하로 입력해 주세요.');
        this.$refs.groupauthNm.focus();
        return false;
      }

      if( this.isEmpty(this.detail.useYn) ){
        alert('사용여부를 선택해 주세요!!!');
        return false;
      }

      bytes = this.maxByteCheck(this.detail.commentTxt);
      if( bytes > 4000 ){
        "현재 " + bytes + "바이트 입력하셨습니다.\n 4000 바이트 이하로 입력하세요."
        this.detail.commentTxt = this.detail.commentTxt.substring(0, 4000);
        return false;
      }

      return true;
    },
    resetInfo(){
      this.detail.groupauthId = '';
      this.detail.groupauthNm = '';
      this.detail.commentTxt = '';
      this.detail.upgroupauthId = '';
      this.detail.upgroupauthNm = '';
      this.detail.useYn = '';
    }
  }
}
</script>

<style lang="css" scoped>

</style>
