<template>
  <ModalPopup
    title="휴가리스트"
    :visible.sync="modalVisible"
    width="1300px"
  >
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="contents-wrap">
    <div class="tbl-row">
      <table>
        <colgroup>
          <col class="col-row-title" />
          <col />
          <col class="col-row-title" />
          <col />
        </colgroup>
        <tbody>
          <tr>
            <th scope="row">직원명</th>
            <td>{{ vacationInfo.empNm }}</td>
            <th scope="row">년</th>
            <td>{{ vacationInfo.year }}</td>
          </tr>
          <tr>
            <th scope="row">입사년도</th>
            <td colspan="3">{{ vacationInfo.joinDt }}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div>
			<div class="vacation-info-box">
        <div class="vacation-info-cell">
          <span class="vacation-info-label">총 연차 : {{ vacationInfo.vacTotalCnt }} ( 기본 연차 {{ vacationInfo.homefaxTxt }} / 이월연차 {{ vacationInfo.carriedVac }} )</span>
        </div>
        <div class="vacation-info-cell">
          <span class="vacation-info-label">사용 연차 :</span>
          <span class="vacation-info-value">{{ vacationInfo.vacUsed }}</span>
        </div>
        <div class="vacation-info-cell">
          <span class="vacation-info-label">잔여 연차 :</span>
          <span class="vacation-info-value remain-value" :class="{ 'is-minus': vacationInfo.vacExtra < 0 }">{{ vacationInfo.vacExtra }}</span>
        </div>
		  </div>
		</div>

    <div class="grid-box">
      <div class="grid-tool-box">
          <div></div>    
      </div>
      <grid 
          class="tui-grid"
          ref="tuiGrid"
          :options="gridProps.options"
          :data="gridProps.data"
          :columns="gridProps.columns"
          @click="onGridClick"
      />
    </div>
    <VacationPopup ref="vacPop" :adminYn="'Y'" @closedVacationPopup="closedVacationPopup"/>
  </div> 
  </ModalPopup>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { Grid } from "@toast-ui/vue-grid";
import { Grids } from "@/mixins/Grids";
import ModalPopup from "@/components/ModalPopup";
import { Empty } from "@/mixins/Empty";
import VacationPopup from "@/views/myPage/pop/VacationPopup";
import Loading from "vue-loading-overlay";
import {
	CheckboxRenderer,
	CellButtonRenderer,
	ValidTextRenderer,
	ClickableTextRenderer
} from '@/components/renderer/GridRenderer';

export default {
  name: "VacationList",
  mixins: [Empty, Grids],
  components: {
    grid: Grid,
    ModalPopup,
    VacationPopup,
    Loading,
  },
  props:[
    "vacationInfo"
  ],
  data() {
    return {
      gridHeader: [
        {
          header: "휴가번호",
          name: "vacNo",
          width: 0,
          align: "center",
          hidden: true,
        },
        {
          header: "신청일자",
          name: "requestYmd",
          width: 150,
          align: "center",
          sortable: true,
        },
        {
          header: "휴무종류",
          name: "vacTypNm",
          formatter: this.gDetail,
          width: 150,
          align: "center",
          sortable: true,
        },
        {
          header: "휴가기간",
          name: "period",
          width: 300,
          align: "center",
          sortable: true,
        },
        {
          header: "확정여부",
          name: "confirmState",
          width: 120,
          align: "center",
          sortable: true,
        },
        {
          header: "확정일",
          name: "confirmYmd",
          width: 150,
          align: "center",
          sortable: true,
        },
        {
          header: "확정자",
          name: "confirmEmpNm",
          width: 150,
          align: "center",
          sortable: true,
        },
        {
          header: "PDF다운",
          name: "pdfDownload",
          width: 150,
          formatter: this.gLink,
          align: "center",
          formatter() {
              return `
                <button class="btn btn-inner">
                  <span class="ico ico-pdf"></span>PDF
                </button>
              `;
            },
        },
      ],
      modalVisible: false,
      vacationList: [],
      isLoading: false,
      fullPage: true,
    };
  },
  computed: {
    gridProps() {
      return {
        options: {
          rowHeight: 48,
          scrollX: false,
          scrollY: true,
          bodyHeight: 476,
          showDummyRows: true,
          selectionUnit: "row",
        },
        columns: [...this.gridHeader],
        data: this.vacationList,
      };
    },
  },
  watch: {
    mobileHidden(val, oldVal) {},
    gridProps(val, oldVal) {
      this.$refs.tuiGrid.invoke("resetData", val.data);
    },
  },
  methods: {
    // 상세 팝업 오픈
    openVacationListPop(empNo, year) {
      
      if(this.isEmpty(empNo)) {
        alert("회원번호가 없습니다.");
        return false;
      }

      this.isLoading = true;
      this.modalVisible = true;

      //this.vacationList = [];
      let params = {};
      params.empNo = empNo;
      params.year = year;
      
      const apiUrl = "/api/report/vacation-state-history";
      const data = this.$http
        .post(apiUrl, params)
        .then((solve) => {

          if (solve.data.result) {
            this.vacationList = solve.data.data;
          } else {
            throw new Error("연차정보를 가져오는데 실패하였습니다.");
          }
        })
        .catch((e) => {
          console.log(e);
        }) .finally(() => {
          this.isLoading = false;
        });
      
    },
    // 그리드 클릭
    onGridClick(event) {
      if (event.targetType !== "columnHeader") {
        if (event.columnName === "vacTypNm") {
          const rowData = this.$refs.tuiGrid.invoke("getRow", event.rowKey);
          this.$refs.vacPop.open(rowData.vacNo);
          
        } else if (event.columnName == "pdfDownload") {
          const rowData = this.$refs.tuiGrid.invoke("getRow", event.rowKey);
          this.downloadPdf(rowData);
        }
      }
    },
    downloadPdf(rowData) {

      this.isLoading = true;
      let vacNo = {
        vacNo: rowData.vacNo,
        type: "download",
      };
      const profileDownload = this.$http
        .post("/api/utils/report/getReportPrint", vacNo, {
          responseType: "arraybuffer",
        })
        .then((solve) => {

          if (solve.status === 200) {
            let fileName = "report2.pdf";
            let blob = new Blob([solve.data], {
              type: solve.headers["content-type"],
            });
            let link = document.createElement("a");
            link.href = window.URL.createObjectURL(blob);
            link.target = "_self";
            link.download = fileName;
            link.click();
          } else {
            alert(2);
            throw new Error("근태신청서 다운로드를 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
          throw new Error("근태신청서 다운로드를 실패하였습니다.");
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    closedVacationPopup (rtnObj) {
      if( !this.isEmpty(rtnObj)){
        if(rtnObj == 'success'){this.search();}
      }
    },
  },
};
</script>
