<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="page-title-box">
      <h1 class="page-title">임직원 연차 사용 통계</h1>
      <ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
        <li class="breadcrumb">홈</li>
        <li class="breadcrumb">보고자료</li>
        <li class="breadcrumb">임직원 연차 사용 통계</li>
      </ol>
    </div>
    <div class="search-input-box">
      <div class="search-input-row">
        <div class="search-item">
          <label for="boardAuthor">년</label>
          <div class="search-value">
            <vSelect
              id="searchYear"
              v-model="searchData.year"
              :options="options.yearList"
              :clearable="false"
              :searchable="true"
            />
          </div>
        </div>
        <div class="search-item">
          <label for="regDateFrom">직원명</label>
          <div class="search-value date-range-box">
            <InputBox type="text" :value.sync="searchData.empNm"/>
          </div>
        </div>
        <div class="search-item confirm-box">
          <button type="button" class="btn" @click="excelDownload">엑셀다운로드</button>
          <button type="button" class="btn btn-primary" @click.passive.stop="search">검색</button>
        </div>
      </div>
    </div>
    <div class="grid-box">
      <grid 
          class="tui-grid"
          ref="tuiGrid"
          :options="gridProps.options"
          :data="gridProps.data"
          :columns="gridProps.columns"
          @click="onGridClick"
      />
    </div>
    
    <VacationListPop
      ref="vacationListPop"
      :vacationInfo="vacationInfo"
    />
  </div>
</template>
<script>
import { mapActions } from "vuex";
import { Grid } from "@toast-ui/vue-grid";
import { Grids } from "@/mixins/Grids";
import vSelect from "vue-select";
import InputBox from "@/components/InputBox";
import DatePicker from "@/components/Datepicker";
import ModalPopup from "@/components/ModalPopup";
//import OperatingRatioDetailPop from "@/views/report/pop/OperatingRatioDetailPop";
import VacationListPop from "@/views/report/pop/VacationListPop";
import { Empty } from "@/mixins/Empty";
import { DateFunc } from "@/mixins/DateFunc";
import Loading from "vue-loading-overlay";

export default {
  name: "VacationHistoryList",
  mixins: [Empty, DateFunc, Grids],
  props: ["typeCode"],
  components: {
    grid: Grid,
    vSelect,
    InputBox,
    DatePicker,
    ModalPopup,
   // OperatingRatioDetailPop,
    VacationListPop,
    Loading,
  },
  data() {
    return {
      gridHeader: [
        {
          header: "사원번호",
          name: "empNo",
          width: 0,
          align: "center",
          hidden: true,
        },
        {
          header: "사원명",
          name: "empNm",
          formatter: this.gDetail,
          width: 150,
          align: "center",
          sortable: true,
        },
        {
          header: "소속",
          name: "dptNm",
          width: 150,
          align: "center",
        },
        {
          header: "연도",
          name: "year",
          width: 110,
          align: "center",
        },
        {
          header: "기본 연차",
          name: "homefaxTxt",
          width: 150,
          align: "center",
        },
        {
          header: "이월 연차",
          name: "carriedVac",
          width: 150,
          align: "center",
        },
        {
          header: "총 연차",
          name: "vacTotalCnt",
          width: 150,
          align: "center",
        },
        {
          header: "사용 연차",
          name: "vacUsed",
          width: 150,
          align: "center",
        },
        {
          header: "잔여 연차",
          name: "vacExtra",
          width: 150,
          align: "center",
        },
      ],
      searchData: {
        year: {},
        empNm: "",
      },
      options: {
        yearList: [],
      },
      //휴가리스트 정보
      vacationInfo: {},
      vacationHistoryList: [],
      boardDetail: {},
      isLoading: false,
      fullPage: true,      
    };
  },
  created() {
    this.initValue();
    this.search();
  },
  computed: {
    mobileHidden() {
      return this.$store.getters.isMobile;
    },
    gridProps() {
      return {
        options: {
          rowHeight: 48,
          scrollX: false,
          scrollY: true,
          bodyHeight: 576,
          showDummyRows: false,
          selectionUnit: "row",
        },
        columns: [...this.gridHeader],
        data: this.vacationHistoryList,
      };
    },
  },
  watch: {
    mobileHidden(val, oldVal) {},
    // 라우터의 typeCode 변경에 따른 컴포넌트 내용 변경
    typeCode(val, oldVal) {
      this.callVacationHistoryInfo();
      this.resetSearch();
    },
    gridProps(val, oldVal) {
      this.$refs.tuiGrid.invoke("resetData", val.data);
    },
  },
  methods: {
    initValue() {
      const currentYear = new Date().getFullYear();
      
      let tempArr = [];
      for (let i = 2014; i <= currentYear+1; i++) {
        let obj = {};
        obj.label = i;
        obj.value = obj.label;
        tempArr.push(obj);
      }

      this.options.yearList = tempArr;
      this.searchData.year = currentYear;
    },
    //엑셀다운로드
    excelDownload() {
      
      this.isLoading = true;

      let param = {};
            
      if(typeof this.searchData.year == 'object') {
        param.year = this.searchData.year.value;
      } else {
        param.year = this.searchData.year;
      }
      
      const vacationDownload =  this.$http
        .post("/api/report/vacation-excel-download", param, 
        {
            responseType: 'arraybuffer',
        })
        .then((solve) => {
          
          if (solve.status === 200) {
              let fileName = '연차사용현황.xls';

              let blob = new Blob([solve.data], {type: solve.headers['content-type']})
              let link = document.createElement('a');
              link.href = window.URL.createObjectURL(blob);
              link.target = '_self';
              link.download = fileName;
              link.click();
              
          } else {
            throw new Error("엑셀 다운로드를 실패하였습니다.");
          }

          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
      });
      
    },
    //연차사용 현황(연도별) 정보 조회
    async callVacationHistoryInfo(params) {
      this.isLoading = true;

      if (params === void 0)
        params = {
          typCd: this.typeCode,
        };

      const apiUrl = "/api/report/vacation-history";
      const data = await this.$http
        .post(apiUrl, params)
        .then((solve) => {
          
          if (solve.data.result) {
            
            this.vacationHistoryList = solve.data.data;

          } else {
            throw new Error("목록을 가져오는데 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    // 그리드 클릭
    onGridClick(event) {
      if (event.targetType !== "columnHeader") {
        if (event.columnName === "empNm") {
          const eventRow = this.$refs.tuiGrid.invoke("getRow", event.rowKey);
          
          this.vacationInfo = eventRow;

          this.$refs.vacationListPop.openVacationListPop(eventRow.empNo, eventRow.year);
        }
      }
    },
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data.value);
    },
    // 검색
    search() {
      
      let yearVal =  this.searchData.year;

      if(typeof this.searchData.year == 'object') {
        yearVal = this.searchData.year.value;
      }

      const param = {
        year: yearVal,
        empNm: this.searchData.empNm,
      };

      this.callVacationHistoryInfo(param);
    },
    // 검색조건 초기화
    resetSearch() {
      this.searchData.titleTxt = "";
      this.searchData.regstaffNm = "";
      this.$refs.fromDate.instance.setDate(null);
      this.$refs.toDate.instance.setDate(null);
    },
  },
};
</script>
