<template>
	<ModalPopup :title="'패스워드변경 - 마스터'" type="type-black" :visible.sync="modalVisible" width="500px">
    <p class="modal-box-title">로그인ID와 비밀번호는 4~12자리로 입력하세요.</p>
    <div class="modal-white-box">
				<div class="form-table">
					<table>
						<colgroup>
							<col class="col-form-title" style="width: 130px;">
							<col>
						</colgroup>
						<tbody>
							
              <tr>
								<th scope="row" class="required">새비밀번호</th>
								<td>
									<InputBox type="password" v-model="passwdInfo.newPw" :value.sync="passwdInfo.newPw" />
								</td>
							</tr>
							<tr>
								<th scope="row" class="required">새비밀번호확인</th>
								<td>
									<InputBox type="password" v-model="passwdInfo.checkPw" :value.sync="passwdInfo.checkPw" />
								</td>
							</tr>
						</tbody>
					</table>
				</div>
		</div>
		<div class="modal-popup-btn-box">
			<button type="button" class="btn btn-primary" @click.prevent.stop="save">수정</button>
			<button type="button" class="btn" @click.prevent.stop="closed">닫기</button>
		</div>
    
  </ModalPopup>
</template>

<script>
import { mapState, mapActions } from "vuex";
import 'vue-select/dist/vue-select.css';

import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";

import InputBox from '@/components/InputBox';

import ModalPopup from "@/components/ModalPopup";


export default {
  name: "PwdChangeMasterPopup",
  mixins: [Check,Empty],
  components: {
    InputBox,
    ModalPopup
  },
  props:{
    
  },
  mounted() {
    
  },
  data() {
    return {
      modalVisible: false,
      passwdInfo: { newPw: '' , 
                    checkPw: '',
                    masterYn: 'Y',
                    empNo: ''
      },
    };
  },
  computed: {
    ...mapState({
      main: (state) => state.main,
    }),
  },
  created() {
    this.token();
  },
  methods: {
    
    open(empNo) {

      if(this.isEmpty(empNo) ){
        //임직원번호가 없으면 alert 메세지 노출
        alert('임직원번호가 없습니다.');
        return false;
      } else {

        this.passwdInfo.empNo = empNo;
        
      }

      this.modalVisible = true;
    },
    closed (obj) {
      this.modalVisible = false;
      this.resetInfo();
      //부모 함수 콜백
      this.$emit('closedPasswdChangePopup', obj);
    },
    save(){      
      if( !this.validation() ){ return false; }

      this.$http.post("/api/member/setPasswordChange", 
                this.passwdInfo,
              )
              .then((response) => {
                
                if(response.data.statusCode != 200){
                  alert(response.data.message);
                  return false;
                } else {
                  alert("비밀번호가 변경되었습니다.");
                }
                
                this.closed('success');
              }
            );
      
    },
    validation() {

      if( this.isEmpty(this.passwdInfo.newPw) ){
        alert('새비밀번호를 입력해 주세요');
        return false;
      }

      if(this.passwdInfo.newPw.length < 4 || this.passwdInfo.newPw.length > 12) {
        alert('새비밀번호를 4~12자리로 입력하세요');
        return false;
      }

      if( this.isEmpty(this.passwdInfo.checkPw) ){
        alert('새비밀번호 확인을 입력해 주세요');
        return false;
      }

      if(this.passwdInfo.checkPw.length < 4 || this.passwdInfo.checkPw.length > 12) {
        alert('새비밀번호 확인을 4~12자리로 입력하세요');
        return false;
      }

      if(this.passwdInfo.newPw != this.passwdInfo.checkPw){
			  alert('새 비밀번호와 비밀번호 확인이 일치 하지 않습니다.'); 
			  return false;
		  } 

      if(!confirm("비밀번호를 변경하시겠습니까?")){ 
        return false; 
      }

      return true;
    },
    resetInfo() { 

      this.passwdInfo.newPw = '';
      this.passwdInfo.checkPw = '';

    },  
  },
};
</script>