<template>
  <modal-popup
    :title="'경조메일 수신자 등록/상세'"
    :visible="seen"
    @update:visible="close"
    width="900px"
    height="400px"
  >
    <div class="grid-tool-box">
      <div></div>
      <div>
        <button
          v-if="isDetail"
          type="button"
          class="btn btn-primary"
          @click="modify"
        >
          수정
        </button>
        <button v-else type="button" class="btn btn-primary" @click="submit">
          저장
        </button>
        <button type="button" class="btn" @click="close">닫기</button>
      </div>
    </div>
    <div class="tbl-row write-mode">
      <table>
        <colgroup>
          <col class="col-form-title" />
          <col />
          <col class="col-form-title" />
          <col />
        </colgroup>
        <tbody>
          <tr>
            <template v-if="isDetail">
              <th scope="row">이름</th>
              <td>
                <p>{{ form.empNm }}</p>
              </td>
            </template>
            <template v-else>
              <th scope="row" class="required">부서/팀</th>
              <td>
                <v-select
                  v-model="form.dptNo"
                  :reduce="(dpt) => dpt.code"
                  :required="!form.dptNo"
                  :options="options.dptNoList"
                  :clearable="false"
                  :searchable="false"
                  @input="filterEmp"
                />
              </td>
            </template>
            <th scope="row" class="required">사용여부</th>
            <td>
              <v-select
                v-model="form.useYn"
                :reduce="(yn) => yn.value"
                :required="!form.useYn"
                :options="options.useYnList"
                :clearable="false"
                :searchable="false"
              />
            </td>
          </tr>
          <template v-if="isDetail">
            <tr>
              <th scope="row">부서/팀</th>
              <td>
                <p>{{ form.dptNm }}</p>
              </td>
              <th scope="row">직책</th>
              <td>
                <p>{{ form.gradeNm }}</p>
              </td>
            </tr>
          </template>
          <template v-else>
            <tr>
              <th scope="row" class="required">사원명</th>
              <td colspan="3">
                <v-select
                  v-model="form.empNo"
                  :reduce="(emp) => emp.value"
                  :required="!form.empNo"
                  :options="options.empNoList"
                  :clearable="false"
                  :searchable="false"
                />
              </td>
            </tr>
          </template>
        </tbody>
      </table>
    </div>
  </modal-popup>
</template>

<script>
import "vue-select/dist/vue-select.css";

import { mapState } from "vuex";
import ModalPopup from "@/components/ModalPopup";
import vSelect from "vue-select";
import InputBox from "@/components/InputBox";

export default {
  name: "FamilyEventMailManagePop",
  components: {
    ModalPopup,
    InputBox,
    vSelect,
  },
  props: ["isDetail"],
  data() {
    return {
      seen: false,
      form: {
        menuId: "10093",
        empNo: "",
        dptNo: "",
        empNm: "",
        dptNm: "",
        gradeNm: "",
        useYn: "Y",
      },
      options: {
        dptNoList: [{ label: "전체", code: "" }],
        empNoList: [{ label: "전체", value: "" }],
        useYnList: [
          { label: "사용", value: "Y" },
          { label: "미사용", value: "N" },
        ],
      },
    };
  },
  created() {
    if (!this.isDetail) {
      this.$http
        .get("/api/system/family-event-mail/pop", {})
        .then((response) => {
          if (response.data) {
            for (const dptNo of response.data.data.dptNoList) {
              this.options.dptNoList.push(dptNo);
            }
          }
        })
        .catch((e) => {
          console.error(e);
        });
    }
  },
  methods: {
    open() {
      this.seen = true;
    },
    close() {
      this.resetForm();
      this.seen = false;
    },
    resetForm() {
      Object.assign(this.$data.form, this.$options.data().form);
    },
    openDetail(param) {
      this.$http
        .post("/api/system/family-event-mail/detail", param)
        .then((response) => {
          if (response.data.data) {
            const detail = response.data.data;
            this.form.empNm = detail.empNm;
            this.form.dptNm = detail.dptNm;
            this.form.gradeNm = detail.gradeNm;
            this.form.empNo = detail.empNo;
            this.form.menuId = detail.menuId;
            this.form.useYn = detail.useYn;

            this.seen = true;
          }
        })
        .catch((e) => {
          console.error(e);
        });
    },
    submit() {
      let param = {
        menuId: this.form.menuId,
        empNo: this.form.empNo,
        useYn: this.form.useYn,
      };

      this.$http
        .post("/api/system/family-event-mail/create", param)
        .then((response) => {
          if (response) {
            alert(response.data.data.msg);
            this.$emit("created");
            this.seen = false;
          }
        })
        .catch((e) => {
          console.error(e);
        });
    },
    modify() {
      let param = {
        menuId: this.form.menuId,
        empNo: this.form.empNo,
        useYn: this.form.useYn,
      };

      this.$http
        .post("/api/system/family-event-mail/update", param)
        .then((response) => {
          if (response) {
            alert(response.data.data.msg);
            this.$emit("modified");
            this.seen = false;
          }
        })
        .catch((e) => {
          console.error(e);
        });
    },
    filterEmp(dptNo) {
      this.$http
        .post("/api/member/getEmployeeList", { dptNo })
        .then((response) => {
          if (response) {
            let empNoList = [
              { label: "전체", value: "" },
              ...response.data.contents,
            ];
            this.$set(this.options, "empNoList", empNoList);
          }
        })
        .catch((e) => {
          console.error(e);
        });
    },
  },
};
</script>
