import Vue from "vue";
import App from "./App.vue";
import router from "./router";
// import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import axios from "axios";
import vueMoment from "vue-moment";
import store from './store'
import vSelect from 'vue-select';
import UUID from 'vue-uuid';

vSelect.props.components.default = () => ({
	/*Deselect: {
		render: createElement => createElement('span', ''),
	},*/
	OpenIndicator: {
		render: createElement => createElement('img', {attrs: {'src': require('./assets/images/ui/ui_form_select_arrow.png')}}),
	},
});

Vue.component(vSelect);
Vue.use(UUID);
Vue.use(vueMoment);

Vue.config.silent = true;
Vue.config.productionTip = false;
//Vue.config.devtools = false;

//공통으로 처리하기 위해서
axios.defaults.headers.common['Content-Type'] = 'application/json';

axios.interceptors.request.use(
	config => {
		let token = localStorage.getItem('token');
		if("/api/member/jwt/refreshVerify" === config.url) {
			token = sessionStorage.getItem("refreshtoken");
		}
		config.headers['Authorization'] = token;
		return config;
	},
	error => {
		Promise.reject(error);
	}
);

axios.interceptors.response.use(response =>{
		return response;
	}, function (error) {
		const originalRequest = error.config;

		if(error.response.status === 401 && originalRequest.url === "/api/member/jwt/refreshVerify") {
			sessionStorage.removeItem('refreshtoken');
			localStorage.removeItem('token');
			router.push('/');
			return Promise.reject(error);
		}

		if(error.response.status === 401 && !originalRequest._retry) {
			let refreshToken = sessionStorage.getItem('refreshtoken');
			originalRequest._retry = true;
			return axios.post('/api/member/jwt/refreshVerify', {}, {})
				.then((res) => {
					if(res.headers.authorization !== null &&  res.headers.authorization !== "undefined") {
						localStorage.setItem('token', res.headers.authorization);
						originalRequest.headers["Authorization"] = res.headers.authorization;
						return axios(originalRequest);
					} else {
						sessionStorage.removeItem('refreshtoken');
						return router.push('/');
					}
				})
		}
		return Promise.reject(error).catch(()=>{});
	});

Vue.prototype.$http = axios;
axios.post("/api/code/getCommonCode", {}, {})
  .then((response) => {
	  store.state.code.codeInfo = response.data;
	  new Vue({
	    router,
	    store,
	    render: (h) => h(App)
	  }).$mount("#app");
  });
