<template>
  <ModalPopup
    :title="'임직원등록'"
    type="type-black"
    :visible.sync="modalVisible"
    width="900px"
  >
    <div class="modal-white-box">
      <div class="form-table">
        <table>
          <colgroup>
            <col class="col-form-title" />
            <col style="width: 286px;" />
            <col class="col-form-title" />
            <col style="width: 266px;" />
          </colgroup>
          <tbody>
            <tr>
              <th scope="row" rowspan="6">
                <span class="offscreen">프로필 사진</span>
              </th>
              <td rowspan="6" class="vertical-middle">
                <div class="profile-img-box">
                  <span
                    class="profile-img"
                    :style="{ backgroundImage: 'url(' + this.bgImg + ')' }"
                  ></span>
                  <span class="profile-select">
                    <FileButton @change="onChangeImages" />
                  </span>
                </div>
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">임직원번호</th>
              <td>
                <InputBox
                  type="text"
                  :value.sync="memInfo.empNo"
                  disabled="true"
                />
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">로그인ID</th>
              <td>
                <InputBox
                  type="text"
                  ref="loginId"
                  :value.sync="memInfo.loginId"
                />
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">임직원명</th>
              <td>
                <InputBox type="text" ref="empNm" :value.sync="memInfo.empNm" />
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">비밀번호</th>
              <td>
                <div class="input-flex-box">
                  <InputBox
                    type="password"
                    ref="passwdTxt"
                    :value.sync="memInfo.passwdTxt"
                  />
                </div>
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">비밀번호 확인</th>
              <td>
                <div class="input-flex-box">
                  <InputBox
                    type="password"
                    ref="passwdTxt2"
                    :value.sync="memInfo.passwdTxt2"
                  />
                </div>
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">부서(팀)</th>
              <td>
                <vSelect
                  v-model="memInfo.dptNo"
                  ref="dptNo"
                  :options="code.dptCds"
                  :clearable="false"
                  :searchable="false"
                  :reduce="(label) => label.code"
                />
              </td>
              <th scope="row" class="required">직책</th>
              <td>
                <vSelect
                  v-model="memInfo.positionCd"
                  ref="positionCd"
                  :options="code.positionCds"
                  :clearable="false"
                  :searchable="false"
                  :reduce="(label) => label.code"
                />
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">직급</th>
              <td>
                <vSelect
                  v-model="memInfo.gradeCd"
                  ref="gradeCd"
                  :options="code.gradeCds"
                  :clearable="false"
                  :searchable="false"
                  :reduce="(label) => label.code"
                />
              </td>
              <th scope="row" class="required">E-Mail</th>
              <td>
                <InputBox
                  type="email"
                  ref="emailTxt"
                  :value.sync="memInfo.emailTxt"
                  inputWidth="100%"
                />
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">휴대폰번호</th>
              <td>
                <InputBox
                  type="text"
                  ref="mobilTxt"
                  :value.sync="memInfo.mobilTxt"
                  maxlength="10"
                />
              </td>
              <th scope="row">전화번호</th>
              <td>
                <InputBox type="text" :value.sync="memInfo.comptelTxt" />
              </td>
            </tr>
            <tr>
              <th scope="row">주소</th>
              <td colspan="3">
                <div class="address-box">
                  <InputBox
                    class="zip-code"
                    type="text"
                    :value.sync="memInfo.zipnumCd"
                    disabled="true"
                  />
                  <button type="button" class="btn" @click="openDaumAddrPop">
                    우편번호찾기
                  </button>
                  <InputBox
                    class="address-pre"
                    type="text"
                    :value.sync="memInfo.zipaddrTxt"
                    disabled="true"
                  />
                  <InputBox
                    class="address-sub"
                    type="text"
                    :value.sync="memInfo.addressTxt"
                  />
                </div>
              </td>
            </tr>
            <tr v-if="groupAuthCode == '10001'">
              <th scope="row" class="required">권한그룹</th>
              <td colspan="3" class="td-rdo">
                <vSelect
                  v-model="memInfo.groupauthId"
                  ref="groupauthId"
                  :options="groupAuthList"
                  :clearable="false"
                  :searchable="false"
                  :reduce="(label) => label.code"
                />
              </td>
            </tr>
            <tr>
              <th scope="row">프로젝트<br />메일수신 여부</th>
              <td colspan="3" class="td-rdo">
                <div class="rdo-box">
                  <CheckBox
                    class="ui-chk"
                    v-model="memInfo.pmYn"
                    :value="'Y'"
                    :text="'메일 수신자로 추가할 경우 체크해주세요.'"
                    :checked="'Y' == memInfo.pmYn"
                    @checkChange="pmYnCheckboxChange"
                  />
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <p class="modal-box-title">부가정보</p>
    <div class="modal-white-box">
      <div class="form-table">
        <table>
          <colgroup>
            <col class="col-form-title" />
            <col style="width: 286px;" />
            <col class="col-form-title" />
            <col style="width: 266px;" />
          </colgroup>
          <tbody>
            <tr>
              <th scope="row">생년월일</th>
              <td>
                <div class="sun-luna-date-box">
                  <DatePicker ref="birthdayTxt" />
                  <RadioButton
                    :text="'양력'"
                    :value="'Y'"
                    name="radioSolarYn"
                    :checked="'Y' == memInfo.solarYn"
                    @radioChange="solarYnRadioChange"
                  />
                  <RadioButton
                    :text="'음력'"
                    :value="'N'"
                    name="radioSolarYn"
                    :checked="'N' == memInfo.solarYn"
                    @radioChange="solarYnRadioChange"
                  />
                </div>
              </td>
              <th scope="row">성별</th>
              <td class="td-rdo">
                <div class="rdo-box">
                  <RadioButton
                    :text="'남자'"
                    :value="'M'"
                    name="radioGenderType"
                    :checked="'M' == memInfo.genderType"
                    @radioChange="genderTypeRadioChange"
                  />
                  <RadioButton
                    :text="'여자'"
                    :value="'F'"
                    name="radioGenderType"
                    :checked="'F' == memInfo.genderType"
                    @radioChange="genderTypeRadioChange"
                  />
                </div>
              </td>
            </tr>
            <tr>
              <th scope="row">결혼기념일</th>
              <td>
                <div class="sun-luna-date-box">
                  <DatePicker ref="marryYmd" />
                  <RadioButton
                    :text="'기혼'"
                    :value="'Y'"
                    name="radioMarryYn"
                    :checked="'Y' == memInfo.marryYn"
                    @radioChange="marryYnRadioChange"
                  />
                  <RadioButton
                    :text="'미혼'"
                    :value="'N'"
                    name="radioMarryYn"
                    :checked="'N' == memInfo.marryYn"
                    @radioChange="marryYnRadioChange"
                  />
                </div>
              </td>
              <th scope="row">집전화번호</th>
              <td>
                <InputBox type="text" :value.sync="memInfo.telTxt" />
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">입사일</th>
              <td>
                <DatePicker ref="joinDt" />
              </td>
              <th scope="row">퇴사일</th>
              <td>
                <DatePicker ref="outDt" />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="modal-popup-btn-box">
      <button type="button" class="btn btn-primary" @click.prevent.stop="save">
        등록
      </button>
      <button type="button" class="btn" @click.prevent.stop="closed">
        닫기
      </button>
    </div>
    <daum-address-pop
      ref="daumAddrPop"
      :width="'750px'"
      @closedAddrPop="closedAddrPop"
    />
  </ModalPopup>
</template>

<script>
import { mapState, mapActions } from "vuex";
import "vue-select/dist/vue-select.css";
import vSelect from "vue-select";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import CheckBox from "@/components/CheckBox";
import InputBox from "@/components/InputBox";
import DatePicker from "@/components/Datepicker";
import ModalPopup from "@/components/ModalPopup";
import RadioButton from "@/components/RadioButton";
import FileButton from "@/components/FileButton";
import daumAddressPop from "@/views/myPage/pop/DaumAddressPop";

export default {
  name: "MemberRegiPopup",
  mixins: [Check, Empty],
  components: {
    DatePicker,
    CheckBox,
    InputBox,
    vSelect,
    ModalPopup,
    RadioButton,
    FileButton,
    daumAddressPop,
  },
  props: {
    adminYn: {
      default: "N",
    },
  },
  mounted() {},
  data() {
    return {
      codeSearchData: ["10000", "10300", "10100", "10500"],
      modalVisible: false,
      memInfo: {
        empNo: "",
        loginId: "",
        empNm: "",
        passwdTxt: "",
        passwdTxt2: "",
        dptNo: "",
        positionCd: "",
        gradeCd: "",
        emailTxt: "",
        mobilTxt: "",
        comptelTxt: "",
        zipnumCd: "",
        zipaddrTxt: "",
        addressTxt: "",
        pmYn: "",
        birthdayTxt: "",
        solarYn: "Y",
        genderType: "M",
        marryYmd: "",
        marryYn: "N",
        telTxt: "",
        joinDt: "",
        outDt: "",
        profileImg: "",
        groupauthId: "",
      },
      code: {
        dptCds: [],
        positionCds: [],
        gradeCds: [],
        outCds: [],
      },
      groupAuthCode: "",
      groupAuthList: [],
      bgImg: "/img/common_nav_profile_no_img_126.84e2c562.png",
    };
  },
  computed: {
    ...mapState({
      main: (state) => state.main,
    }),
  },
  created() {
    this.token();
  },
  methods: {
    onChangeImages(e) {
      const fileName = e.event.target.files[0].name;
      const file = e.event.target.files[0];
      this.bgImg = window.URL.createObjectURL(file);

      //이미지 업로드
      const fd = new FormData();
      fd.append("uploadFiles", e.event.target.files[0]);
      fd.append("editorYn", "N");

      this.$http
        .post("/api/common/fileUploadRename", fd, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          this.memInfo.profileImg = response.data.data[0].filePath;
          return response;
        })
        .catch((ex) => {
          alert("이미지 등록중 오류가 발생했습니다.");
          return false;
        });
    },
    open(dptNo) {
      this.memInfo.dptNo = dptNo;

      this.codeSearch();

      //this.memInfo.empNo = empNo;
      let parms = {};
      //parms.empNo = this.memInfo.empNo;
      //임직원번호 채번
      this.$http
        .post("/api/member/getEmpNo", parms)
        .then((response) => {
          if (response.data.statusCode != 200) {
            alert("오류가 발생했습니다.");
            return false;
          }

          let rtnObj = response.data.data.contents;
          this.memInfo.empNo = rtnObj;

          //로그인 권한그룹코드
          this.groupAuthCode = response.data.data.groupAuthCode;

          //권한그룹
          this.groupAuthList = response.data.data.groupAuthList;
          this.memInfo.groupauthId = { code: "10000", label: "직원" };
        })
        .catch((e) => {
          console.log("임직원번호 조회 error : ", e);
        });

      this.modalVisible = true;
    },
    closed(obj) {
      this.modalVisible = false;
      this.resetInfo();
      //부모 함수 콜백
      this.$emit("closedMemberRegiPopup", obj);
    },
    codeSearch() {
      let searchparms = {};
      searchparms.codes = this.codeSearchData;

      this.$http
        .post("/api/code/getCodeSearch", searchparms)
        .then((response) => {
          //코드 세팅
          this.code.dptCds = response.data.data[this.codeSearchData[0]];
          this.code.dptCds.unshift({ label: "선택", code: "" });

          if (typeof this.memInfo.dptNo === "object") {
            this.memInfo.dptNo = this.code.dptCds[0];
          }

          this.code.positionCds = response.data.data[this.codeSearchData[1]];
          this.code.positionCds.unshift({ label: "선택", code: "" });
          this.memInfo.positionCd = this.code.positionCds[0];

          this.code.gradeCds = response.data.data[this.codeSearchData[2]];
          this.code.gradeCds.unshift({ label: "선택", code: "" });
          this.memInfo.gradeCd = this.code.gradeCds[0];
        })
        .catch((e) => {
          console.log("공통코드 조회 error : ", e);
        });
    },
    openDaumAddrPop() {
      this.$refs.daumAddrPop.open();
    },
    closedAddrPop(obj) {
      this.memInfo.zipnumCd = obj.zonecode;
      this.memInfo.zipaddrTxt = obj.address;
    },
    save() {
      if (!this.validation()) {
        return false;
      }

      this.$http.post("/api/member/insert", this.memInfo).then((response) => {
        if (response.data.statusCode != 200) {
          alert(response.data.message);
          return false;
        }

        this.closed("success");
      });
    },
    onRadioChange(event) {
      this.vacTypCd = event.currentTarget.value;
    },
    onInputFocusOut(event) {
      let params = {};
      params.empNm = this.detail.vacDeputy;

      this.$http
        .post("/api/vacation/getTelNumberByName", JSON.stringify(params), {
          headers: {
            Authorization: localStorage.getItem("token"),
            "Content-Type": "application/json",
          },
        })
        .then((response) => {
          if (response.data.statusCode != 200) {
            alert("오류가 발생했습니다.");
            return false;
          }
          this.detail.vacTel = response.data.data.mobilTxt;
        });
    },
    validation() {
      if (this.isEmpty(this.memInfo.empNo)) {
        alert("임직원번호는 필수입니다.\n 관리자에게 문의하시기 바랍니다.");
        return false;
      }

      if (this.isEmpty(this.memInfo.loginId)) {
        alert("로그인ID는 필수 입력입니다.");
        this.$refs.loginId.focus();
        return false;
      }

      if (this.isEmpty(this.memInfo.empNm)) {
        alert("임직원명은 필수 입력입니다.");
        this.$refs.empNm.focus();
        return false;
      }

      if (this.isEmpty(this.memInfo.passwdTxt)) {
        alert("비밀번호는 필수 입력입니다.");
        this.$refs.passwdTxt.focus();
        return false;
      }

      if (this.isEmpty(this.memInfo.passwdTxt2)) {
        alert("비밀번호확인은 필수 입력입니다.");
        this.$refs.passwdTxt2.focus();
        return false;
      }

      if (this.memInfo.passwdTxt != this.memInfo.passwdTxt2) {
        alert(
          "비밀번호가 일치하지 않습니다.\n" +
            this.memInfo.passwdTxt +
            "/" +
            this.memInfo.passwdTxt2
        );
        return false;
      }

      let dptNo = this.memInfo.dptNo;

      if (typeof this.memInfo.dptNo == "object") {
        dptNo = this.memInfo.dptNo.code;
        this.memInfo.dptNo = this.memInfo.dptNo.code;
      }

      if (this.isEmpty(dptNo)) {
        alert("부서를 선택해주세요.");
        //this.$refs.dptNo.focus();
        return false;
      }

      let positionCd = this.memInfo.positionCd;

      if (typeof this.memInfo.positionCd === "object") {
        positionCd = this.memInfo.positionCd.code;
        this.memInfo.positionCd = this.memInfo.positionCd.code;
      }

      if (this.isEmpty(positionCd)) {
        alert("직책을 선택해주세요.");
        //this.$refs.positionCd.focus();
        return false;
      }

      let gradeCd = this.memInfo.gradeCd;

      if (typeof this.memInfo.gradeCd === "object") {
        gradeCd = this.memInfo.gradeCd.code;
        this.memInfo.gradeCd = this.memInfo.gradeCd.code;
      }

      if (this.isEmpty(gradeCd)) {
        alert("직급을 선택해주세요.");
        //this.$refs.gradeCd.focus();
        return false;
      }

      if (this.isEmpty(this.memInfo.emailTxt)) {
        alert("E-Mail은 필수 입력입니다.");
        this.$refs.emailTxt.focus();
        return false;
      }

      if (this.isEmpty(this.memInfo.mobilTxt)) {
        alert("휴대폰번호는 필수 입력입니다.");
        this.$refs.mobilTxt.focus();
        return false;
      }

      let groupauthId = this.memInfo.groupauthId;

      if (typeof this.memInfo.groupauthId === "object") {
        groupauthId = this.memInfo.groupauthId.code;
        this.memInfo.groupauthId = this.memInfo.groupauthId.code;
      }

      if (this.isEmpty(groupauthId)) {
        alert("권한그룹을 선택해주세요.");
        //this.$refs.groupauthId.focus();
        return false;
      }

      if (this.isNotEmpty(this.$refs.birthdayTxt.instance.getDate())) {
        this.memInfo.birthdayTxt = this.getDateByDelimeter(
          this.$refs.birthdayTxt.instance.getDate(),
          "-"
        );
      }

      if (this.memInfo.marryYn == "Y") {
        if (this.isEmpty(this.$refs.marryYmd.instance.getDate())) {
          alert("결혼기념일을 입력해 주세요");
          this.$refs.marryYmd.focus();
          return false;
        }
      }

      if (this.isNotEmpty(this.$refs.marryYmd.instance.getDate())) {
        this.memInfo.marryYmd = this.getDateByDelimeter(
          this.$refs.marryYmd.instance.getDate(),
          "-"
        );
      }

      if (this.isEmpty(this.$refs.joinDt.instance.getDate())) {
        alert("입사일은 필수 입력입니다.");
        this.$refs.joinDt.focus();
        return false;
      } else {
        this.memInfo.joinDt = this.getDateByDelimeter(
          this.$refs.joinDt.instance.getDate(),
          "-"
        );
      }

      return true;
    },
    resetInfo() {
      this.memInfo.loginId = "";
      this.memInfo.empNm = "";
      this.memInfo.passwdTxt = "";
      this.memInfo.passwdTxt2 = "";
      this.memInfo.emailTxt = "";
      this.memInfo.mobilTxt = "";
      this.memInfo.comptelTxt = "";
      this.memInfo.zipnumCd = "";
      this.memInfo.zipaddrTxt = "";
      this.memInfo.addressTxt = "";
      this.memInfo.pmYn = "";

      this.memInfo.birthdayTxt = "";
      this.memInfo.solarYn = "Y";
      this.memInfo.genderType = "M";
      this.memInfo.marryYmd = "";
      this.memInfo.marryYn = "N";
      this.memInfo.telTxt = "";
      this.memInfo.joinDt = "";
      this.memInfo.outDt = "";
    },
    solarYnRadioChange(e) {
      this.memInfo.solarYn = e.target.value;
    },
    genderTypeRadioChange(e) {
      this.memInfo.genderType = e.target.value;
    },
    marryYnRadioChange(e) {
      this.memInfo.marryYn = e.target.value;
    },
    pmYnCheckboxChange(e) {
      if (e.target.checked) {
        this.memInfo.pmYn = "Y";
      } else {
        this.memInfo.pmYn = "";
      }
    },
  },
};
</script>
