<template>
  <div class="contents-wrap">
    <p class="p-title">{{ menuName }}</p>
    <div>
      <div class="page-title-box">
        <h1 class="page-title"></h1>
        <ol class="breadcrumb-list">
          <li class="breadcrumb">홈</li>
          <li class="breadcrumb">마이 페이지</li>
          <li class="breadcrumb">{{ menuName }}</li>
        </ol>
      </div>
    </div>
    <div class="search-input-box" v-if="!this.$store.state.isMobile">
      <div class="search-input-row">
        <div class="search-item">
          <label for="searchYear">년도</label>
          <div class="search-value">
            <v-select
              id="searchYear"
              v-model="searchParam.year"
              :reduce="(opt) => opt.value"
              :options="options.yearList"
              :clearable="false"
              :searchable="true"
            ></v-select>
          </div>
        </div>
        <div class="search-item confirm-box">
          <button
            type="button"
            @click.passive.stop="search"
            class="btn btn-primary"
          >
            검색
          </button>
        </div>
      </div>
    </div>
    <div class="grid-footer-box">
      <p>
        * pdf 파일을 열어서 보기 위해서는 뷰어 프로그램이 필요합니다.
        <a href="https://get.adobe.com/kr/reader/" target="_blank"
          >Adobe Reader 설치하기</a
        >
      </p>
      <p>
        * 프로필변경에서 SIGN 이미지를 등록하시면 신청자와 확정자의 SIGN이
        출력됩니다.
      </p>
    </div>
    <div class="grid-box">
      <div class="grid-tool-box">
        <div></div>
        <div>
          <button type="button" class="btn btn-primary" @click="popUpRequest">
            경조금 신청
          </button>
          <button type="button" class="btn" @click="popUpCancel">
            신청 취소
          </button>
        </div>
      </div>
      <grid
        class="tui-grid"
        ref="tuiGrid"
        :data="gridProps.data"
        :columns="gridProps.columns"
        :options="gridProps.options"
        @click="onClick"
      />
      <div class="per-page-box" v-if="!this.$store.state.isMobile">
        <span class="total-count-text">
          총
          <span class="count">{{ totalCnt }}</span
          >개
        </span>
        <div class="select-box">
          <v-select
            v-model="perPage"
            :reduce="(opt) => opt.value"
            :options="options.pageList"
            :clearable="false"
            :searchable="false"
            @input="onPerPageInput"
          ></v-select>
        </div>
      </div>
    </div>
    <family-event-pop
      ref="requestPop"
      :isDetail="false"
      :descs="pop.descs"
      :upcodes="pop.upcodes"
      @created="search"
    ></family-event-pop>
    <family-event-pop
      ref="detailPop"
      :isDetail="true"
      :descs="pop.descs"
      :upcodes="pop.upcodes"
      @modified="search"
    ></family-event-pop>
    <family-event-cancel-pop
      ref="cancelPop"
      @canceled="search"
    ></family-event-cancel-pop>
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
  </div>
</template>

<script>
import { mapState } from "vuex";
import "tui-grid/dist/tui-grid.css";
import { Grid } from "@toast-ui/vue-grid";
import "vue-select/dist/vue-select.css";
import vSelect from "vue-select";
import { Check } from "@/mixins/Check";
import { Grids } from "@/mixins/Grids";
import FamilyEventPop from "@/views/myPage/pop/FamilyEventPop";
import FamilyEventCancelPop from "@/views/myPage/pop/FamilyEventCancelPop";
import Loading from "vue-loading-overlay";

import TuiGrid from "tui-grid";
TuiGrid.setLanguage("ko");

export default {
  name: "FamilyEventList",
  mixins: [Check, Grids],
  props: ["menuName"],
  components: {
    grid: Grid,
    vSelect,
    FamilyEventPop,
    FamilyEventCancelPop,
    loading: Loading,
  },
  data() {
    return {
      perPage: 10,
      totalCnt: 0,
      isLoading: false,
      fullPage: true,
      gridProps: {},
      searchParam: {
        year: {},
      },
      options: {
        yearList: [],
        pageList: [
          { label: "10", value: 10 },
          { label: "20", value: 20 },
          { label: "50", value: 50 },
          { label: "100", value: 100 },
        ],
      },
      pop: {
        descs: [],
        upcodes: ["11500", "11600"],
      },
      showReqModal: false,
    };
  },
  created() {
    this.token();
    this.gridProps = {
      options: {
        rowHeight: 48,
        scrollX: false,
        scrollY: true,
        bodyHeight: this.$store.state.isMobile ? "auto" : 480,
        showDummyRows: false,
        selectionUnit: "row",
        display: { noData: "No Data" },
        pageOptions: {
          useClient: true,
          perPage: this.perPage,
          visiblePages: 5,
        },
      },
      columns: [
        { header: "순번", name: "famEvnNo", hidden: true },
        { header: "신청일자", name: "requestDt", align: "center" },
        { header: "신청인", name: "empNm", align: "center" },
        {
          header: "경조구분",
          name: "famEvnNm",
          align: "center",
          formatter: this.gDetail,
        },
        { header: "경조일자", name: "famEvnDt", align: "center" },
        { header: "경조금액", name: "famEvnItem", align: "center" },
        { header: "증빙서류", name: "evidencialNm", align: "center" },
        { header: "상태", name: "confirmState", align: "center" },
        { header: "확정일", name: "approveDt", align: "center" },
        { header: "확정자", name: "approveNm", align: "center" },
        {
          header: "PDF 다운",
          name: "pdfDownload",
          align: "center",
          formatter: this.gLink,
        },
      ],
      data: [],
    };

    const currentYear = new Date().getFullYear();
    let tempArr = [];
    for (let i = 0; i < 5; i++) {
      let obj = {};
      obj.label = currentYear - i;
      obj.value = obj.label;
      tempArr.push(obj);
    }
    this.options.yearList = tempArr;
    this.searchParam.year = tempArr[0].value;

    this.$http
      .get("/api/myPage/family-event/pop", {})
      .then((response) => {
        if (response.data) {
          this.pop.descs = response.data.data.descs;
        }
      })
      .catch((e) => {
        console.error(e);
      });

    this.search();
  },
  methods: {
    onClick(ev) {
      if (ev.columnName == "famEvnNm") {
        const rowData = this.$refs.tuiGrid.invoke("getRow", ev.rowKey);

        this.$http
          .post("/api/myPage/family-event/detail/" + rowData.famEvnNo, {})
          .then((response) => {
            this.$refs.detailPop.openDetail(response.data.data);
          })
          .catch((e) => {
            console.error(e);
          });
      } else if (ev.columnName == "pdfDownload") {
        const rowData = this.$refs.tuiGrid.invoke("getRow", ev.rowKey);
        this.downloadPdf(rowData);
      }
    },
    search() {
      let params = {};
      params.year = this.searchParam.year;

      this.isLoading = true;
      this.$http
        .post("/api/myPage/family-event/list", params)
        .then((response) => {
          if (response.data.data) {
            this.totalCnt = response.data.data.contents.length;
            this.$refs.tuiGrid.invoke(
              "resetData",
              response.data.data.contents,
              {}
            );
          }
        })
        .catch((e) => {
          console.error(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data);
    },
    popUpRequest() {
      this.$refs.requestPop.open();
    },
    popUpCancel() {
      this.$refs.cancelPop.open();
    },
    downloadPdf(rowData) {
      this.isLoading = true;
      let param = {
        famEvnNo: rowData.famEvnNo,
        requestDt: rowData.requestDt,
        empNm: rowData.empNm,
      };
      const profileDownload = this.$http
        .post("/api/utils/report/getFamReport", param, {
          responseType: "arraybuffer",
        })
        .then((solve) => {
          if (solve.status === 200) {
            let fileName =
              rowData.empNm +
              "_경조금신청서_" +
              solve.headers.requestdt +
              ".pdf";
            let blob = new Blob([solve.data], {
              type: solve.headers["content-type"],
            });
            let link = document.createElement("a");
            link.href = window.URL.createObjectURL(blob);
            link.target = "_self";
            link.download = fileName;
            link.click();
          } else {
            alert(2);
            throw new Error("경조금신청서 다운로드를 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
          throw new Error("경조금신청서 다운로드를 실패하였습니다.");
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
  },
};
</script>
<style scoped lang="scss">
.p-title {
  margin-top: 25px;
  font-size: 24px;
  font-weight: 700;

  ~ div {
    margin-top: 15px;
  }
}
</style>
