import axios from "axios";
import router from "../router";
import { menu } from "@/store/menu";

export const login = {
    modules:{
    menu
    },
  state: {
    accessToken: null,
    userInfo: {
      loginId: "",
      passwdTxt: "",
    },
    errShow: false,
    errMsg: "",
  },
  getters: {
    // getUserName(state) {
    //   return state.userInfo.userName;
    // },
    // getPassword(state) {
    //   return state.userInfo.password;
    // },
  },
  mutations: {
    printErrMsg(state, payload) {
      this.errMsg = payload.errMsg;
      this.errShow = payload.errShow;
    },
  },
  actions: {
    printErrMsg({ commit }, payload) {
      commit("printErrMsg", payload);
    },
    handleLogin({ commit}, loginObj){
      let promise = axios
        .post("/api/member/jwt/login", loginObj)
      return promise;
    },
    handleLogout({commit}){
      let promise = axios
        .post("/api/member/jwt/logOut", {}, {})
        sessionStorage.removeItem("refreshtoken");
        localStorage.removeItem("token");
        return promise;
    },
  },
};
