<template>
	<div :id="targetId" class="tui-tree-wrap"></div>
</template>
<script>
import Tree from 'tui-tree';

let target;

export default {
	name: 'Tree',
	props: {
		data: {
			type: Array,
			default: function() {
				return [];
			},
		},
		nodeIdPrefix: {
			type: String,
			default: 'tui-tree-node-',
		},
		nodeDefaultState: {
			type: String,
			default: 'closed',
			validator: function (value) {
				return ['closed', 'opened'].indexOf(value) !== -1;
			}
		},
		stateLabels: {
			type: Object,
			default: function() {
				return {
					opened: '-',
					closed: '+',
				}
			},
		},
		template: {
			type: Object,
			default: function() {
				return {
					internalNode: ('<div class="tui-tree-content-wrapper">' +
						'<button type="button" class="tui-tree-toggle-btn tui-js-tree-toggle-btn">' +
						'<span class="tui-ico-tree"></span>' +
						'{{stateLabel}}' +
						'</button>' +
						'<span class="tui-tree-text tui-js-tree-text" style="cursor:pointer">' +
						'{{text}}' +
						'</span>' +
						'<input class="tui-tree-text tui-js-tree-value" type="hidden">' + '{{value}}' + '</input>' +
						'</div>' +
						'<ul class="tui-tree-subtree tui-js-tree-subtree">' +
						'{{children}}' +
					'</ul>'),
					leafNode: ('<div class="tui-tree-content-wrapper">' +
						'<button type="button" class=" tui-tree-file-btn tui-js-tree-toggle-btn">' +
						'<span class="tui-ico-tree"></span>' +
						'{{stateLabel}}' +
						'</button>' +
						'<span class="tui-tree-text tui-js-tree-text" style="cursor:pointer">' +
						'<span class="tui-tree-ico"></span>' +
						'{{text}}' +
						'</span>' +
						'<input class="tui-tree-text tui-js-tree-value" type="hidden">' + '{{value}}' + '</input>' +
					'</div>')
				}
			}
		},
		//Let us know the hostname. If you don't want to send the hostname, please set to false.
		usageStatistics: {
			type: Boolean,
			default: false,
		},
		//stateLabels 이아닌 text 부분 클릭시 토글 동작 여부
		textClickToggle: {
			type: Boolean,
			default: false,
		}
	},
	data() {
		return {
			uid: null,
			targetId: null,
			instance: null,
		}
	},
	created() {
		this.uid = this._uid;
		this.targetId = 'tui-tree-' + this.uid;
	},
	mounted() {
		this.setTreeData(this.data);
	},
	methods: {
		onNodeClick(event) {
			// Branch
			if (!this.instance.isLeaf(event.nodeId) && this.textClickToggle) {
				this.instance.toggle(event.nodeId);
			}
			//else {
				this.$emit('selectLeaf', event);
			//}
		},
		setTreeData(treeData){
			//console.log('setTreeData');
			target = document.getElementById(this.targetId);
			this.instance = new Tree(target, {
				data: treeData,
				nodeDefaultState: this.nodeDefaultState,
				template: this.template,
			});
			this.instance.enableFeature('Selectable', {
				selectedClassName: 'tui-tree-selected',
			}).on('select', this.onNodeClick);
		},
		clearTreeData(){
			//console.log('clearTreeData');
			this.setTreeData([]);
		}
	}
}
</script>
