<template lang="html">
  <div>
    <ModalPopup :title="'프로젝트팀 상세'" :visible.sync="modalVisible" width="800px">
      <loading :active.sync="isLoading" :is-full-page="true"/>
      <div class="form-table">
        <table>
        <colgroup>
          <col class="col-form-title">
          <col style="width: 100%;">
          <col class="col-form-title">
          <col style="width: 100%;">
        </colgroup>
        <tbody>
          <tr>
            <th scope="row" class="required">프로젝트팀</th>
            <td>
              <InputBox type="text" ref="projectNm" v-model="detail.projectNm" :value.sync="detail.projectNm"  />
            </td>
            <th scope="row">상태</th>
            <td>
              <InputBox type="text" ref="projectNm" v-model="detail.state" :value.sync="detail.state" disabled="true" />
            </td>
          </tr>

          <tr>
            <th scope="row" class="required">시작일</th>
            <td>
              <DatePicker ref="startDatePicker" :date.sync="detail.startDt" />
            </td>
            <th scope="row" class="required">종료일</th>
            <td>
              <DatePicker ref="endDatePicker" :date.sync="detail.endDt" />
            </td>
          </tr>

          <tr>
            <th scope="row" class="required">팀관리자</th>
            <td colspan="3">
              <InputBox type="text" v-model="detail.teamManagerNm" :value.sync="detail.teamManagerNm" disabled="disabled" />
              <button type="button" @click.passive.stop="searchEmployee" class="btn" >검색</button>
            </td>
          </tr>

          <tr>
            <th scope="row">프로젝트 팀원</th>
            <td colspan="3">
              <div class="grid-box">
                <button type="button" class="btn btn-control" @click="addMember">추가</button>
                <button type="button" class="btn btn-control" @click="removeMember">삭제</button>
                <grid class="tui-grid" ref="tuiGrid" :data="gridProps.data" :columns="gridProps.columns" :options="gridProps.options" />
              </div>
            </td>
          </tr>

          <tr>
            <th scope="row">기타</th>
            <td colspan="3">
              <textarea style="width:460px; height:70px" v-model="detail.etc"/>
            </td>
        </tr>
        </tbody>
      </table>
      </div>
      <div class="modal-popup-btn-box">
        <button type="button" class="btn btn-primary" @click.prevent.stop="save">저장</button>
      </div>
      <ProjectMemberSearchPop ref="memberSearchPop" @closeMemberSearchPop="closeMemberSearchPop"/>
    </ModalPopup>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import "tui-grid/dist/tui-grid.css";
import 'tui-date-picker/dist/tui-date-picker.css';
import Loading from "vue-loading-overlay";
import { Grid } from "@toast-ui/vue-grid";
import { Grids } from "@/mixins/Grids";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import {CheckboxRenderer} from '@/components/renderer/GridRenderer';
import InputBox from '@/components/InputBox';
import DatePicker from '@/components/Datepicker';
import ModalPopup from "@/components/ModalPopup";
import ProjectMemberSearchPop from "@/views/system/pop/ProjectMemberSearchPop";

import TuiGrid from 'tui-grid';
TuiGrid.setLanguage('ko');

export default {
  name: "ProjectTeamDetailPop",
  mixins: [Check,Empty],
  components: {
    InputBox,
    grid: Grid,
    DatePicker,
    loading: Loading,
    ModalPopup,
    ProjectMemberSearchPop
  },
  data() {
    return {
      modalVisible: false,
      detail: { projectNo: '', projectNm: '', teamManagerNo: '',  teamManagerNm:'', startDt: '', endDt: '', etc: '', state: '' },
      isLoading: false,
      gridProps : {
        options: {
          rowHeight: 48,
          rowHeaders: [{
            type: 'checkbox',
            header: `
              <span class="ui-chk">
                <input id="chkAll" type="checkbox" name="_checked"><label for="chkAll"></label>
              </span>`,
            renderer: {
              type: CheckboxRenderer,
            },
            }],
          scrollX: false,
          scrollY: true,
          bodyHeight: 240,
          showDummyRows: false,
          selectionUnit: 'row',
          display: { noData: 'No Data' },
        },
        columns: [
          {header: "순번", name: "projectNo", hidden: true},
          {header: "empNo", name: "empNo", hidden: true},
          {header: "이름", name: "empNm",align: "center", sortable: true},
        ],
        data: []
      }
    };
  },
  computed: {
  },
  created() {
  },
  watch: {
    modalVisible(val, oldVal) {
      if (!val) {
        this.resetData(); 
      }
    },
  },
  methods: {
    open(projectNo) {
      if( this.isNotEmpty(projectNo) ){
        this.detail.projectNo = projectNo;
        this.searchMst();
        this.searchMember();
      }
      this.modalVisible = true;
    },
    closed (obj) {
      this.modalVisible = false;
      //부모 함수 콜백
      this.$emit('closedPopup', obj);
    },
    searchMst(){
      let searchparms = {};
      searchparms.projectNo = this.detail.projectNo;
      this.isLoading = true;
      this.$http.post( "/api/system/team-manage/getProjectTeamMstDetail",searchparms)
          .then((response) => {
            this.detail = response.data.data[0];
            this.$refs.startDatePicker.setDate(new Date(response.data.data[0].startDt));
            this.$refs.endDatePicker.setDate(new Date(response.data.data[0].endDt));
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    searchMember(){
      let searchparms = {};
      searchparms.projectNo = this.detail.projectNo;
      this.isLoading = true;
      this.$http.post( "/api/system/team-manage/getProjectMemberList",searchparms)
          .then((response) => {
            this.$refs.tuiGrid.invoke('resetData', response.data.data, {});
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    save(){
      if(!this.validation()){return false;}
      
      let params = {};
      let apiUrl = "/api/system/team-manage/insertProjectTeamMst";
      if( this.isEmpty(this.detail.projectNo) ){
        const gridRows = this.$refs.tuiGrid.invoke("getModifiedRows");
        let lootList = gridRows.createdRows;
        let tempArr = [];
        for(let i in lootList) {
          let addObj = {};
          addObj.empNo = lootList[i].empNo
          tempArr.push(addObj);
        }
        params.teamList = tempArr;
      } else {
        apiUrl = "/api/system/team-manage/updateProjectTeamMst";
        params.projectNo = this.detail.projectNo;
      }
      
      this.isLoading = true;
      params.projectNm = this.detail.projectNm;
      params.teamManagerNo = this.detail.teamManagerNo;
      params.startDt = this.getDateByDelimeter(this.$refs.startDatePicker.instance.getDate(), '-');
      params.endDt = this.getDateByDelimeter(this.$refs.endDatePicker.instance.getDate(), '-' );
      params.etc = this.detail.etc;
      this.$http.post( apiUrl, params)
          .then((response) => {
            if(response.data.statusCode != 200){
              alert('오류가 발생하였습니다.!!');
              return false;
            } else {
              alert(response.data.message);
              this.closed('success');
            }
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    searchEmployee(){
      let info = {};
      info.type = 'manager';
      info.projectNo = this.detail.projectNo;
      this.$refs.memberSearchPop.open(info);
    },
    addMember(){
      let info = {};
      info.type = 'member';
      info.projectNo = this.detail.projectNo;
      this.$refs.memberSearchPop.open(info);
    },
    removeMember(){
      let gridRows = this.$refs.tuiGrid.invoke("getCheckedRows");
      if(this.isEmpty(gridRows)){
        alert('선택한 내용이 없습니다.');
        return false;
      }

      if( this.isEmpty(this.detail.projectNo)) this.$refs.tuiGrid.invoke('removeCheckedRows', true);
      else {
        let params = {};
        params.teamList = gridRows;
        params.projectNo = this.detail.projectNo;
        this.isLoading = true;
        this.$http.post("/api/system/team-manage/deleteProjectTeamMember", params)
            .then((response) => {
              alert(response.data.message);
              this.searchMember();
            })
            .catch((e) => {
              console.log(e);
              this.isLoading = false;
            })
            .finally(() => {
              //this.isLoading = false;
            });
      }
    },
    closeMemberSearchPop (rtnObj) {
      if( this.isNotEmpty(rtnObj)){
        if( rtnObj.type == 'manager' ){
          this.detail.teamManagerNo = rtnObj.empNo;
          this.detail.teamManagerNm = rtnObj.empNm;
        } else {
          if( this.isEmpty(this.detail.projectNo)){
            let dataList = rtnObj.list;
            for(let i=0; i < dataList.length; i++) this.$refs.tuiGrid.invoke('appendRow',dataList[i], {});
          } else this.searchMember();
        }
      }
    },    
    validation(){
      if( this.isEmpty(this.detail.projectNm) ){
        alert('프로젝트명을 입력해 주세요.');
        this.$refs.projectNm.focus();
        return false;
      }

      let bytes = this.maxByteCheck(this.detail.projectNm);
      if( bytes > 200 ){
        this.detail.projectNm = this.detail.projectNm.substring(0, 200);
        alert('프로젝트명을 200 바이트 이하로 입력해 주세요.');
        this.$refs.projectNm.focus();
        return false;
      }

      if(this.isEmpty(this.$refs.startDatePicker.instance.getDate()) ) {
        alert('시작일를 입력해주세요.');
        this.$refs.startDatePicker.focus();
        return false;
      } else if( this.isEmpty(this.$refs.endDatePicker.instance.getDate()) ) {
        alert('종료일를 입력해주세요.');
        this.$refs.endDatePicker.focus();
        return false;
      } else {
        this.detail.startDt = this.getDateByDelimeter(this.$refs.startDatePicker.instance.getDate(), '-');
        this.detail.endDt = this.getDateByDelimeter(this.$refs.endDatePicker.instance.getDate(), '-' );
      }

      if(this.detail.startDt > this.detail.endDt) {
        alert("시작일이 종료일보다 큽니다.");
        this.$refs.startDatePicker.focus();
        return false;
      }

      bytes = this.maxByteCheck(this.detail.etc);
      if( bytes > 1000 ){
        "현재 " + bytes + "바이트 입력하셨습니다.\n 1000 바이트 이하로 입력하세요."
        this.detail.commentTxt = this.detail.etc.substring(0, 1000);
        return false;
      }

      return true;
    },
    resetData(){
      this.detail.projectNo = '';
      this.detail.projectNm = '';
      this.detail.teamManagerNo = '';
      this.detail.teamManagerNm = '';
      this.detail.startDt = '';
      this.detail.endDt = '';
      this.detail.etc = '';
    }
  }
}
</script>

<style lang="css" scoped>

</style>
