<template>
  <ModalPopup title="자료 업로드" :visible.sync="modalVisible" width="960px">
    <div class="tbl-row write-mode">
      <table>
        <colgroup>
          <col class="col-row-title" />
          <col style="width: 100%" />
          <col class="col-row-title" />
          <col style="width: 100%" />
        </colgroup>
        <tbody>
          <tr>
            <th scope="row" class="required">자료 유형</th>
            <td colspan="3">
              <vSelect
                :options="getResourceTypeExceptAll"
                label="label"
                v-model="writeForm.resType"
                :reduce="(getResourceTypeExceptAll) => getResourceTypeExceptAll.code"
                :clearable="false"
                :searchable="false"
              ></vSelect>
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">자료 제목</th>
            <td colspan="3">
              <InputBox
                type="text"
                ref="titleTxt"
                :value.sync="writeForm.titleTxt"
                inputWidth="100%"
              />
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">게시여부</th>
            <td>
              <RadioButton
                :text="'게시'"
                :value="'Y'"
                name="rgResourcePostYN"
                @radioChange="dispRadioChange"
                :checked="'Y' == writeForm.dispYn"
              />
              <RadioButton
                :text="'미게시'"
                :value="'N'"
                name="rgResourcePostYN"
                @radioChange="dispRadioChange"
                :checked="'N' == writeForm.dispYn"
              />
            </td>
            <th scope="row">정렬순서</th>
            <td>
              <InputBox type="text" :value.sync="writeForm.dispSeq" />
            </td>
          </tr>
          <tr v-if="this.isNotEmpty(this.writeForm.resNo)">
            <th scope="row">등록자</th>
            <td class="plain-text">{{ this.writeForm.regstaffNm }}</td>
            <th scope="row">등록일</th>
            <td class="plain-text">{{ this.writeForm.regDt }}</td>
          </tr>
          <tr v-if="this.isNotEmpty(this.writeForm.resNo)">
            <th scope="row">최종수정자</th>
            <td class="plain-text">{{ this.writeForm.modstaffNm }}</td>
            <th scope="row">최종수정일</th>
            <td class="plain-text">{{ this.writeForm.modDt }}</td>
          </tr>
          <tr>
            <th scope="row">첨부파일</th>
            <td colspan="3">
              <div class="input-flex-box">
                <InputBox type="text" placeholder="" :value="selectedFile.name" readonly />
                <FileButton label="파일선택" @change="onFileChange" />
              </div>
              <div class="attach-file-box">
                <ul class="attached-file-list">
                  <li class="file-item" v-if="this.writeForm.uploadfile1">
                    <div class="file-item-info">
                      <span class="file-name" v-if="this.isNotEmpty(this.writeForm.originalFileName1)">{{ this.writeForm.originalFileName1 }}</span>
                      <span class="file-name" v-else>{{ this.writeForm.uploadfile1 }}</span>
                    </div>
                  </li>
                </ul>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="modal-popup-btn-box">
      <button type="button" class="btn btn-primary" @click="save">
        <template v-if="this.isEmpty(writeForm.resNo)">
        업로드
        </template>
        <template v-else>
        수정
        </template>
      </button>
      <button type="button" class="btn" @click="cancel">취소</button>
    </div>
  </ModalPopup>
</template>

<script>
import "@toast-ui/editor/dist/toastui-editor.css";
import { Editor } from "@toast-ui/vue-editor";

import InputBox from "@/components/InputBox";
import RadioButton from "@/components/RadioButton";
import FileButton from "@/components/FileButton";
import vSelect from "vue-select";
import DatePicker from "@/components/Datepicker";
import ModalPopup from "@/components/ModalPopup";

import { Empty } from "@/mixins/Empty";
import { DateFunc } from "@/mixins/DateFunc";
import { mapActions } from "vuex";

export default {
  name: "ResourceManageWritePop",
  props: [
    "resourceType",
    "search",
  ],
  mixins: [Empty, DateFunc],
  components: {
    Editor,
    InputBox,
    RadioButton,
    FileButton,
    vSelect,
    DatePicker,
    ModalPopup,
  },
  data() {
    return {
      modalVisible: false,
      writeForm: {
        resType: "",
        titleTxt: "",
        resNo: "",
        dispYn: "Y",
        dispSeq: 1,
        regstaffNm: "",
        regDt: "",
        modstaffNm: "",
        modDt: "",
        uploadfile1: "",
        originalFileName1: "",
      },
      selectedFile: "",
      //originalFileName: "",
    };
  },
  watch: {
    modalVisible(val, oldVal) {
      if (!val) {
        this.resetInfo();
      }
    },
  },
  computed: {
    // 부모 게시판 유형중에서 '전체' 제외
    getResourceTypeExceptAll() {
      const exceptAll = [...this.resourceType.options];
      exceptAll.shift();
      return exceptAll;
    },
  },
  methods: {
    resourceInfosearch() {
      
      const apiUrl = "/api/system/resource-manage/get-detail/" + this.writeForm.resNo;
      //const apiUrl = "/api/resource-manage/get-detail";
      const data = this.$http
        .get(apiUrl)
        .then((response) => {

          if (response.data.statusCode === 200) {
            this.writeForm = response.data.data;
            //this.originalFileName = response.data.data.uploadfile1;
          } else {
            throw new Error("자료실 상세정보를 가져오는데 실패하였습니다.");
          }
        })
        .catch((e) => {
          console.log(e);
        });

    },
    // 파일 변경시
    onFileChange(e) {
      
      if (e.event.target.files.length < 1) {
        return false;
      }

      //파일 업로드
      const fd = new FormData();
      fd.append('uploadFiles', e.event.target.files[0]);
      fd.append('editorYn', 'N');

       this.$http
          .post("/api/common/fileUploadRename", fd, {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          })
          .then((response) => {
            this.writeForm.uploadfile1 = response.data.data[0].filePath;
            this.writeForm.originalFileName1 = response.data.data[0].originalFileName;
            return response;
          }).catch((ex)=> {
              alert("이미지 등록중 오류가 발생했습니다.");
              return false;
          });


    },
    // 팝업 열기
    openPop(resNo) {
      this.modalVisible = true;

      //자료실 번호가 있는경우 자료실 정보 불러오기
      if (this.isNotEmpty(resNo)) {
        this.writeForm.resNo = resNo;
        this.resourceInfosearch();
      } else {
        this.writeForm.resType = this.getResourceTypeExceptAll[0];
      }

    },
    // 팝업 닫기
    cancel() {
      this.modalVisible = false;
    },
    // 게시여부 변경시
    dispRadioChange(e) {
      this.writeForm.dispYn = e.target.value;
    },
    // 저장시 validation
    save() {
      
      if (this.isEmpty(this.writeForm.resType)) {
        alert("자료 유형을 선택하세요.");
        return false;
      }
      if (this.isEmpty(this.writeForm.titleTxt)) {
        alert("자료 제목을 입력하세요.");
        this.$refs.titleTxt.focus();
        return false;
      }

      this.callResourceSave();
    },
    // 저장 API 호출
    async callResourceSave() {
      this.isLoading = true;

      let params = {};
      params = this.writeForm;

      if(typeof this.writeForm.resType === 'object') {
        params.resType = this.writeForm.resType.code;
      }

      const save = await this.$http
        .post("/api/system/resource-manage/save", this.writeForm)
        .then((solve) => {
          
          if (solve.data.statusCode === 200) {
            this.modalVisible = false;
            this.search();
          } else {
            throw new Error("자료를 저장하는데 실패하였습니다.");
          }

          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    resetInfo() {
      this.writeForm.resType = '';
      this.writeForm.titleTxt = '';
      this.writeForm.dispYn = 'Y';
      this.writeForm.dispSeq = 1;
    },
  },
};
</script>