<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="true"/>
    <div class="page-title-box">
			<h1 class="page-title">권한그룹관리</h1>
			<ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
				<li class="breadcrumb">홈</li>
				<li class="breadcrumb">시스템</li>
				<li class="breadcrumb">권한그룹관리</li>
			</ol>
		</div>
    <p class="p-title" style="font-size: 13px;">* [그룹ID]을 선택하시면 그룹의 상세정보를 확인할 수 있습니다.</p>
    <GroupDetailPop ref="datailPop" @closedPop="closedPop" />
    <GroupMenuPop ref="menuPop"  @closedGroupMenuPop="closedGroupMenuPop" />

    <div class="grid-box">
      <div class="grid-tool-box">
        <div>
          <button type="button" class="btn btn-control" @click="openDetailPop">등록</button>
          <button type="button" class="btn btn-control" @click.passive.stop="updateGroup">수정</button>
          <button type="button" class="btn btn-control" @click.passive.stop="scheduler">특일정보가져오기</button>
          <button type="button" class="btn btn-control" @click="vacScheduler">생일자메일</button>
        </div>
      </div>
      <grid class="tui-grid" ref="groupGrid" :data="groupGrid.data" :columns="groupGrid.columns" :options="groupGrid.options"
            @click ="cellClick" @editingFinish="onGroupGridEditingFinish"/>
    </div>

    <p class="p-title" style="font-size: 13px;">* 위의 그리드에서 [그룹명]을 선택하시면 그룹에 해당하는 메뉴를 확인할 수 있습니다.</p>
    <div class="grid-box">

      <div class="grid-tool-box">
  			<div>
  				<button type="button" class="btn btn-control" @click.passive.stop="addMenu">추가</button>
  				<button type="button" class="btn btn-control" @click.passive.stop="deleteMenu">삭제</button>
        </div>
  		</div>
      <grid class="tui-grid" ref="menuGrid" :data="menuGrid.data" :columns="menuGrid.columns" :options="menuGrid.options"/>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import "tui-grid/dist/tui-grid.css";
import Loading from "vue-loading-overlay";
import { Check } from "@/mixins/Check";
import { Grids } from "@/mixins/Grids";
import { Grid } from "@toast-ui/vue-grid";
import { Empty } from "@/mixins/Empty";
import {CheckboxRenderer, ClickableTextRenderer} from '@/components/renderer/GridRenderer';
import GroupDetailPop from '@/views/system/pop/GroupDetailPop';
import GroupMenuPop from '@/views/system/pop/GroupMenuPop';

import TuiGrid from 'tui-grid';
TuiGrid.setLanguage('ko');

export default {
  name: "Group",
  mixins: [Check, Empty, Grids],
  components: {
    grid: Grid,
    loading: Loading,
    GroupDetailPop,
    GroupMenuPop
  },
  data() {
    return {
      groupGrid: {},
      menuGrid: {},
      selectId: '',
      isLoading: false,
    }
  },
  created() {
    this.groupGrid = {
      options: { rowHeight: 48,
                 rowHeaders: [{type: 'checkbox',
                               header: `<span class="ui-chk"><input id="groupChkAll" type="checkbox" name="_checked"><label for="groupChkAll"></label></span>`,
                               renderer: {type: CheckboxRenderer,},}],
                scrollX: false,
                scrollY: true,
                bodyHeight: 240,
                showDummyRows: false,
                selectionUnit: 'row',
                display: { noData: 'No Data' },
                editingEvent: 'click',
      },
      columns: [
        {header: "그룹ID",  name: "groupauthId", width: 300, minWidth: 150,align: "center", sortable: true, formatter: this.gLink, },
        {header: "그룹명",  name: "groupauthNm", width: 300, minWidth: 150,align: "center", sortable: true, formatter: this.gDetail,},
        {header: "설명",    name: "commentTxt", align: "center", sortable: true, },
        {header: "사용여부", name: "useYn", align: "center",sortable: true, formatter: 'listItemText', editor: this.gSelect("YN"), },
        {header: "upgroupauthId",    name: "upgroupauthId", hidden: true, },
        {header: "upgroupauthNm",    name: "upgroupauthNm", hidden: true, },
      ],
      data: [],
    };

    this.menuGrid = {
      options: { rowHeight: 48,
                 rowHeaders: [{type: 'checkbox',
                               header: `<span class="ui-chk"><input id="menuChkAll" type="checkbox" name="_checked"><label for="menuChkAll"></label></span>`,
                               renderer: {type: CheckboxRenderer,},}],
                scrollX: false,
                scrollY: false,
                bodyHeight: 240,
                showDummyRows: false,
                selectionUnit: 'row',
                pageOptions: {
                  						useClient: true,
                  						perPage: 5,
                              visiblePages: 5,
                  					},
                display: { noData: 'No Data' },
      },
      columns: [
        {header: "메뉴ID",       name: "menuId",      width: 150, minWidth: 100,align: "center", sortable: true,},
        {header: "메뉴명",       name: "menuNm", width: 200, minWidth: 150,align: "center", sortable: true,},
        {header: "프로그램URL",  name: "menuurlTxt",   align: "center",sortable: true, width: 300},
        {header: "그룹ID",       name: "groupauthId",   align: "center",sortable: true, },
        {header: "메뉴여부",     name: "menuTxt",   align: "center",sortable: true,}
      ],
      data: [],
    };
    this.gridHandlers().search();
  },
  computed: {
      ...mapState({
        main: (state) => state.main,
        fileInfo: (state) => state.fileInfo,
      }),
    },
    methods: {
      ...mapActions({
        fileDownload: "fileDownload",
      }),
      vacScheduler(){
        this.$http.post("/api/scheduler/sendbirthDayMaill", {})
                .then((response) => {
                  console.log(response);
                });
     },
      gridHandlers: function(){
        const _self = this;

        return {
          search: function() {
              _self.isLoading = true;
            _self.$http.post( "/api/auth/getAuthGroupList",{})
                .then((response) => {
                  _self.$refs.groupGrid.invoke('resetData', response.data.contents, )
              }).finally(() => {
                  _self.isLoading = false;
              });
          },
          menuSearch: function(clickValue) {
            let searchparms = {};
            searchparms.groupauthId = clickValue;
            _self.$http.post( "/api/auth/getAuthGroupMenuList",searchparms)
                .then((response) => {
                  _self.$refs.menuGrid.invoke('resetData', response.data.contents)
                });
          }
        }

      },
      cellClick(ev){

        const clickValue = this.$refs.groupGrid.invoke('getValue',ev.rowKey, 'groupauthId');

        this.selectId = clickValue;
        if(ev.columnName === 'groupauthId') {
          this.gridHandlers().menuSearch(clickValue);
        } else if(ev.columnName === 'groupauthNm') {
          let info = {};
          info.groupauthId = clickValue;
          info.type = 'mod';
          this.$refs.datailPop.open(info);
        }
      },
      onGroupGridEditingFinish(ev){
        this.$refs.groupGrid.invoke("check",ev.rowKey);
      },
      openDetailPop(){
        const rowData = this.$refs.groupGrid.invoke("getRow",1);
        let info = {};
        info.upgroupauthId = rowData.upgroupauthId;
        info.upgroupauthNm = rowData.upgroupauthNm;
        info.type = 'reg';
        this.$refs.datailPop.open(info);
      },
      closedPop: function (rtnObj) {
        if( !this.isEmpty(rtnObj)){
          if(rtnObj == 'success'){this.gridHandlers().search();}
        }
      },
      updateGroup: function(){
        const gridRows = this.$refs.groupGrid.invoke("getCheckedRows");
        if( this.isEmpty(gridRows)){
          alert("선택항목이 없습니다.");return false;
        }

        let params = {};
        params.authList = gridRows;

        this.$http.post("/api/auth/updateGroupAuthGrid", params)
            .then((response) => {
              if(response.data.statusCode != 200){
                alert(response.data.message);
                return false;
              }else{
                alert(response.data.message);
                this.gridHandlers().search();
              }
            });
      },
      addMenu: function () {
        if( this.isEmpty(this.selectId) ){
          alert('그룹ID를 선택해주세요.');
          return false;
        }
        this.$refs.menuPop.open(this.selectId);
      },
      closedGroupMenuPop: function (rtnObj) {
        if( !this.isEmpty(rtnObj)){
          this.gridHandlers().menuSearch(this.selectId);
        }
      },
      deleteMenu(){
        const gridRows = this.$refs.menuGrid.invoke("getCheckedRows");
        if( this.isEmpty(gridRows)){
          alert("선택항목이 없습니다.");return false;
        }

        let params = {};
        params.authList = gridRows;

        this.$http.post("/api/auth/deleteGroupAuthMenuGrid", params)
            .then((response) => {
              if(response.data.statusCode != 200){
                alert(response.data.message);
                return false;
              }else{
                alert(response.data.message);
                this.gridHandlers().menuSearch(this.selectId);
              }
            });
      },
      downLoadFile: function() {
        //파일 다운로드
        this.fileInfo.fileName = '한글.xlsx'; //파일name 확장자 포함
        this.fileInfo.filePath = '/1763/한글.xlsx'; //파일 패스 포함
        let result = this.fileDownload(this.fileInfo);
        },
    scheduler : function() {
            this.$http.post("/api/scheduler/getData", {})
                .then((response) => {
                  if(response.data.statusCode != 200){
                    alert(response.data.message);
                    return false;
                  }else{
                    alert(response.data.message);
                    this.gridHandlers().menuSearch(this.selectId);
                  }
            });
        }
     }
  };
</script>

<style scoped lang="scss">
.p-title {
	margin-top: 25px;
	font-size: 24px;
	font-weight: 700;

	~ div {
		margin-top: 15px;
	}
}
</style>
