<template>
  <div class="grid-box">
    <div style="margin-bottom: 13px;">
      <button type="button" class="btn btn-control" @click="addRow()">추가</button>
      <button type="button" class="btn btn-control" @click="delRow()">삭제</button>
    </div>    
    <Grid
      class="tui-grid"
      ref="licenseGrid"
      :options="gridProps.options"
      :data="gridProps.data"
      :columns="gridProps.columns"
      @click="onGridClick"
    />
    <div class="per-page-box" v-if="!this.$store.state.isMobile">
      <span class="total-count-text">
        총
        <span class="count">{{ gridProps.data.length }}</span>개
      </span>
    </div>
  </div>
</template>

<script>
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";
import InputBox from "@/components/InputBox";
import { CheckboxRenderer } from "@/components/renderer/GridRenderer";

export default {
  name: "ProfileManageLicenseList",
  props: [
    "licenseGridHeader",
    "profileManageLicenseList",
  ],
  components: {
    Grid,
    vSelect,
    InputBox,
  },
  data() {
    return {
      pageData: {
        data: [
          { label: "10", value: 10 },
          { label: "20", value: 20 },
          { label: "50", value: 50 },
          { label: "100", value: 100 },
        ],
        selected: "10",
      },
    };
  },
  computed: {
    gridProps() {
      return {  
        options: {        
          rowHeight: 48,
          rowHeaders: [
            {
              type: "checkbox",
              header: `
						<span class="ui-chk">
							<input id="chkAll" type="checkbox" name="_checked"><label for="chkAll"></label>
						</span>`,
              renderer: {
                type: CheckboxRenderer,
              },
            },
          ],            
          bodyHeight: 150,
          showDummyRows: false,
          selectionUnit: "row",
          editingEvent: 'click',
        },
        columns: [...this.licenseGridHeader],
        data: this.profileManageLicenseList.licenseGridList.data.contents,
      };
    },
  },
  watch: {
    gridProps(val, oldVal) {
      this.$refs.licenseGrid.invoke("resetData", val.data);
    },
  },
  methods: {
    onGridClick(event) {
      if (
        event.targetType !== "columnHeader" &&
        event.columnName === "empNm"
      ) {
        const eventRow = this.$refs.licenseGrid.invoke("getRow", event.rowKey);

   
      }
    },
    onPerPageInput(data) {
      this.$refs.licenseGrid.invoke("setPerPage", data.value);
    },
    addRow : function(){
      let row = {}; //row생성시 초기삽이 있으면 설정.
      let options = {}; //옵션을 설정
      this.$refs.licenseGrid.invoke('prependRow', row, options);
    },
    delRow : function(){
      this.$refs.licenseGrid.invoke('removeCheckedRows', false);
    },
    getGridData : function(){
      return this.$refs.licenseGrid.invoke('getData');
    },            
    focus :function(index){
      this.$refs.licenseGrid.invoke('focus',index, 'period', true);
    }
  },
};
</script>