<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="true"/> 
    <div class="page-title-box">
      <h1 class="page-title">연차휴가신청</h1>
      <ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
        <li class="breadcrumb">홈</li>
        <li class="breadcrumb">마이 페이지</li>
        <li class="breadcrumb">연차휴가신청</li>
      </ol>
    </div>
    <div class="search-input-box" v-if="!this.$store.state.isMobile">
      <div class="search-input-row">
        <div class="search-item">
          <label for="companyPos">년도</label>
          <div class="search-value">
            <InputBox type="text" v-model="year" :value.sync="year" disabled="true" />
          </div>
        </div>
        <div class="search-item confirm-box">
          <button type="button" @click.passive.stop="search" class="btn btn-primary">검색</button>
        </div>
      </div>
    </div>
    <div class="page-title-box" v-if="!this.$store.state.isMobile">
      <h1 class="page-title">나의 휴가정보</h1>
    </div>
    <div class="vacation-info-box">
      <div class="vacation-info-cell">
        <span class="vacation-info-label">총 연차 ( 기본 연차 {{ vacationInfo.homefaxTxt }} / 이월연차 {{ vacationInfo.preyearVac }} )</span>
        <span class="vacation-info-value">{{ vacationInfo.vacCalTotal }}</span>
      </div>
      <div class="vacation-info-cell">
        <span class="vacation-info-label">사용 연차</span>
        <span class="vacation-info-value">{{ vacationInfo.vacUsedThisYear }}</span>
      </div>
      <div class="vacation-info-cell">
        <span class="vacation-info-label">잔여 연차</span>
        <span class="vacation-info-value remain-value" :class="{ 'is-minus': -1 < 0 }">{{ vacationInfo.vacExtra }}</span>
      </div>
    </div>
    <div class="grid-box">
        <div class="grid-tool-box">
          <div>
						<button type="button" class="btn" @click="cancelVacation">신청 취소</button>
            <button type="button" class="btn btn-primary" @click="handleVacation('reg')">휴가 신청</button>
          </div>
        </div>
        <grid class="tui-grid" ref="tuiGrid" :data="gridProps.data" :columns="gridProps.columns" :options="gridProps.options"
              @click ="onClick" />
        <div class="per-page-box" v-if="!this.$store.state.isMobile">
					<span class="total-count-text">총 <span class="count">{{ totalCnt}}</span>개</span>
					<div class="select-box">
						<vSelect v-model="perPage" :reduce="label => label.value" :options="pageList.options" :clearable="false" :searchable="false"
                    @input="onPerPageInput" ></vSelect>
					</div>
				</div>
    </div>
    <div class="grid-footer-box">
			<p>* pdf 파일을 열어서 보기 위해서는 뷰어 프로그램이 필요합니다. <a href="https://get.adobe.com/kr/reader/" target="_blank">Adobe Reader 설치하기</a></p>
			<p>* 프로필변경에서 SIGN 이미지를 등록하시면 신청자와 확정자의 SIGN이 출력됩니다.</p>
      <p v-if="isNotEmpty(vacationInfo.vacMessage)">* {{vacationInfo.vacMessage}}</p>
		</div>
    <VacationPopup ref="vacPop"  @closedVacationPopup="closedVacationPopup"/>
    <VacationCancelPopup ref="vacCancelPop"  @closedVacationCancelPopup="closedVacationCancelPopup"/>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import "tui-grid/dist/tui-grid.css";
import { Grid } from "@toast-ui/vue-grid";
import { Grids } from "@/mixins/Grids";
import 'vue-select/dist/vue-select.css';
import Loading from "vue-loading-overlay";
import vSelect from 'vue-select'
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import {CheckboxRenderer} from '@/components/renderer/GridRenderer';
import InputBox from '@/components/InputBox';
import VacationPopup from "@/views/myPage/pop/VacationPopup";
import VacationCancelPopup from "@/views/myPage/pop/VacationCancelPopup";

import TuiGrid from 'tui-grid';
TuiGrid.setLanguage('ko');

export default {
  name: "VacationList",
  mixins: [Check,Empty,Grids],
  components: {
    grid: Grid,
    vSelect,
    loading: Loading,
    InputBox,
    VacationPopup,
    VacationCancelPopup
  },
  data() {
    return {
      vacationInfo: {homefaxTxt: 0, preyearVac: 0,  vacCalTotal: 0, vacExtra: 0, vacMessage: ''},
      pageList: {options: [{label: '10', value: 10},{label: '20', value: 20},{label: '50', value: 50},{label: '100', value: 100}],},
      perPage: 10,
      totalCnt: 0,
      gridProps : {},
      year : '',
      isLoading: false,
    };
  },
  created() {
    this.getCurrentYear();
    this.gridProps = {
        options: {
          rowHeight: 48,
          scrollX: false,
          scrollY: true,
          bodyHeight: this.$store.state.isMobile ? 'auto' : 480,
					showDummyRows: false,
					selectionUnit: 'row',
          display: { noData: 'No Data' },
          pageOptions: {
            useClient: true,
            perPage: this.perPage,
            visiblePages: 5,
          },
				},
        columns: [
          {header: "순번", name: "vacNo", hidden: true},
          {header: "신청일자", name: "requestYmd", align: "left",sortable: true, hidden: this.$store.state.isMobile},
          {header: "휴가유형", name: "vacTypNm", align: "center",sortable: true, formatter: this.gDetail},
          {header: "휴가기간", name: "period", align: "left", sortable: true, width: this.$store.state.isMobile ? "250" : "350"},
          {header: "휴가일수", name: "days", align: "center", sortable: true, hidden: this.$store.state.isMobile},
          {header: "상태", name: "confirmState", align: "center", sortable: true},
          {header: "승인날짜",name: "confirmYmd", align: "left", sortable: true, hidden: this.$store.state.isMobile},
          {header: "승인자",name: "confirmEmpNm", align: "left", sortable: true, hidden: this.$store.state.isMobile},
          {header: "PDF 다운",name: "pdfDownload", align: "center", sortable: false, hidden: this.$store.state.isMobile}
        ],
        data:[]
    };
  },
  mounted() {
    this.getVacationState();
    this.search();
  },
  computed: {
    ...mapState({
      main: (state) => state.main,
    }),
  },
  methods: {
    onClick(ev) {
      if( this.isEmpty(ev.rowKey)) return false;
      const rowData = this.$refs.tuiGrid.invoke("getRow",ev.rowKey);

      if( ev.columnName == "vacTypNm" ){
        this.handleVacation(rowData.vacNo);
      } else if(ev.columnName == "pdfDownload"){
        this.downloadPdf(rowData);
      }
    },
    handleVacation(inParam) {
      let vacNo = '';
      if(inParam != 'reg')vacNo = inParam;
      this.$refs.vacPop.open(vacNo);
    },
    getCurrentYear(){
      let searchparms = {type:"Y", term:0, format:"yyyy"};

      this.$http.post("/api/common/getCurrentDate",searchparms)
          .then((response) => {
            this.year = response.data.data;
          });
    },
    getVacationState(){
      let searchparms = {};
      searchparms.year = this.year;

      this.$http.post("/api/vacation/getVacationState",searchparms)
          .then((response) => {
            this.vacationInfo = response.data.data[0];
          });
    },
    search(){
      let searchparms = {};
      searchparms.year = this.year;
      this.isLoading = true;
      this.$http.post("/api/vacation/vacationList", searchparms,)
          .then((response) => {
            if (response.data.data) {
              this.totalCnt = response.data.data.contents.length;
              let contentsList = response.data.data.contents;
              this.$refs.tuiGrid.invoke('resetData', contentsList, {});              
            }
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    cancelVacation(){
      this.$refs.vacCancelPop.open();
    },
    closedVacationPopup (rtnObj) {
      if( !this.isEmpty(rtnObj)){
        if(rtnObj == 'success') this.search();
      }
    },
    closedVacationCancelPopup (rtnObj) {
      if( !this.isEmpty(rtnObj)){
        if(rtnObj == 'success') this.search();
      }
    },
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data);
    },
    downloadPdf(rowData) {
      this.isLoading = true;
      let params = {
        vacNo: rowData.vacNo,
        type: "download"
      };
      const profileDownload = this.$http
        .post("/api/utils/report/getReportPrint", params, {
          responseType: "arraybuffer",
        })
        .then((solve) => {
          if (solve.status === 200) {
            let fileName =
              rowData.period + "_휴가신청서.pdf";
            let blob = new Blob([solve.data], {
              type: solve.headers["content-type"],
            });
            let link = document.createElement("a");
            link.href = window.URL.createObjectURL(blob);
            link.target = "_self";
            link.download = fileName;
            link.click();
          } else {
            throw new Error("휴가신청서 다운로드를 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
          throw new Error("휴가신청서 다운로드를 실패하였습니다.");
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
  }
};
</script>
<style scoped lang="scss">
.p-title {
	margin-top: 25px;
	font-size: 24px;
	font-weight: 700;

	~ div {
		margin-top: 15px;
	}
}
</style>
