<template>
  <ModalPopup title="달력보기" :visible.sync="modalCalendarVisible" width="1200px">
					<div class="calendar-nav">
						<button type="button" class="btn-cal-nav btn-prev" @click="onCalendarNavBtnClick"></button>
						<span class="current-year-month">{{ this.calenderYearMonth }}</span>
						<button type="button" class="btn-cal-nav btn-next" @click="onCalendarNavBtnClick"></button>
					</div>
					<Calendar ref="mainCalendar" class="main-calendar" style="height: 600px;"
							  :calendars="this.calendarList" :schedules="this.calendarData.scheduleList"
							  :theme="this.calendarData.theme" :view="this.calendarProps.view"
							  :isReadOnly="false"
                :month="this.calendarData.monthOptions"
							  @clickSchedule="onClickSchedule" @beforeCreateSchedule="onCreateSchedule" 
							  @beforeDeleteSchedule="onDeleteSchedule" @beforeUpdateSchedule="onUpdateSchedule" />
                <!-- :isReadOnly="this.$store.state.userInfo.calendarReadOnly" -->
				</ModalPopup>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { Check } from "@/mixins/Check";
import { Comm } from "@/mixins/Comm";
import {Calendar} from '@toast-ui/vue-calendar';
import ModalPopup from "@/components/ModalPopup";
import moment from 'moment';

import 'tui-calendar/dist/tui-calendar.css';
import 'tui-date-picker/dist/tui-date-picker.css';
import 'tui-time-picker/dist/tui-time-picker.css';

export default {
  name: "CalendarPop",
  components: {
    Calendar,
	  ModalPopup,
  },
  data() {
    return {
      calenderYearMonth: '',
	    calendarProps: {
	      view: 'month',
	    },
      //isReadOnly: true,
      modalCalendarVisible: false, 
      calendarList: [
        {
          id: '1',
          name: '개인 일정',
          bgColor: '#a1b56c',
        },
        {
          id: '2',
          name: '개인 - Year 반복',
          bgColor: '#a1b56c',
        },
        {
          id: '3',
          name: '개인 - Month 반복',
          bgColor: '#a1b56c',
        },
        {
          id: '4',
          name: '개인 - Year/Month 반복',
          bgColor: '#a1b56c',
        },
        // {
        //   id: '99',
        //   name: '생일',
        //   bgColor: '#F781F3',
        // },
      ],
      addCalendarList: [
        {
          id: '5',
          name: '전체 일정',
          bgColor: '#fc1a40',
        },
        {
          id: '6',
          name: '전체 - Year 반복',
          bgColor: '#fc1a40',
        },
        {
          id: '7',
          name: '전체 - Month 반복',
          bgColor: '#fc1a40',
        },
        {
          id: '8',
          name: '전체 - Year/Month 반복',
          bgColor: '#fc1a40',
        },
      ],
      calendarData: {
        monthOptions: {
          daynames: ['일', '월', '화', '수', '목', '금', '토'],
          visibleScheduleCount: 5,
        },
        yearText: '',
        monthText: '',
        theme: {
          'common.holiday.color': '#fc1a40',
          'common.saturday.color': '#009afa',
          'common.today.color': '#fff',
          'month.dayname.height': '37px',
          'month.dayname.textAlign': 'center',
          'month.holidayExceptThisMonth.color': '#ffcaca',
          'month.dayExceptThisMonth.color': '#ccc',
          'month.day.fontSize': '12px',
          'month.schedule.height': '16px',
        },
        scheduleList: [],
        searchMonth: '',
	      searchYear: '',
      }
    }
  },
  watch: {
    modalCalendarVisible(val, oldVal) {
      if (!val) {
        this.resetData(); 
      }
    },
  },
  methods: {
    onShowCalendar() {
      let that = this;
      this.calendarData.scheduleList = [];
      this.modalCalendarVisible = true;
      let searchparams = {
        curYear: this.calendarData.searchYear,
        curMonth: this.calendarData.searchMonth,
      };
      
      if (this.$store.state.userInfo.groupauthId == '10001' && this.calendarList.length < 5) {
        for (let i in this.addCalendarList) {
          this.calendarList.push(this.addCalendarList[i]);
        }
      }

     this.$http.post("/api/main/getScheduleCalendar", searchparams)
          .then((response) => {
        this.$refs.mainCalendar.invoke('createSchedules',response.data.scheduleInfo);
        this.setYearMonthText();
      });
    },
    onClickSchedule : function(event) {
      // console.log('@clickSchedule="onClickSchedule"');
      // console.log(event);
    },
    onCreateSchedule : function(event){
      // console.log('@beforeCreateSchedule="onCreateSchedule"');
      // console.log(event);

      let saveData = {};
      saveData.scheNo = event.id;
      saveData.calendarId = event.calendarId;
      saveData.titleTxt = event.title;
      saveData.startDt = this.makeTZDateToDate(event.start);
      saveData.endDt = this.makeTZDateToDate(event.end);
      //saveData.isAllDay = event.isAllDay;

      this.makeScheduleChecked(saveData.calendarId, saveData);

      if (saveData.calendarId == '99') {
        alert('생일로 등록 불가합니다. \n\n생일은 프로필에서 관리 가능합니다.');
        return;
      }

      // debugger;

      this.callScheduleSave(saveData, 'C');
    },
    onUpdateSchedule : function(event){
      // console.log('@beforeUpdateSchedule="onUpdateSchedule"');
      // console.log(event);
      
      let saveData = {};
      // 기본 데이터
      saveData.scheNo = event.schedule.id;
      saveData.calendarId = event.schedule.calendarId;
      saveData.titleTxt = event.schedule.title;
      saveData.startDt = this.makeTZDateToDate(event.schedule.start);
      saveData.endDt = this.makeTZDateToDate(event.schedule.end);
      //saveData.isAllDay = event.isAllDay;
      
      // 변경 데이터
      if (event.changes.calendarId != null) {
        saveData.calendarId = event.changes.calendarId;
      }
      if (event.changes.title != undefined) {
        saveData.titleTxt = event.changes.title;
      }
      if (event.changes.start != undefined) {
        saveData.startDt = this.makeTZDateToDate(event.changes.start);
      }
      if (event.changes.end != undefined) {
        saveData.endDt = this.makeTZDateToDate(event.changes.end);
      }
      this.makeScheduleChecked(saveData.calendarId, saveData);
      
      this.callScheduleSave(saveData, 'U');
    },
    onDeleteSchedule : function(event) {
      // console.log('@beforeDeleteSchedule="onDeleteSchedule"');
      // console.log(event);
      
      let saveData = {};
      saveData.scheNo = event.schedule.id;
      
      this.callScheduleSave(saveData, 'D');
    },
    setCalendarYearMonth() {
        if (!this.modalCalendarVisible) return new Date().getTime();
      return this.$refs.mainCalendar.invoke('getDateRangeStart');
    },
    onCalendarNavBtnClick(event) {
      if (event.currentTarget.classList.contains('btn-prev')) {
      this.$refs.mainCalendar.invoke('prev');
      } else if (event.currentTarget.classList.contains('btn-next')) {
      this.$refs.mainCalendar.invoke('next');
      }
      this.setYearMonthText();
      this.onShowCalendar();
    },
    setYearMonthText() {
      const {invoke} = this.$refs.mainCalendar;
      const calDate = invoke('getDate');
      let year = calDate.getFullYear();
      this.calendarData.searchYear = year;
      let month = calDate.getMonth() + 1;
      this.calendarData.searchMonth = month;
      let date = calDate.getDate();       
      this.calenderYearMonth =  year + '년 ' + month + '월'
    },
    resetData() {
      this.calendarData.searchYear = '';
      this.calendarData.searchMonth = '';
      this.$emit('monthScheduleSearch');
    },
    // 저장 API 호출
    async callScheduleSave(saveData, type) {
      this.isLoading = true;
      
      let url = 'saveScheduleCalendar';
      let message = '일정을 생성하는데 실패하였습니다.';
      if (type == 'D') {
        url = 'removeScheduleCalendar';
        message = '일정을 삭제하는데 실패하였습니다.';
      } else if (type == 'U') {
        message = '일정을 변경하는데 실패하였습니다.';
      }
      
      const save = await this.$http
        .post("/api/main/"+url, saveData)
        .then((solve) => {
          if (solve.data.statusCode === 200) {
            this.onShowCalendar();
            
          } else {
            if (type == 'D') {
              throw new Error(message);
            }
          }

          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });

      return save;
    },
    lpad(str, padLen, padStr) { /* str : 원 문자열, padLen : 최대 채우고자 하는 길이, padStr : 채우고자하는 문자(char) */
      if (padStr.length > padLen) {
        console.log("오류 : 채우고자 하는 문자열이 요청 길이보다 큽니다");
        return str;
      }
      str += ""; // 문자로
      padStr += ""; // 문자로
      while (str.length < padLen)
        str = padStr + str;
      str = str.length >= padLen ? str.substring(0, padLen) : str;
      return str;
    },
    makeTZDateToDate(date) {
      let newDate = date.getFullYear() +'-'+ this.lpad(eval(date.getMonth()+1), 2, '0') +'-'+ this.lpad(date.getDate(), 2, '0') 
                  +' '+ this.lpad(date.getHours(), 2, '0') +':'+ this.lpad(date.getMinutes(), 2, '0') +':'+ this.lpad(date.getSeconds(), 2, '0');
      return newDate;
    },
    makeScheduleChecked(calendarId, saveData) {
      /* 
        1. 일정, 2. 매년 반복, 3. 매달 반복, 4. 매년 / 매달 반복, 
        5. 전체일정, 6. 전체일정 + 매년 반복, 7. 전체일정 + 매달 반복, 8. 전체일정 + 매년 / 매달 반복,
      */
      // 초기값
      saveData.entireSche = 'N';
      saveData.recurYear = 'N'; 
      saveData.recurMonth = 'N';
      // 5 ~ 8 전체 일정
      if (Number(calendarId) > 4) saveData.entireSche = 'Y';
      // 2, 4, 6, 8 매년 반복
      if (calendarId == "2" || calendarId == "4" || calendarId == "6" || calendarId == "8") saveData.recurYear = 'Y';
      // 3, 4, 7, 8 매달 반복
      if (calendarId == "3" || calendarId == "4" || calendarId == "7" || calendarId == "8") saveData.recurMonth = 'Y';

      return saveData;
    }
  },
};
</script>
