<template>
  <div class="grid-box">
    <Grid
      class="tui-grid"
      ref="tuiGrid"
      :options="gridProps.options"
      :data="gridProps.data"
      :columns="gridProps.columns"
      @click="onGridClick"
    />
    <div class="per-page-box" v-if="!isMobile">
      <span class="total-count-text">
        총
        <span class="count">{{ gridProps.data.length }}</span
        >개
      </span>
      <div class="select-box">
        <vSelect
          ref="perPage"
          :options="pageData.data"
          :clearable="false"
          :searchable="false"
          v-model="perPage"
          :reduce="(label) => label.value"
          @input="onPerPageInput"
        ></vSelect>
      </div>
    </div>
  </div>
</template>

<script>
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";

export default {
  name: "BoardList",
  props: ["gridHeader", "boardList", "openDetailPop", "isMobile"],
  components: {
    Grid,
    vSelect,
  },
  data() {
    return {
      perPage: 10,
      pageData: {
        data: [
          { label: "10", value: 10 },
          { label: "20", value: 20 },
          { label: "50", value: 50 },
          { label: "100", value: 100 },
        ],
        // selected: "10",
      },
    };
  },
  computed: {
    gridProps() {
      return {
        options: {
          rowHeight: 48,
          scrollX: false,
          scrollY: true,
          bodyHeight: 480,
          showDummyRows: false,
          selectionUnit: "row",
          pageOptions: {
            useClient: true,
            perPage: this.perPage,
            visiblePages: 5,
          },
        },
        columns: [...this.gridHeader],
        data: this.boardList,
      };
    },
  },
  watch: {
    gridHeader(val, oldVal) {
      this.$refs.tuiGrid.invoke("setColumns", val);
    },
    gridProps(val, oldVal) {
      this.$refs.tuiGrid.invoke("resetData", val.data);
    },
  },
  methods: {
    // 그리드 클릭
    onGridClick(event) {
      if (event.targetType !== "columnHeader") {
        if (event.columnName === "titleTxt") {
          const eventRow = this.$refs.tuiGrid.invoke("getRow", event.rowKey);
          this.openDetailPop(eventRow);
        }
      }
    },
    // 페이지 갯수보기 변경
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data);
    },
  },
};
</script>