<template>
	<div class="login">
		<div :class="{'login-box': true, 'valid-fail': login.errShow}">
			<img class="login-logo" src="~assets/images/login/login_logo.png" alt="3TOP 로고">
			<div class="login-form" role="form">
				<div class="login-form-input-box">
					<input type="text" autocomplete="loginId" name="loginId" v-model="userInfo.loginId"
						   placeholder="Username" aria-placeholder="Username" >
					<input type="password" autocomplete="password" name="패스워드 입력 폼" v-model="userInfo.passwdTxt"
						   placeholder="Password" aria-placeholder="Password" @keyup.enter="fnLogin">
					<p class="valid-fail-text" v-if="login.errShow">아이디 또는 비밀번호가 틀립니다.</p>
					<span class="ui-chk">
						<input id="chkSaveId" type="checkbox" v-model="chkSaveId"><label for="chkSaveId"><span>아이디 저장</span></label>
					</span>
				</div>
				<div class="login-form-footer">
					<button type="button" @click.prevent="fnLogin">로그인</button>
					<p>3TOP 임직원 인트라넷 입니다. 도움이 필요하면 관리자에게 문의하세요. 인트라넷 관리자 : 허강혁차장<span>sky@3top.co.kr</span></p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
// @ is an alias to /src
import Vue from "vue";
import { EventBus } from '@/modules/event-bus';
import { mapState, mapGetters, mapActions } from "vuex";
import VueCookies from 'vue-cookies';
import { Empty } from "@/mixins/Empty";
Vue.use(VueCookies)

export default {
  name: "Login",
  mixins: [Empty],
  data() {
    return {
		chkSaveId : false
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.login.userInfo,
      login: (state) => state.login,
	  code: (state) => state.code
    }),
    ...mapGetters({
    })
  },
  created() {
	  if( this.isNotEmpty(this.$cookies.get('chkSaveId')) ){
		this.chkSaveId = true;
		this.userInfo.loginId = this.$cookies.get('intraId');
	  }
  },
  methods: {
    ...mapActions({
      printErrMsg: "printErrMsg",
      handleLogin: "handleLogin",
    }),
  fnLogin() {
    // 로그인
	this.login.errShow = false;
    if (this.userInfo.loginId === "") {
      let paramObj = {
        errMsg: "좋은말 할때 아이디 입력해라.",
        errShow: true,
      };
      this.printErrMsg(paramObj);
      this.login.errMsg = paramObj.errMsg;
      this.login.errShow = true;

    } else if (this.userInfo.passwdTxt === "") {
      let paramObj = {
        errMsg: "장난하냐? 비밀번호 안 넣었다.",
        errShow: true,
      };
      this.printErrMsg(paramObj);
      this.login.errMsg = paramObj.errMsg;
      this.login.errShow = true;

    } else {
      let result = this.handleLogin(this.userInfo);
      result.then( (data) => {
        if (data.data.statusCode === 200) {
          localStorage.setItem("token", data.headers.authorization);
		  sessionStorage.setItem("refreshtoken", data.headers.refreshtoken);
		  if(this.chkSaveId) {
			  this.$cookies.set('chkSaveId', true, '30d');
			  this.$cookies.set('intraId', this.userInfo.loginId, '30d');
		  } else {
			  this.$cookies.remove('chkSaveId');
			  this.$cookies.remove('intraId');
		  }
		  EventBus.$emit('handle-token', true);
			this.$router.push("/main");

        } else {
			localStorage.removeItem('token');
            sessionStorage.removeItem('refreshtoken');
			this.login.errShow = true;
            this.$router.push("/", ()=>{});
        }
      }).catch(()=> {
        //this.printErrMsg
      })
    }
  }
  },
  mounted(){
    EventBus.$emit('handle-token', false);
  }
};
</script>

<style scoped lang="scss">
//@import '@/assets/scss/scss_header';
.login {
	position: relative;
	height: 100vh;
	background: {
		image: url(~assets/images/login/login_bg.png);
		repeat: no-repeat;
		position: bottom center;
		color: #f1f1f1;
	}
}

.login-box {
	position: absolute;
	width: 400px;
	height: 480px;
	padding: 51px 40px 74px;
	background: #1c1c1c;
	text-align: center;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);

	.login-form {
		display: flex;
		align-items: center;
		flex-direction: column;
		-webkit-box-pack: justify;
		-ms-flex-pack: justify;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		height: calc(100% - 93px);
		margin-top: 60px;

		.login-form-input-box, .login-form-footer {
			width: 100%;
			flex: 0 1 auto;
		}

		.login-form-input-box {
			text-align: left;

			input {
				&[type=text], &[type=password] {
					width: 100%;
					height: 36px;
					border: 1px solid #ddd;
					padding: 0 9px;
					font-size: 14px;

					&:focus {
						border-color: #000;
					}

					+ input[type=text], + input[type=password] {
						margin-top: 10px;
					}
				}

				&[type=checkbox] {
					+ label {
						span {
							color: #fff;
							font-size: 13px;
						}
					}
				}
			}

			.valid-fail-text {
				margin-top: 10px;
			}

			.ui-chk {
				margin-top: 10px;
			}
		}

		.login-form-footer {
			button {
				width: 100%;
				height: 48px;
				background: $primaryColor;
				border: none;
				border-radius: 24px;
				color: #fff;
				font-size: 18px;

				&:hover {
					background: #007bc8;
				}
			}

			p {
				color: #a8a8a8;
				margin-top: 17px;
				line-height: 18px;
				word-break: break-word;
				text-align: left;
				@include calc_letter_spacing(-10, 12px);

				span {
					display: inline-block;
					color: $primaryColor;
					margin-left: 7px;
					letter-spacing: 0;
				}
			}
		}
	}

	&.valid-fail {
		.login-form {
			input {
				&[type=text], &[type=password] {
					border-color: $errorColor;
				}
			}
		}
	}
}
</style>
