<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="page-title-box">
      <h1 class="page-title">{{ boardInfo.text }}</h1>
      <ol class="breadcrumb-list" v-if="!isMobile">
        <li class="breadcrumb">홈</li>
        <li class="breadcrumb">게시판</li>
        <li class="breadcrumb">{{ boardInfo.text }}</li>
      </ol>
    </div>
    <div class="search-input-box">
      <div class="search-input-row">
        <div class="search-item w100">
          <label for="boardTitle">제목</label>
          <div class="search-value">
            <InputBox
              id="titleTxt"
              type="text"
              inputWidth="100%"
              :value.sync="searchData.titleTxt"
              :targetMethod="search"
            />
          </div>
        </div>
      </div>
      <div class="search-input-row">
        <div class="search-item" v-if="!isMobile">
          <label for="boardAuthor">등록자</label>
          <div class="search-value">
            <InputBox
              id="regstaffId"
              type="text"
              :value.sync="searchData.regstaffNm"
              :targetMethod="search"
            />
          </div>
        </div>
        <div class="search-item" v-if="!isMobile">
          <label for="regDateFrom">등록일</label>
          <div class="search-value date-range-box">
            <DatePicker ref="fromDate" />
            <span class="tilde-text">~</span>
            <DatePicker ref="toDate" />
          </div>
        </div>
        <div class="search-item confirm-box">
          <button type="button" class="btn" @click="resetSearch">초기화</button>
          <button type="button" class="btn btn-primary" @click="search">
            검색
          </button>
        </div>
      </div>
    </div>
    <BoardList
      ref="boardList"
      :gridHeader="gridHeader"
      :boardList="boardList"
      :openDetailPop="openDetailPop"
      :isMobile="isMobile"
    />
    <BoardDetailPop ref="datailPop" />
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";
import InputBox from "@/components/InputBox";
import DatePicker from "@/components/Datepicker";
import ModalPopup from "@/components/ModalPopup";
import BoardList from "@/views/board/list/BoardList";
import BoardDetailPop from "@/views/board/pop/BoardDetailPop";
import { Empty } from "@/mixins/Empty";
import { DateFunc } from "@/mixins/DateFunc";
import Loading from "vue-loading-overlay";

export default {
  name: "Board",
  mixins: [Empty, DateFunc],
  props: ["typeCode", "upcode"],
  components: {
    Grid,
    vSelect,
    InputBox,
    DatePicker,
    ModalPopup,
    BoardList,
    BoardDetailPop,
    Loading,
  },
  data() {
    return {
      boardInfo: {},
      searchData: {
        titleTxt: "",
        regstaffNm: "",
      },
      boardList: [],
      isLoading: false,
      fullPage: true,
    };
  },
  created() {
    this.setBoardInfo();
  },
  computed: {
    ...mapState(["code", "isMobile"]),
    gridHeader() {
      return [
        {
          header: "게시글번호",
          name: "bbsNo",
          width: 0,
          align: "center",
          hidden: true,
        },
        {
          header: "제목",
          name: "titleTxt",
          formatter(data) {
            return `
                <div class="board-title-box">
                  <a class="board-title clickable-text">${data.value}</a>
                  ${
                    Math.floor(
                      (new Date() - new Date(data.row.regDt)) /
                        (1000 * 60 * 60 * 24)
                    ) <= 5
                      ? '<span class="ico-new">새 게시물</span>'
                      : ""
                  }
                </div>
              `;
          },
          sortable: true,
        },
        {
          header: "등록자",
          name: "regstaffNm",
          width: 200,
          align: "center",
          hidden: this.isMobile,
          sortable: true,
        },
        {
          header: "등록일",
          name: "regDt",
          width: this.isMobile ? 100 : 250,
          align: "center",
          sortable: true,
        },
        {
          header: "조회수",
          name: "viewCnt",
          width: 150,
          align: "center",
          hidden: this.isMobile,
          sortable: true,
        },
      ];
    },
  },
  watch: {
    // 라우터의 typeCode 변경에 따른 컴포넌트 내용 변경
    typeCode(val, oldVal) {
      this.resetSearch();
      this.setBoardInfo();
    },
  },
  methods: {
    // 상세 컴포넌트 호출
    openDetailPop(eventRow) {
      this.$refs.datailPop.openDetailPop(eventRow.bbsNo);
    },
    // 게시판(사내공지, 인사발령, 신규직원소개, 경조사) 정보 세팅
    setBoardInfo(params) {
      const boardInfo = this.code.codeInfo
        .filter((x) => x.value === this.upcode)
        .shift()
        .list.filter((x) => x.value === this.typeCode)
        .shift();
      this.boardInfo = boardInfo;
      this.callBoardList();
    },
    // 게시판(사내공지, 인사발령, 신규직원소개, 경조사) 목록 API 조회
    async callBoardList(params) {
      this.isLoading = true;

      if (params === void 0)
        params = {
          typCd: this.typeCode,
        };

      const apiUrl = "/api/board/get-list";
      const data = await this.$http
        .post(apiUrl, params)
        .then((solve) => {
          if (solve.data.result) {
            this.boardList = solve.data.data;
          } else {
            throw new Error("게시물 목록을 가져오는데 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    // 검색
    search() {
      const param = {
        typCd: this.typeCode,
        titleTxt: this.searchData.titleTxt,
        regstaffNm: this.searchData.regstaffNm,
      };

      if (!this.isMobile) {
        const fromDate = this.$refs.fromDate.instance.getDate();
        const toDate = this.$refs.toDate.instance.getDate();

        if (!this.isEmpty(fromDate)) {
          param.fromDate = this.formatDate(fromDate, "-");
        }
        if (!this.isEmpty(toDate)) {
          param.toDate = this.formatDate(toDate, "-");
        }
      }

      this.callBoardList(param);
    },
    // 초기화 버튼 클릭시
    resetSearch() {
      this.searchData.titleTxt = "";
      this.searchData.regstaffNm = "";

      if (!this.isMobile) {
        this.$refs.fromDate.instance.setDate(null);
        this.$refs.toDate.instance.setDate(null);
      }
    },
  },
};
</script>
