export const Comm = {
  data() {
    commCode: [];
    return {
      noImage: require("@/assets/images/common/common_nav_logo.png"),
    };
  },
  methods: {
    imageViewMixin(fileObject) {
      //이미지불러오기
      return this.$http.post(
        "/api/common/getImageView",
        JSON.stringify(fileObject),
        {
          responseType: "blob",
          headers: {
            Authorization: localStorage.getItem("token"),
            "Content-Type": "application/json",
          },
        }
      );
    },
    imageBlobMixin(imageBlob) {
      //이미지변환하기
      return window.URL.createObjectURL(new Blob([imageBlob]));
    },
    imageError(e) {
      //노이미지
      e.target.src = this.noImage;
    },
    commSelect(upcode, text, grid) {
      let realCommCode = this.$store.state.code.codeInfo;
      let selectCode = [];
      let labelKey = grid ? "text" : "label";
      let a = [{ [labelKey]: text, value: null }];
      if (upcode === "YN") {
        selectCode = [
          { text: "사용", value: "Y" },
          { text: "미사용", value: "N" },
        ];
      } else {
        for (var i in realCommCode) {
          if (realCommCode[i].value === upcode) {
            selectCode = realCommCode[i].list;
            break;
          }
        }
      }

      let returlCode = selectCode.map((obj) => {
        return {
          [labelKey]: obj.text,
          value: obj.value,
        };
      });

      returlCode =
        text !== undefined && text !== "" ? [...a, ...returlCode] : returlCode;
      return returlCode;
    },
    replaceLucyFilter(text) {
      text = text.replace(/&lt;/gi, "<");
      text = text.replace(/&gt;/gi, ">");
      text = text.replace(/&amp;/gi, "&");
      text = text.replace(/&quot;/gi, '"');
      text = text.replace(/&lsquo;/gi, "'");
      text = text.replace(/&rsquo;/gi, "'");

      return text;
    },
    headerUserInfo() {
      let searchparams = {};
      this.$http
        .post("/api/main/headerUserInfo", searchparams)
        .then((response) => {
          //this.totalCnt = response.data.data.contents.length;
          //debugger;
          if (response.data) {
            // 회원
            var memberInfo = response.data.memberInfo;
            this.$store.state.userInfo.empNo = memberInfo.empNo;
            this.$store.state.userInfo.userNm = memberInfo.empNm;
            this.$store.state.userInfo.groupauthId = memberInfo.groupauthId;
            this.$store.state.userInfo.emailTxt = memberInfo.emailTxt;
            this.$store.state.userInfo.dptNm = memberInfo.dptNm;
            this.$store.state.userInfo.gradeNm = memberInfo.gradeNm;
            this.$store.state.userInfo.mobilTxt = memberInfo.mobilTxt;
            this.$store.state.userInfo.profileImg = memberInfo.profileImg;
            this.$store.state.userInfo.calendarReadOnly =
              this.$store.state.userInfo.groupauthId == "10001" ? false : true;
            this.headerUserImageView();
          }
        });
    },
    headerUserImageView() {
      //이미지 view
      this.fileInfo.fileName = this.$store.state.userInfo.profileImg;
      this.fileInfo.filePath = this.$store.state.userInfo.profileImg;
      this.imageViewMixin(this.fileInfo).then((response) => {
        if (response.data.size > 0) {
          this.$store.state.userInfo.profileImg = this.imageBlobMixin(
            response.data
          );
        } else {
          this.$store.state.userInfo.profileImg =
            "/img/common_nav_profile_no_img_126.84e2c562.png";
        }
      });
    },
    memberInfoPopup() {
      this.$refs.memInfoPop.open(this.$store.state.userInfo.empNo);
      return;
    },
  },
};
