export const Grids = {
    data(){
        commCode : [];
    },
    created() {
        this.commCode = this.$store.state.code.code;
    },
    methods: {
      gLink:function(cell){ return "<u class='valid-success-text' style='cursor:pointer'>"+cell.value+"</u>" },
      gDetail:function(cell){ return "<u style='cursor:pointer;color:#00aa00'>"+cell.value+"</u>" },
      gMLink:function(cell){
          let uLink = cell.value;
          if(this.$store.state.isMobile) uLink =  "<u class='valid-success-text' style='cursor:pointer'>"+cell.value+"</u>"
          return  uLink;
      },
      gSelect(upcode, text) {
          let selectCode = "";
          let a = [{text:text, value: ''}];
          if(upcode === "YN") {
              selectCode =  [{text:'사용', value: 'Y'}, {text:'미사용', value: 'N'}];
          } else {
              for(var i in this.commCode) {
                  if(this.commCode[i].value === upcode) {
                      selectCode = this.commCode[i].list;
                      break;
                  }
              }
          }
          return { type:'select', options: { listItems: (text !== undefined && text !== "") ? [...a, ...selectCode] : selectCode } };
      },
  }
}
