<template>
  <ModalPopup
    :title="'임직원개인정보'"
    type="type-black"
    :visible.sync="modalVisible"
    width="900px"
  >
    <div class="modal-white-box">
      <div class="form-table">
        <table>
          <colgroup>
            <col class="col-form-title" />
            <col style="width: 286px;" />
            <col class="col-form-title" />
            <col style="width: 266px;" />
          </colgroup>
          <tbody>
            <tr>
              <th scope="row" rowspan="5">
                <span class="offscreen">프로필 사진</span>
              </th>
              <td rowspan="5" class="vertical-middle">
                <div class="profile-img-box">
                  <!-- <img
                    class="profile-img"
                    :src="bgImg"
                    @error="imageLoadError"
                  /> -->
                  <span class="profile-img" :style="{backgroundImage: 'url('+ this.bgImg +')'}"></span>
                  <span class="profile-select">
                    <FileButton @change="onChangeImages" />
                  </span>
                </div>
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">임직원번호</th>
              <td>
                <InputBox
                  type="text"
                  :value.sync="memInfo.empNo"
                  disabled="true"
                />
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">로그인ID</th>
              <td>
                <InputBox
                  type="text"
                  ref="loginId"
                  :value.sync="memInfo.loginId"
                  disabled="true"
                />
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">임직원명</th>
              <td>
                <InputBox
                  type="text"
                  ref="empNm"
                  :value.sync="memInfo.empNm"
                  disabled="true"
                />
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">비밀번호</th>
              <td>
                <div class="input-flex-box">
                  <InputBox
                    type="password"
                    ref="passwdTxt"
                    :value.sync="memInfo.passwdTxt"
                    disabled="true"
                  />
                  <button
                    type="button"
                    class="btn"
                    @click="onPasswordPopupShow"
                  >
                    비밀번호 변경
                  </button>
                </div>
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">부서(팀)</th>
              <td>
                <vSelect
                  v-model="memInfo.dptNo"
                  :options="code.dptCds"
                  :clearable="false"
                  :searchable="false"
                  :reduce="(label) => label.code"
                />
              </td>
              <th scope="row" class="required">직책</th>
              <td>
                <vSelect
                  v-model="memInfo.positionCd"
                  :options="code.positionCds"
                  :clearable="false"
                  :searchable="false"
                  :reduce="(label) => label.code"
                />
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">직급</th>
              <td>
                <vSelect
                  v-model="memInfo.gradeCd"
                  :options="code.gradeCds"
                  :clearable="false"
                  :searchable="false"
                  :reduce="(label) => label.code"
                />
              </td>
              <th scope="row" class="required">E-Mail</th>
              <td>
                <InputBox
                  type="email"
                  ref="emailTxt"
                  :value.sync="memInfo.emailTxt"
                  inputWidth="100%"
                />
              </td>
            </tr>
            <tr>
              <th scope="row" class="required">휴대폰번호</th>
              <td>
                <InputBox
                  type="text"
                  ref="mobilTxt"
                  :value.sync="memInfo.mobilTxt"
                  maxlength="10"
                />
              </td>
              <th scope="row">전화번호</th>
              <td>
                <InputBox type="text" :value.sync="memInfo.comptelTxt" />
              </td>
            </tr>
            <tr>
              <th scope="row">주소</th>
              <td colspan="3">
                <div class="address-box">
                  <InputBox
                    class="zip-code"
                    type="text"
                    :value.sync="memInfo.zipnumCd"
                    disabled="true"
                  />
                  <button type="button" class="btn" @click="openDaumAddrPop">
                    우편번호찾기
                  </button>
                  <InputBox
                    class="address-pre"
                    type="text"
                    :value.sync="memInfo.zipaddrTxt"
                    disabled="true"
                  />
                  <InputBox
                    class="address-sub"
                    type="text"
                    :value.sync="memInfo.addressTxt"
                  />
                </div>
              </td>
            </tr>
            <tr v-if="groupAuthCode == '10001' && 'Y' === masterYn">
              <th scope="row" class="required">권한그룹</th>
              <td colspan="3" class="td-rdo">
                <vSelect
                  v-model="memInfo.groupauthId"
                  :options="groupAuthList"
                  :clearable="false"
                  :searchable="false"
                  :reduce="(label) => label.code"
                />
              </td>
            </tr>
            <!--<tr>
								<th scope="row">프로젝트<br/>메일수신 여부</th>
								<td colspan="3" class="td-rdo">
                  <div class="rdo-box">
                    <CheckBox class="ui-chk" v-model="memInfo.pmYn" :value="'Y'" :text="'메일 수신자로 추가할 경우 체크해주세요.'" :checked="'Y' == memInfo.pmYn" @checkChange="pmYnCheckboxChange" />
                  </div>
								</td>
							</tr>
              <tr>
								<th scope="row">메일 수신자</th>
								<td colspan="3">
                  <vSelect v-model="pmEmpNm" :options="pmList" :clearable="false" :searchable="false" @input="changedValue($event)"/>
                  <span class="tilde-text"></span>
                  <button type="button" class="btn" @click="delMailUserList">삭제</button>
                  <div class="attach-file-box">
                    <ul class="attached-file-list">
                      <li class="file-item" v-for="(item, index) in mailUserList">
                        <div class="file-item-info">
                          <span class="file-name">{{ index+1 }}.{{ item.label }}</span>
                        </div>
                      </li>
                    </ul>
                  </div>
								</td>
							</tr>-->
          </tbody>
        </table>
      </div>
    </div>
    <p class="modal-box-title">부가정보</p>
    <div class="modal-white-box">
      <div class="form-table">
        <table>
          <colgroup>
            <col class="col-form-title" />
            <col style="width: 286px;" />
            <col class="col-form-title" />
            <col style="width: 266px;" />
          </colgroup>
          <tbody>
            <tr>
              <th scope="row">생년월일</th>
              <td>
                <div class="sun-luna-date-box">
                  <DatePicker ref="birthdayTxt" />
                  <RadioButton
                    :text="'양력'"
                    :value="'Y'"
                    name="radioSolarYn"
                    :checked="'Y' == memInfo.solarYn"
                    @radioChange="solarYnRadioChange"
                  />
                  <RadioButton
                    :text="'음력'"
                    :value="'N'"
                    name="radioSolarYn"
                    :checked="'N' == memInfo.solarYn"
                    @radioChange="solarYnRadioChange"
                  />
                </div>
              </td>
              <th scope="row">성별</th>
              <td class="td-rdo">
                <div class="rdo-box">
                  <RadioButton
                    :text="'남자'"
                    :value="'M'"
                    name="radioGenderType"
                    :checked="'M' == memInfo.genderType"
                    @radioChange="genderTypeRadioChange"
                  />
                  <RadioButton
                    :text="'여자'"
                    :value="'F'"
                    name="radioGenderType"
                    :checked="'F' == memInfo.genderType"
                    @radioChange="genderTypeRadioChange"
                  />
                </div>
              </td>
            </tr>
            <tr>
              <th scope="row">결혼기념일</th>
              <td>
                <div class="sun-luna-date-box">
                  <DatePicker ref="marryYmd" />
                  <RadioButton
                    :text="'기혼'"
                    :value="'Y'"
                    name="radioMarryYn"
                    :checked="'Y' == memInfo.marryYn"
                    @radioChange="marryYnRadioChange"
                  />
                  <RadioButton
                    :text="'미혼'"
                    :value="'N'"
                    name="radioMarryYn"
                    :checked="'N' == memInfo.marryYn"
                    @radioChange="marryYnRadioChange"
                  />
                </div>
              </td>
              <th scope="row">집전화번호</th>
              <td>
                <InputBox type="text" :value.sync="memInfo.telTxt" />
              </td>
            </tr>
            <tr v-if="'Y' === masterYn">
              <th scope="row" class="required">입사일</th>
              <td>
                <DatePicker ref="joinDt" />
              </td>
              <th scope="row">퇴사일</th>
              <td>
                  <div class="input-flex-box">
                  <DatePicker ref="outDt" />
                <button
                  type="button"
                  class="btn"
                  @click="onOutBtn"
                >
                  퇴사처리
                </button>
                </div>
              </td>
            </tr>
            <tr v-else>
                <th scope="row" class="required">입사일</th>
                <td>
                  <DatePicker ref="joinDt" />
                </td>
            </tr>
            <tr v-if="'Y' === masterYn">
              <th scope="row">퇴사사유</th>
              <td>
                <vSelect
                  v-if="!memInfo.outCdFlag"
                  v-model="memInfo.outCd"
                  :options="code.outCds"
                  :clearable="false"
                  :searchable="false"
                  :reduce="(label) => label.code"
                />
                <InputBox
                  type="text"
                  v-else
                  :value.sync="memInfo.outNm"
                  disabled="true"
                />
              </td>
              <th scope="row">퇴사상세사유</th>
              <td>
                <div class="text-area-box" v-if="!memInfo.outCdFlag">
                  <textarea rows="1" v-model="memInfo.outcommTxt"></textarea>
                </div>
                <InputBox
                  type="text"
                  v-if="memInfo.outCdFlag"
                  :value.sync="memInfo.outcommTxt"
                  disabled="true"
                />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <template>
      <p class="modal-box-title">
        이미지정보(가로와 세로 길이가 동일한 이미지를 등록해주세요.)
      </p>
      <div class="modal-white-box">
        <div class="form-table">
          <table>
            <colgroup>
              <col class="col-form-title" />
              <col style="width: 286px;" />
              <col class="col-form-title" />
              <col style="width: 266px;" />
            </colgroup>
            <tbody>
              <tr>
                <th scope="row">SIGN 이미지</th>
                <td>
                  <div class="input-flex-box">
                    <FileButton label="파일선택" @change="onChangeSignImage" />
                  </div>
                  <div class="attach-file-box">
                    <ul class="attached-file-list">
                      <li class="file-item" v-if="memInfo.signImg">
                        <div class="file-item-info">
                          <span
                            class="file-name"
                            @click="() => downloadFile('signImg')"
                            >{{ memInfo.signImg }}</span
                          >
                          <!--<button
                          type="button"
                          class="btn-remove-file"
                          @click="() => deleteFile('signImg')"
                        >
                        파일삭제
                        </button>-->
                        </div>
                      </li>
                    </ul>
                  </div>
                </td>
                <th scope="row">SIGN 이미지</th>
                <td>
                  <div class="attach-sign-box">
                    <div class="sign-view-box">
                      <img v-if="this.isNotEmpty(memInfo.signImg)" class="img-selected-sign" :src="signBgImg"/>
                      <!--<span v-if="false" class="img-selected-sign" :style="{backgroundImage: 'url('+ this.signBgImg +')'}"></span>-->
                    </div>
                    <!--<button type="button" v-if="this.isNotEmpty(memInfo.signImg)" class="btn btn-control">등록됨</button>-->
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </template>
    <div class="modal-popup-btn-box">
      <button
        type="button"
        v-if="this.isEmpty(memInfo.outDt)"
        class="btn btn-primary"
        @click.prevent.stop="save"
      >
        수정
      </button>
      <button type="button" class="btn" @click.prevent.stop="closed">
        닫기
      </button>
    </div>
    <daum-address-pop
      ref="daumAddrPop"
      :width="'750px'"
      @closedAddrPop="closedAddrPop"
    />
    <PwdChangeMasterPopup
      ref="passwdChgPop"
      @closedPasswdChangePopup="closedPasswdChangePopup"
    />
  </ModalPopup>
</template>

<script>
import { mapState, mapActions } from "vuex";
import "vue-select/dist/vue-select.css";
import vSelect from "vue-select";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import { Comm } from "@/mixins/Comm";
import CheckBox from "@/components/CheckBox";
import InputBox from "@/components/InputBox";
import DatePicker from "@/components/Datepicker";
import ModalPopup from "@/components/ModalPopup";
import RadioButton from "@/components/RadioButton";
import FileButton from "@/components/FileButton";
import PwdChangeMasterPopup from "@/views/member/pop/PwdChangeMasterPopup";
import daumAddressPop from "@/views/myPage/pop/DaumAddressPop";

export default {
  name: "MemberInfoPopup",
  mixins: [Check, Empty, Comm],
  components: {
    DatePicker,
    CheckBox,
    InputBox,
    vSelect,
    ModalPopup,
    PwdChangeMasterPopup,
    RadioButton,
    FileButton,
    daumAddressPop,
  },
  props: ["masterYn"],
  mounted() {},
  data() {
    return {
      tmpMailUserList: [],
      mailUserList: [],
      pmEmpNm: "",
      codeSearchData: ["10000", "10300", "10100", "10500"],
      modalVisible: false,
      memInfo: {
        masterYn: "N",
        empNo: "",
        loginId: "",
        empNm: "",
        passwdTxt: "",
        dptNo: "",
        positionCd: "",
        gradeCd: "",
        emailTxt: "",
        mobilTxt: "",
        comptelTxt: "",
        zipnumCd: "",
        zipaddrTxt: "",
        addressTxt: "",
        pmYn: "",
        pmEmpNo: "",
        birthdayTxt: "",
        solarYn: "Y",
        genderType: "M",
        marryYmd: "",
        marryYn: "N",
        telTxt: "",
        joinDt: "",
        outDt: "",
        outCd: "선택",
        profileImg: "",
        signImg: "",
        groupauthId: "",
        outcommTxt: "",
        outDtFlag: false,
        outCdFlag: false,
        outcommTxtFlag: false,
      },
      fileInfo: { fileName: "", filePath: "", imageSrc: "" },
      code: {
        dptCds: [],
        positionCds: [],
        gradeCds: [],
        outCds: [],
      },
      groupAuthCode: "",
      groupAuthList: [],
      pmList: [],
      mailUserNm: [],
      bgImg: "",
      signBgImg: "",
      errorImg: "/img/common_nav_profile_no_img_126.84e2c562.png",
    };
  },
  computed: {
    ...mapState({
      main: (state) => state.main,
    }),
  },
  watch: {},
  // created() {
  //   this.token();
  // },
  methods: {
    imageLoadError() {
      this.bgImg = this.errorImg;
    },
    onChangeImages(e) {
      const fileName = e.event.target.files[0].name;
      const file = e.event.target.files[0];
      this.bgImg = window.URL.createObjectURL(file);

      //이미지 업로드
      const fd = new FormData();
      fd.append("uploadFiles", e.event.target.files[0]);
      fd.append("editorYn", "N");

      this.$http
        .post("/api/common/fileUploadRename", fd, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          this.memInfo.profileImg = response.data.data[0].filePath;
          return response;
        })
        .catch((ex) => {
          alert("이미지 등록중 오류가 발생했습니다.");
          return false;
        });
    },
    changedValue(e) {
      //vSelect Box
      let mailUserCode = e.code;
      let mailUserNm = e.label;

      if (this.isNotEmpty(mailUserCode)) {
        let dupChk = false;

        for (let i in this.mailUserList) {
          if (this.mailUserList[i].code == mailUserCode) {
            dupChk = true;
            continue;
          }
        }

        if (dupChk) {
          alert("이미 등록되어있는 수신자입니다");

          //vSelect Box
          this.pmEmpNm = this.pmList[0];

          return false;
        }

        let mailUserObj = {};
        mailUserObj.code = mailUserCode;
        mailUserObj.label = mailUserNm;

        this.tmpMailUserList.push(mailUserObj);
        this.mailUserList = this.tmpMailUserList.slice();

        let mailUserCd = this.mailUserList.map((a) => a.code);

        if (mailUserCd.length > 0) {
          this.memInfo.pmEmpNo = mailUserCd.join(", ");
        }

        this.pmEmpNm = this.pmList[0];
      }
    },
    mailUserNameSearch() {
      let parms = {};
      parms.pmEmpNo = this.memInfo.pmEmpNo;

      this.$http
        .post("/api/member/getMailUserName", parms)
        .then((response) => {
          if (response.data.statusCode != 200) {
            alert("오류가 발생했습니다.");
            return false;
          }

          this.mailUserList = response.data.data.mailUserList;
        })
        .catch((e) => {
          console.log("메일 수신자 조회 error : ", e);
        });
    },
    open(empNoVal) {

      if (this.isEmpty(empNoVal)) {
        //임직원번호가 없으면 alert 메세지 노출
        alert("임직원번호가 없습니다.");
        return false;
      } else {
        this.codeSearch();
        this.resetInfo();

        let searchParam = {};
        searchParam.empNo = empNoVal;
        searchParam.masterYn = this.masterYn;

        this.$http
          .post("/api/member/infoPopup", searchParam)
          .then((response) => {
            if (response.data.statusCode != 200) {
              alert("오류가 발생했습니다.");
              return false;
            }

            let rtnObj = response.data.data.contents;

            this.memInfo = rtnObj;

            this.fileInfo.fileName = this.memInfo.profileImg;
            this.fileInfo.filePath = this.memInfo.profileImg;

            this.$refs.birthdayTxt.instance.setDate(
              new Date(rtnObj.birthdayTxt)
            );
            this.$refs.marryYmd.instance.setDate(new Date(rtnObj.marryYmd));

            this.$refs.joinDt.instance.setDate(new Date(rtnObj.joinDt));

            if(this.masterYn === "Y" && this.isNotEmpty(rtnObj.outDt)) {
                this.$refs.outDt.instance.setDate(new Date(rtnObj.outDt));
            }


            if (this.isNotEmpty(this.memInfo.profileImg)) {
              //임직원 이미지 정보
              this.setProfileImg();
            }

            if (this.isNotEmpty(this.memInfo.signImg)) {
              this.fileInfo.fileName = this.memInfo.signImg;
              this.fileInfo.filePath = this.memInfo.signImg;
              //임직원 sign 이미지 정보
              this.setSignImg();
            }

            if (this.isNotEmpty(this.memInfo.outcommTxt)) {
              this.memInfo.outcommTxtFlag = true;
            }

            //로그인 권한그룹코드
            this.groupAuthCode = response.data.data.groupAuthCode;

            //권한그룹
            this.groupAuthList = response.data.data.groupAuthList;

            this.pmList = response.data.data.pmList;

            if (this.isNotEmpty(this.pmList)) {
              //vSelect 임시
              this.pmList.unshift({ label: "없음", code: "" });
              this.pmEmpNm = this.pmList[0];
            }

            //메일수신자명 조회
            this.mailUserNameSearch();
          })
          // .catch((e) => {
          //   console.log("임직원정보 조회 error : ", e);
          // });
      }


      this.modalVisible = true;
    },
    closed(obj) {
      this.modalVisible = false;
      this.resetInfo();
      //부모 함수 콜백
      this.$emit("closedMemberInfoPopup", obj);
    },
    codeSearch() {
      let searchparms = {};
      searchparms.codes = this.codeSearchData;

      this.$http
        .post("/api/code/getCodeSearch", searchparms)
        .then((response) => {
          //코드 세팅
          this.code.dptCds = response.data.data[this.codeSearchData[0]];
          this.code.positionCds = response.data.data[this.codeSearchData[1]];
          //this.code.gradeCds.unshift({label: "전체", code: ""});

          this.code.gradeCds = response.data.data[this.codeSearchData[2]];

          this.code.outCds = [
            "선택",
            ...response.data.data[this.codeSearchData[3]],
          ];
        })
        .catch((e) => {
          console.log("공통코드 조회 error : ", e);
        });
    },
    search() {},
    save() {
      if (!this.validation()) {
        return false;
      }

      this.memInfo.masterYn = this.masterYn;

      this.$http.post("/api/member/modify", this.memInfo).then((response) => {
        if (response.data.statusCode != 200) {
          alert(response.data.message);
          return false;
      } else {
          alert('수정되었습니다.');
      }

        this.headerUserInfo();
        this.closed("success");
      });
    },
    validation() {
      if (this.isEmpty(this.memInfo.empNo)) {
        alert("임직원번호는 필수입니다.\n 관리자에게 문의하시기 바랍니다.");
        return false;
      }

      if (this.isEmpty(this.memInfo.loginId)) {
        alert("로그인ID는 필수 입력입니다.");
        this.$refs.loginId.focus();
        return false;
      }

      if (this.isEmpty(this.memInfo.empNm)) {
        alert("임직원명은 필수 입력입니다.");
        this.$refs.empNm.focus();
        return false;
      }

      if (this.isEmpty(this.memInfo.passwdTxt)) {
        alert("비밀번호는 필수 입력입니다.");
        this.$refs.passwdTxt.focus();
        return false;
      }

      let dptNo = this.memInfo.dptNo;

      if (typeof this.memInfo.dptNo == "object") {
        dptNo = this.memInfo.dptNo.code;
        this.memInfo.dptNo = this.memInfo.dptNo.code;
      }

      if (this.isEmpty(dptNo)) {
        alert("부서를 선택해주세요.");
        return false;
      }

      let positionCd = this.memInfo.positionCd;

      if (typeof this.memInfo.positionCd === "object") {
        positionCd = this.memInfo.positionCd.code;
        this.memInfo.positionCd = this.memInfo.positionCd.code;
      }

      if (this.isEmpty(positionCd)) {
        alert("직책을 선택해주세요.");
        return false;
      }

      let gradeCd = this.memInfo.gradeCd;

      if (typeof this.memInfo.gradeCd === "object") {
        gradeCd = this.memInfo.gradeCd.code;
        this.memInfo.gradeCd = this.memInfo.gradeCd.code;
      }

      if (this.isEmpty(gradeCd)) {
        alert("직급을 선택해주세요.");
        return false;
      }

      if (this.isEmpty(this.memInfo.emailTxt)) {
        alert("E-Mail은 필수 입력입니다.");
        this.$refs.emailTxt.focus();
        return false;
      }

      if (this.isEmpty(this.memInfo.mobilTxt)) {
        alert("휴대폰번호는 필수 입력입니다.");
        this.$refs.mobilTxt.focus();
        return false;
      }

      let groupauthId = this.memInfo.groupauthId;

      if (typeof this.memInfo.groupauthId === "object") {
        groupauthId = this.memInfo.groupauthId.code;
        this.memInfo.groupauthId = this.memInfo.groupauthId.code;
      }
      if (this.groupAuthCode == "10001" && this.isEmpty(groupauthId)) {
        alert("권한그룹을 선택해주세요.");
        return false;
      }

      if (this.isNotEmpty(this.$refs.birthdayTxt.instance.getDate())) {
        this.memInfo.birthdayTxt = this.getDateByDelimeter(
          this.$refs.birthdayTxt.instance.getDate(),
          "-"
        );
      }

      if (this.memInfo.marryYn == "Y") {
        if (this.isEmpty(this.$refs.marryYmd.instance.getDate())) {
          alert("결혼기념일을 입력해 주세요");
          return false;
        }
      }

      if (this.isNotEmpty(this.$refs.marryYmd.instance.getDate())) {
        this.memInfo.marryYmd = this.getDateByDelimeter(
          this.$refs.marryYmd.instance.getDate(),
          "-"
        );
      }

      if (this.isEmpty(this.$refs.joinDt.instance.getDate())) {
        alert("입사일은 필수 입력입니다.");
        return false;
      } else {
        this.memInfo.joinDt = this.getDateByDelimeter(
          this.$refs.joinDt.instance.getDate(),
          "-"
        );
      }

      return true;
    },
    resetInfo() {
      this.memInfo.loginId = "";
      this.memInfo.empNm = "";
      this.memInfo.passwdTxt = "";
      this.memInfo.passwdTxt2 = "";
      this.memInfo.emailTxt = "";
      this.memInfo.mobilTxt = "";
      this.memInfo.comptelTxt = "";
      this.memInfo.zipnumCd = "";
      this.memInfo.zipaddrTxt = "";
      this.memInfo.addressTxt = "";
      this.memInfo.pmYn = "";

      this.memInfo.birthdayTxt = "";
      this.memInfo.solarYn = "Y";
      this.memInfo.genderType = "M";
      this.memInfo.marryYmd = "";
      this.memInfo.marryYn = "N";
      this.memInfo.telTxt = "";
      this.memInfo.joinDt = "";
      this.memInfo.outDt = "";

      this.tmpMailUserList = [];
      this.mailUserList = [];
      this.pmList = [];
      this.bgImg = "";
      this.signBgImg = "";
    },
    openDaumAddrPop() {
      this.$refs.daumAddrPop.open();
    },
    closedAddrPop(obj) {
      this.memInfo.zipnumCd = obj.zonecode;
      this.memInfo.zipaddrTxt = obj.address;
    },
    onPasswordPopupShow() {
      this.$refs.passwdChgPop.open(this.memInfo.empNo);
    },
    closedPasswdChangePopup(rtnObj) {
      if (!this.isEmpty(rtnObj)) {
        if (rtnObj == "success") {
          //this.search();
        }
      }
    },
    delMailUserList() {
      this.tmpMailUserList = [];
      this.mailUserList = [];
      this.memInfo.pmEmpNo = "";
    },
    solarYnRadioChange(e) {
      this.memInfo.solarYn = e.target.value;
    },
    genderTypeRadioChange(e) {
      this.memInfo.genderType = e.target.value;
    },
    marryYnRadioChange(e) {
      this.memInfo.marryYn = e.target.value;
    },
    pmYnCheckboxChange(e) {
      if (e.target.checked) {
        this.memInfo.pmYn = "Y";
      } else {
        this.memInfo.pmYn = "";
      }
    },
    onOutBtn() {
      if (this.isEmpty(this.$refs.outDt.instance.getDate())) {
          alert("퇴사일자를 입력해 주세요.");
          return false;
      } else {
          this.memInfo.outDt = this.getDateByDelimeter(
            this.$refs.outDt.instance.getDate(),
            "-"
          );
      }

      if (this.isEmpty(this.memInfo.outCd)) {
        alert("퇴사사유를 선택해주세요.");
        return false;
      }

      if (!confirm("퇴사처리 하시겠습니까?")) {
        return false;
      }

      this.$http
        .post("/api/member/setRetire", this.memInfo)
        .then((response) => {
          if (response.data.statusCode != 200) {
            alert(response.data.message);
            return false;
          }

          this.closed("success");
        });
    },
    onChangeSignImage(e) {

      const fileName = e.event.target.files[0].name;
      const file = e.event.target.files[0];
      this.signBgImg = window.URL.createObjectURL(file);

      //업로드
      const fd = new FormData();
      fd.append("uploadFiles", e.event.target.files[0]);
      fd.append("editorYn", "N");

      this.$http
        .post("/api/common/fileUploadRename", fd, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          this.memInfo.signImg = response.data.data[0].filePath;
          return response;
        })
        .catch((ex) => {
          alert("SIGN 이미지 등록중 오류가 발생했습니다.");
          return false;
        });
    },
    setProfileImg() {
      this.imageViewMixin(this.fileInfo).then((response) => {
        if (response.data.size > 0) {
          this.bgImg = this.imageBlobMixin(response.data);
        } else {
          this.bgImg = "/img/common_nav_profile_no_img_126.84e2c562.png";
        }
      });
    },
    setSignImg() {
      this.imageViewMixin(this.fileInfo).then((response) => {
        if (response.data.size > 0) {
          this.signBgImg = this.imageBlobMixin(response.data);
        } else {
          this.signBgImg = "";
        }
      });
    },
  },
};
</script>
