<template>
  <div id="app" class="app-wrap" :class="{'mobile': this.$store.state.isMobile, 'mobile-menu-open': this.$store.state.mobileMenuOpen}">
    <Header/>
    <div class="main-wrap">
			<router-view />
		</div>
  </div>
</template>

<script>

import Header from "@/components/common/Header";

export default {
  name: "App",
  components: {
    Header,
  },
  data(){
    return {
      toggleProfile: false,
      menuopen: false
    }
  },
  methods: {
      onToggleProfile(event) {
          this.toggleProfile = !this.toggleProfile;
      },
      onLogout(event) {
          this.$store.state.loggedIn = false;
          this.$router.push('/');
      },
  },
  mounted() {
    const mediaQueryList = window.matchMedia("(min-width: 1024px)");
    const store = this.$store;
    mediaQueryList.addListener(matchMobile);
    matchMobile();

    function matchMobile() {
      if (mediaQueryList.matches) {
        store.state.isMobile = false;
      } else {
        store.state.isMobile = true;
      }
    }
  }
};
</script>

<style lang="scss">
.app-wrap {
	font-family: 'Open sans', 'Noto Sans KR', sans-serif;
	font-size: 0;
}
</style>
