<template lang="html">
  <div>
    <ModalPopup :title="'코드관리'" :visible.sync="modalVisible" width="700px">
      <loading :active.sync="isLoading" :is-full-page="true"/>
      <div class="form-table">
        <table>
        <colgroup>
          <col class="col-form-title">
          <col style="width: 100%;">
          <col class="col-form-title">
          <col style="width: 100%;">
        </colgroup>
        <tbody>
          <tr>
            <th scope="row" class="requried">상위코드명</th>
            <td>
              <InputBox type="text" :value.sync="detail.upcodeNm" disabled="true" />
            </td>
            <th scope="row" class="requried">상위코드ID</th>
            <td>
              <InputBox type="text" :value.sync="detail.upcodeCd" disabled="true" />
            </td>
          </tr>
          <tr>
            <th scope="row" class="requried">코드명</th>
            <td>
              <InputBox type="text" ref="codeNm" v-model="detail.codeNm" :value.sync="detail.codeNm"  />
            </td>
            <th scope="row" class="requried">코드ID</th>
            <td>
              <InputBox type="text" v-model="detail.codeCd" :value.sync="detail.codeCd" disabled="true" />
            </td>
          </tr>
          <tr>
            <th scope="row">사용여부</th>
            <td>
              <vSelect v-model="detail.useYn" :reduce="label => label.value" :options="useYnList.options" :clearable="false" :searchable="false"></vSelect>
            </td>
            <th scope="row">정렬순서</th>
            <td>
              <InputBox type="text" ref="sortSeq" v-model="detail.sortSeq" :value.sync="detail.sortSeq" />
            </td>
          </tr>
          <tr>
            <th scope="row">임시필드1</th>
            <td>
              <InputBox type="text" v-model="detail.tmpfield1Txt" :value.sync="detail.tmpfield1Txt" />
            </td>
            <th scope="row">임시필드2</th>
            <td>
              <InputBox type="text" :v-model="detail.tmpfield2Txt" :value.sync="detail.tmpfield2Txt" />
            </td>
          </tr>
          <tr>
            <th scope="row">임시필드3</th>
            <td>
              <InputBox type="text" v-model="detail.tmpfield3Txt" :value.sync="detail.tmpfield3Txt" />
            </td>
            <th scope="row">내부연동코드</th>
            <td>
              <InputBox type="text" v-model="detail.insideTxt" :value.sync="detail.insideTxt" />
            </td>
          </tr>
          <tr>
            <th scope="row">비고</th>
            <td colspan="3">
              <div class="text-area-box">
                <textarea rows="3" v-model="detail.commentTxt"></textarea>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      </div>
      <div class="modal-popup-btn-box">
        <button type="button" class="btn btn-primary" @click.prevent.stop="save">저장</button>
        <button type="button" class="btn" @click.prevent.stop="closed">취소</button>
      </div>
    </ModalPopup>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import 'vue-select/dist/vue-select.css';
import vSelect from 'vue-select'
import Loading from "vue-loading-overlay";
import InputBox from '@/components/InputBox';
import ModalPopup from "@/components/ModalPopup";

export default {
  name: "CodeDetailPop",
  mixins: [Check, Empty],
  components: {
    InputBox,
    vSelect,
    loading: Loading,
    ModalPopup
  },
  props:{
    upcodeCd: {
			default: '00000',
    },
    upcodeNm: {
			default: '공통코드',
		}
  },
  data() {
    return {
      modalVisible: false,
      useYnList: {options:[{label: '선택하세요', value: '' },{label: '사용', value: 'Y' },{label: '미사용', value: 'N'}]},
      detail: { upcodeNm: '', upcodeCd: '', codeNm: '', codeCd: '', useYn: '', sortSeq: '',
                tmpfield1Txt: '', tmpfield2Txt: '', tmpfield3Txt: '', insideTxt: '', commentTxt: ''},
      isLoading: false,
    };
  },
  computed: {
    // ...mapState(["main"]),
    ...mapState({
      main: (state) => state.main,
    }),
  },
  created() {
  },
  methods: {
    open(codeCd) {
      if( this.isNotEmpty(codeCd) ){
        let searchparms = {};
        searchparms.codeCd = codeCd;

        this.$http.post("/api/code/getCodeInfo",searchparms)
            .then((response) => {
              if(response.data.statusCode != 200){
                  alert('오류가 발생했습니다.');
                  return false;
              }
              let rtnObj = response.data.data[0];
              this.detail = rtnObj;
            })
            .catch((e) => {
              console.log(e);
            })
            .finally(() => {
              this.isLoading = false;
            });
      }else{
        this.resetInfo();
        this.detail.upcodeCd = this.upcodeCd;
        this.detail.upcodeNm = this.upcodeNm;
      }
      this.modalVisible = true;
    },
    save(){
      if( !this.validation() ){ return false; }
      let apiUrl = '/api/code/systemCodeAddEnd';
      if( !this.isEmpty(this.detail.codeCd) ){
        apiUrl = '/api/code/systemCodeModifyEnd';
      }

      this.isLoading = true;
      this.$http.post(apiUrl, this.detail)
              .then((response) => {
                if(response.data.statusCode != 200){
                  alert(response.data.message);
                  return false;
                } else{
                  alert(response.data.message);
                }
                this.closed('success');
              }
            )
            .catch((e) => {
              console.log(e);
            })
            .finally(() => {
              this.isLoading = false;
            });
    },
    validation(){
      if( this.isEmpty(this.detail.codeNm) ){
        alert("코드명은 필수값입니다.");
        this.$refs.codeNm.focus();
        return false;
      }

      let bytes = this.maxByteCheck(this.detail.codeNm);
      if( bytes > 50 ){
        this.detail.codeNm = this.detail.codeNm.substring(0, 100);
        alert("코드명은 100 바이트 이하로 입력하세요.");
        this.$refs.codeNm.focus();
        return false;
      }

      if( this.isEmpty(this.detail.useYn) ){
        alert('사용여부를 선택해 주세요!!!');
        return false;
      }

      if(this.isNotEmpty(this.detail.tmpfield1Txt)) {
          bytes = this.maxByteCheck(this.detail.tmpfield1Txt);
          if( bytes > 20 ){
            alert("임시필드1은 20 바이트 이하로 입력해 주세요.");
            return false;
          }
      }

      if(this.isNotEmpty(this.detail.tmpfield2Txt)) {
          bytes = this.maxByteCheck(this.detail.tmpfield2Txt);
          if( bytes > 20 ){
            alert("임시필드2는 20 바이트 이하로 입력해 주세요.");
            return false;
          }
      }

      if(this.isNotEmpty(this.detail.tmpfield3Txt)) {
          bytes = this.maxByteCheck(this.detail.tmpfield3Txt);
          if( bytes > 20 ){
            alert("임시필드3는 20 바이트 이하로 입력해 주세요");
            return false;
          }
      }

      if(this.isNotEmpty(this.detail.insideTxt)) {
          bytes = this.maxByteCheck(this.detail.insideTxt);
          if( bytes > 20 ){
            alert("내부연동코드는 20 바이트 이하로 입력해 주세요");
            return false;
          }
      }

      if(this.isNotEmpty(this.detail.commentTxt)) {
          bytes = this.maxByteCheck(this.detail.commentTxt);
          if( bytes > 4000 ){
            alert("비고는 4000 바이트 이하로 입력해 주세요");
            this.detail.commentTxt = this.detail.commentTxt.substring(0, 4000);
            return false;
          }
      }

      return true;
    },
    closed (obj) {
      this.modalVisible = false;
      //부모 함수 콜백
      this.$emit('closedDetailPopup', obj);
    },
    resetInfo(){
      this.detail.upcodeNm= '';
      this.detail.upcodeCd= '';
      this.detail.codeNm= '';
      this.detail.codeCd= '';
      this.detail.useYn= '';
      this.detail.sortSeq= '';
      this.detail.tmpfield1Txt= '';
      this.detail.tmpfield2Txt= '';
      this.detail.tmpfield3Txt= '';
      this.detail.insideTxt= '';
      this.detail.commentTxt= '';
    }
  }
}
</script>

<style lang="css" scoped>

</style>
