<template>
  <div class="contents-wrap">
    <p class="p-title">{{ menuName }}</p>
    <div>
      <div class="page-title-box">
        <h1 class="page-title"></h1>
        <ol class="breadcrumb-list">
          <li class="breadcrumb">홈</li>
          <li class="breadcrumb">인트라넷 관리</li>
          <li class="breadcrumb">{{ menuName }}</li>
        </ol>
      </div>
    </div>
    <div class="search-input-box" v-if="!this.$store.state.isMobile">
      <div class="search-input-row">
        <div class="search-item">
          <label for="dptNm">부서/팀</label>
          <div class="search-value">
            <v-select
              placeholder="전체"
              v-model="searchParam.dptNo"
              :reduce="(dpt) => dpt.value"
              :options="options[10000]"
              :clearable="false"
              :searchable="true"
            >
            </v-select>
          </div>
        </div>
        <div class="search-item">
          <label for="empNm">임직원</label>
          <div class="search-value">
            <input-box
              type="text"
              :value.sync="searchParam.empNm"
              :targetMethod="search"
            />
          </div>
        </div>
      </div>
      <div class="search-input-row">
        <div class="search-item">
          <label for="useYn">사용 여부</label>
          <div class="search-value">
            <v-select
              placeholder="전체"
              v-model="searchParam.useYn"
              :reduce="(yn) => yn.value"
              :options="options.useYnList"
              :clearable="false"
              :searchable="true"
            ></v-select>
          </div>
        </div>
        <div class="search-item">
          <label for="rgstDt">등록일</label>
          <div class="search-value">
            <div class="search-value date-range-box">
              <date-picker ref="startDt" :date.sync="searchParam.startDt" />
              <span class="tilde-text">~</span>
              <date-picker ref="endDt" :date.sync="searchParam.endDt" />
            </div>
          </div>
        </div>
        <div class="search-item confirm-box">
          <button
            type="button"
            @click.passive.stop="search"
            class="btn btn-primary"
          >
            검색
          </button>
        </div>
      </div>
    </div>
    <div class="grid-box">
      <div class="grid-tool-box">
        <div>
          <button type="button" class="btn btn-primary" @click="writePop">
            수신자 등록
          </button>
          <button type="button" class="btn" @click="enableReceiver">
            사용처리
          </button>
          <button type="button" class="btn" @click="disableReceiver">
            미사용처리
          </button>
          <button type="button" class="btn" @click="deleteReceiver">
            삭제
          </button>
        </div>
        <div>
          <button type="button" class="btn" @click="resetSearchForm">
            초기화
          </button>
        </div>
      </div>
      <grid
        class="tui-grid"
        ref="tuiGrid"
        :data="gridProps.data"
        :columns="gridProps.columns"
        :options="gridProps.options"
        @click="onClick"
      />
      <div class="per-page-box" v-if="!this.$store.state.isMobile">
        <span class="total-count-text">
          총
          <span class="count">{{ totalCnt }}</span
          >개
        </span>
        <div class="select-box">
          <v-select
            v-model="perPage"
            :reduce="(opt) => opt.value"
            :options="options.pageList"
            :clearable="false"
            :searchable="false"
            @input="onPerPageInput"
          ></v-select>
        </div>
      </div>
    </div>
    <family-event-mail-manage-pop
      ref="mailReceiverWritePop"
      :is-detail="false"
      @created="search"
    ></family-event-mail-manage-pop>
    <family-event-mail-manage-pop
      :is-detail="true"
      ref="mailReceiverDetailPop"
      @modified="search"
    ></family-event-mail-manage-pop>
    <loading
      :active.sync="isLoading"
      :on-cancel="loadingCancel"
      :is-full-page="fullPage"
    ></loading>
  </div>
</template>

<script>
import "tui-grid/dist/tui-grid.css";
import "vue-select/dist/vue-select.css";

import { mapState } from "vuex";
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";
import { Check } from "@/mixins/Check";
import { Grids } from "@/mixins/Grids";
import { Comm } from "@/mixins/Comm";
import { DateFunc } from "@/mixins/DateFunc";
import InputBox from "@/components/InputBox";
import DatePicker from "@/components/Datepicker";
import { CheckboxRenderer } from "@/components/renderer/GridRenderer";
import FamilyEventMailManagePop from "@/views/system/pop/FamilyEventMailManagePop";
import Loading from "vue-loading-overlay";

import TuiGrid from "tui-grid";
TuiGrid.setLanguage("ko");

export default {
  name: "FamilyEventMailManage",
  mixins: [Check, DateFunc, Grids, Comm],
  props: ["menuName", "upcodes"],
  components: {
    grid: Grid,
    vSelect,
    InputBox,
    DatePicker,
    FamilyEventMailManagePop,
    loading: Loading,
  },
  data() {
    return {
      perPage: 10,
      totalCnt: 0,
      isLoading: false,
      fullPage: true,
      gridProps: {},
      searchParam: {
        dptNo: null,
        useYn: "",
        empNm: "",
        startDt: "",
        endDt: "",
      },
      options: {
        "10000": [],
        useYnList: [
          { label: "전체", value: "" },
          { label: "사용", value: "Y" },
          { label: "미사용", value: "N" },
        ],
        pageList: [
          { label: "10", value: 10 },
          { label: "20", value: 20 },
          { label: "50", value: 50 },
          { label: "100", value: 100 },
        ],
      },
    };
  },
  created() {
    this.token();

    for (let upcode of this.upcodes) {
      this.options[upcode] = this.commSelect(upcode, "전체");
    }

    this.formatterInstall();
    this.initDates();

    this.gridProps = {
      options: {
        rowHeight: 48,
        rowHeaders: [
          {
            type: "checkbox",
            header: `
							<span class="ui-chk">
								<input id="chkAll" type="checkbox" name="_checked"><label for="chkAll"></label>
							</span>`,
            renderer: {
              type: CheckboxRenderer,
            },
          },
        ],
        scrollX: false,
        scrollY: true,
        bodyHeight: this.$store.state.isMobile ? "auto" : 480,
        showDummyRows: false,
        selectionUnit: "row",
        display: { noData: "No Data" },
        pageOptions: {
          useClient: true,
          perPage: this.perPage,
          visiblePages: 5,
        },
      },
      columns: [
        { header: "메뉴번호", name: "menuId", hidden: true },
        { header: "회원번호", name: "empNo", hidden: true },
        { header: "사용여부", name: "useYn", hidden: true },
        { header: "소속", name: "dptNm", align: "center" },
        {
          header: "이름",
          name: "empNm",
          align: "center",
          formatter: this.gDetail,
        },
        { header: "직책", name: "gradeNm", align: "center" },
        { header: "사용여부", name: "useYnNm", align: "center" },
        {
          header: "등록일",
          name: "regDt",
          align: "center",
        },
        { header: "등록자", name: "regEmpNm", align: "center" },
        { header: "수정일", name: "modDt", align: "center" },
        { header: "수정자", name: "modEmpNm", align: "center" },
      ],
      data: [],
    };

    this.search();
  },
  methods: {
    initDates() {
      let today = new Date();
      let startDate = new Date(2016, 0, 1);
      let endDate = new Date(
        new Date(today.getFullYear(), today.getMonth() + 1, 1) -
          60 * 24 * 24 * 1000
      );

      this.searchParam.startDt = startDate;
      this.searchParam.endDt = endDate;

      this.$nextTick(() => {
        this.$refs.startDt.setDate(startDate);
        this.$refs.endDt.setDate(endDate);
      });
    },
    onClick(ev) {
      if (ev.columnName == "empNm") {
        const rowData = this.$refs.tuiGrid.invoke("getRow", ev.rowKey);
        let param = {
          menuId: rowData.menuId,
          empNo: rowData.empNo,
        };
        this.$refs.mailReceiverDetailPop.openDetail(param);
      }
    },
    search(ev) {
      let params = {
        dptNo: this.searchParam.dptNo,
        empNm: this.searchParam.empNm,
        useYn: this.searchParam.useYn,
        startDt: this.searchParam.startDt.format("yyyyMMdd"),
        endDt: this.searchParam.endDt.format("yyyyMMdd"),
      };

      this.isLoading = true;
      this.$http
        .post("/api/system/family-event-mail/list", params)
        .then((response) => {
          if (response.data.data) {
            this.totalCnt = response.data.data.contents.length;
            this.$refs.tuiGrid.invoke(
              "resetData",
              response.data.data.contents,
              {}
            );
          } else {
            console.log("no data");
          }
        })
        .catch((e) => {
          console.error(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data);
    },
    loadingCancel() {
      console.error("LOADING CANCEL?");
    },
    resetSearchForm() {
      Object.assign(this.$data.searchParam, this.$options.data().searchParam);
      this.initDates();
    },
    writePop() {
      this.$refs.mailReceiverWritePop.open();
    },
    enableReceiver() {
      let checked = this.$refs.tuiGrid.invoke("getCheckedRows");
      if (!checked || checked.length == 0) {
        alert("선택된 항목이 없습니다.");
        return false;
      }
      this.isLoading = true;
      this.$http
        .post("/api/system/family-event-mail/enable", checked)
        .then((response) => {
          if (response.data.data) {
            alert(response.data.data.msg);
          }
        })
        .catch((e) => {
          console.error(e);
        })
        .finally(() => {
          this.isLoading = false;
          this.search();
        });
    },
    disableReceiver() {
      let checked = this.$refs.tuiGrid.invoke("getCheckedRows");
      if (!checked || checked.length == 0) {
        alert("선택된 항목이 없습니다.");
        return false;
      }
      this.isLoading = true;
      this.$http
        .post("/api/system/family-event-mail/disable", checked)
        .then((response) => {
          if (response.data.data) {
            alert(response.data.data.msg);
          }
        })
        .catch((e) => {
          console.error(e);
        })
        .finally(() => {
          this.isLoading = false;
          this.search();
        });
      console.log(checked);
    },
    deleteReceiver() {
      let checked = this.$refs.tuiGrid.invoke("getCheckedRows");
      if (!checked || checked.length == 0) {
        alert("선택된 항목이 없습니다.");
        return false;
      }
      this.isLoading = true;
      this.$http
        .post("/api/system/family-event-mail/delete", checked)
        .then((response) => {
          if (response.data.data) {
            alert(response.data.data.msg);
          }
        })
        .catch((e) => {
          console.error(e);
        })
        .finally(() => {
          this.isLoading = false;
          this.search();
        });
    },
  },
};
</script>
<style scoped lang="scss">
.p-title {
  margin-top: 25px;
  font-size: 24px;
  font-weight: 700;

  ~ div {
    margin-top: 15px;
  }
}
</style>
