<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="true"/>
    <div>
			<div class="page-title-box">
				<h1 class="page-title">{{menuName}}</h1>
				<ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
					<li class="breadcrumb">홈</li>
					<li class="breadcrumb">{{upMenuName}}</li>
					<li class="breadcrumb">{{menuName}}</li>
				</ol>
			</div>
		</div>
    <div v-if="!this.$store.state.isMobile">
      <div class="search-input-box" >
        <div class="search-input-row">
          <div class="search-item" v-if="this.upMenuPath == 'system'">
            <label for="departName">부서(팀)</label>
            <div class="search-value">
              <v-select v-model="searchParams.dptNo" :reduce="label => label.value" :options="dptCodeList.options" :clearable="false" :searchable="true" />
            </div>
          </div>
          <div class="search-item">
            <label for="empNm">임직원</label>
            <div class="search-value">
              <InputBox type="text" :value.sync="searchParams.empNm" v-model="searchParams.empNm"
              :targetMethod="search" />
            </div>
          </div>
        </div>
        <div class="search-input-row">
          <div class="search-item">
            <label for="companyPos">상태</label>
            <div class="search-value">
              <vSelect v-model="searchParams.confirmYn" :reduce="label => label.value" :options="confirmCodeList.options"
                :clearable="false" :searchable="true" />
            </div>
          </div>
          <div class="search-item">
            <label for="companyPos">휴가기간</label>
            <div class="search-value">
              <div class="date-range-box">
                <DatePicker ref="startDatePicker" :date.sync="startDt" />
                <span class="tilde-text">~</span>
                <DatePicker ref="endDatePicker" :date.sync="endDt" />
              </div>
            </div>
          </div>
          <div class="search-item confirm-box">
            <button type="button" @click.passive.stop="search" class="btn btn-primary">검색</button>
          </div>
        </div>
      </div>
    </div>
    <div>
      <div class="grid-box">
          <div class="grid-tool-box" v-if="!this.$store.state.isMobile">
            <div>
              <button type="button" class="btn" @click="vacationConfirm">승인</button>
              <button type="button" class="btn" @click="vacationReject">반려</button>
              <button type="button" class="btn" @click="today">금일 휴가자</button>
            </div>
            <div>
              <button type="button" class="btn" @click="cancelVacation">신청 취소</button>
              <button type="button" class="btn btn-primary" @click="handleVacation('reg')">휴가 신청</button>
            </div>
          </div>
          <div class="grid-tool-box" v-else>
            <div>
              <vSelect v-model="year" :reduce="label => label.value" :options="yearList.options"
                :clearable="false" :searchable="false" @input="search"/>
            </div>
            <div/>
          </div>
          <grid class="tui-grid" ref="tuiGrid" :data="gridProps.data" :columns="gridProps.columns" :options="gridProps.options"
                @click ="onClick" />
          <div class="per-page-box" v-if="!this.$store.state.isMobile">
            <span class="total-count-text">총 <span class="count">{{ totalCnt}}</span>개</span>
            <div class="select-box">
              <vSelect v-model="perPage" :reduce="label => label.value" :options="pageList.data" :clearable="false" :searchable="false"
                      @input="onPerPageInput" />
            </div>
          </div>
      </div>
    </div>
    <VacationPopup ref="vacPop" :adminYn="'Y'" :pmYn="searchParams.pmYn" @closedVacationPopup="closedVacationPopup"/>
    <VacationCancelPopup ref="vacCancelPop" :adminYn="'Y'" :pmYn="searchParams.pmYn" @closedVacationCancelPopup="closedVacationCancelPopup"/>
    <MemberVacationUsedPop ref="vacUsedPop" :pmYn="searchParams.pmYn" :adminYn="'Y'" />
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import "tui-grid/dist/tui-grid.css";
import 'tui-date-picker/dist/tui-date-picker.css';
import { Grid } from "@toast-ui/vue-grid";
import { Grids } from "@/mixins/Grids";
import 'vue-select/dist/vue-select.css';
import vSelect from 'vue-select'
import Loading from "vue-loading-overlay";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import { Comm } from "@/mixins/Comm";
import {CheckboxRenderer} from '@/components/renderer/GridRenderer';
import DatePicker from '@/components/Datepicker';
import InputBox from '@/components/InputBox';
import VacationPopup from "@/views/myPage/pop/VacationPopup";
import VacationCancelPopup from "@/views/myPage/pop/VacationCancelPopup";
import MemberVacationUsedPop from "@/views/system/pop/MemberVacationUsedPop";

import TuiGrid from 'tui-grid';
TuiGrid.setLanguage('ko');

export default {
  name: "VacationAdminList",
  mixins: [Check,Empty,Grids,Comm],
  components: {
    grid: Grid,
    vSelect,
    loading: Loading,
    InputBox,
    DatePicker,
    VacationPopup,
    VacationCancelPopup,
    MemberVacationUsedPop
  },
  props: {
		upMenuPath: {
			default: 'system'
		}
  },
  watch: {
    upMenuPath(val, oldVal) {
      if( val == 'mypage' ){
        this.upMenuName = '마이페이지';
        this.menuName = '팀장승인관리';
        this.searchParams.pmYn = 'Y';
      }else{
        this.upMenuName = '인트라넷관리';
        this.menuName = '휴가신청관리';
        this.searchParams.pmYn = '';
      }
      
      let date = new Date();
      let firstDate = this.getDateByDelimeter(new Date(date.getFullYear(), date.getMonth(), 1),'-' );
      let lastDt = this.getDateByDelimeter(new Date(date.getFullYear(), date.getMonth()+1, 0),'-' );

      if( !this.$store.state.isMobile ) {
        this.$refs.startDatePicker.setDate(new Date(firstDate));
        this.$refs.endDatePicker.setDate(new Date(lastDt));
        this.search();
      }
      else this.setYearSelectBox();
    },
  },  
  data() {
    return {
      pageList: {data: [{label: '10', value: 10},{label: '20', value: 20},{label: '50', value: 50},{label: '100', value: 100}],},
      perPage: 10,
      totalCnt: 0,
      upMenuName : '인트라넷관리',
      menuName : '휴가신청관리',
      gridProps : {},
      startDt : new Date(),
      endDt: new Date(),
      year : '',
      searchParams: { dptNo : '', empNm: '', confirmYn: '', startDt: '', endDt: '', pmYn: '' },
      isLoading: false,
      dptCodeList : { options: []},
      yearList: { options: [],},
      confirmCodeList: {
        options: [{ label: '전체', value: '' },
                  { label: '휴가승인', value: 'Y' },
                  { label: '휴가신청', value: 'N' },
                  { label: '취소신청', value: 'CR' },
                  { label: '취소승인', value: 'CA' },
                  { label: '반려', value: 'RJ' }]
      }
    };
  },
  created() {
    this.dptCodeList.options = this.commSelect("10000", "선택하세요");
    this.gridProps = {
        options: {
          rowHeight: 48,
          rowHeaders: this.$store.state.isMobile ? [] : [{
						type: 'checkbox',
						header: `
							<span class="ui-chk">
								<input id="chkAll" type="checkbox" name="_checked"><label for="chkAll"></label>
							</span>`,
						renderer: {
							type: CheckboxRenderer,
						},
					}],
					scrollX: !this.$store.state.isMobile,
          scrollY: true,
          bodyHeight: this.$store.state.isMobile ? 'auto' : 480,
					showDummyRows: false,
					selectionUnit: 'row',
          display: { noData: 'No Data' },
          pageOptions: {
            useClient: true,
            perPage: this.perPage,
            visiblePages: 5,
          },
				},
        columns: [
          {header: "vacNo", name: "vacNo", hidden: true},
          {header: "empNo", name: "empNo", align: "left", hidden: true},
          {header: "regstaffNo", name: "regstaffNo", align: "left", hidden: true},
          {header: "confirmYn", name: "confirmYn", align: "center", hidden: true},
          {header: "신청일", name: "requestYmd", align: "left", sortable: true, minWidth: "95", hidden: this.$store.state.isMobile},
          {header: "이름", name: "empNm", align: "left", sortable: true, minWidth: "80", formatter: this.gDetail},
          {header: "소속", name: "dptNm", align: "center", sortable: true, minWidth: "100", hidden: this.$store.state.isMobile},
          {header: "휴가기간",name: "period", align: "left", sortable: true, minWidth: "230"},
          {header: "휴가유형",name: "vacTypNm", align: "center", sortable: true, minWidth: "85", formatter: this.gDetail},
          {header: "상태", name: "confirmState", align: "center", sortable: true, minWidth: "85"},
          {header: "확정일",name: "confirmYmd", align: "left", sortable: true, minWidth: "100", hidden: this.$store.state.isMobile},
          {header: "승인자",name: "confirmEmpNm", align: "center", sortable: true, minWidth: "80", hidden: this.$store.state.isMobile},
          {header: "PDF 다운",name: "pdfDownload", align: "center", minWidth: "95", hidden: this.$store.state.isMobile},
          {header: "등록일시", name: "regDt", align: "left", sortable: true, minWidth: "160", hidden: this.$store.state.isMobile},
          {header: "등록자", name: "regstaffNm", align: "left", sortable: true, minWidth: "80", hidden: this.$store.state.isMobile}
        ],
        data: []
    };
  },
  mounted() {
    if( this.upMenuPath == 'mypage' ){
      this.upMenuName = '마이페이지';
      this.menuName = '팀장승인관리';
      this.searchParams.pmYn = 'Y';
    }

    let date = new Date();
    let firstDate = this.getDateByDelimeter(new Date(date.getFullYear(), date.getMonth(), 1),'-' );
    let lastDt = this.getDateByDelimeter(new Date(date.getFullYear(), date.getMonth()+1, 0),'-' );

    if( !this.$store.state.isMobile ) {
      this.$refs.startDatePicker.setDate(new Date(firstDate));
      this.$refs.endDatePicker.setDate(new Date(lastDt));
      this.search();
    }
    else this.setYearSelectBox();
  },
  computed: {
    ...mapState({
      main: (state) => state.main,
    }),
  },
  methods: {
    setYearSelectBox(){
      let searchparms = {type:"Y", term:0, format:"yyyy"};

      this.$http.post("/api/common/getCurrentDate",searchparms)
          .then((response) => {
            let currentYear = response.data.data;
            let tempArr = [];

            for ( let i = Number(currentYear) + 1; i > 2012; i--) {
              let obj = {};
              obj.label = i;
              obj.value = i;
              tempArr.push(obj);
            }
            this.yearList.options = tempArr;
            this.year = currentYear;
            this.search();
      });
    },
    onClick(ev) {
      if( this.isEmpty(ev.rowKey)) return false;
      const rowData = this.$refs.tuiGrid.invoke("getRow",ev.rowKey);
      if( ev.columnName == "vacTypNm" ){
        this.handleVacation(rowData.vacNo);
      } else if(ev.columnName == "pdfDownload"){
        this.downloadPdf(rowData);
      } else if(ev.columnName == "empNm"){
        let param = { empNo:rowData.empNo, empNm:rowData.empNm};
        this.$refs.vacUsedPop.open(param);
      }
    },
    handleVacation(inParam) {
      let vacNo = '';
      if(inParam != 'reg'){
        vacNo = inParam;
      }
      this.$refs.vacPop.open(vacNo);
    },
    search(){
      if( !this.$store.state.isMobile ) {
        if(this.isEmpty(this.startDt)) this.searchParams.startDt = '';
        else this.searchParams.startDt = this.getDateByDelimeter(this.startDt, '-');
        if(this.isEmpty(this.endDt)) this.searchParams.startDt = '';
        else this.searchParams.endDt = this.getDateByDelimeter(this.endDt, '-' );
      } else {
        this.searchParams.startDt = this.year + '-01-01';
        this.searchParams.endDt = this.year + '-12-31';
      }
      this.isLoading = true;
      this.$http.post("/api/vacation/getVacationAdminListData",this.searchParams)
          .then((response) => {
            if (response.data.data) {
              this.totalCnt = response.data.data.contents.length;
              let contentsList = response.data.data.contents;
              this.$refs.tuiGrid.invoke('resetData', contentsList, {});
            }
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    today(){
      let todayParams = {};
      todayParams.today = this.getDateByDelimeter(new Date(), '-' );
      if( this.upMenuPath == 'mypage' ){
        todayParams.pmYn = 'Y';
      }
      this.isLoading = true;
      this.$http.post("/api/vacation/getVacationAdminListData",todayParams)
          .then((response) => {
            if (response.data.data) {
              this.totalCnt = response.data.data.contents.length;
              let contentsList = response.data.data.contents;
              this.$refs.tuiGrid.invoke('resetData', contentsList, {});
            }
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    cancelVacation(){
      this.$refs.vacCancelPop.open();
    },
    vacationConfirm(){
      const gridRows = this.$refs.tuiGrid.invoke("getCheckedRows");
      if(this.isEmpty(gridRows)){
        alert('선택한 내용이 없습니다.');
        return false;
      }

      let params = {};
      params.vacationList = gridRows;
      this.isLoading = true;
      this.$http.post("/api/vacation/setVacationConfirm", params)
          .then((response) => {
            alert(response.data.message);
            this.search();
          })
          .catch((e) => {
            console.log(e);
            this.isLoading = false;
          })
          .finally(() => {
            //this.isLoading = false;
          });
    },
    vacationReject(){
      const gridRows = this.$refs.tuiGrid.invoke("getCheckedRows");
      if(this.isEmpty(gridRows)){
        alert('선택한 내용이 없습니다.');
        return false;
      }

      let params = {};
      params.vacationList = gridRows;
      this.isLoading = true;
      this.$http.post("/api/vacation/setVacationReject", params)
          .then((response) => {
            alert(response.data.message);
            this.search();
          })
          .catch((e) => {
            console.log(e);
            this.isLoading = false;
          })
          .finally(() => {
            //this.isLoading = false;
          });
    },
    closedVacationPopup (rtnObj) {
      if( !this.isEmpty(rtnObj)){
        if(rtnObj == 'success'){this.search();}
      }
    },
    closedVacationCancelPopup (rtnObj) {
      if( !this.isEmpty(rtnObj) && rtnObj == 'success') this.search();
    },
    onPerPageInput(data) {
			this.$refs.tuiGrid.invoke("setPerPage", data);
    },
    downloadPdf(rowData) {
      this.isLoading = true;
      let params = {
        vacNo: rowData.vacNo,
        type: "download"
      };
      const profileDownload = this.$http
        .post("/api/utils/report/getReportPrint", params, {
          responseType: "arraybuffer",
        })
        .then((solve) => {
          if (solve.status === 200) {
            let fileName =
              rowData.empNm + "_" + rowData.period + "_휴가신청서.pdf";
            let blob = new Blob([solve.data], {
              type: solve.headers["content-type"],
            });
            let link = document.createElement("a");
            link.href = window.URL.createObjectURL(blob);
            link.target = "_self";
            link.download = fileName;
            link.click();
          } else {
            throw new Error("휴가신청서 다운로드를 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
          throw new Error("휴가신청서 다운로드를 실패하였습니다.");
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
  }
};
</script>

<style scoped lang="scss">
.p-title {
	margin-top: 25px;
	font-size: 24px;
	font-weight: 700;

	~ div {
		margin-top: 15px;
	}
}
</style>
