<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="page-title-box">
      <h1 class="page-title">자료실관리</h1>
      <ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
        <li class="breadcrumb">홈</li>
        <li class="breadcrumb">인트라넷관리</li>
        <li class="breadcrumb">자료실관리</li>
      </ol>
    </div>
    <div class="search-input-box">
      <div class="search-input-row">
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="resourceType">자료 유형</label>
          <div class="search-value">
            <vSelect
              id="resourceType"
              :options="resourceType.options"
              v-model="searchData.resourceType"
              :clearable="false"
              :searchable="false"
            ></vSelect>
          </div>
        </div>
        <div class="search-item w100">
          <label for="resourceTitle">자료 제목</label>
          <div class="search-value">
            <InputBox
              id="resourceTitle"
              type="text"
              inputWidth="100%"
              :value.sync="searchData.titleTxt"
            />
          </div>
        </div>
      </div>
      <div class="search-input-row">
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="uploadfile1">파일명</label>
          <div class="search-value">
            <InputBox
              id="uploadfile1"
              type="text"
              :value.sync="searchData.uploadfile1"
            />
          </div>
        </div>
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="regDateFrom">등록일</label>
          <div class="search-value date-range-box">
            <DatePicker ref="fromDate" />
            <span class="tilde-text">~</span>
            <DatePicker ref="toDate" />
          </div>
        </div>
        <div class="search-item confirm-box">
          <button type="button" class="btn" @click="resetSearch">초기화</button>
          <button type="button" class="btn btn-primary" @click="search">
            검색
          </button>
        </div>
      </div>
    </div>
    <div class="grid-box">
      <div class="grid-tool-box">
        <div v-if="!this.$store.state.isMobile">
          <button type="button" class="btn btn-control" @click.passive.stop="updateResourceGrid('U')">저장</button>
          <button type="button" class="btn btn-control" @click.passive.stop="updateResourceGrid('D')">삭제</button>
        </div>
        <div>
          <button type="button" class="btn btn-primary" @click="openResourceManagePop()">
            자료 업로드
          </button>
        </div>
      </div>
      <grid
        class="tui-grid"
        ref="tuiGrid"
        :options="gridProps.options"
        :data="gridProps.data"
        :columns="gridProps.columns"
        @click="onGridClick"
        @editingFinish="onCheckAfterGridEditing"
      />
      <div class="per-page-box" v-if="!this.$store.state.isMobile">
        <span class="total-count-text">
          총
          <span class="count">{{ gridProps.data.length }}</span
          >개
        </span>
        <div class="select-box">
          <vSelect
            ref="perPage"
            :options="pageData.data"
            :clearable="false"
            :searchable="false"
            v-model="pageData.selected"
            @input="onPerPageInput"
          ></vSelect>
        </div>
      </div>
    </div>

    <ResourceManagePop
      ref="detailPop"
      :resourceType="resourceType"
      :search="search"
    />
  </div>
</template>
<script>
import { Grids } from "@/mixins/Grids";
import { Grid } from "@toast-ui/vue-grid";
import "codemirror/lib/codemirror.css";
import InputBox from "@/components/InputBox";
import vSelect from "vue-select";
import DatePicker from "@/components/Datepicker";
import { CheckboxRenderer } from "@/components/renderer/GridRenderer";
import ResourceManagePop from "@/views/system/pop/ResourceManagePop";
import { Empty } from "@/mixins/Empty";
import { DateFunc } from "@/mixins/DateFunc";
import Loading from "vue-loading-overlay";

export default {
  name: "ResourceManage",
  props: ["typeCode"],
  mixins: [Empty, DateFunc, Grids],
  components: {
    grid: Grid,
    InputBox,
    vSelect,
    DatePicker,
    ResourceManagePop,
    Loading,
  },
  data() {
    return {
      searchData: {
        resourceType: "전체",
        titleTxt: "",
        uploadfile1: "",
        status: "",
        originalFileName1: "",
      },
      resourceType: {
        options: [],
      },
      pageData: {
        data: [
          { label: "10", value: 10 },
          { label: "20", value: 20 },
          { label: "50", value: 50 },
          { label: "100", value: 100 },
        ],
        selected: "10",
      },
      selectCode: [{text:'게시', value: 'Y'}, {text:'미게시', value: 'N'}],
      resourceList: [],
      isLoading: false,
      fullPage: true,
    };
  },
  computed: {
    isMobile() {
      return this.$store.state.isMobile;
    },
    gridProps() {
      return {
        options: {
          rowHeight: 48,
          rowHeaders: [
            {
              type: "checkbox",
              header: `
						<span class="ui-chk">
							<input id="chkAll" type="checkbox" name="_checked"><label for="chkAll"></label>
						</span>`,
              renderer: {
                type: CheckboxRenderer,
              },
            },
          ],
          scrollX: false,
          scrollY: true,
          bodyHeight: 480,
          showDummyRows: false,
          selectionUnit: "row",
          pageOptions: {
            useClient: true,
            perPage: 10,
            visiblePages: 5,
          },
          editingEvent: 'click',
        },
        columns: [
          {
            header: "글번호",
            name: "resNo",
            width: 0,
            align: "center",
            hidden: true,
          },
          {
            header: "자료 유형",
            name: "resType",
            width: 130,
            align: "center",
          },
          {
            header: "제목",
            name: "titleTxt",
            formatter(data) {
              return `
                <div class="board-title-box">
                  <a class="board-title clickable-text">${data.value}</a>
                  ${
                    Math.floor(
                      (new Date() - new Date(data.row.regDt)) /
                        (1000 * 60 * 60 * 24)
                    ) <= 5
                      ? '<span class="ico-new">새 게시물</span>'
                      : ""
                  }
                </div>
              `;
            },
            width: 250,
            sortable: true,
          },
          {
            header: "파일명",
            name: "originalFileName1",
            width: 300,
            formatter(data) {
              return `
                <div class="board-title-box">
                  <a class="board-title clickable-text">${data.value}</a>
                </div>
              `;
            },
            sortable: true,
          },
          {
            header: "파일명",
            name: "uploadfile1",
            width: 200,
            hidden: true,
          },
          {
            header: "정렬순서",
            name: "dispSeq",
            validation: {dataType: 'number'},
            defaultValue: 1,
            onBeforeChange(ev) {
              if(Number(ev.nextValue)){
                ev.value = Number(ev.nextValue);
                return ev;
              } else {
                //alert("숫자만 입력 가능합니다.");
                ev.stop();
              }
            },
            editor: { type: 'text'},
            width: 100,
            align: "center",
            sortable: true,
          },
          {
            header: "게시여부",
            name: "dispYn",
            formatter(data) {
              return data.value === "Y" ? "게시" : "미게시";
            },
            editor: { type:'select', options: { listItems: [...this.selectCode] } },
            width: 100,
            align: "center",
            sortable: true,
          },
          {
            header: "등록자",
            name: "regstaffNm",
            width: 90,
            align: "center",
            sortable: true,
          },
          {
            header: "등록일",
            name: "regDt",
            width: 160,
            align: "center",
            sortable: true,
          },
        ],
        data: this.resourceList,
      };
    },
  },
  created() {
    this.callResourceManageInfo();
  },
  watch: {
    gridProps(val, oldVal) {
      this.$refs.tuiGrid.invoke("resetData", val.data);
    },
  },
  methods: {
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data.value);
    },
    onGridClick(event) {
      if (event.targetType !== "columnHeader") {

        const eventRow = this.$refs.tuiGrid.invoke("getRow", event.rowKey);

        if (event.columnName === "titleTxt") {
          //this.openUpdatePop(eventRow.resNo);
          this.openResourceManagePop(eventRow.resNo);
        }

        if (event.columnName === "originalFileName1") {
          this.callFileDownload(eventRow);
        }

      }
    },
    openResourceManagePop(resNo) {
      this.$refs.detailPop.openPop(resNo);
    },
    async callResourceManageInfo(params) {
      this.isLoading = true;

      if (params === void 0)
        params = {
          upcode: this.typeCode,
        };

      const apiUrl = "/api/system/resource-manage/get-info";
      const data = await this.$http
        .post(apiUrl, params)
        .then((solve) => {
          
          if (solve.data.result) {
            this.resourceList = solve.data.data.resourceList;
            this.resourceType.options = [
              "전체",
              ...solve.data.data.resourceType[this.typeCode],
            ];
          } else {
            throw new Error("게시물 목록을 가져오는데 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    search() {
      const param = {
        upcode: this.typeCode,
        resType: this.searchData.resourceType.code,
        titleTxt: this.searchData.titleTxt,
        uploadfile1: this.searchData.uploadfile1,
      };
      
      const fromDate = this.$refs.fromDate.instance.getDate();
      const toDate = this.$refs.toDate.instance.getDate();
      if (this.isNotEmpty(fromDate)) {
        param.fromDate = this.formatDate(fromDate, "-");
      }
      if (this.isNotEmpty(toDate)) {
        param.toDate = this.formatDate(toDate, "-");
      }

      this.callResourceManageInfo(param);
    },
    resetSearch() {
      this.searchData.titleTxt = "";
      this.searchData.uploadfile1 = "";
      this.searchData.originalFileName1 = "";
      this.searchData.resourceType = "전체";
      this.$refs.fromDate.instance.setDate(null);
      this.$refs.toDate.instance.setDate(null);
      //그리드 초기화
      //this.resourceList = [];
    },
    updateResourceGrid(gubun) {
        
      const gridRows = this.$refs.tuiGrid.invoke("getCheckedRows");
      if( this.isEmpty(gridRows)){
        alert("선택항목이 없습니다.");return false;
      }

      let params = {};
      params.resourceList = gridRows;
      params.status = gubun;

      this.$http.post("/api/system/resource-manage/grid-modify", params)
          .then((response) => {
            if(response.data.statusCode != 200){
              alert(response.data.message);
              return false;
            }else{
              alert(response.data.message);
              this.callResourceManageInfo();
            }
          });

      },
      // 그리드 클릭시 자료 다운로드
    callFileDownload(eventRow) {
      // 게시판 파일 다운로드 경로 정해지면 수정필요
      this.$store.dispatch("fileDownload", {
        fileName: eventRow.uploadfile1,
        filePath: eventRow.uploadfile1,
        oFileName: eventRow.originalFileName1,
      });
    },
    onCheckAfterGridEditing(e) {
      this.$refs.tuiGrid.invoke("check", e.rowKey);
    },
  },
};
</script>
