<template>
   <div>
		<ModalPopup title="우편번호 찾기" :visible.sync="modalVisible" :width="this.width">
		  <vue-daum-postcode @complete="addrInfo = $event" />
		</ModalPopup>
		</div>
</template>

<script>
import ModalPopup from "@/components/ModalPopup";
import { VueDaumPostcode } from "vue-daum-postcode"

export default {
  name: "DaumAddressPopup",
  components: {
    ModalPopup,
    VueDaumPostcode
  },
  props:{
    width:{
     default:'700px' 
    }
  },
  mounted() {
  },
  data() {
    return {
      modalVisible: false,
      addrInfo: {}
    };
  },
  computed: {

  },
  created() {
  },
  watch: {
    addrInfo(val, oldVal) {
      this.closedAddrPop();
    },
  },
  methods: {
    open() {this.modalVisible = true;},
    closedAddrPop () {
      this.modalVisible = false;
      //부모 함수 콜백
      this.$emit('closedAddrPop', this.addrInfo);
    }
  },
};
</script>