<template>
  <ModalPopup :title="'휴가 취소 신청'" :visible.sync="modalVisible" width="772px">
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="search-input-box">
      <div class="search-input-row">
        <div class="search-item">
          <label for="tempItem">상태</label>
          <div class="search-value">
            <vSelect id="tempItem" :options="confirmCodeList.options" :clearable="false" :searchable="true"
                      :reduce="label  => label.value" v-model="selectConfirmCode"></vSelect>
          </div>
        </div>
        <div class="search-item confirm-box">
            <button type="button" @click.passive.stop="search" class="btn btn-primary">검색</button>
        </div>
      </div>
    </div>
    <div class="grid-box">
      <div class="grid-tool-box">
        <div class="left-box">
          <button type="button" class="btn btn-control" @click.prevent.stop="cancelVacation" >취소신청</button>
        </div>
      </div>
      <grid class="tui-grid" ref="tuiGrid" :data="gridProps.data" :columns="gridProps.columns" :options="gridProps.options" 
            @click ="onClick" />
      <div class="per-page-box" v-if="!this.$store.state.isMobile">
        <span class="total-count-text">총 <span class="count">{{ totalCnt}}</span>개</span>
        <div class="select-box">
          <vSelect v-model="perPage" :options="pageList.data" :clearable="false" :searchable="false"
                  @input="onPerPageInput" ></vSelect>
        </div>
      </div>
    </div>
    <div class="modal-popup-btn-box" v-if="!this.$store.state.isMobile">
      <button type="button" class="btn btn-primary" @click.prevent.stop="cancelVacation" >신청</button>
      <button type="button" class="btn" @click.prevent.stop="closed">취소</button>
    </div>
  </ModalPopup>
</template>

<script>
import { mapState, mapActions } from "vuex";
import "tui-grid/dist/tui-grid.css";
import { Grid } from "@toast-ui/vue-grid";
import vSelect from 'vue-select'
import { DateFunc } from "@/mixins/DateFunc";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import ModalPopup from "@/components/ModalPopup";
import {CheckboxRenderer} from '@/components/renderer/GridRenderer';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import TuiGrid from 'tui-grid';
TuiGrid.setLanguage('ko');

export default {
  name: "VacationCancelPopup",
  mixins: [Check,Empty,DateFunc],
  components: {
    vSelect,
    grid: Grid,
    ModalPopup,
    Loading
  },
  props:{
    adminYn: {
			default: 'N',
		},
    pmYn: {
			default: '',
    }
  },
  data() {
    return {
      modalVisible : false,
      isLoading: false,
      fullPage: true,
      selectConfirmCode : '',
      confirmCodeList: {
        options: [{ label: '전체', value: '' },
                  { label: '휴가승인', value: 'Y' },
                  { label: '휴가신청', value: 'N' },
                  { label: '취소신청', value: 'CR' },
                  { label: '취소승인', value: 'CA' },
                  { label: '반려', value: 'RJ' },
                  { label: '취소', value: 'C' }]
      },
      year: {},
      pageList: {data: [{label: '10', value: 10},{label: '20', value: 20},{label: '50', value: 50},{label: '100', value: 100}],},
      perPage: 10,
      totalCnt: 0,
      gridProps : {
        options: {
          rowHeight: 48,
          rowHeaders: [{
						type: 'checkbox',
						header: `
							<span class="ui-chk">
								<input id="chkAll" type="checkbox" name="_checked"><label for="chkAll"></label>
							</span>`,
						renderer: {
							type: CheckboxRenderer,
						},
          }],
          scrollX: false,
          scrollY: true,
          bodyHeight: 480,
					showDummyRows: false,
					selectionUnit: 'row',
          display: { noData: 'No Data' },
          pageOptions: {
            useClient: true,
            visiblePages: 5,
            perPage: 10,
          },
				},
        columns: [
          {header: "순번", name: "vacNo", hidden: true},
          {header: "신청일자", name: "requestYmd", align: "left", minWidth: "100", hidden: this.$store.state.isMobile},
          {header: "신청자", name: "empNm", align: "left", minWidth: "90", hidden: this.$store.state.isMobile},
          {header: "휴무유형코드",name: "vacTypCd", hidden: true,},
          {header: "휴무유형",name: "vacTypNm", align: "center",width: this.$store.state.isMobile ? "130" : "220"},
          {header: "휴가기간",name: "period", align: "left", width: this.$store.state.isMobile ? "220" : "350"},
          {header: "휴가일수", name: "days", align: "center", minWidth: "90", hidden: this.$store.state.isMobile},
          {header: "상태", name: "confirmState", align: "center", width: this.$store.state.isMobile ? "120" : "220"},
          {header: "상태코드", name: "confirmYn", hidden: true},
          {header: "시작일", name: "startDt", hidden: true},
          {header: "종료일", name: "endDt", hidden: true},
        ],
        data: []
      }
    };
  },
  computed: {
  },
  created() {
    this.setYearInfo();
  },
  watch: {
    modalVisible(val, oldVal) {
      if (!val) {
        this.resetData();
      }
    },
  },
  methods: {
    open() {
      this.search();
      this.modalVisible = true;
    },
    closed (obj) {
      this.modalVisible = false;
      //부모 함수 콜백
      this.$emit('closedVacationCancelPopup', obj);
    },
    search(){
      let apiUrl = "/api/vacation/getVacationRequestList";
      if('Y' == this.adminYn){
        apiUrl = "/api/vacation/getVacationCancelByAdmin";
      }

      let searchparams = {};
      searchparams.confirmYn = this.selectConfirmCode;
      searchparams.year = this.year.value;
      searchparams.pmYn = this.pmYn;

      this.isLoading = true;
      this.$http.post(apiUrl, searchparams)
          .then((response) => {
            if (response.data.data) {
              this.totalCnt = response.data.data.contents.length;
              this.$refs.tuiGrid.invoke('resetData', response.data.data.contents, {});
            } 
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    onClick(ev) {
      const rowData = this.$refs.tuiGrid.invoke("getRow",ev.rowKey);
      if( ev.columnName == "cancelBtn" ){
        this.cancel(rowData);
      }
    },
    cancelVacation(){
      const gridRows = this.$refs.tuiGrid.invoke("getCheckedRows");

      if( this.isEmpty(gridRows)){
        alert("선택항목이 없습니다.");
        return false;
      }

      for(let i=0;i<gridRows.length; i++){
        let rowData = gridRows[i];
        if( rowData.confirmYn == 'CR' || rowData.confirmYn == 'CA' || rowData.confirmYn == 'RJ' || rowData.confirmYn == 'C' ){
          alert('취소신청이 불가능 합니다.');
          return false;
        }
      }
      
      let params = {};
      params.vacationSearchList = gridRows;
      this.isLoading = true;
      this.$http.post("/api/vacation/setVacationCancel", params)
          .then((response) => {
            if(response.data.statusCode != 200){
              alert(response.data.message);
            }else{
              alert(response.data.message);
              this.closed('success');
            }
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    onPerPageInput(data) {
			this.$refs.tuiGrid.invoke("setPerPage", data.value);
    },
    setYearInfo(){
      let searchparms = {type:"Y", term:0, format:"yyyy"};

      this.$http.post("/api/common/getCurrentDate",searchparms)
          .then((response) => {
            this.year = response.data.data;
          });
    },
    resetData(){
      this.selectConfirmCode = '';
      this.year = {};
    }
  },
};
</script>