<template>
  <div class="grid-box">
    <p class="grid-info-text">※ 작성방법</p>
    <div class="grid-tool-box">
      <div>
        <p class="grid-info-text">
          - 교육 수강인원이 다수일 경우 보고서는 1개만 제출 (단, 의견 및 소감은
          각자 작성)
          <br />
          - 교육생 논의 후 종합의견 작성바람.
        </p>
      </div>
      <div>
        <button
          type="button"
          class="btn btn-primary"
          @click="this.openInsertPop"
        >
          보고서 작성
        </button>
      </div>
    </div>
    <Grid
      class="tui-grid"
      ref="tuiGrid"
      :options="gridProps.options"
      :data="gridProps.data"
      :columns="gridProps.columns"
      @click="onGridClick"
    />
    <div class="per-page-box" v-if="!this.$store.state.isMobile">
      <span class="total-count-text">
        총
        <span class="count">{{ gridProps.data.length }}</span
        >개
      </span>
      <div class="select-box">
        <vSelect
          ref="perPage"
          :options="pageData.data"
          :clearable="false"
          :searchable="false"
          v-model="perPage"
          :reduce="(label) => label.value"
          @input="onPerPageInput"
        ></vSelect>
      </div>
    </div>
  </div>
</template>

<script>
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";

export default {
  name: "EduReportList",
  props: [
    "gridHeader",
    "eduReportList",
    "onTitleGridClick",
    "openDetailPop",
    "openInsertPop",
  ],
  components: {
    Grid,
    vSelect,
  },
  data() {
    return {
      perPage: 10,
      pageData: {
        data: [
          { label: "10", value: 10 },
          { label: "20", value: 20 },
          { label: "50", value: 50 },
          { label: "100", value: 100 },
        ],
        // selected: "10",
      },
    };
  },
  computed: {
    gridProps() {
      return {
        options: {
          rowHeight: 48,
          scrollX: false,
          scrollY: true,
          bodyHeight: 480,
          showDummyRows: false,
          selectionUnit: "row",
          pageOptions: {
            useClient: true,
            perPage: this.perPage,
            visiblePages: 5,
          },
        },
        columns: [...this.gridHeader],
        data: this.eduReportList,
      };
    },
  },
  watch: {
    // 그리드 정보 주시/변경
    gridProps(val, oldVal) {
      this.$refs.tuiGrid.invoke("resetData", val.data);
    },
  },
  methods: {
    // 그리드 클릭
    onGridClick(event) {
      if (event.targetType !== "columnHeader") {
        if (event.columnName === "eduTxt") {
          const eventRow = this.$refs.tuiGrid.invoke("getRow", event.rowKey);
          this.onTitleGridClick(eventRow);
        }
      }
    },
    // 페이지 갯수보기 변경
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data);
    },
  },
};
</script>