<template>
  <ModalPopup title="게시판 글쓰기" :visible.sync="modalVisible" width="1120px">
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="tbl-row write-mode">
      <table>
        <colgroup>
          <col class="col-row-title" />
          <col style="width: 100%" />
          <col class="col-row-title" />
          <col style="width: 100%" />
        </colgroup>
        <tbody>
          <tr>
            <th scope="row" class="required">유형</th>
            <td colspan="3">
              <vSelect
                :options="getBoardTypeExceptAll"
                label="text"
                v-model="form.typCd"
                :reduce="(getBoardTypeExceptAll) => getBoardTypeExceptAll.value"
                :clearable="false"
                :searchable="false"
              ></vSelect>
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">제목</th>
            <td colspan="3">
              <InputBox
                ref="titleTxt"
                type="text"
                :value.sync="form.titleTxt"
                inputWidth="100%"
              />
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">게시여부</th>
            <td colspan="3">
              <RadioButton
                :text="'게시'"
                :value="'Y'"
                name="rgBoardPostYN"
                @radioChange="dispRadioChange"
                :checked="form.dispYn === 'Y'"
              />
              <RadioButton
                :text="'미게시'"
                :value="'N'"
                name="rgBoardPostYN"
                @radioChange="dispRadioChange"
                :checked="form.dispYn === 'N'"
              />
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">중요여부</th>
            <td>
              <RadioButton
                :text="'일반게시물'"
                :value="'0'"
                :checked="'0' === form.importantYn"
                name="rgBoardImportance"
                @radioChange="importantRadioChange"
              />
              <RadioButton
                :text="'중요게시물'"
                :value="'1'"
                name="rgBoardImportance"
                :checked="'1' === form.importantYn"
                @radioChange="importantRadioChange"
              />
            </td>
            <th scope="row">정렬순서</th>
            <td>
              <InputBox type="text" :value.sync="form.dispSeq" />
            </td>
          </tr>
          <tr v-if="form.importantYn === '1'">
            <th scope="row" class="required">게시기간</th>
            <td colspan="3">
              <div class="date-range-box">
                <DatePicker ref="fromDate" :date="new Date(form.startDt)" />
                <span class="tilde-text">~</span>
                <DatePicker ref="toDate" :date="new Date(form.endDt)" />
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">내용</th>
            <td colspan="3">
              <Editor
                ref="editor"
                :initialValue="form.contentsTxt"
                initial-edit-type="markdown"
                preview-style="vertical"
                :options="editorOptions"
              />
            </td>
          </tr>
          <tr>
            <th scope="row">첨부파일</th>
            <td colspan="3">
              <div class="input-flex-box">
                <InputBox type="text" placeholder="" readonly />
                <FileButton label="파일선택" @change="onFileChange" />
              </div>
              <div class="attach-file-box">
                <ul class="attached-file-list">
                  <li class="file-item" v-if="form.uploadfile1">
                    <div class="file-item-info">
                      <span class="file-name" @click="() => downloadFile(1)">{{
                        form.originalFileName1
                      }}</span>
                      <button
                        type="button"
                        class="btn-remove-file"
                        @click="() => deleteFile(1)"
                      >
                        파일삭제
                      </button>
                    </div>
                  </li>
                  <li class="file-item" v-if="form.uploadfile2">
                    <div class="file-item-info">
                      <span class="file-name" @click="() => downloadFile(2)">{{
                        form.originalFileName2
                      }}</span>
                      <button
                        type="button"
                        class="btn-remove-file"
                        @click="() => deleteFile(2)"
                      >
                        파일삭제
                      </button>
                    </div>
                  </li>
                  <li class="file-item" v-if="form.uploadfile3">
                    <div class="file-item-info">
                      <span class="file-name" @click="() => downloadFile(3)">{{
                        form.originalFileName3
                      }}</span>
                      <button
                        type="button"
                        class="btn-remove-file"
                        @click="() => deleteFile(3)"
                      >
                        파일삭제
                      </button>
                    </div>
                  </li>
                </ul>
              </div>
            </td>
          </tr>
          <tr v-if="this.isNotEmpty(boardDetail.bbsNo)">
            <th scope="row">등록자</th>
            <td class="plain-text">{{ boardDetail.regstaffNm }}</td>
            <th scope="row">등록일</th>
            <td class="plain-text">{{ boardDetail.regDt }}</td>
          </tr>
          <tr v-if="this.isNotEmpty(boardDetail.bbsNo)">
            <th scope="row">최종수정자</th>
            <td class="plain-text">{{ boardDetail.modstaffNm }}</td>
            <th scope="row">최종수정일</th>
            <td class="plain-text">{{ boardDetail.modDt }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="modal-popup-btn-box">
      <button type="button" class="btn btn-primary" @click="save">저장</button>
      <button type="button" class="btn" @click="cancel">취소</button>
    </div>
  </ModalPopup>
</template>

<script>
import Vue from "vue";
import axios from "axios";
import "@toast-ui/editor/dist/toastui-editor.css";
import { Editor } from "@toast-ui/vue-editor";

import InputBox from "@/components/InputBox";
import RadioButton from "@/components/RadioButton";
import FileButton from "@/components/FileButton";
import vSelect from "vue-select";
import DatePicker from "@/components/Datepicker";
import ModalPopup from "@/components/ModalPopup";

import { Empty } from "@/mixins/Empty";
import { DateFunc } from "@/mixins/DateFunc";
import { Comm } from "@/mixins/Comm";
import { mapGetters, mapActions } from "vuex";
import Loading from "vue-loading-overlay";

Vue.prototype.$axios = axios;

function uploadImage(blob, callback) {
  let formData = new FormData();
  formData.append("uploadFiles", blob);
  formData.append("editorYn", "Y");
  let imgPath = "이미지 등록중 오류가 발생했습니다.";

  axios
    .post("/api/common/fileUploadRename", formData, {
      headers: { "Content-Type": "multipart/form-data" },
    })
    .then((response) => {
      imgPath = response.data.data[0].filePath;
      callback(imgPath, "alt");
      return false;
    })
    .catch((ex) => {
      console.log("uploadImage ERR!!!!! : ", ex);
      callback(imgPath, "alt");
      return false;
    });
}

export default {
  name: "BoardManageWritePop",
  props: ["boardType", "search"],
  mixins: [Empty, DateFunc, Comm],
  components: {
    Editor,
    InputBox,
    RadioButton,
    FileButton,
    vSelect,
    DatePicker,
    ModalPopup,
    Loading,
  },
  data() {
    return {
      modalVisible: false,
      form: {
        typCd: "10610",
        bbsNo: "",
        titleTxt: "",
        dispYn: "Y",
        dispSeq: 1,
        importantYn: "0",
        contentsTxt: " ",
        startDt: "",
        endDt: "",
        uploadfile1: "",
        uploadfile2: "",
        uploadfile3: "",
        originalFileName1: "",
        originalFileName2: "",
        originalFileName3: "",
      },
      boardDetail: {},
      // selectedFile: "",
      isLoading: false,
      fullPage: true,
      editorOptions: {
        hooks: {
          addImageBlobHook: async function (blob, callback) {
            return await uploadImage(blob, callback);
          },
        },
      },
    };
  },
  watch: {
    modalVisible(val, oldVal) {
      if (!val) {
        this.resetPop();
      }
    },
  },
  computed: {
    // 부모 게시판 유형중에서 '전체' 제외
    getBoardTypeExceptAll() {
      const exceptAll = [...this.boardType.options];
      exceptAll.shift();
      return exceptAll;
    },
  },
  methods: {
    ...mapGetters(["getUsedMarkdownFromDt"]),
    ...mapActions({
      fileDownload: "fileDownload",
      fileUpload: "fileUpload",
    }),
    // 파일 변경시
    onFileChange(e) {
      console.log(e);
      if (e.event.target.files.length < 1) {
        return false;
      }

      const file = e.event.target.files[0];
      e.event.target.value = null;

      if (
        this.isNotEmpty(this.form.uploadfile1) &&
        this.isNotEmpty(this.form.uploadfile2) &&
        this.isNotEmpty(this.form.uploadfile3)
      ) {
        alert("파일은 3개까지 등록 가능합니다.");
        return false;
      }

      this.callUploadFile(file);
    },
    // 파일 업로드
    async callUploadFile(file) {
      const fd = new FormData();
      fd.append("uploadFiles", file);
      fd.append("editorYn", "N");

      const save = await this.$http
        .post("/api/common/fileUploadRename", fd, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((solve) => {
          const { filePath, originalFileName } = solve.data.data[0];

          if (this.isEmpty(this.form.uploadfile1)) {
            this.form.uploadfile1 = filePath;
            this.form.originalFileName1 = originalFileName;
          } else if (this.isEmpty(this.form.uploadfile2)) {
            this.form.uploadfile2 = filePath;
            this.form.originalFileName2 = originalFileName;
          } else if (this.isEmpty(this.form.uploadfile3)) {
            this.form.uploadfile3 = filePath;
            this.form.originalFileName3 = originalFileName;
          }

          return solve;
        });
    },
    // 팝업 열기
    openCreatePop() {
      this.modalVisible = true;
    },
    // 팝업 열기
    async openUpdatePop(eventRow) {
      await this.callBoardManageDetail(eventRow.bbsNo);
      this.modalVisible = true;
    },
    // 팝업 닫기
    cancel() {
      this.modalVisible = false;
    },
    // 게시여부 변경시
    dispRadioChange(e) {
      this.form.dispYn = e.target.value;
    },
    // 중요게시물 변경시
    importantRadioChange(e) {
      this.form.importantYn = e.target.value;
    },
    // 게시판 상세 조회
    callBoardManageDetail(bbsNo) {
      this.isLoading = true;

      const apiUrl = "/api/board/get-detail/" + bbsNo;
      const promise = this.$http
        .get(apiUrl)
        .then((solve) => {
          if (solve.data.statusCode === 200) {
            this.boardDetail = solve.data.data;

            const date = this.formatDate(this.boardDetail.regDt, "");

            if (date < this.getUsedMarkdownFromDt()) {
              this.form.contentsTxt = this.replaceLucyFilter(
                this.boardDetail.contentsTxt
              );
            }

            this.form = this.boardDetail;
          } else {
            throw new Error("게시물 상세정보를 가져오는데 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });

      return promise;
    },
    // 저장시 validation
    save() {
      const saveData = this.form;
      saveData.contentsTxt = this.$refs.editor.invoke("getMarkdown");

      if (this.isEmpty(saveData.typCd)) {
        alert("게시판 유형을 선택하세요.");
        return false;
      }
      if (this.isEmpty(saveData.titleTxt)) {
        alert("제목을 입력하세요.");
        this.$refs.titleTxt.focus();
        return false;
      }

      if (saveData.importantYn === "1") {
        saveData.startDt = this.$refs.fromDate.instance.getDate();
        saveData.endDt = this.$refs.toDate.instance.getDate();
        if (this.isNotEmpty(saveData.startDt)) {
          saveData.startDt = this.formatDate(saveData.startDt, "-");
        } else {
          alert("게시기간 시작일을 입력하세요.");
          return false;
        }
        if (this.isNotEmpty(saveData.endDt)) {
          saveData.endDt = this.formatDate(saveData.endDt, "-");
        } else {
          alert("게시기간 종료일을 입력하세요.");
          return false;
        }
      }

      if (this.isEmpty(saveData.contentsTxt)) {
        alert("내용을 입력하세요.");
        this.$refs.editor.invoke("focus");
        return false;
      }

      this.callBoardSave(saveData);
    },
    // 저장 API 호출
    async callBoardSave(saveData) {
      this.isLoading = true;

      const promise = await this.$http
        .post("/api/system/board-manage/save", saveData)
        .then((solve) => {
          if (solve.data.statusCode === 200) {
            this.modalVisible = false;
            this.search();
          } else {
            throw new Error("게시물을 저장하는데 실패하였습니다.");
          }

          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });

      return promise;
    },
    // 첨부파일 삭제
    deleteFile(i) {
      if (confirm("파일을 정말 삭제하시겠습니까?")) {
        this.callDeleteFile(i);
      }
    },
    // 첨부파일 삭제
    callDeleteFile(i) {
      const filePath = this.form[`uploadfile${i}`];

      const fd = new FormData();
      fd.append("filePath", filePath);

      const promist = this.$http
        .post("/api/common/removeFile", fd, {
          headers: {
            "Content-Type": "application/json",
          },
        })
        .then((solve) => {
          this.form[`uploadfile${i}`] = null;
          this.form[`originalFileName${i}`] = null;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    // 첨부파일 다운로드
    downloadFile(i) {
      const filePath = this.form[`uploadfile${i}`];
      const oFileName = this.form[`originalFileName${i}`];
      this.fileDownload({ filePath, oFileName });
    },
    resetPop() {
      this.boardDetail = {};
      this.form = {
        typCd: "10610",
        bbsNo: "",
        titleTxt: "",
        dispYn: "Y",
        dispSeq: 1,
        importantYn: "0",
        contentsTxt: " ",
        startDt: "",
        endDt: "",
        uploadfile1: "",
        uploadfile2: "",
        uploadfile3: "",
        originalFileName1: "",
        originalFileName2: "",
        originalFileName3: "",
      };
    },
  },
};
</script>