<template>
  <div class="nav-wrap" v-if="this.menuopen">
    <nav class="nav-bar" v-if="!isMobile">
      <router-link class="nav-logo" to="/main">
        <img src="~assets/images/common/common_nav_logo.png" alt="">
      </router-link>
      <ul class="nav-menu">
        <li class="nav-menu-item" v-for="menu in menus" :key="menu.menuNm">
          <router-link :to="{}">{{menu.menuNm}}</router-link>
          <ul class="nav-sub-menu" v-if="menu.subMenu">
            <li class="nav-menu-item" v-for="sub in menu.subMenu">
              <router-link :to="sub.menuurlTxt"><span class="menu-name">{{sub.menuNm}}</span></router-link>
            </li>
          </ul>
        </li>
        <!-- e : 관리자 메뉴 -->
      </ul>
      <div class="nav-tools">
        <!-- <div class="nav-tools-item alarm-box">
          <button type="button"
              :class="{'btn-alarm': true, 'has-alarm': this.$store.state.alarmList.length > 0}">알림
          </button>
          <ul class="alarm-list" v-if="this.$store.state.alarmList.length > 0">
            <li class="alarm-item" v-for="item in this.$store.state.alarmList" :key="item.uid">
              <router-link :to="item.link" :class="{'alarm-new': item.isNew}">
                <span class="alarm-item-title">{{ item.title }}</span>
              </router-link>
            </li>
          </ul>
        </div> -->
        <div class="nav-tools-item profile-box">
          <!-- DESC : 200904 수정 | 프로필 백그라운드 이미지 span으로 분리 -->
          <button type="button" class="profile-btn">
            <span class="profile-img" :style="{backgroundImage: 'url('+ this.$store.state.userInfo.profileImg +')'}"></span>안녕하세요 {{this.$store.state.userInfo.userNm+'님'}}
          </button>
          <div class="profile-float-box">
            <div class="profile-box-top">
              <div class="profile-img-box">
                <span class="profile-img" :style="{backgroundImage: 'url('+ this.$store.state.userInfo.profileImg +')'}"></span>
                <!-- <span class="profile-select">
                  <input id="profileSelect" type="file">
                  <label for="profileSelect">프로필 이미지 선택</label>
                </span> -->
              </div>
              <div class="profile-info">
                <p class="profile-name">{{this.$store.state.userInfo.userNm}}</p>
                <p class="profile-email">{{this.$store.state.userInfo.emailTxt}}</p>
                <!-- <router-link tag="button" type="button" to="/my">프로필 변경</router-link> -->
                <button type="button" class="btn btn-round" @click.prevent.stop="profilePop()">프로필 변경</button>
              </div>
            </div>
            <div class="profile-box-bottom">
              <button type="button" @click.prevent="onLogout">로그아웃</button>
            </div>
          </div>
        </div>
      </div>
    </nav>
    <nav class="nav-bar m-nav-bar" v-else>
			<div class="m-menu-box" v-if="this.$store.state.mobileMenuOpen">
				<div class="profile-box">
					<div class="profile-img-box">
						<span class="profile-img" :style="{backgroundImage: 'url('+ this.$store.state.userInfo.profileImg +')'}"></span>
					</div>
					<div class="profile-info">
						<p class="profile-name">{{this.$store.state.userInfo.userNm}}</p>
						<p class="profile-detail">{{this.$store.state.userInfo.dptNm}} / {{this.$store.state.userInfo.gradeNm}}</p>
					</div>
				</div>
				<div class="m-nav-menu">
                    <div v-for="menu in menus" :key="menu.menuId">
                        <router-link  :class="{'m-nav-item':true, 'contact': sub.menuId === '10006',
                                                                    'apply-vacation': sub.menuId === '10008',
                                                                    'apply-manage': sub.menuId === '10031',
                                                                    'apply-notice': sub.menuId === '10011',
                                              }"
                                @click.native="onMobileMenuClose"
                                :to="sub.menuurlTxt"
                                :key="sub.mobileYn"
                                v-for="sub in menu.subMenu" v-if="sub.mobileYn === 'Y'">{{sub.menuNm}}</router-link>
                    </div>

					<!-- <router-link class="m-nav-item contact" @click.native="onMobileMenuClose" to="/address/address">주소록</router-link>
					<router-link class="m-nav-item apply-vacation" @click.native="onMobileMenuClose" to="/mypage/vacation">휴가신청</router-link>
					<router-link class="m-nav-item vacation-manage" @click.native="onMobileMenuClose" to="/mypage/teamLeaderVacationManage">휴가관리</router-link>
					<router-link class="m-nav-item notice" @click.native="onMobileMenuClose" to="/board/notice">공지사항</router-link> -->
				</div>
			</div>
			<div class="nav-bar-inner">
				<button type="button" class="btn-menu" @click="onMobileMenuOpen">메뉴 펼침</button>
				<router-link class="nav-logo" to="/main">
					<img src="~assets/images/common/common_nav_logo.png" alt="">
				</router-link>
				<!-- <div class="nav-tools">
					<div class="nav-tools-item alarm-box">
						<button type="button"
								:class="{'btn-alarm': true, 'has-alarm': this.$store.state.alarmList.length > 0}">알림
						</button>
						<ul class="alarm-list" v-if="this.$store.state.alarmList.length > 0">
							<li class="alarm-item" v-for="item in this.$store.state.alarmList" :key="item.uid">
								<router-link :to="item.link" :class="{'alarm-new': item.isNew}">
									<span class="alarm-item-title">{{ item.title }}</span>
								</router-link>
							</li>
						</ul>
					</div>
				</div> -->
				<div class="m-menu-dimmed" v-if="this.$store.state.mobileMenuOpen" @click="onMobileMenuClose"></div>
			</div>
		</nav>
    <MemberInfoPopup ref="memInfoPop" />
  </div>
</template>
<script>
import "tui-grid/dist/tui-grid.css";
import 'tui-pagination/dist/tui-pagination.css';
import 'tui-date-picker/dist/tui-date-picker.css';
import 'tui-time-picker/dist/tui-time-picker.css';
import 'vue-select/dist/vue-select.css';
import { EventBus } from '@/modules/event-bus';
import { mapState, mapActions } from "vuex";
import { Comm } from "@/mixins/Comm";
import ModalPopup from "@/components/ModalPopup";
import MemberInfoPopup from "@/views/member/pop/MemberInfoPopup";

  export default {
    name: "Header",
    mixins: [Comm],
    data() {
      return {
        menuopen: true,
        menustate : '',
        fileInfo: {fileName: '', filePath: '', imageSrc: ''},
      }
    },
    components : {
      ModalPopup,
      MemberInfoPopup,
    },
    computed: {
      ...mapState({
        menus: (state) => {
          //adminUserMenu
          return state.menu.userMenu.menu;
        },
        isMobile: (state) => {
          //adminUserMenu
          return state.isMobile;
        },
      }),
    },
    created() {

        if(localStorage.getItem("token")) this.headerUserInfo();
        if(localStorage.getItem("token")) this.commonMenu();
    },
    methods: {
      ...mapActions({
        handleLogout: "handleLogout"
      }),
      handleClick () {
        EventBus.$emit('handle-click', true);
      },
      menuinfo() {
      },
      onLogout(){
        let result = this.handleLogout();
        result.then( (data) => {
          if (data.status === 200) {
            EventBus.$emit('handle-token', false);
            this.$router.push("/");
          }
        }).catch(()=> {
          //this.printErrMsg
        })
      },
      onMobileMenuOpen() {
        this.$store.state.mobileMenuOpen = true;
      },
      onMobileMenuClose() {
        this.$store.state.mobileMenuOpen = false;
      },
      profilePop() {
        this.memberInfoPopup();
        return;
      },
      commonMenu(){
          this.$http.post("/api/system/menu/getCommonMenuInfo", {}, {})
          .then((response) => {
            if(response.data.statusCode === 200){
              this.$store.state.menu.userMenu.menu = response.data.data;
            }
          });
      },
    },
    mounted() {
      EventBus.$on('handle-token', state => {

          if(state) {
              this.commonMenu();
              this.headerUserInfo();
          }
          this.menuopen = state;
      });
    }
  }
</script>
