<template>
  <ModalPopup
    title="교육훈련 결과 보고서 등록"
    :visible.sync="modalVisible"
    width="1120px"
  >
    <div class="tbl-row write-mode">
      <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
      <table>
        <colgroup>
          <col class="col-row-title" />
          <col style="width: 100%" />
          <col class="col-row-title" />
          <col style="width: 100%" />
        </colgroup>
        <tbody>
          <tr>
            <th scope="row" class="required">교육 강좌</th>
            <td>
              <InputBox
                ref="eduTxt"
                type="text"
                :value.sync="form.eduTxt"
                inputWidth="100%"
              />
            </td>
            <th scope="row" class="required">교육 기관</th>
            <td>
              <InputBox
                ref="eduAgency"
                type="text"
                :value.sync="form.eduAgency"
                inputWidth="100%"
              />
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">교육 기간</th>
            <td>
              <div class="date-range-box">
                <DatePicker ref="fromDate" :date="new Date(form.eduStartDt)" />
                <span class="tilde-text">~</span>
                <DatePicker ref="toDate" :date="new Date(form.eduEndDt)" />
              </div>
            </td>
            <th scope="row" class="required">교육 장소</th>
            <td>
              <InputBox
                ref="eduAddr"
                type="text"
                :value.sync="form.eduAddr"
                inputWidth="100%"
              />
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">참석자</th>
            <td colspan="3">
              <div class="grid-box">
                <Grid
                  class="tui-grid inline-block"
                  ref="memberGrid"
                  :options="memberGridProps.options"
                  :data="memberGridProps.data"
                  :columns="memberGridProps.columns"
                  @click="addAttend"
                />
                <span class="tilde-text custom-m50"></span>
                <Grid
                  class="tui-grid inline-block"
                  ref="attendGrid"
                  :options="attendGridProps.options"
                  :data="attendGridProps.data"
                  :columns="attendGridProps.columns"
                  @click="removeAttend"
                />
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">교육 내용 및 개요</th>
            <td colspan="3">
              <Editor
                ref="eduContents"
                :initialValue="form.eduContents"
                initial-edit-type="markdown"
                preview-style="vertical"
              />
            </td>
          </tr>
          <tr>
            <th scope="row">실무 또는 당사 활용 방법</th>
            <td colspan="3">
              <Editor
                ref="eduApplication"
                :initialValue="form.eduApplication"
                initial-edit-type="markdown"
                preview-style="vertical"
              />
            </td>
          </tr>
          <tr>
            <th scope="row">의견 및 소감 (개인별)</th>
            <td colspan="3">
              <Editor
                ref="eduOpinion"
                :initialValue="form.eduOpinion"
                initial-edit-type="markdown"
                preview-style="vertical"
              />
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="modal-popup-btn-box">
      <button type="button" class="btn btn-primary" @click="save">저장</button>
      <button type="button" class="btn" v-if="modify" @click="remove">
        삭제
      </button>
      <button type="button" class="btn" @click="cancel">취소</button>
    </div>
  </ModalPopup>
</template>

<script>
import "@toast-ui/editor/dist/toastui-editor.css";
import { Editor } from "@toast-ui/vue-editor";

import InputBox from "@/components/InputBox";
import RadioButton from "@/components/RadioButton";
import FileButton from "@/components/FileButton";
import vSelect from "vue-select";
import DatePicker from "@/components/Datepicker";
import ModalPopup from "@/components/ModalPopup";

import { Empty } from "@/mixins/Empty";
import { DateFunc } from "@/mixins/DateFunc";
import { Grid } from "@toast-ui/vue-grid";
import Loading from "vue-loading-overlay";

export default {
  name: "EduReportWritePop",
  props: ["eduReportDetail", "resetDetail", "search"],
  mixins: [Empty, DateFunc],
  components: {
    Editor,
    InputBox,
    RadioButton,
    FileButton,
    vSelect,
    DatePicker,
    ModalPopup,
    Grid,
    Loading,
  },
  data() {
    return {
      modalVisible: false,
      form: {
        eduNo: "",
        eduTxt: "",
        eduAgency: "",
        eduAddr: "",
        eduStartDt: "",
        eduEndDt: "",
        eduContents: " ",
        eduApplication: " ",
        eduOpinion: " ",
      },
      memberList: [],
      attendList: [],
      modify: false,
      isLoading: false,
      fullPage: true,
    };
  },
  watch: {
    modalVisible(val, oldVal) {
      if (!val) {
        this.resetDetail();
        this.modify = false;
        this.memberList = [];
        this.attendList = [];
      }
    },
    // 부모 props와 상세 입력/수정 form 동기화
    eduReportDetail(val, oldVal) {
      this.form = this.eduReportDetail;
    },
  },
  computed: {
    memberGridProps() {
      return {
        options: {
          rowHeight: 10,
          width: 390,
          scrollX: false,
          scrollY: true,
          bodyHeight: 200,
          showDummyRows: true,
          selectionUnit: "row",
        },
        columns: [
          {
            header: "임직원명",
            name: "empNm",
            width: 390,
            align: "center",
          },
          {
            header: "임직원번호",
            name: "empNo",
            width: 0,
            align: "center",
            hidden: true,
          },
        ],
        data: [...this.memberList],
      };
    },
    attendGridProps() {
      return {
        options: {
          rowHeight: 10,
          width: 390,
          scrollX: false,
          scrollY: true,
          bodyHeight: 200,
          showDummyRows: true,
          selectionUnit: "row",
        },
        columns: [
          {
            header: "참석자명",
            name: "empNm",
            width: 390,
            align: "center",
          },
          {
            header: "임직원번호",
            name: "empNo",
            width: 0,
            align: "center",
            hidden: true,
          },
        ],
        data: [...this.attendList],
      };
    },
  },
  methods: {
    // 신규 팝업 열기
    openPop() {
      this.callMemberList();
      this.modalVisible = true;
    },
    // 수정 팝업 열기
    openUpdatePop() {
      const attendArray = this.eduReportDetail.eduAttendee.split(",");
      this.callMemberList(attendArray);
      this.modify = true;
      this.modalVisible = true;
    },
    // 닫기
    cancel() {
      this.modalVisible = false;
    },
    // 임직원 리스트 조회
    async callMemberList(attendArray) {
      this.isLoading = true;

      const apiUrl = "/api/member/list";
      const data = await this.$http
        .post(apiUrl, {})
        .then((solve) => {
          this.memberList = solve.data.contents;

          this.$refs.memberGrid.invoke("resetData", this.memberList);

          if (void 0 !== attendArray) {
            let memberGridData = this.$refs.memberGrid.invoke("getData");
            const attendGridData = [];

            for (let attendNo of attendArray) {
              memberGridData = memberGridData.filter((x) => {
                if (x.empNo === attendNo) {
                  attendGridData.push(x);
                }
                return x.empNo !== attendNo;
              });
            }
            this.memberList = memberGridData;
            this.$refs.memberGrid.invoke("resetData", this.memberList);

            this.attendList = this.sortByRowKey(attendGridData);
            this.$refs.attendGrid.invoke("resetData", this.attendList);
          }

          return solve;
        })
        .catch((e) => console.log(e))
        .finally(() => {
          this.isLoading = false;
        });
    },
    // 저장
    save() {
      const saveData = this.form;
      saveData.eduContents = this.$refs.eduContents.invoke("getMarkdown");
      saveData.eduApplication = this.$refs.eduApplication.invoke("getMarkdown");
      saveData.eduOpinion = this.$refs.eduOpinion.invoke("getMarkdown");

      if (this.isEmpty(saveData.eduTxt)) {
        alert("교육 강좌를 입력하세요.");
        this.$refs.eduTxt.focus();
        return false;
      }
      if (this.isEmpty(saveData.eduAgency)) {
        alert("교육 기관을 입력하세요.");
        this.$refs.eduAgency.focus();
        return false;
      }

      saveData.eduStartDt = this.$refs.fromDate.instance.getDate();
      saveData.eduEndDt = this.$refs.toDate.instance.getDate();
      if (this.isNotEmpty(saveData.eduStartDt)) {
        saveData.eduStartDt = this.formatDate(saveData.eduStartDt, "-");
      } else {
        alert("교육기간 시작일을 입력하세요.");
        this.$refs.fromDate.focus();
        this.$refs.fromDate.click();
        return false;
      }
      if (this.isNotEmpty(saveData.eduEndDt)) {
        saveData.eduEndDt = this.formatDate(saveData.eduEndDt, "-");
      } else {
        alert("교육기간 종료일을 입력하세요.");
        this.$refs.toDate.focus();
        this.$refs.toDate.click();
        return false;
      }

      if (this.isEmpty(saveData.eduAddr)) {
        alert("교육 장소를 입력하세요.");
        this.$refs.eduAddr.focus();
        return false;
      }

      const attendCount = this.$refs.attendGrid.invoke("getRowCount");
      if (attendCount < 1) {
        alert("참석자를 입력하세요.");
        return false;
      }
      saveData.eduAttendeearry = this.$refs.attendGrid
        .invoke("getData")
        .map((x) => {
          return x.empNo;
        })
        .join(",");

      if (this.isEmpty(saveData.eduContents)) {
        alert("교육 내용 및 개요를 입력하세요.");
        this.$refs.eduContents.invoke("focus");
        return false;
      }

      this.callEduReportSave(saveData);
    },
    // 저장 API 호출
    async callEduReportSave(saveData) {
      this.isLoading = true;

      const save = await this.$http
        .post("/api/board/save-edu-report", saveData)
        .then((solve) => {
          if (solve.data.statusCode === 200) {
            this.modalVisible = false;
            this.search();
          } else {
            throw new Error("게시물을 저장하는데 실패하였습니다.");
          }

          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });

      return save;
    },
    // 삭제 API 호출
    async callEduReportRemove() {
      this.isLoading = true;

      const save = await this.$http
        .post("/api/board/remove-edu-report", {
          eduNo: this.eduReportDetail.eduNo,
        })
        .then((solve) => {
          if (solve.data.statusCode === 200) {
            this.modalVisible = false;
            this.search();
          } else {
            throw new Error("게시물을 삭제하는데 실패하였습니다.");
          }

          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    // 임직원 => 참석자
    addAttend(event) {
      if (void 0 === event.rowKey) return false;

      const eventRow = this.$refs.memberGrid.invoke("getRow", event.rowKey);
      this.memberList = this.memberList.filter(
        (x) => x.empNo !== eventRow.empNo
      );
      this.$refs.memberGrid.invoke("resetData", this.memberList);

      this.attendList = this.sortByRowKey(this.attendList.concat(eventRow));
      this.$refs.attendGrid.invoke("resetData", this.attendList);
    },
    // 참석자 => 임직원
    removeAttend(event) {
      if (void 0 === event.rowKey) return false;

      const eventRow = this.$refs.attendGrid.invoke("getRow", event.rowKey);
      this.attendList = this.attendList.filter(
        (x) => x.empNo !== eventRow.empNo
      );
      this.$refs.attendGrid.invoke("resetData", this.attendList);

      this.memberList = this.sortByRowKey(this.memberList.concat(eventRow));
      this.$refs.memberGrid.invoke("resetData", this.memberList);
    },
    // 회원 키순 정렬
    sortByRowKey(target) {
      return target.sort((a, b) => {
        if (a.rowKey > b.rowKey) {
          return 1;
        }
        if (a.rowKey < b.rowKey) {
          return -1;
        }
        return 0;
      });
    },
    // 삭제
    remove() {
      if (confirm("정말로 삭제하시겠습니까?")) {
        this.callEduReportRemove();
      }
    },
  },
};
</script>
<style scoped lang="scss">
.inline-block {
  display: inline-block;
}
.custom-m50 {
  margin: 0 60px !important;
}
</style>