<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="page-title-box">
      <h1 class="page-title">자료실</h1>
      <ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
        <li class="breadcrumb">홈</li>
        <li class="breadcrumb">게시판</li>
        <li class="breadcrumb">자료실</li>
      </ol>
    </div>
    <div class="search-input-box">
      <div class="search-input-row">
        <div class="search-item w100">
          <label for="boardTitle">제목</label>
          <div class="search-value">
            <InputBox
              id="titleTxt"
              type="text"
              inputWidth="100%"
              :value.sync="searchData.titleTxt"
              :targetMethod="search"
            />
          </div>
        </div>
      </div>
      <div class="search-input-row">
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="resourceType">자료유형</label>
          <div class="search-value">
            <vSelect
              id="resourceType"
              :options="resourceType.options"
              v-model="searchData.resType"
              :clearable="false"
              :searchable="false"
              label="text"
            ></vSelect>
          </div>
        </div>
        <div class="search-item w100" v-if="!this.$store.state.isMobile">
          <label for="uploadfile1">파일명</label>
          <div class="search-value">
            <InputBox
              id="uploadfile1"
              type="text"
              inputWidth="100%"
              :value.sync="searchData.uploadfile1"
              :targetMethod="search"
            />
          </div>
        </div>
        <div class="search-item confirm-box">
          <button type="button" class="btn" @click="resetSearch">초기화</button>
          <button type="button" class="btn btn-primary" @click="search">
            검색
          </button>
        </div>
      </div>
    </div>
    <BoardList
      ref="boardList"
      :gridHeader="gridHeader"
      :boardList="resourceList"
      :openDetailPop="callFileDownload"
    />
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";
import InputBox from "@/components/InputBox";
import DatePicker from "@/components/Datepicker";
import ModalPopup from "@/components/ModalPopup";
import BoardList from "@/views/board/list/BoardList";
import Loading from "vue-loading-overlay";

export default {
  name: "Resource",
  props: ["typeCode"],
  components: {
    Grid,
    vSelect,
    InputBox,
    DatePicker,
    ModalPopup,
    BoardList,
    Loading,
  },
  data() {
    return {
      gridHeader: [
        {
          header: "게시글번호",
          name: "resNo",
          width: 0,
          align: "center",
          hidden: true,
        },
        {
          header: "자료유형",
          name: "resType",
          width: 150,
          align: "center",
          sortable: true,
        },
        {
          header: "제목",
          name: "titleTxt",
          formatter(data) {
            return `
                <div class="board-title-box">
                  <a class="board-title clickable-text">${data.value}</a>
                  ${
                    Math.floor(
                      (new Date() - new Date(data.row.regDt)) /
                        (1000 * 60 * 60 * 24)
                    ) <= 5
                      ? '<span class="ico-new">새 게시물</span>'
                      : ""
                  }
                </div>
              `;
          },
          sortable: true,
        },
        {
          header: "파일명",
          name: "originalFileName1",
          width: 300,
          align: "center",
          sortable: true,
        },
        {
          header: "등록자",
          name: "regstaffNm",
          width: 150,
          align: "center",
          sortable: true,
        },
        {
          header: "등록일",
          name: "regDt",
          width: 200,
          align: "center",
          sortable: true,
        },
      ],
      resourceType: {
        options: [],
      },
      searchData: {
        titleTxt: "",
        resType: {
          sortSeq: 0,
          syscodeYn: "N",
          text: "전체",
          upcodeCd: "",
          value: "",
        },
        uploadfile1: "",
      },
      resourceList: [],
      isLoading: false,
      fullPage: true,
    };
  },
  created() {
    this.setResourceInfo();
  },
  computed: {
    ...mapState(["code"]),
    mobileHidden() {
      return this.$store.getters.isMobile;
    },
  },
  watch: {
    mobileHidden(val, oldVal) {},
  },
  methods: {
    setResourceInfo() {
      this.resourceType.options = [
        {
          sortSeq: 0,
          syscodeYn: "N",
          text: "전체",
          upcodeCd: "",
          value: "",
        },
        ...this.code.codeInfo.filter((x) => x.value === this.typeCode).shift()
          .list,
      ];
      this.callResourceList();
    },
    // 자료실 목록 API 조회
    async callResourceList(params) {
      this.isLoading = true;

      if (params === void 0)
        params = {
          upcode: this.typeCode,
        };

      const apiUrl = "/api/board/get-resource-list";
      const data = await this.$http
        .post(apiUrl, params)
        .then((solve) => {
          if (solve.data.result) {
            this.resourceList = solve.data.data;
          } else {
            throw new Error("게시물 목록을 가져오는데 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    // 검색
    search() {
      const param = {
        titleTxt: this.searchData.titleTxt,
        uploadfile1: this.searchData.uploadfile1,
        resType: this.searchData.resType.value,
        upcode: this.typeCode,
      };

      this.callResourceList(param);
    },
    // 검색조건 초기화
    resetSearch() {
      this.searchData.titleTxt = "";
      this.searchData.uploadfile1 = "";
      this.searchData.resType = {
        sortSeq: 0,
        syscodeYn: "N",
        text: "전체",
        upcodeCd: "",
        value: "",
      };
    },
    // 그리드 클릭시 자료 다운로드
    callFileDownload(eventRow) {
      this.$store.dispatch("fileDownload", {
        oFileName: eventRow.originalFileName1,
        filePath: eventRow.uploadfile1,
      });
    },
  },
};
</script>
