<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="true"/>
    <div>
			<div class="page-title-box">
				<h1 class="page-title">{{menuName}}</h1>
				<ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
					<li class="breadcrumb">홈</li>
					<li class="breadcrumb">{{upMenuName}}</li>
					<li class="breadcrumb">{{menuName}}</li>
				</ol>
			</div>
		</div>
    <div v-if="!this.$store.state.isMobile">
      <div class="search-input-box" >
        <div class="search-input-row">
          <div class="search-item">
            <label for="year">년도</label>
            <div class="search-value">
              <vSelect v-model="searchParams.year" :reduce="label => label.value" :options="yearList.options"
                :clearable="false" :searchable="false" @input="search"/>
            </div>
          </div>
          <div class="search-item" v-if="searchParams.pmYn!='Y'">
            <label for="departName">부서(팀)</label>
            <div class="search-value">
              <v-select v-model="searchParams.dptNo" :reduce="label => label.value" :options="dptCodeList.options" :clearable="false" :searchable="true" />
            </div>
          </div>
          <div class="search-item">
            <label for="empNm">임직원</label>
            <div class="search-value">
              <InputBox type="text" :value.sync="searchParams.empNm" v-model="searchParams.empNm"
              :targetMethod="search" />
            </div>
          </div>
          <div class="search-item confirm-box">
            <button type="button" @click.passive.stop="search" class="btn btn-primary">검색</button>
          </div>
        </div>
      </div>
    </div>
    <div>
      <div class="grid-box">
          <div class="grid-tool-box">
          <div/>
          </div>
          <grid class="tui-grid" ref="tuiGrid" :data="gridProps.data" :columns="gridProps.columns" :options="gridProps.options"
                @click ="onClick" />
          <div class="per-page-box" v-if="!this.$store.state.isMobile">
            <span class="total-count-text">총 <span class="count">{{ totalCnt}}</span>개</span>
            <div class="select-box">
              <vSelect v-model="perPage" :reduce="label => label.value" :options="pageList.data" :clearable="false" :searchable="false"
                      @input="onPerPageInput" />
            </div>
          </div>
      </div>
    </div>
    <MemberVacationUsedPop ref="vacPop" :pmYn="searchParams.pmYn" />
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import "tui-grid/dist/tui-grid.css";
import { Grid } from "@toast-ui/vue-grid";
import { Grids } from "@/mixins/Grids";
import 'vue-select/dist/vue-select.css';
import vSelect from 'vue-select'
import Loading from "vue-loading-overlay";
import { Check } from "@/mixins/Check";
import { Comm } from "@/mixins/Comm";
import { Empty } from "@/mixins/Empty";
import InputBox from '@/components/InputBox';
import MemberVacationUsedPop from "@/views/system/pop/MemberVacationUsedPop";

import TuiGrid from 'tui-grid';
TuiGrid.setLanguage('ko');

export default {
  name: "VacationStateList",
  mixins: [Check,Grids,Comm,Empty],
  components: {
    grid: Grid,
    vSelect,
    loading: Loading,
    InputBox,
    MemberVacationUsedPop
  },
  props: {
		upMenuPath: {
			default: 'system'
    },
	},
  data() {
    return {
      pageList: {data: [{label: '10', value: 10},{label: '20', value: 20},{label: '50', value: 50},{label: '100', value: 100}],},
      perPage: 10,
      totalCnt: 0,
      upMenuName : '인트라넷관리',
      menuName : '휴가현황',
      gridProps : {},
      searchParams: { dptNo : '', empNm: '', year: '', pmYn: '' },
      isLoading: false,
      dptCodeList : { options: []},
      yearList: { options: [],},
    };
  },
  watch: {
    upMenuPath(val, oldVal) {
      if( val == 'mypage' ){
        this.upMenuName = '마이페이지';
        this.menuName = '팀원휴가현황';
        this.searchParams.pmYn = 'Y';
      }else{
        this.upMenuName = '인트라넷관리';
        this.menuName = '휴가현황';
        this.searchParams.pmYn = '';
      }
      this.searchParams.dptNo = '';
      this.searchParams.year = '';
      this.searchParams.empNm = '';
      this.setYearSelectBox();
    },
  },  
  created() {
    this.dptCodeList.options = this.commSelect("10000", "선택하세요");
    this.gridProps = {
        options: {
          rowHeight: 48,
					scrollX: false,
          scrollY: true,
          bodyHeight: this.$store.state.isMobile ? 'auto' : 480,
					showDummyRows: false,
					selectionUnit: 'row',
          display: { noData: 'No Data' },
          pageOptions: {
            useClient: true,
            perPage: this.perPage,
            visiblePages: 5,
          },
				},
        columns: [
          {header: "사원명", name: "empNm", align: "center", sortable: true, width: '80', formatter: this.gDetail},
          {header: "사번", name: "empNo", align: "center", sortable: true, width: '120'},
          {header: "소속", name: "dptNm", align: "center", sortable: true, width: '100'},
          {header: "연도", name: "year", align: "center", width: '80'},
          {header: "기본 연차", name: "homefaxTxt", align: "center", sortable: true, width: '80'},
          {header: "이월 연차", name: "preyearVac", align: "center", sortable: true, width: '80'},
          {header: "총 연차", name: "vacCalTotal", align: "center", sortable: true, width: '80'},
          {header: "사용 연차",name: "vacUsedThisYear", align: "center", sortable: true, width: '80'},
          {header: "잔여 연차",name: "vacExtra", align: "center", sortable: true, width: '80'},
        ],
        data: []
    };
  },
  mounted() {
    if( this.upMenuPath == 'mypage' ){
      this.upMenuName = '마이페이지';
      this.menuName = '팀원휴가현황';
      this.searchParams.pmYn = 'Y';
    }

    this.setYearSelectBox();
  },
  computed: {
    ...mapState({
      main: (state) => state.main,
    }),
  },
  methods: {
    onClick(ev) {
      if( this.isEmpty(ev.rowKey)) return false;
      const rowData = this.$refs.tuiGrid.invoke("getRow",ev.rowKey);
      if( ev.columnName == "empNm" ){
        let param = { empNo:rowData.empNo, empNm:rowData.empNm};
        this.$refs.vacPop.open(param);
      } 
    },
    setYearSelectBox(){
      let searchparms = {type:"Y", term:0, format:"yyyy"};

      this.$http.post("/api/common/getCurrentDate",searchparms)
          .then((response) => {
            let currentYear = response.data.data;
            let tempArr = [];

            for ( let i = Number(currentYear) ; i > 2012; i--) {
              let obj = {};
              obj.label = i;
              obj.value = i;
              tempArr.push(obj);
            }
            this.yearList.options = tempArr;
            this.searchParams.year = currentYear;
            this.search();
      });
    },
    search(){
      this.isLoading = true;
      this.$http.post("/api/vacation/getVacationStateAll",this.searchParams)
          .then((response) => {
            if (response.data.data) {
              this.totalCnt = response.data.data.contents.length;
              let contentsList = response.data.data.contents;
              this.$refs.tuiGrid.invoke('resetData', contentsList, {});
            }
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    onPerPageInput(data) {
			this.$refs.tuiGrid.invoke("setPerPage", data);
    },
  }
};
</script>

<style scoped lang="scss">
.p-title {
	margin-top: 25px;
	font-size: 24px;
	font-weight: 700;

	~ div {
		margin-top: 15px;
	}
}
</style>
