import { uuid } from 'vue-uuid';

/**
 * Row 체크박스 렌더러
 */
class CheckboxRenderer {
	constructor(props) {
		const {grid, rowKey} = props;
		const uid = uuid.v1();
		const span = document.createElement('span');
		span.className = 'ui-chk';

		const input = document.createElement('input');
		input.id = uid;

		const label = document.createElement('label');
		label.className = 'custom-input';
		label.setAttribute('for', uid);

		span.appendChild(input);
		span.appendChild(label);

		input.type = 'checkbox';

		input.addEventListener('change', () => {
			if (input.checked) {
				grid.check(rowKey);
			} else {
				grid.uncheck(rowKey);
			}
			this.setRowCheckedClass(props, input.checked);
		});

		this.el = span;
		this.render(props);
		this.setRowCheckedClass(props, input.checked);
	}

	setRowCheckedClass(props, checked) {
		const {grid, rowKey} = props;
		if (checked) {
			grid.addRowClassName(rowKey, 'row-checked');
		} else {
			grid.removeRowClassName(rowKey, 'row-checked');
		}
	}

	getElement() {
		return this.el;
	}

	render(props) {
		const input = this.el.querySelector('input');
		const checked = Boolean(props.value);

		input.checked = checked;
	}
}

/**
 * Cell Button Renderer (Multi Button)
 */
class CellButtonRenderer {
	constructor(props) {
		const {grid, rowKey} = props;
		const div = document.createElement('div');
		div.className = 'cell-btn-group';
		// <button type="button" class="btn btn-inner"><span class="ico ico-pdf"></span>PDF</button>

		props.value.forEach((item) => {
			let button, icon;
			button = document.createElement('button');
			button.className = 'btn btn-inner';

			if (item.type !== undefined && item.type !== null && item.type !== '') {
				icon = document.createElement('span');
				icon.className = 'ico';
				switch (item.type) {
					case 'pdf':
						icon.className += ' ico-pdf';
						button.appendChild(icon);
						break;
					default:
						break;
				}
			}
			button.appendChild(document.createTextNode(item.label));

			button.addEventListener('click', (event) => {
				if (item.callback !== undefined && typeof item.callback === "function") {
					item.callback(event, item);
				}
			});
			div.appendChild(button);
		});


		this.el = div;
		this.render(props);
	}

	getElement() {
		return this.el;
	}

	render(props) {

	}
}

/**
 * Valid Text Renderer
 * valid: [null, 'invalid', 'vaild']
 */
class ValidTextRenderer {
	constructor(props) {
		const {grid, rowKey} = props;
		// <div class="tui-grid-cell-content">Birdy</div>
		const div = document.createElement('div');
		div.className = 'tui-grid-cell-content error-test-text';

		if (props.value.valid === 'invalid') {
			div.className += ' valid-fail-text';
		}
		else if (props.value.valid === 'valid') {
			div.className += ' valid-success-text';
		}

		div.appendChild(document.createTextNode(props.value.label));

		this.el = div;
		this.render(props);
	}

	getElement() {
		return this.el;
	}

	render(props) {

	}
}
/**
 * Clickable Text Renderer
 * data: Object
 */
class ClickableTextRenderer {
	constructor(props) {
		const {grid, rowKey} = props;
		// <div class="tui-grid-cell-content">Birdy</div>
		const div = document.createElement('div');
		div.className = 'tui-grid-cell-content clickable-text';

		props.value.data.rowKey = rowKey;
		div.appendChild(document.createTextNode(props.value.label));

		div.addEventListener('click', (event) => {
			props.value.callback(event, props.value.data);
		});

		this.el = div;
		this.render(props);
	}

	getElement() {
		return this.el;
	}

	render(props) {

	}
}

export {CheckboxRenderer, CellButtonRenderer, ValidTextRenderer, ClickableTextRenderer}