<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="page-title-box">
      <h1 class="page-title">게시판관리</h1>
      <ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
        <li class="breadcrumb">홈</li>
        <li class="breadcrumb">인트라넷관리</li>
        <li class="breadcrumb">게시판관리</li>
      </ol>
    </div>
    <div class="search-input-box">
      <div class="search-input-row">
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="boardType">유형</label>
          <div class="search-value">
            <vSelect
              id="boardType"
              :options="boardType.options"
              v-model="searchData.boardType"
              :reduce="(options) => options.value"
              :clearable="false"
              :searchable="false"
              label="text"
            ></vSelect>
          </div>
        </div>
        <div class="search-item w100">
          <label for="boardTitle">제목</label>
          <div class="search-value">
            <InputBox
              id="boardTitle"
              type="text"
              inputWidth="100%"
              :value.sync="searchData.titleTxt"
              :targetMethod="search"
            />
          </div>
        </div>
      </div>
      <div class="search-input-row">
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="boardAuthor">등록자</label>
          <div class="search-value">
            <InputBox
              id="boardAuthor"
              type="text"
              :value.sync="searchData.regstaffNm"
              :targetMethod="search"
            />
          </div>
        </div>
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="regDateFrom">등록일</label>
          <div class="search-value date-range-box">
            <DatePicker ref="fromDate" />
            <span class="tilde-text">~</span>
            <DatePicker ref="toDate" />
          </div>
        </div>
        <div class="search-item confirm-box">
          <button type="button" class="btn" @click="resetSearch">초기화</button>
          <button type="button" class="btn btn-primary" @click="search">
            검색
          </button>
        </div>
      </div>
    </div>
    <div class="grid-box">
      <p class="grid-info-text">
        ※ [중요 게시물]로 선택된 게시물만 시작일과 종료일이 적용 됩니다.
      </p>
      <div class="grid-tool-box">
        <div v-if="!this.$store.state.isMobile">
          <button
            type="button"
            class="btn btn-control"
            @click="saveModifiedGridRows"
          >
            저장
          </button>
          <button type="button" class="btn btn-control" @click="removeGridRows">
            삭제
          </button>
        </div>
        <div>
          <button type="button" class="btn btn-primary" @click="openCreatePop">
            글쓰기
          </button>
        </div>
      </div>
      <Grid
        class="tui-grid"
        ref="tuiGrid"
        :options="gridProps.options"
        :data="gridProps.data"
        :columns="gridProps.columns"
        @click="onGridClick"
        @editingFinish="onCheckAfterGridEditing"
      />
      <div class="per-page-box" v-if="!this.$store.state.isMobile">
        <span class="total-count-text">
          총
          <span class="count">{{ gridProps.data.length }}</span
          >개
        </span>
        <div class="select-box">
          <vSelect
            ref="perPage"
            :options="pageData.data"
            :clearable="false"
            :searchable="false"
            v-model="perPage"
            :reduce="(label) => label.value"
            @input="onPerPageInput"
          ></vSelect>
        </div>
      </div>
    </div>

    <BoardManageWritePop
      ref="detailPop"
      :boardType="boardType"
      :search="search"
    />
  </div>
</template>
<script>
import "codemirror/lib/codemirror.css";
import { Grid } from "@toast-ui/vue-grid";
import { Empty } from "@/mixins/Empty";
import { DateFunc } from "@/mixins/DateFunc";
import { mapState } from "vuex";
import { CheckboxRenderer } from "@/components/renderer/GridRenderer";
import InputBox from "@/components/InputBox";
import vSelect from "vue-select";
import DatePicker from "@/components/Datepicker";
import Loading from "vue-loading-overlay";
import BoardManageWritePop from "@/views/system/pop/BoardManageWritePop";

export default {
  name: "BoardManage",
  props: ["typeCode"],
  mixins: [Empty, DateFunc],
  components: {
    Grid,
    InputBox,
    vSelect,
    DatePicker,
    BoardManageWritePop,
    Loading,
  },
  data() {
    return {
      perPage: 10,
      searchData: {
        boardType: "",
        titleTxt: "",
        regstaffNm: "",
      },
      boardType: {
        options: [],
      },
      pageData: {
        data: [
          { label: "10", value: 10 },
          { label: "20", value: 20 },
          { label: "50", value: 50 },
          { label: "100", value: 100 },
        ],
        selected: "10",
      },
      gridSelect: {
        dispSeq: [],
        dispYn: [
          { text: "게시", value: "Y" },
          { text: "미게시", value: "N" },
        ],
        importantYn: [
          { text: "중요게시물", value: 1 },
          { text: "일반게시물", value: 0 },
        ],
      },
      boardList: [],
      isLoading: false,
      fullPage: true,
    };
  },
  computed: {
    ...mapState(["code"]),
    isMobile() {
      return this.$store.state.isMobile;
    },
    gridProps() {
      return {
        options: {
          rowHeight: 48,
          rowHeaders: [
            {
              type: "checkbox",
              header: `
						<span class="ui-chk">
							<input id="chkAll" type="checkbox" name="_checked"><label for="chkAll"></label>
						</span>`,
              renderer: {
                type: CheckboxRenderer,
              },
            },
          ],
          scrollX: false,
          scrollY: true,
          bodyHeight: 480,
          showDummyRows: false,
          selectionUnit: "row",
          pageOptions: {
            useClient: true,
            perPage: this.perPage,
            visiblePages: 5,
          },
          editingEvent: "click",
        },
        columns: [
          {
            header: "게시글번호",
            name: "bbsNo",
            width: 0,
            align: "center",
            hidden: true,
          },
          {
            header: "게시판유형",
            name: "typNm",
            width: 80,
            align: "center",
          },
          {
            header: "제목",
            name: "titleTxt",
            formatter(data) {
              return `
                <div class="board-title-box">
                  <a class="board-title clickable-text">${data.value}</a>
                  ${
                    Math.floor(
                      (new Date() - new Date(data.row.regDt)) /
                        (1000 * 60 * 60 * 24)
                    ) <= 5
                      ? '<span class="ico-new">새 게시물</span>'
                      : ""
                  }
                </div>
              `;
            },
            sortable: true,
          },
          {
            header: "조회수",
            name: "viewCnt",
            width: 72,
            align: "center",
            sortable: true,
          },
          {
            header: "정렬순서",
            name: "dispSeq",
            editor: {
              type: "text",
            },
            width: 72,
            align: "center",
            sortable: true,
          },
          {
            header: "게시여부",
            name: "dispYn",
            formatter(data) {
              return data.value === "Y" ? "게시" : "미게시";
            },
            editor: {
              type: "select",
              options: { listItems: [...this.gridSelect.dispYn] },
            },
            width: 80,
            align: "center",
            sortable: true,
          },
          {
            header: "중요도",
            name: "importantYn",
            width: 90,
            formatter(data) {
              return parseInt(data.value) === 1 ? "중요게시물" : "일반게시물";
            },
            editor: {
              type: "select",
              options: { listItems: [...this.gridSelect.importantYn] },
            },
            align: "center",
            sortable: true,
          },
          {
            header: "시작일",
            name: "startDt",
            width: 100,
            align: "center",
            sortable: true,
          },
          {
            header: "종료일",
            name: "endDt",
            width: 100,
            align: "center",
            sortable: true,
          },
          {
            header: "등록자",
            name: "regstaffNm",
            width: 80,
            align: "center",
            sortable: true,
          },
          {
            header: "등록일",
            name: "regDt",
            width: 100,
            align: "center",
            sortable: true,
          },
        ],
        data: this.boardList,
      };
    },
  },
  created() {
    this.setBoardManageInfo();
  },
  watch: {
    // 그리드 내용 변경시
    gridProps(val, oldVal) {
      this.$refs.tuiGrid.invoke("resetData", val.data);
    },
  },
  methods: {
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data);
    },
    // 그리드 클릭
    onGridClick(event) {
      if (
        event.targetType !== "columnHeader" &&
        event.columnName === "titleTxt"
      ) {
        const eventRow = this.$refs.tuiGrid.invoke("getRow", event.rowKey);
        this.openUpdatePop(eventRow);
      }
    },
    // 신규 팝업 오픈
    openCreatePop() {
      this.$refs.detailPop.openCreatePop();
    },
    // 수정 팝업 오픈
    openUpdatePop(eventRow) {
      this.$refs.detailPop.openUpdatePop(eventRow);
    },
    // 유형 공통코드 세팅
    setBoardManageInfo() {
      this.boardType.options = [
        {
          sortSeq: 0,
          syscodeYn: "N",
          text: "전체",
          upcodeCd: "",
          value: "",
        },
        ...this.code.codeInfo.filter((x) => x.value === this.typeCode).shift()
          .list,
      ];
      this.callBoardManageList();
    },
    // 게시판관리 목록 조회
    async callBoardManageList(params) {
      this.isLoading = true;

      if (params === void 0)
        params = {
          upcode: this.typeCode,
        };

      const apiUrl = "/api/system/board-manage/get-list";
      const data = await this.$http
        .post(apiUrl, params)
        .then((solve) => {
          if (solve.data.result) {
            this.boardList = solve.data.data;
          } else {
            throw new Error("게시물 목록을 가져오는데 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    // 검색
    search() {
      const param = {
        upcode: this.typeCode,
        typCd: this.searchData.boardType,
        titleTxt: this.searchData.titleTxt,
        regstaffNm: this.searchData.regstaffNm,
      };

      const fromDate = this.$refs.fromDate.instance.getDate();
      const toDate = this.$refs.toDate.instance.getDate();
      if (this.isNotEmpty(fromDate)) {
        param.fromDate = this.formatDate(fromDate, "-");
      }
      if (this.isNotEmpty(toDate)) {
        param.toDate = this.formatDate(toDate, "-");
      }

      this.callBoardManageList(param);
    },
    // 검색조건 초가화
    resetSearch() {
      this.searchData.titleTxt = "";
      this.searchData.regstaffNm = "";
      this.searchData.boardType = "";
      this.$refs.fromDate.instance.setDate(null);
      this.$refs.toDate.instance.setDate(null);
    },
    saveModifiedGridRows() {
      this.isLoading = true;
      const checkedRowsData = this.$refs.tuiGrid.invoke("getCheckedRows");

      const apiUrl = "/api/system/board-manage/grid-save";
      const data = this.$http
        .post(apiUrl, checkedRowsData)
        .then((solve) => {
          if (solve.data.statusCode === 200) {
            alert("게시물을 수정하였습니다.");
            this.search();
          } else {
            throw new Error("게시물을 수정하는데 실패하였습니다.");
          }
        })
        .catch((e) => {
          console.log(e);
        })
        .finally((f) => {
          this.isLoading = false;
        });
    },
    removeGridRows() {
      this.isLoading = true;
      const checkedRowsData = this.$refs.tuiGrid.invoke("getCheckedRows");

      const apiUrl = "/api/system/board-manage/grid-remove";
      const data = this.$http
        .post(apiUrl, checkedRowsData)
        .then((solve) => {
          if (solve.data.statusCode === 200) {
            alert("게시물을 삭제하였습니다.");
            this.search();
          } else {
            throw new Error("게시물을 삭제하는데 실패하였습니다.");
          }
        })
        .catch((e) => {
          console.log(e);
        })
        .finally((f) => {
          this.isLoading = false;
        });
    },
    onCheckAfterGridEditing(e) {
      this.$refs.tuiGrid.invoke("check", e.rowKey);
    },
  },
};
</script>
