<template>
  <div class="contents-wrap">
    <p class="p-title">{{ menuName }}</p>
    <div>
      <div class="page-title-box">
        <h1 class="page-title"></h1>
        <ol class="breadcrumb-list">
          <li class="breadcrumb">홈</li>
          <li class="breadcrumb">인트라넷 관리</li>
          <li class="breadcrumb">{{ menuName }}</li>
        </ol>
      </div>
    </div>
    <div class="search-input-box" v-if="!this.$store.state.isMobile">
      <div class="search-input-row">
        <div class="search-item">
          <label for="empNm">신청자명</label>
          <div class="search-value">
            <input-box
              type="text"
              :value.sync="searchParam.empNm"
              :targetMethod="search"
            />
          </div>
        </div>
        <div class="search-item">
          <label for="reqUserNm">요청자</label>
          <div class="search-value">
            <input-box
              type="text"
              :value.sync="searchParam.reqUserNm"
              :targetMethod="search"
            />
          </div>
        </div>
      </div>
      <div class="search-input-row">
        <div class="search-item">
          <label for="itemCode">품명</label>
          <div class="search-value">
            <v-select
              v-model="searchParam.itemCode"
              :reduce="(no) => no.value"
              :options="options[11900]"
              :clearable="false"
              :searchable="true"
            >
            </v-select>
          </div>
        </div>
        <div class="search-item">
          <label for="searchDates">희망일</label>
          <div class="search-value">
            <div class="search-value date-range-box">
              <date-picker ref="startDt" :date.sync="searchParam.startDt" />
              <span class="tilde-text">~</span>
              <date-picker ref="endDt" :date.sync="searchParam.endDt" />
            </div>
          </div>
        </div>
      </div>
      <div class="search-input-row">
        <div class="search-item">
          <label for="reqTypeCode">요청구분</label>
          <div class="search-value">
            <v-select
              v-model="searchParam.reqTypeCode"
              :reduce="(yn) => yn.value"
              :options="options[12000]"
              :clearable="false"
              :searchable="true"
            ></v-select>
          </div>
        </div>
        <div class="search-item">
          <label for="approvalSt">승인여부</label>
          <div class="search-value">
            <v-select
              v-model="searchParam.approvalSt"
              :reduce="(st) => st.value"
              :options="options.approvalStList"
              :clearable="false"
              :searchable="true"
            ></v-select>
          </div>
        </div>
        <div class="search-item confirm-box">
          <button
            type="button"
            @click.passive.stop="resetSearchForm"
            class="btn"
          >
            초기화
          </button>
          <button
            type="button"
            @click.passive.stop="search"
            class="btn btn-primary"
          >
            검색
          </button>
        </div>
      </div>
    </div>
    <div class="grid-box">
      <div class="grid-tool-box">
        <div></div>
        <div>
          <button type="button" class="btn btn-primary" @click="approve">
            승인
          </button>
          <button type="button" class="btn" @click="rejectStart">반려</button>
          <button type="button" class="btn" @click="requestItem">
            구매신청
          </button>
        </div>
      </div>
      <grid
        class="tui-grid"
        ref="tuiGrid"
        :data="gridProps.data"
        :columns="gridProps.columns"
        :options="gridProps.options"
        @click="onClick"
      />
      <div class="per-page-box" v-if="!this.$store.state.isMobile">
        <span class="total-count-text">
          총
          <span class="count">{{ totalCnt }}</span
          >개
        </span>
        <div class="select-box">
          <v-select
            v-model="perPage"
            :reduce="(opt) => opt.value"
            :options="options.pageList"
            :clearable="false"
            :searchable="false"
            @input="onPerPageInput"
          ></v-select>
        </div>
      </div>
    </div>
    <reject-reason-pop
      ref="rejectReasonPop"
      @submitted="reject"
    ></reject-reason-pop>
    <buy-request-manage-pop
      ref="requestPop"
      :isDetail="false"
      :upcodes="pop.upcodes"
      @created="search"
    ></buy-request-manage-pop>
    <buy-request-manage-pop
      ref="detailPop"
      :isDetail="true"
      :upcodes="pop.upcodes"
      @modified="search"
    ></buy-request-manage-pop>
    <loading
      :active.sync="isLoading"
      :on-cancel="loadingCancel"
      :is-full-page="fullPage"
    ></loading>
  </div>
</template>

<script>
import "tui-grid/dist/tui-grid.css";
import "vue-select/dist/vue-select.css";

import { mapState } from "vuex";
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";
import { Check } from "@/mixins/Check";
import { Grids } from "@/mixins/Grids";
import { DateFunc } from "@/mixins/DateFunc";
import { Comm } from "@/mixins/Comm";
import InputBox from "@/components/InputBox";
import DatePicker from "@/components/Datepicker";
import { CheckboxRenderer } from "@/components/renderer/GridRenderer";
import RejectReasonPop from "./pop/RejectReasonPop";
import BuyRequestManagePop from "./pop/BuyRequestManagePop";
import Loading from "vue-loading-overlay";

import TuiGrid from "tui-grid";
TuiGrid.setLanguage("ko");

export default {
  name: "BuyRequestManage",
  mixins: [Check, DateFunc, Grids, Comm],
  props: ["menuName", "upcodes"],
  components: {
    grid: Grid,
    vSelect,
    InputBox,
    DatePicker,
    loading: Loading,
    RejectReasonPop,
    BuyRequestManagePop,
  },
  data() {
    return {
      perPage: 10,
      totalCnt: 0,
      isLoading: false,
      fullPage: true,
      gridProps: {},
      searchParam: {
        empNm: "",
        reqUserNm: "",
        startDt: "",
        endDt: "",
        reqTypeCode: null,
        approvalSt: "",
        itemCode: null,
      },
      options: {
        "11900": [],
        "12000": [],
        approvalStList: [
          { label: "선택", value: "" },
          { label: "승인대기", value: "W" },
          { label: "반려", value: "F" },
          { label: "승인완료", value: "S" },
        ],
        pageList: [
          { label: "10", value: 10 },
          { label: "20", value: 20 },
          { label: "50", value: 50 },
          { label: "100", value: 100 },
        ],
      },
      pop: {
        upcodes: ["11900", "12000"],
      },
    };
  },
  computed: {
    ...mapState({
      codeInfo: (state) => state.codeInfo,
    }),
  },
  created() {
    this.token();
    this.formatterInstall();
    this.initDates();
    for (let upcode of this.upcodes) {
      this.options[upcode] = this.commSelect(upcode, "전체");
    }

    this.gridProps = {
      options: {
        rowHeight: 48,
        rowHeaders: [
          {
            type: "checkbox",
            header: `
							<span class="ui-chk">
								<input id="chkAll" type="checkbox" name="_checked"><label for="chkAll"></label>
							</span>`,
            renderer: {
              type: CheckboxRenderer,
            },
          },
        ],
        scrollX: false,
        scrollY: true,
        bodyHeight: this.$store.state.isMobile ? "auto" : 480,
        showDummyRows: false,
        selectionUnit: "row",
        display: { noData: "No Data" },
        pageOptions: {
          useClient: true,
          perPage: this.perPage,
          visiblePages: 5,
        },
      },
      columns: [
        { header: "신청번호", name: "buyReqNo", hidden: true },
        { header: "회원번호", name: "empNo", hidden: true },
        { header: "반려사유", name: "rejectReason", hidden: true },
        { header: "PM승인여부", name: "pmApprovalYn", hidden: true },
        { header: "MGR승인여부", name: "mgrApprovalYn", hidden: true },
        {
          header: "이름",
          name: "empNm",
          align: "center",
          formatter: this.gDetail,
        },
        { header: "품명", name: "itemNm", align: "center" },
        { header: "수량", name: "itemQty", align: "center" },
        { header: "요청구분", name: "reqTypeNm", align: "center" },
        { header: "사유", name: "reqReason", align: "center" },
        { header: "희망일", name: "hopeDt", align: "center" },
        { header: "등록일자", name: "regDt", align: "center" },
        { header: "비고", name: "remarks", align: "center" },
        {
          header: "승인여부",
          name: "approveNm",
          align: "center",
          formatter: this.gApproveNm,
        },
        {
          header: "PDF 다운",
          name: "pdfDownload",
          align: "center",
          formatter: this.gLink,
        },
      ],
      data: [],
    };

    this.search();
  },
  methods: {
    initDates() {
      this.searchParam.startDt = "";
      this.searchParam.endDt = "";
      this.$nextTick(() => {
        this.$refs.startDt.instance.setNull();
        this.$refs.endDt.instance.setNull();
      });
    },
    resetSearchForm() {
      Object.assign(this.$data.searchParam, this.$options.data().searchParam);
      this.initDates();
    },
    gApproveNm(cell) {
      if (cell.value == "반려") {
        return "<u style='cursor:pointer;color:green'>" + cell.value + "</u>";
      } else {
        return cell.value;
      }
    },
    onClick(ev) {
      if (ev.columnName == "empNm") {
        const rowData = this.$refs.tuiGrid.invoke("getRow", ev.rowKey);

        this.$http
          .post("/api/system/buy-request-manage/detail/" + rowData.buyReqNo)
          .then((response) => {
            if (response.data.data) {
              this.$refs.detailPop.openDetail(response.data.data);
            }
          })
          .catch((e) => {
            console.error(e);
          });
      } else if (ev.columnName == "pdfDownload") {
        const rowData = this.$refs.tuiGrid.invoke("getRow", ev.rowKey);
        this.downloadPdf(rowData);
      } else if (ev.columnName == "approveNm") {
        const rowData = this.$refs.tuiGrid.invoke("getRow", ev.rowKey);
        if (rowData.approveNm == "반려") {
          this.$refs.rejectReasonPop.openDetail(rowData.rejectReason);
        }
      }
    },
    search(ev) {
      let params = {
        empNm: this.searchParam.empNm,
        reqUserNm: this.searchParam.reqUserNm,
        reqTypeCode: this.searchParam.reqTypeCode,
        approvalSt: this.searchParam.approvalSt,
        itemCode: this.searchParam.itemCode,
      };
      if (this.searchParam.startDt) {
        params.startDt = this.searchParam.startDt.format("yyyyMMdd");
      }
      if (this.searchParam.endDt) {
        params.endDt = this.searchParam.endDt.format("yyyyMMdd");
      }

      this.isLoading = true;
      this.$http
        .post("/api/system/buy-request-manage/list", params)
        .then((response) => {
          if (response.data.data) {
            this.totalCnt = response.data.data.contents.length;
            this.$refs.tuiGrid.invoke(
              "resetData",
              response.data.data.contents,
              {}
            );
          } else {
            console.log("no data");
          }
        })
        .catch((e) => {
          console.error(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data);
    },
    loadingCancel() {},
    approve() {
      let checked = this.$refs.tuiGrid.invoke("getCheckedRows");
      if (!checked || checked.length == 0) {
        alert("선택된 항목이 없습니다.");
        return false;
      }

      this.isLoading = true;
      this.$http
        .post("/api/system/buy-request-manage/approve", checked)
        .then((response) => {
          if (response.data.data) {
            alert(response.data.data.msg);
          } else {
            console.log("no data");
          }
        })
        .catch((e) => {
          console.error(e);
        })
        .finally(() => {
          this.isLoading = false;
          this.search();
        });
      console.log(checked);
    },
    rejectStart() {
      let checked = this.$refs.tuiGrid.invoke("getCheckedRows");
      if (!checked || checked.length == 0) {
        alert("선택된 항목이 없습니다.");
        return false;
      }

      this.$refs.rejectReasonPop.open();
    },
    reject(rejectReason) {
      if (!rejectReason) {
        alert("반려 사유를 입력해주세요.");
        return false;
      }
      let checked = this.$refs.tuiGrid.invoke("getCheckedRows");
      if (!checked || checked.length == 0) {
        alert("선택된 항목이 없습니다.");
        return false;
      }

      const param = {
        rejectReason,
        buyReqList: checked,
      };

      this.isLoading = true;
      this.$http
        .post("/api/system/buy-request-manage/reject", param)
        .then((response) => {
          if (response.data.data) {
            alert(response.data.data.msg);
          } else {
            console.log("no data");
          }
        })
        .catch((e) => {
          console.error(e);
        })
        .finally(() => {
          this.isLoading = false;
          this.search();
        });
    },
    requestItem() {
      this.$refs.requestPop.open();
    },
    downloadPdf(rowData) {
      this.isLoading = true;
      let buyReqNo = {
        buyReqNo: rowData.buyReqNo,
        type: "download",
        regDt: rowData.regDt,
        empNm: rowData.empNm,
      };
      const profileDownload = this.$http
        .post("/api/utils/report/getBuyRequestReport", buyReqNo, {
          responseType: "arraybuffer",
        })
        .then((solve) => {
          if (solve.status === 200) {
            let fileName =
              rowData.empNm + "_구매신청서_" + solve.headers.regdt + ".pdf";
            let blob = new Blob([solve.data], {
              type: solve.headers["content-type"],
            });
            let link = document.createElement("a");
            link.href = window.URL.createObjectURL(blob);
            link.target = "_self";
            link.download = fileName;
            link.click();
          } else {
            alert(2);
            throw new Error("구매신청서 다운로드를 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
          throw new Error("구매신청서 다운로드를 실패하였습니다.");
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
  },
};
</script>
<style scoped lang="scss">
.p-title {
  margin-top: 25px;
  font-size: 24px;
  font-weight: 700;

  ~ div {
    margin-top: 15px;
  }
}
</style>
