<template>
	<ModalPopup :title="'임직원 상세정보'" type="type-black" :visible.sync="modalVisible" width="900px">
    <div class="modal-white-box">
				<div class="form-table">
					<table>
						<colgroup>
							<col class="col-form-title">
							<col style="width: 286px;">
							<col class="col-form-title">
							<col style="width: 266px;">
						</colgroup>
						<tbody>
              <tr>
								<th scope="row" colspan="4"><span class="offscreen"> 기본정보</span></th>
							</tr>
							<tr>
								<th scope="row" rowspan="5"><span class="offscreen"> 사진</span></th>
								<td rowspan="5" class="vertical-middle">
									<div class="profile-img-box">
										<span class="profile-img" :style="{backgroundImage: 'url('+ this.bgImg +')'}"></span>
                    <!-- <img class="profile-img" :src="bgImg" @error="imageLoadError"> -->
									</div>
								</td>
							</tr>
							<tr>
								<th scope="row" >임직원명</th>
								<td>
									<InputBox type="text" :value.sync="memInfo.empNm" disabled="true" />
								</td>
							</tr>
              <tr>
								<th scope="row" >부서(팀)</th>
								<td>
									<InputBox type="text" :value.sync="memInfo.dptNm" disabled="true" />
								</td>
							</tr>
              <tr>
								<th scope="row" >직급</th>
								<td>
									<InputBox type="text" :value.sync="memInfo.gradeNm" disabled="true" />
								</td>
							</tr>
              <tr>
								<th scope="row" >E-Mail</th>
								<td>
									<InputBox type="email" :value.sync="memInfo.emailTxt" inputWidth="100%" disabled="true" />
								</td>
							</tr>
							<tr>
								<th scope="row" >휴대폰번호</th>
								<td>
									<a :href="`tel:${memInfo.mobilTxt}`" v-if="this.$store.state.isMobile"><InputBox type="text" :value.sync="memInfo.mobilTxt" disabled="true" /></a>
									<InputBox type="text" :value.sync="memInfo.mobilTxt" disabled="true" v-if="!this.$store.state.isMobile"/>
								</td>
								<th scope="row" >전화번호</th>
								<td>
									<a :href="`tel:${memInfo.comptelTxt}`" v-if="this.$store.state.isMobile"><InputBox type="text" :value.sync="memInfo.comptelTxt" disabled="true" /></a>
									<InputBox type="text" :value.sync="memInfo.comptelTxt" disabled="true" v-if="!this.$store.state.isMobile"/>
								</td>
							</tr>
							<!-- <tr>
								<th scope="row">주소</th>
								<td colspan="3">
									<div class="address-box">
										<InputBox class="zip-code" type="text" :value.sync="memInfo.zipnumCd" disabled="true" />
                    <button type="button" class="btn" disabled="true" >우편번호찾기</button>
										<InputBox class="address-pre" type="text" :value.sync="memInfo.zipaddrTxt" disabled="true" />
										<InputBox class="address-sub" type="text" :value.sync="memInfo.addressTxt" disabled="true" />
									</div>
								</td>
							</tr> -->
              <tr>
								<th scope="row">입사일</th>
								<td colspan="3">
									<div class="address-box">
										<InputBox type="text" :value.sync="memInfo.joinDt" disabled="true"/>
									</div>
								</td>
							</tr>
						</tbody>
					</table>
				</div>
		</div>
		<div class="modal-popup-btn-box">
			<button type="button" class="btn" @click.prevent.stop="closed">닫기</button>
		</div>
  </ModalPopup>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import { Comm } from "@/mixins/Comm";
import InputBox from '@/components/InputBox';
import ModalPopup from "@/components/ModalPopup";

export default {
  name: "MemberInfoViewPopup",
  mixins: [Check,Empty,Comm],
  components: {
    ModalPopup,
    InputBox
  },
  mounted() {
  },
  watch: {
    modalVisible(val, oldVal) {
      if (!val) {
        this.resetData();
      }
    },
  },
  data() {
    return {
      modalVisible: false,
      bgImg: "",
      errorImg: "/img/common_nav_profile_no_img_126.84e2c562.png",
      fileInfo: { fileName: "", filePath: "", imageSrc: "" },
      memInfo: { empNm: '', dptNm: '', gradeNm: '', joinDt: '', profileImg: "",
                 emailTxt: '', mobilTxt: '', comptelTxt: '',
                 zipnumCd: '', zipaddrTxt: '', addressTxt: ''
      }
    };
  },
  computed: {
    ...mapState({
      main: (state) => state.main,
    }),
  },
  created() {
  },
  methods: {
    imageLoadError () {
      this.bgImg = this.errorImg;
    },
    open(empNo) {
      //console.log('empNo = ' + empNo);
      if(this.isEmpty(empNo) ){
        //임직원번호가 없으면 alert 메세지 노출
        alert('임직원번호가 없습니다.');
        return false;
      } else {
        let parms = {};
        parms.empNo = empNo;

        this.$http.post("/api/member/getMemberinfo", parms,)
            .then((response) => {
              //console.log(response.data);
              if(response.data.statusCode != 200){
                alert('오류가 발생했습니다.');
                return false;
              }

              let rtnObj = response.data.data.contents;
              this.memInfo = rtnObj;

              if (this.isNotEmpty(this.memInfo.profileImg)) {
                this.fileInfo.fileName = this.memInfo.profileImg;
                this.fileInfo.filePath = this.memInfo.profileImg;
                //this.bgImg = this.memInfo.profileImg;
                //임직원 이미지 정보
                this.setProfileImg();
              }
            })
            .catch((e) => {
              console.log("임직원정보 조회 error : ",e);
            });
      }

      this.modalVisible = true;
    },
    setProfileImg() {
      this.imageViewMixin(this.fileInfo).then((response) => {
        if (response.data.size > 0) {
          this.bgImg = this.imageBlobMixin(
            response.data
          );
        } else {
          this.bgImg =
            "/img/common_nav_profile_no_img_126.84e2c562.png";
        }
      });
    },
    closed(obj) {
      //console.log("close obj==>",obj);
      this.modalVisible = false;
      //부모 함수 콜백
      this.$emit('closeMemberInfoViewPopup', obj);
    },
    resetData(){
      this.memInfo.empNm = '';
      this.memInfo.dptNm = '';
      this.memInfo.gradeNm = '';
      this.memInfo.emailTxt = '';
      this.memInfo.mobilTxt = '';
      this.memInfo.comptelTxt = '';
      this.memInfo.zipnumCd = '';
      this.memInfo.zipaddrTxt = '';
      this.memInfo.addressTxt = '';
      this.memInfo.joinDt = '';
      this.bgImg = '';
    }
  }
};
</script>
