<template>
  <modal-popup
    :title="'경조금 신청 취소'"
    :visible="seen"
    @update:visible="close"
    width="900px"
  >
    <div class="search-input-box">
      <div class="search-input-row">
        <div class="search-item">
          <label for="searchYear">년도</label>
          <div class="search-value">
            <v-select
              id="searchYear"
              v-model="searchParam.year"
              :reduce="(year) => year.value"
              :options="options.yearList"
              :clearable="false"
              :searchable="true"
            ></v-select>
          </div>
        </div>
        <div class="search-item">
          <label for="confirmYn">상태</label>
          <div class="search-value">
            <v-select
              id="confirmYn"
              v-model="searchParam.confirmYn"
              :reduce="(confirm) => confirm.value"
              :options="options.confirmYnList"
              :clearable="false"
              :searchable="true"
            ></v-select>
          </div>
        </div>
        <div class="search-item confirm-box">
          <button
            type="button"
            @click.passive.stop="search"
            class="btn btn-primary"
          >
            검색
          </button>
        </div>
      </div>
    </div>
    <div class="grid-box">
      <grid
        class="tui-grid"
        ref="cnclListGrid"
        :data="gridProps.data"
        :columns="gridProps.columns"
        :options="gridProps.options"
        @click="onClick"
      />
    </div>
  </modal-popup>
</template>

<script>
import ModalPopup from "@/components/ModalPopup";
import "tui-grid/dist/tui-grid.css";
import { Grid } from "@toast-ui/vue-grid";
import "vue-select/dist/vue-select.css";
import vSelect from "vue-select";

export default {
  name: "FamilyEventCancelPopup",
  components: {
    ModalPopup,
    vSelect,
    grid: Grid,
  },
  data() {
    return {
      seen: false,
      totalCnt: 0,
      options: {
        yearList: [],
        confirmYnList: [
          { label: "전체", value: "" },
          { label: "경조금 승인", value: "Y" },
          { label: "경조금 신청", value: "N" },
          { label: "취소신청", value: "CR" },
          { label: "취소승인", value: "CA" },
          { label: "반려", value: "RJ" },
        ],
      },
      searchParam: {
        year: new Date().getFullYear(),
        confirmYn: "",
      },
      gridProps: {},
    };
  },
  created() {
    const currentYear = new Date().getFullYear();
    let tempArr = [];
    for (let i = 0; i < 5; i++) {
      let obj = {};
      obj.label = currentYear - i;
      obj.value = obj.label;
      tempArr.push(obj);
    }
    this.options.yearList = tempArr;

    this.gridProps = {
      options: {
        rowHeight: 48,
        scrollX: false,
        scrollY: true,
        bodyHeight: 480,
        showDummyRows: false,
        selectionUnit: "row",
        display: { noData: "No Data" },
        pageOptions: {
          useClient: true,
          perPage: 10,
          visiblePages: 5,
        },
      },
      columns: [
        { header: "순번", name: "famEvnNo", hidden: true },
        { header: "신청일", name: "requestDt", align: "center" },
        { header: "신청자", name: "empNm", align: "center" },
        { header: "경조금종류", name: "famEvnNm", align: "center" },
        { header: "경조일자", name: "famEvnDt", align: "center" },
        { header: "확정여부", name: "confirmState", align: "center" },
        {
          header: "취소버튼",
          name: "confirmYn",
          align: "center",
          formatter: this.gButton,
        },
      ],
      data: [],
    };
  },
  methods: {
    onClick(ev) {
      if (ev.columnName == "confirmYn") {
        const rowData = this.$refs.cnclListGrid.invoke("getRow", ev.rowKey);
        const param = {
          famEvnNo: rowData.famEvnNo,
        };
        this.cancel(param);
      }
    },
    gButton(cell) {
      if (cell.value && (cell.value == "Y" || cell.value == "N")) {
        return "<button type='button' class='btn' @click='cancel'>취소신청</button>";
      }
    },
    open() {
      this.seen = true;
      this.search();
    },
    close() {
      this.resetForm();
      this.seen = false; //emit?
    },
    resetForm() {
      Object.assign(this.$data.searchParam, this.$options.data().searchParam);
    },
    search() {
      let params = {};
      params.year = this.searchParam.year;
      params.confirmYn = this.searchParam.confirmYn;

      this.$http
        .post("/api/myPage/family-event/list", params)
        .then((response) => {
          if (response.data.data) {
            this.totalCnt = response.data.data.contents.length;
            this.$refs.cnclListGrid.invoke(
              "resetData",
              response.data.data.contents,
              {}
            );
          }
        })
        .catch((e) => {
          console.error(e);
        });
    },
    cancel(param) {
      this.$http
        .post("/api/myPage/family-event/cancel", param)
        .then((response) => {
          if (response.data.data) {
            alert(response.data.data.msg);
            this.$emit("canceled");
            this.seen = false;
          }
        })
        .catch((e) => {
          console.error(e);
        });
    },
  },
};
</script>
