<template>
	<ModalPopup :title="titleNm" :visible.sync="modalVisible" width="772px">
    <loading :active.sync="isLoading" :is-full-page="true"/>
    <div class="form-table">
      <div class="modal-popup-btn-box" v-if="this.$store.state.isMobile">
        <div v-if="pmYn == 'Y'">
          <button type="button" class="btn btn-primary" @click.prevent.stop="vacationConfirm" >승인</button>
          <button type="button" class="btn" @click.prevent.stop="vacationReject" >반려</button>
        </div>
        <div v-else>
          <button type="button" class="btn btn-primary" @click.prevent.stop="save" :disabled="!editorble" >신청</button>
          <button type="button" class="btn" @click.prevent.stop="closed">취소</button>
        </div>
      </div>
      <table>
        <colgroup>
          <col class="col-form-title">
          <col style="width: 100%;">
          <col class="col-form-title">
          <col style="width: 100%;">
        </colgroup>
        <tbody>
          <tr v-if="detail.vacNo != '' ">
            <th scope="row">부서/팀</th>
            <td colspan="3">
              <InputBox type="text" :value.sync="detail.dptNm" placeholder="홀더" disabled="true" />
            </td>
          </tr>
          <tr v-if="detail.vacNo != '' ">
            <th scope="row">이름</th>
            <td>
              <InputBox type="text" :value.sync="detail.empNm" disabled="true" />
            </td>
            <th scope="row" v-if="!this.$store.state.isMobile">직책</th>
            <td v-if="!this.$store.state.isMobile">
              <InputBox type="text" :value.sync="detail.gradeNm" placeholder="홀더" disabled="true" />
            </td>
          </tr>
          <tr v-if="adminYn == 'Y' && detail.vacNo == ''">
            <th scope="row">임직원</th>
            <td colspan="3">
              <vSelect :options="employeeList.options" :clearable="false" :searchable="true" v-model="selectedEmployee"/>
              <span class="tilde-text"></span>
              <InputBox type="text" :value.sync="searchEmpNm" v-model="searchEmpNm" @focusout="getEmployeeList" />
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">휴가유형</th>
            <td colspan="3">
              <v-select v-model="detail.vacTypCd" :reduce="label  => label.value" :options="vacTypeList.options"
                :clearable="false" :searchable="true" :disabled="!editorble" />
            </td>
          </tr>
          <tr v-if="!this.$store.state.isMobile">
            <th scope="row" class="required">휴가기간</th>
            <td colspan="3">
              <div class="date-range-box">
                <DatePicker ref="startDatePicker" :isDisabled="!editorble" />
                <span class="tilde-text">~</span>
                <DatePicker ref="endDatePicker" :isDisabled="!editorble" />
              </div>
            </td>
          </tr>
          <tr v-if="this.$store.state.isMobile">
            <th scope="row" class="required">휴가기간</th>
            <td colspan="3">
              <div class="date-range-box">
                <DatePicker ref="startDatePicker" :isDisabled="!editorble" :isReadonly="editorble"/>
                <span class="tilde-text">~</span>
              </div>
            </td>
          </tr>
          <tr v-if="this.$store.state.isMobile">
            <td colspan="4">
              <div class="date-range-box">
                <DatePicker ref="endDatePicker" :isDisabled="!editorble" :isReadonly="editorble"/>
                <span class="tilde-text"></span>
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row">사유</th>
            <td colspan="3">
              <div class="text-area-box">
                <textarea v-model="detail.memoTxt" rows="4" @keyup="maxByteCheck(detail.memoTxt,300)" :disabled="!editorble" />
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row">비고</th>
            <td colspan="3">
              <div class="text-area-box">
                <textarea v-model="detail.vacComment" rows="2" @keyup="maxByteCheck(detail.vacComment,100)" :disabled="!editorble" />
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row">업무 대리인</th>
            <td>
              <InputBox type="text" ref="vacDeputy" @focusout="onInputFocusOut" v-model="detail.vacDeputy" :value.sync="detail.vacDeputy" :disabled="!editorble" />
            </td>
            <th scope="row">긴급 연락처</th>
            <td>
              <InputBox type="text" v-model="detail.vacTel" :value.sync="detail.vacTel" :disabled="!editorble" />
            </td>
          </tr>
          <tr>
            <th scope="row">첨부파일</th>
            <td colspan="3">
              <div class="input-flex-box">
                <InputBox type="text" disabled="true" />
                <FileButton label="파일선택"  @change="onFileChange" v-if="editorble" />
              </div>
              <div class="attach-file-box">
                <ul class="attached-file-list">
                  <li class="file-item" v-if="isNotEmpty(detail.originalFileName1)">
                    <div class="file-item-info">
                      <span class="file-name" @click="() => downloadFile(detail.uploadfile1, detail.originalFileName1)">{{ detail.originalFileName1 }}</span>
                      <button type="button" class="btn-remove-file" @click.prevent.stop="deleteFile(1)" v-if="editorble">파일삭제</button>
                    </div>
                  </li>
                  <li class="file-item" v-if="isNotEmpty(detail.originalFileName2)">
                    <div class="file-item-info">
                      <span class="file-name" @click="() => downloadFile(detail.uploadfile2, detail.originalFileName2)">{{ detail.originalFileName2 }}</span>
                      <button type="button" class="btn-remove-file" @click.prevent.stop="deleteFile(2)" v-if="editorble">파일삭제</button>
                    </div>
                  </li>
                  <li class="file-item" v-if="isNotEmpty(detail.originalFileName3)">
                    <div class="file-item-info">
                      <span class="file-name" @click="() => downloadFile(detail.uploadfile3, detail.originalFileName3)">{{ detail.originalFileName3 }}</span>
                      <button type="button" class="btn-remove-file" @click.prevent.stop="deleteFile(3)" v-if="editorble">파일삭제</button>
                    </div>
                  </li>
                </ul>
              </div>
            </td>
          </tr>
          <tr>
            <th scope="row">내역</th>
            <td colspan="3">
              <div class="text-area-box">
                <textarea v-model="historyTxt" rows="3" :disabled="!disabled" />
              </div>
            </td>
          </tr>
          <tr v-if="detail.confirmYn == 'Y'">
            <th scope="row"></th>
            <td colspan="3">
              <div class="text-area-box">
                <textarea :value="'이 휴가 신청건은 확정이 되었습니다. \n확정이 된 건은 수정할 수 없습니다.'" rows="2" :disabled="!disabled" />
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="modal-popup-btn-box" v-if="!this.$store.state.isMobile && editorble">
      <button type="button" class="btn btn-primary" @click.prevent.stop="save" :disabled="!editorble" >저장</button>
      <button type="button" class="btn" @click.prevent.stop="closed">취소</button>
    </div>
  </ModalPopup>
</template>

<script>
import { mapState, mapActions } from "vuex";
import 'vue-select/dist/vue-select.css';
import vSelect from 'vue-select'
import Loading from "vue-loading-overlay";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import { Comm } from "@/mixins/Comm";
import InputBox from '@/components/InputBox';
import DatePicker from '@/components/Datepicker';
import ModalPopup from "@/components/ModalPopup";
import FileButton from "@/components/FileButton";

export default {
  name: "VacationPopup",
  mixins: [Check,Empty,Comm],
  components: {
    DatePicker,
    InputBox,
    vSelect,
    loading: Loading,
    ModalPopup,
    FileButton
  },
  props:{
    adminYn: {
			default: 'N',
    },
    pmYn: {
			default: '',
    }
  },
  mounted() {},
  data() {
    return {
      titleNm: ' 휴가신청',
      editorble: true,
      modalVisible: false,
      selectedEmployee : {},
      employeeList: { placeholder: '선택', options: [] },
      vacTypeList: { placeholder: '선택', options: [] },
      searchEmpNm : '',
      historyTxt : '',
      detail: { vacNo: '', vacTypCd: null, empNm: '', empNo: '',
                dptNm: '', gradeNm: '', adminYn: this.adminYn,
                startDt: '', endDt: '', memoTxt: '', vacComment: '', vacDeputy: '', vacTel: '',
                uploadfile1: '', uploadfile2: '', uploadfile3: '',
                originalFileName1 : '', originalFileName2 : '', originalFileName3 : '',
                requestYmd: '', confirmYmd: '', confirmYn: '', confirmEmpNo: '',
                cancelReqYmd: '', cancelApprovYmd: '', cancelApprovEmp: '', rejectYmd: '', rejectEmpNo: ''
      },
      isLoading: false,
    };
  },
  computed: {
    ...mapState({
      main: (state) => state.main,
    }),
  },
  created() {
    this.vacTypeList.options = this.commSelect("10400", "선택하세요");
  },
  watch: {
    modalVisible(val, oldVal) {
      if (!val) {
        this.resetData();
      }
    },
  },
  methods: {
    open(vacNo) {
      if('Y' == this.adminYn && this.$store.state.isMobile ){
        this.titleNm == '휴가승인';
      }
      //직원 목록 가져와서 selectBox보여주기
      if('Y' == this.adminYn && this.isEmpty(vacNo) ){
        this.getEmployeeList();
      }

      if ( this.isNotEmpty(vacNo) && ('Y' == this.pmYn || 'Y' == this.adminYn)) {
        this.editorble = false;
      }

      if( vacNo != '' ){
        this.detail.vacNo = vacNo;
        this.search();
      }
      this.modalVisible = true;
    },
    closed (obj) {
      this.modalVisible = false;
      //부모 함수 콜백
      this.$emit('closedVacationPopup', obj);
    },
    search(){
      let params = {};
      params.vacNo = this.detail.vacNo;
      this.isLoading = true;
      this.$http.post("/api/vacation/getVacationDetail", params)
          .then((response) => {
            if(response.data.statusCode != 200){
              alert('오류가 발생했습니다.');
              return false;
            }

            let rtnObj = response.data.data[0];
            this.detail = rtnObj;
            this.$refs.startDatePicker.setDate(new Date(rtnObj.minVacYmd));
            this.$refs.endDatePicker.setDate(new Date(rtnObj.maxVacYmd));

            if(this.isNotEmpty(this.detail.requestYmd)) this.historyTxt = this.detail.requestYmd + ' 휴가신청';
            if(this.isNotEmpty(this.detail.confirmYmd)) this.historyTxt += '\n' + this.detail.confirmYmd + ' 휴가승인';
            if(this.isNotEmpty(this.detail.cancelReqYmd)) this.historyTxt += '\n' + this.detail.cancelReqYmd + ' 취소신청';
            if(this.isNotEmpty(this.detail.cancelApprovYmd)) this.historyTxt += '\n' + this.detail.cancelApprovYmd + ' 취소승인';
            if(this.isNotEmpty(this.detail.rejectYmd)) this.historyTxt += '\n' + this.detail.rejectYmd + ' 반려';

            if ( this.pmYn == 'Y' && this.$store.state.isMobile) {
              this.detail.empNo = rtnObj.empNo;
            }

            if ( this.detail.confirmYn == 'Y' || this.detail.confirmYn == 'C' || this.detail.confirmYn == 'RJ') {
              this.editorble = false;
            }
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    save(){
      if( !this.validation() ){ return false; }

      let confirmMsg = '휴가신청을 하시겠습니까?';
      if( !this.isEmpty(this.detail.vacNo) )
        confirmMsg = '휴가조정을 하시겠습니까? 이전에 신청한 정보는 삭제됩니다.';

      if(!confirm(confirmMsg)){ return false; }
      this.isLoading = true;
      this.$http.post('/api/vacation/setVacationDetail', this.detail)
              .then((response) => {
                alert(response.data.message);
                if(response.data.statusCode == 200) this.closed('success');
              }
            )
            .catch((e) => {
              console.log(e);
            })
            .finally(() => {
              this.isLoading = false;
            });
    },
    onFileChange(e) {
      if( this.isNotEmpty(this.detail.uploadfile1) &&
          this.isNotEmpty(this.detail.uploadfile2) &&
          this.isNotEmpty(this.detail.uploadfile3) ){
        alert('3개 파일 까지 업로드 할 수 있습니다.');
        return false;
      }

      let formData = new FormData();
      formData.append('uploadFiles', e.event.target.files[0]);
      formData.append('editorYn', 'N');
      let imgPath = "이미지 등록중 오류가 발생했습니다.";
      this.isLoading = true;
      this.$http.post('/api/common/fileUploadRename', formData,
          { headers: {'Content-Type': 'multipart/form-data'}}
          ).then((response) => {
              if( this.isEmpty(this.detail.uploadfile1) ) {
                this.detail.uploadfile1 = response.data.data[0].filePath;
                this.detail.originalFileName1 = response.data.data[0].originalFileName;
              } else if( this.isEmpty(this.detail.uploadfile2) ) {
                this.detail.uploadfile2 = response.data.data[0].filePath;
                this.detail.originalFileName2 = response.data.data[0].originalFileName;
              } else if( this.isEmpty(this.detail.uploadfile3)) {
                this.detail.uploadfile3 = response.data.data[0].filePath;
                this.detail.originalFileName3 = response.data.data[0].originalFileName;
              }
          }).catch((ex)=> {
              console.log("onFileChange ERR!!!!! : ", ex)
          }).finally(() => {
            this.isLoading = false;
          })
    },
    onInputFocusOut(event){
      let params = {};
      params.empNm = this.detail.vacDeputy;

      this.isLoading = true;
      this.$http.post("/api/vacation/getTelNumberByName", params)
            .then((response) => {
              if(response.data.statusCode != 200) alert('전화번호 조회중 오류가 발생했습니다.');
              else this.detail.vacTel = response.data.data.mobilTxt;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    validation(){
      if( 'Y' == this.adminYn && this.isEmpty(this.detail.vacNo) ){
        if(this.isEmpty(this.selectedEmployee)){
          alert('임직원을 선택해 주세요!!!');
          return false;
        }
        this.detail.empNo = this.selectedEmployee.value;
      }

      if( this.isEmpty(this.detail.vacTypCd) ){
        alert('휴가유형을 선택해 주세요!!!');
        return false;
      }

      if(this.isEmpty(this.$refs.startDatePicker.instance.getDate()) ) {
        alert('시작일를 입력해주세요.');
        this.$refs.startDatePicker.focus();
        return false;
      } else if( this.isEmpty(this.$refs.endDatePicker.instance.getDate()) ) {
        alert('종료일를 입력해주세요.');
        this.$refs.endDatePicker.focus();
        return false;
      } else {
        this.detail.startDt = this.getDateByDelimeter(this.$refs.startDatePicker.instance.getDate(), '-');
        this.detail.endDt = this.getDateByDelimeter(this.$refs.endDatePicker.instance.getDate(), '-' );
      }

      if(this.detail.startDt > this.detail.endDt) {
        alert("시작일이 종료일보다 큽니다.");
        this.$refs.startDatePicker.focus();
        return false;
      }

      let bytes = this.maxByteCheck(this.detail.memoTxt);
      if( bytes > 300 ){
        "현재 " + bytes + "바이트 입력하셨습니다.\n 300 바이트 이하로 입력하세요."
        this.detail.memoTxt = this.detail.memoTxt.substring(0, 300);
        return false;
      }

      bytes = this.maxByteCheck(this.detail.vacComment);
      if( bytes > 100 ){
        "현재 " + bytes + "바이트 입력하셨습니다.\n 100 바이트 이하로 입력하세요."
        this.detail.vacComment = this.detail.vacComment.substring(0, 100);
        return false;
      }

      if( this.isEmpty(this.detail.vacDeputy) ){
        alert('업무대리인을 입력해주세요.');
        this.$refs.vacDeputy.focus();
        return false;
      }

      if( this.isEmpty(this.detail.vacTel) ){
        alert('긴급연락처를 입력해주세요.');
        return false;
      }

      return true;
    },
    getEmployeeList(){
      let parms = {};
      parms.dptNo = '';
      parms.empNm = this.searchEmpNm;
      parms.pmYn = this.pmYn;

      this.$http.post("/api/member/getEmployeeList", parms)
          .then((response) => {
            if( this.isEmpty(response.data)){
              alert('임직원 조회중 오류가 발생했습니다.');
              return false;
            }
            this.employeeList.options = response.data.contents;
            this.selectedEmployee = {};
          });
    },
    downloadFile(fileUrl, fileName) {
      this.$store.dispatch("fileDownload", {
        fileName: fileUrl,
        filePath: fileUrl,
        oFileName: fileName
      });
    },
    deleteFile(idx) {
      if ( this.isNotEmpty(this.detail.vacNo)) this.deleteModFile(idx);
      else {
        let formData = new FormData();
        if(idx == 1) formData.append('filePath', this.detail.uploadfile1);
        else if(idx == 2) formData.append('filePath', this.detail.uploadfile2);
        else if(idx == 3) formData.append('filePath', this.detail.uploadfile3);
        this.$http.post('/api/common/removeFile', formData,
            { headers: { "Content-Type": "application/json" }, }
            ).then((response) => {
                if(response.data.statusCode != 200){
                  alert("파일삭제중 오류가 발생하엿습니다.");
                }
                alert(response.data.message);
                if(idx == 1) {
                  this.detail.uploadfile1 = '';
                  this.detail.originalFileName1 = '';
                } else if(idx == 2) {
                  this.detail.uploadfile2 = '';
                  this.detail.originalFileName2 = '';
                } else if(idx == 3) {
                  this.detail.uploadfile3 = '';
                  this.detail.originalFileName3 = '';
                }
            }).catch((ex)=> {
                console.log("deleteFile ERR!!!!! : ", ex)
            })
      }
    },
    deleteModFile(idx) {
      let param = {};
      param.vacNo = this.detail.vacNo;
      if(idx == 1) param.uploadfile1 = this.detail.uploadfile1;
      if(idx == 2) param.uploadfile2 = this.detail.uploadfile2;
      if(idx == 3) param.uploadfile3 = this.detail.uploadfile3;

      this.isLoading = true;
      this.$http.post('/api/vacation/removeFile', param,
          { headers: { "Content-Type": "application/json" }, }
          ).then((response) => {
              this.detail.uploadfile1 = response.data.data[0].uploadfile1;
              this.detail.uploadfile2 = response.data.data[0].uploadfile2;
              this.detail.uploadfile3 = response.data.data[0].uploadfile3;
              this.detail.originalFileName1 = response.data.data[0].originalFileName1;
              this.detail.originalFileName2 = response.data.data[0].originalFileName2;
              this.detail.originalFileName3 = response.data.data[0].originalFileName3;
          }).catch((ex)=> {
              console.log("deleteModFile ERR!!!!! : ", ex)
          }).finally(() => {
            this.isLoading = false;
          })
    },
    vacationConfirm(){
      let params = {};
      params.vacationList = [];
      params.vacationList.push(this.detail);
      this.isLoading = true;
      this.$http.post("/api/vacation/setVacationConfirm", params)
          .then((response) => {
            alert(response.data.message);
            this.closed('success');
          })
          .catch((e) => {
            console.log(e);
            this.isLoading = false;
          })
          .finally(() => {
            //this.isLoading = false;
          });
    },
    vacationReject(){
      let params = {};
      params.vacationList = [];
      params.vacationList.push(this.detail);
      this.isLoading = true;
      this.$http.post("/api/vacation/setVacationReject", params)
          .then((response) => {
            alert(response.data.message);
            this.closed('success');
          })
          .catch((e) => {
            console.log(e);
            this.isLoading = false;
          })
          .finally(() => {
            //this.isLoading = false;
          });
    },
    resetData(){
      this.detail.dptNm = '';
      this.detail.gradeNm = '';
      this.detail.empNo = '';
      this.detail.empNm = '';
      this.detail.vacNo = '';
      this.detail.vacTypCd = null;
      this.detail.memoTxt = '';
      this.detail.vacComment = '';
      this.detail.vacDeputy = '';
      this.detail.vacTel = '';
      this.detail.requestYmd = '';
      this.detail.confirmYmd = '';
      this.detail.confirmYn = '';
      this.detail.confirmEmpNo = '';
      this.detail.cancelReqYmd = '';
      this.detail.cancelApprovYmd = '';
      this.detail.cancelApprovEmp = '';
      this.detail.rejectYmd = '';
      this.detail.rejectEmpNo = '';
      this.searchEmpNm = '';
      this.detail.uploadfile1 = '';
      this.detail.uploadfile2 = '';
      this.detail.uploadfile3 = '';
      this.detail.originalFileName1 = '';
      this.detail.originalFileName2 = '';
      this.detail.originalFileName3 = '';
      this.selectedEmployee = {};
      this.editorble = true;
      this.historyTxt = '';
    },
  },
};
</script>
