import axios from "axios";
import Vue from "vue";
import Vuex from "vuex";
import { main } from "@/store/main";
import { login } from "@/store/login";
import { menu } from "@/store/menu";
import { code } from "@/store/code";

Vue.use(Vuex);

export default new Vuex.Store({
    modules: {
        main,
        login,
        menu,
        code,
    },
    state: {
        isMobile: false,
        mobileMenuOpen: false,
        userInfo: { userNm: "", emailTxt: "", mobilTxt: "", dptNm: "", gradeNm: "", profileImg: "", calendarReadOnly: "" },
        //token: sessionStorage.getItem('token'),
        grouprole: "admin",
        alarmList: [
            { title: "홍길동 휴가 승인 요청", link: "/main", uid: "1", isNew: true },
            { title: "2시 챗봇 프로젝트 회의", link: "/main", uid: "2", isNew: false },
            { title: "제안서 확정 요청", link: "/main", uid: "3", isNew: false },
        ],
        companyPosition: {
            placeholder: "직급선택",
            options: [
                { label: "사원", value: 1 },
                { label: "주임", value: 2 },
            ],
        },
        departName: {
            default: "전체",
            options: [
                { label: "전체", value: 0 },
                { label: "대표이사", value: 1 },
                { label: "영업팀", value: 2 },
            ],
        },
        fileInfo: {
            fileName: "",
            filePath: "",
            imageSrc: "",
        },
        mainFiles: {
            fileName: "",
            filePath: "",
            imageSrc: "",
        },
        usedMarkdownFromDt: "20201028",
    },
    mutations: {
        changeUrl({ state }, payload) {
            this.state.fileInfo.imageSrc = payload;
        },
    },
    getters: {
        getIsMobile(state) {
            return state.isMobile;
        },
        getUsedMarkdownFromDt(state) {
            return state.usedMarkdownFromDt;
        },
    },
    actions: {
        //파일다운로드 공통
        fileDownload({ commit }, fileObject) {
            let promise = axios
                .post("/api/common/filedownload", fileObject, { responseType: "blob", headers: { Authorization: localStorage.getItem("token") } })
                .then((response) => {
                    if (response.data.size > 0) {
                        const url = window.URL.createObjectURL(new Blob([response.data], { type: response.headers["content-type"] }));
                        const link = document.createElement("a");
                        link.href = url;
                        link.setAttribute("download", fileObject.oFileName); //or any other extension
                        document.body.appendChild(link);
                        link.click();
                    } else {
                        alert("파일을 찾을 수가 없습니다.");
                    }
                });
            return promise;
        },
    },
});
