<template>
	<span class="ui-chk">
		<input :id="inputId" type="checkbox" :checked="checked" :disabled="disabled" @change="onCheckboxChange"><label :for="inputId"><span>{{ text }}</span></label>
	</span>
</template>

<script>
export default {
	props: {
		text: {
			default: ''
		},
		checked: {
			default: false,
		},
		disabled: {
			default: false,
		},
	},
	data() {
		return {
			uid: null,
			inputId: null,
		}
	},
	created() {
		this.uid = this._uid;
		this.inputId = 'chk' + this.uid;
	},
	methods: {
		onCheckboxChange(event) {
			this.$emit('checkChange', event);
		}
	}
}
</script>