<template> 
		<ModalPopup ref="modalPopup" :title="'프로필 상세'" type="type-black" :visible.sync="modalVisible" width="80%">
			<div class="modal-popup-btn-box"><button type="button" class="btn btn-primary" @click="save()">저장</button><button type="button" class="btn btn-primary" @click="profileDownload()">프로필 다운로드</button></div>
			<p class="modal-box-title">임직원 기본 정보</p>
			<div class="modal-white-box">
				<div class="form-table">
					<table>
						<colgroup>
							<col class="col-form-title">
							<col style="width: 286px;">
							<col class="col-form-title">
							<col style="width: 266px;">
						</colgroup>
						<tbody>
							<tr>
								<th scope="row">성명</th>
								<td>
									<InputBox type="text" disabled="true" v-model="profileManageDetail.empNm"/>
								</td>
								<th scope="row">소속</th>
								<td>
									<InputBox type="text" disabled="true" value="쓰리탑"/>
								</td>
							</tr>
							<tr>
								<th scope="row">직급</th>
								<td>
                  <InputBox type="text" disabled="true" v-model="profileManageDetail.gradeCdNm"/>
								</td>
								<th scope="row">기술등급</th>
								<td>
                  <vSelect
                    :options="this.getTechGradeCdExceptAll"
                    label="label"
                    v-model="writeForm.techGradeCd"
                    :reduce="(getTechGradeCdExceptAll) => getTechGradeCdExceptAll.code"
                    :clearable="false"
                    :searchable="false"
                  ></vSelect>     
								</td>
							</tr>
							<tr>
								<th scope="row">생년월일</th>
								<td>
									<InputBox type="text" disabled="true" v-model="profileManageDetail.birthdayTxt"/>
								</td>
								<th scope="row">총 경력<br>(자동계산)</th>
								<td>
									<InputBox type="text" disabled="true" v-model="profileManageDetail.longcarrer2"/>
								</td>
							</tr>
							<tr>
								<th scope="row">대학교<br>(학교/학과)</th>
								<td>
									<InputBox type="text" inputWidth="90%" :value.sync="writeForm.university"/>
								</td>
								<th scope="row">대학원</th>
								<td>
									<InputBox type="email" inputWidth="100%" :value.sync="writeForm.graduateSchool"/>
								</td>
							</tr>
							<tr>
								<th scope="row">본 사업 참여임무</th>
								<td>
                  <vSelect
                    :options="this.getWorkTypeExceptAll"
                    label="label"
                    v-model="writeForm.workType"
                    :reduce="(getWorkTypeExceptAll) => getWorkTypeExceptAll.code"
                    :clearable="false"
                    :searchable="false"
                  ></vSelect>     
								</td>
								<th scope="row" >참여기간</th>
								<td>
									<InputBox type="email" inputWidth="100%" :value.sync="writeForm.period"/>
								</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
			<p class="modal-box-title">자격증 관리</p>
			<div class="modal-white-box">
        <ProfileManageLicenseList
          ref="licenseGrid"
          :licenseGridHeader="licenseGridHeader"
          :profileManageLicenseList="profileManageDetail"
        />        
			</div>
			<p class="modal-box-title">경력 관리</p>
			<div class="modal-white-box">
        <ProfileManagePeriodList
          ref="periodGrid"
          :periodGridHeader="periodGridHeader"
          :profileManagePeriodList="profileManageDetail"
        />        
			</div>      
			<!--<div class="modal-popup-btn-box">
				<button type="button" class="btn btn-primary">변경</button>
				<button type="button" class="btn">취소</button>
			</div>-->
		</ModalPopup>
</template>
<script>
import ModalPopup from "@/components/ModalPopup";
import InputBox from "@/components/InputBox";
import RadioButton from "@/components/RadioButton";
import FileButton from "@/components/FileButton";
import DatePicker from "@/components/Datepicker";
import ProfileManageLicenseList from "@/views/myPage/ProfileManageLicenseList";
import ProfileManagePeriodList from "@/views/myPage/ProfileManagePeriodList";
import vSelect from "vue-select";
import { Empty } from "@/mixins/Empty";
import { DateFunc } from "@/mixins/DateFunc";

/** 날짜 YYYYMMDD 형식으로 변환 */
Date.prototype.yyyymmdd = function() {
    var yyyy = this.getFullYear().toString();
    var mm = (this.getMonth() + 1).toString();
    var dd = this.getDate().toString();
 
    return yyyy + (mm[1] ? mm : '0'+mm[0]) + (dd[1] ? dd : '0'+dd[0]);
}

export default {
  name: "ProfileManageDetail",
  props: [
          "profileManageDetail"
          ,"resetDetail"
          ,"gradeCd"
          ,"techGradeCd"
          ,"workType"
          ,"workTypeGrid"
        ],
  components: {
    ModalPopup,
		InputBox,
		RadioButton,
		FileButton,
    DatePicker,    
    ProfileManageLicenseList,
    ProfileManagePeriodList,
    vSelect,
  },
  data() {
    return {
      modalVisible: false,
      writeForm :{
        university : "",
        gradeCd : "",
        techGradeCd : "",
        workType : "",
        graduateSchool : "",
        birthdayTxt : "",
        period :"",
        licenseGirdData : {

        },
        periodGirdData : {

        }
      },
      licenseGridHeader: [  
        {
          header: "자격증",
          name: "licenseNm",
          align: "center",
          sortable: true,
          editor: {
              type: 'text',
              maxLength: 30,
          }
        },
        {
          header: "취득일",
          name: "gainDt",
          align: "center",
          sortable: true,
          month : 'true',
          editor: {
            type: 'datePicker',
            options: {
              format: 'yyyy.MM',
              type : 'month',
              language : 'ko', 
              currentText: '오늘',
              monthNames: ['1월','2월','3월','4월','5월','6월','7월','8월','9월','10월','11월','12월'],
              monthNamesShort: ['1월','2월','3월','4월','5월','6월','7월','8월','9월','10월','11월','12월'],
              dayNames: ['일','월','화','수','목','금','토'],
              dayNamesShort: ['일','월','화','수','목','금','토'],
              dayNamesMin: ['일','월','화','수','목','금','토'],
            }
          },
          onBeforeChange(ev) {
              let gain_dt = ev.nextValue.replace(/ /gi, '');
              let format = /^([0-9]{4})\.(0[0-9]|1[0-2])$/;
              if(!format.test(gain_dt)){
                alert("날짜형식에 맞게 작성해주세요.\n (ex: 2015.01)");
                return ev.stop();
              }else{
                return ev;
              }            
           },                       
        },
      ],            
      periodGridHeader: [
        {
          header: "프로젝트명",
          name: "projectNm",
          width: 300,
          align: "center",
          sortable: true,
          editor: {
              type: 'text',
          },
          validation: { required: true }          
        },
        {
          header: "참여기간",
          width: 200,
          name: "period",
          align: "center",
          sortable: true,
          editor: {
              type: 'text',
          },
          onBeforeChange(ev) {
              let period_dt = ev.nextValue.replace(/ /gi, '');
              let format = /^([0-9]{4})\.(0[0-9]|1[0-2])\~([0-9]{4})\.(0[0-9]|1[0-2])$/;
              if(!format.test(period_dt)){
                alert("날짜형식에 맞게 작성해주세요.\n (ex: 2015.01~2015.05)");
                return ev.stop();
              }else{
                return ev;
              }            
           },                    
        },      
        {
          header: "담당업무",
          width: 200,
          name: "work",
          align: "center",
          sortable: true,
          editor: {
              type: 'text',
          }          
        },        
        {
          header: "역 할",
          width: 150,
          name: "role",
          align: "center",
          sortable: true,
          formatter: 'listItemText',
          validation: {
            dataType: 'string',
            required: true
          },        
          editor: {
              type:'select',
              options: {
                listItems: this.workTypeGrid.options
              }
          }       
        },
        {
          header: "고객사",
          width: 150,
          name: "client",
          align: "center",
          sortable: true,
          editor: {
              type: 'text',
          }               
        },
        {
          header: "근무회사",
          width: 150,
          name: "company",
          align: "center",
          sortable: true,
          editor: {
              type: 'text',
          }               
        },
                {
          header: "서버 O/S",
          width: 150,
          name: "serverNm",
          align: "center",
          sortable: true,
          editor: {
              type: 'text',
          }               
        },                        
        {
          header: "언어",
          width: 150,
          name: "programLanguage",
          align: "center",
          sortable: true,
          editor: {
              type: 'text',
          }               
        },        
        {
          header: "DBMS",
          width: 150,
          name: "dbms",
          align: "center",
          sortable: true,
          editor: {
              type: 'text',
          }               
        },                
        {
          header: "프레임워크",
          width: 150,
          name: "framework",
          align: "center",
          sortable: true,
          editor: {
              type: 'text',
          }               
        },             
        {
          header: "TOOL",
          width: 150,
          name: "tool",
          align: "center",
          sortable: true,
          editor: {
              type: 'text',
          }               
        },             
        {
          header: "기타",
          width: 150,
          name: "etc",
          align: "center",
          sortable: true,
          editor: {
              type: 'text',
          }               
        },                      
      ],      
      profileManageLicenseList : [],
      profileManagePeriodList : [],
    };
  },
  watch: {
    profileManageDetail(val, oldVal) {
      this.writeForm = this.profileManageDetail;
    },
  },  
  computed :{
    getGradeCdExceptAll() {
      const exceptAll = [...this.gradeCd.options];
      exceptAll.shift();
      return exceptAll;
    },
    getTechGradeCdExceptAll() {
      const exceptAll = [...this.techGradeCd.options];
      exceptAll.shift();
      return exceptAll;
    },
    getWorkTypeExceptAll() {
      const exceptAll = [...this.workType.options];
      exceptAll.shift();
      return exceptAll;
    },        
  },
  methods: {
    getGridCodeData : function(param){
      
      var codes = [];
      if(null != param){
        for (let index = 0; index < param.length; index++) {
          const code = array[index];
          code.text=e.label;
          code.value=e.code;
          codes.push(code);
          
        }
      }
      return codes;
    },
    openDetailPop: function () {
      this.modalVisible = true;
    },
    addPeriod : function(){

    },    
    delLicense : function(){

    },
    delPeriod : function(){

    },
    async save(){
      let profileManage = this.writeForm;
      let licenseGridData = this.$refs.licenseGrid.getGridData();
      let periodGridData = this.$refs.periodGrid.getGridData();
      profileManage.licenseList = licenseGridData;
      profileManage.periodList = periodGridData;

      for (let index = 0; index < licenseGridData.length; index++) {
        const license = licenseGridData[index];
        
        if(license.licenseNm == "" || license.licenseNm == null){
          alert('자격증명은 필수입니다.');
          this.$refs.licenseGrid.focus(index,'licenseNm');
          return false;
        }        
        if(license.gainDt == "" || license.gainDt == null){
          alert('취득일은 필수입니다.');
          this.$refs.licenseGrid.focus(index,'gainDt');
          return false;
        }else{
          let gainDt = license.gainDt.replace(/ /gi, '');
          let format = /^([0-9]{4})\.(0[0-9]|1[0-2])$/;
          if(!format.test(gainDt)){
            alert("날짜형식에 맞게 작성해주세요.\n (ex: 2015.01)");
            return false;
          }        
        }          
      }

      for (let index = 0; index < periodGridData.length; index++) {
        const period = periodGridData[index];
        
        if(period.projectNm == "" || period.projectNm == null){
          alert('프로젝트명은 필수입니다.');
          this.$refs.periodGrid.focus(index,'projectNm');
          return false;
        }        
        if(period.period == "" || period.period == null){
          alert('참여기간은 필수입니다.');
          this.$refs.periodGrid.focus(index,'period');
          return false;
        }else{
          let period_dt = period.period.replace(/ /gi, '');
          let format = /^([0-9]{4})\.(0[0-9]|1[0-2])\~([0-9]{4})\.(0[0-9]|1[0-2])$/;
          if(!format.test(period_dt)){
            alert("날짜형식에 맞게 작성해주세요.\n (ex: 2015.01~2015.05)");
            return false;
          }        
        }          
      }
      
      if(confirm('저장 하시겠습니까?')){
        this.isLoading = true;
        const save = await this.$http
          .post("/api/myPage/profile-manage/save", profileManage)
          .then((solve) => {
            if (solve.status === 200) {
              alert('저장 되었습니다.');
              this.$parent.callProfileManageDetail(this.profileManageDetail.empNo,'');
              this.$parent.search();
            } else {
              throw new Error("프로필을 저장하는데 실패하였습니다.");
            }

            return solve;
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
      }
    },
    profileDownload :function(){
        this.isLoading = true;
        let profileManage = this.writeForm;
        const profileDownload =  this.$http
          .post("/api/myPage/profile-manage/profile-download", profileManage, 
          {
              responseType: 'arraybuffer',
          })
          .then((solve) => {
            if (solve.status === 200) {
                let fileName = this.profileManageDetail.empNm+'_'+(new Date()).yyyymmdd()+'.xls';

                let blob = new Blob([solve.data], {type: solve.headers['content-type']})
                let link = document.createElement('a');
                link.href = window.URL.createObjectURL(blob);
                link.target = '_self';
                link.download = fileName;
                link.click();
            } else {
              throw new Error("프로필 다운로드를 실패하였습니다.");
            }

            return solve;
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
        });
    }
  },
};
</script>