<template>
  <ModalPopup
    :title="`${boardDetail.typNm} 게시내용`"
    :visible.sync="modalVisible"
    width="1120px"
  >
    <div class="tbl-row">
      <table>
        <colgroup>
          <col class="col-row-title" />
          <col />
          <col class="col-row-title" />
          <col />
        </colgroup>
        <tbody>
          <tr>
            <th scope="row">제목</th>
            <td colspan="3">{{ boardDetail.titleTxt }}</td>
          </tr>
          <tr>
            <th scope="row">내용</th>
            <td colspan="3">
              <div
                class="board-content"
                v-if="isHtmlText"
                v-html="boardDetail.contentsTxt"
              ></div>
              <div class="board-content" v-else>
                <Viewer :initialValue="boardDetail.contentsTxt" />
              </div>
            </td>
          </tr>
          <tr v-if="uploadfiles.length > 0">
            <th scope="row">첨부파일</th>
            <td colspan="3">
              <div class="attach-file-box">
                <ul class="attached-file-list">
                  <li
                    class="file-item"
                    v-for="(file, i) in uploadfiles"
                    :key="i"
                  >
                    <div class="file-item-info">
                      <span class="file-name" @click="() => downloadFile(i)">{{
                        file.originalFileName
                      }}</span>
                    </div>
                  </li>
                </ul>
              </div>
            </td>
          </tr>
          <tr v-if="!this.$store.state.isMobile">
            <th scope="row">등록자</th>
            <td>{{ boardDetail.regstaffNm }}</td>
            <th scope="row">등록일</th>
            <td>{{ boardDetail.regDt }}</td>
          </tr>
          <tr v-if="!this.$store.state.isMobile">
            <th scope="row">최종수정자</th>
            <td>{{ boardDetail.modstaffNm }}</td>
            <th scope="row">최종수정일</th>
            <td>{{ boardDetail.modDt }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </ModalPopup>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import ModalPopup from "@/components/ModalPopup";
import { Viewer } from "@toast-ui/vue-editor";
import { DateFunc } from "@/mixins/DateFunc";
import { Empty } from "@/mixins/Empty";
import { Comm } from "@/mixins/Comm";

export default {
  name: "BoardDetail",
  mixins: [DateFunc, Empty, Comm],
  components: {
    ModalPopup,
    Viewer,
  },
  data() {
    return {
      modalVisible: false,
      boardDetail: {},
      uploadfiles: [],
    };
  },
  watch: {
    modalVisible(val, oldVal) {
      // 팝업 닫기시 상세데이터 초기화
      if (!val) {
        this.resetDetail();
        this.uploadfiles = [];
      }
    },
  },
  computed: {
    // 이전 게시글(HTML)여부
    isHtmlText() {
      return (
        this.formatDate(this.boardDetail.regDt, "") <
        this.getUsedMarkdownFromDt()
      );
    },
  },
  methods: {
    ...mapGetters(["getUsedMarkdownFromDt"]),
    ...mapActions({
      fileDownload: "fileDownload",
    }),
    async openDetailPop(bbsNo) {
      await this.callBoardDetail(bbsNo);
      this.modalVisible = true;
    },
    // 게시판(사내공지, 인사발령, 신규직원소개, 경조사) 상세 API 조회
    callBoardDetail(bbsNo) {
      const apiUrl = "/api/board/get-detail/" + bbsNo;
      const promise = this.$http
        .get(apiUrl)
        .then((solve) => {
          if (solve.data.statusCode === 200) {
            this.boardDetail = solve.data.data;

            const date = this.formatDate(solve.data.data.regDt, "");

            if (date < this.getUsedMarkdownFromDt()) {
              this.boardDetail.contentsTxt = this.replaceLucyFilter(
                solve.data.data.contentsTxt
              );
            }

            for (let i = 0; i < 3; i++) {
              const filePath = solve.data.data[`uploadfile${Number(i) + 1}`];
              const originalFileName =
                solve.data.data[`originalFileName${Number(i) + 1}`];

              if (
                this.isNotEmpty(filePath) &&
                this.isNotEmpty(originalFileName)
              )
                this.uploadfiles.push({ filePath, originalFileName });
            }
          } else {
            throw new Error("게시물 상세정보를 가져오는데 실패하였습니다.");
          }
        })
        .catch((e) => {
          console.log(e);
        });

      return promise;
    },
    // 첨부파일 다운로드
    downloadFile(i) {
      const { filePath } = this.uploadfiles[i];

      this.fileDownload({
        filePath,
        oFileName: this.uploadfiles[i].originalFileName,
      });
    },
    // 상세정보 초기화
    resetDetail() {
      this.boardDetail = {};
    },
  },
};
</script>
