<template>
  <div class="grid-box">
    <div style="margin-bottom: 13px;">
      <button type="button" class="btn btn-control" @click="addRow()">추가</button>
      <button type="button" class="btn btn-control" @click="delRow()">삭제</button>
    </div>        
    <Grid
      class="tui-grid"
      ref="periodGrid"
      :options="gridProps.options"
      :data="gridProps.data"
      :columns="gridProps.columns"
      @click="onGridClick"
    />
  </div>
</template>

<script>
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";
import { CheckboxRenderer } from "@/components/renderer/GridRenderer";

export default {
  name: "ProfileManagePeriodList",
  props: [
    "periodGridHeader",
    "profileManagePeriodList",
  ],
  components: {
    Grid,
    vSelect,
  },
  data() {
    return {
      pageData: {
        data: [
          { label: "10", value: 10 },
          { label: "20", value: 20 },
          { label: "50", value: 50 },
          { label: "100", value: 100 },
        ],
        selected: "10",
      },
    };
  },
  computed: {
    gridProps() {
      return {
        options: {
          rowHeaders: [
            {
              type: "checkbox",
              header: `
						<span class="ui-chk">
							<input id="chkAll" type="checkbox" name="_checked"><label for="chkAll"></label>
						</span>`,
              renderer: {
                type: CheckboxRenderer,
              },
            },
          ],          
          rowHeight: 48,
          bodyHeight: 250,
          showDummyRows: true,
          selectionUnit: "row",
          editingEvent: 'click',
        },
        columns: [...this.periodGridHeader],
        data: this.profileManagePeriodList.periodGridList.data.contents,
      };
    },
  },
  watch: {
    gridProps(val, oldVal) {
      this.$refs.periodGrid.invoke("resetData", val.data);
    },
  },
  methods: {
    onGridClick(event) {
      if (
        event.targetType !== "columnHeader" &&
        event.columnName === "empNm"
      ) {
        const eventRow = this.$refs.periodGrid.invoke("getRow", event.rowKey);

   
      }
    },
    onPerPageInput(data) {
      this.$refs.periodGrid.invoke("setPerPage", data.value);
    },
    addRow : function(){
      let row = {}; //row생성시 초기삽이 있으면 설정.
      let options = {}; //옵션을 설정
      this.$refs.periodGrid.invoke('prependRow', row, options);
    },
    delRow : function(){
      this.$refs.periodGrid.invoke('removeCheckedRows', false);
    },         
    getGridData : function(){
      return this.$refs.periodGrid.invoke('getData');
    },
    focus :function(index){
      this.$refs.periodGrid.invoke('focus', index, 'period', true);
      //this.$refs.periodGrid.instanse('focus', index, 'period', true);
    }                  
  },
};
</script>