<template>
    <div class="contents-wrap">
        <loading :active.sync="isLoading" :is-full-page="true"></loading>
        <div class="page-title-box">
            <h1 class="page-title">임직원 주소록</h1>
            <ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
                <li class="breadcrumb">홈</li>
                <li class="breadcrumb">주소록</li>
                <li class="breadcrumb">임직원 주소록</li>
            </ol>
        </div>
        <div class="search-input-box">
            <div class="search-input-row">
                <div class="search-item">
                    <label for="companyPos">임직원명</label>
                    <div class="search-value">
                        <InputBox type="text" :value.sync="gridSearchParams.empNm" :targetMethod="search"/>
                    </div>
                </div>
                <div class="search-item" v-if="!this.$store.state.isMobile">
                    <label for="departName">부서명</label>
                    <div class="search-value">
                        <vSelect id="companyPos" v-model="gridSearchParams.dptNo" :reduce="label => label.value"
                            :options="dptCodeList.options" :clearable="false" :searchable="false"></vSelect>/>
                    </div>
                </div>
            </div>
            <div class="search-input-row">
                <div class="search-item" v-if="!this.$store.state.isMobile">
                    <label for="companyPos">직급</label>
                    <div class="search-value">
                        <vSelect id="companyPos" v-model="gridSearchParams.gradeCd" :reduce="label => label.value"
                            :options="gradeCodeList.options" :clearable="false" :searchable="false"></vSelect>
                    </div>
                </div>
                <div class="search-item confirm-box">
                    <!--<button type="button" class="btn" @click.passive.stop="download" v-if="!this.$store.state.isMobile">주소록 다운로드</button>-->
                    <button type="button" class="btn btn-primary" @click.passive.stop="search">검색</button>
                </div>
            </div>
        </div>
        <div class="contact-grid-box">
            <div class="contact-tree-box" v-if="!this.$store.state.isMobile">
                <div class="btn-box">
                    <button type="button" class="btn btn-inner" @click="treeOpenAll">확장</button>
                    <button type="button" class="btn btn-inner" @click="treeCloseAll">축소</button>
                </div>
                <Tree ref="memberTree" :data="this.treeData"  @selectLeaf="onTreeSelect"/>
            </div>
            <grid class="tui-grid" ref="memberGrid" :data="memberGrid.data" :columns="memberGrid.columns" :options="memberGrid.options" @click="cellClick"/>
        </div>
        <MemberInfoViewPopup ref="memInfoViewPop"  @closeMemberInfoViewPopup="closeMemberInfoViewPopup"/>
    </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import "tui-grid/dist/tui-grid.css";
import { Grid } from "@toast-ui/vue-grid";
import vSelect from 'vue-select'
import Loading from "vue-loading-overlay";
import { Grids } from "@/mixins/Grids";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import { Comm } from "@/mixins/Comm";
import InputBox from "@/components/InputBox";
import Tree from '@/components/Tree';
import MemberInfoViewPopup from "@/views/member/pop/MemberInfoViewPopup";

import TuiGrid from "tui-grid";
TuiGrid.setLanguage("ko");

export default {
    name: "Member",
    mixins: [Check, Empty, Grids, Comm],
    components: {
        grid: Grid,
        vSelect,
        Loading,
        InputBox,
        Tree,
        MemberInfoViewPopup,
    },
    data() {
        return {
            gridSearchParams: {
                empNm: '',
                dptNo : '',
                gradeCd : ''
            },
            dptCodeList : {options: []},
            gradeCodeList : {options: []},
            selectId: '',
            treeData: [],
            memberGrid: {},
            treeSelect: {
                backgroundColor: 'yellow',
            },
            isLoading: false
        }
    },
    created() {
        this.memberGrid = {
            options: { rowHeight: 20,
                        scrollX: true,
                        scrollY: true,
                        bodyHeight: this.$store.state.isMobile ? 'auto' : 580,
                        showDummyRows: false,
                        selectionUnit: 'row',
                        display: { noData: 'No Data' },
                        // header: { columns: [
                        //          {
                        //          name: 'addressTxtTot',
                        //          align: 'left',
                        //          }
                        //      ]
                        // },
                        columnOptions: {
  resizable: true
},
            },
            columns: [
                {header: "성명", name: "empNm",  width: 120,align: "center", sortable: true, formatter: this.gDetail,},
                {header: "직급", name: "gradeNm", width: 120, align: "center", sortable: true, hidden: this.$store.state.isMobile},
                {header: "부서", name: "dptNm",  width: 150, align: "center", sortable: true, hidden: this.$store.state.isMobile},
                {header: "휴대전화번호", name: "mobilTxt", width: 200, align: "center", sortable: true, formatter: this.gMLink},
                {header: "E-Mail", name: "emailTxt", align: "left", sortable: true, formatter: this.gLink},
                //{header: "주소", name: "addressTxtTot", align: "left", sortable: true, width:500},
                {header: "empNo", name: "empNo", hidden:true },
            ],
            data: [],
        };
        this.gradeCodeList.options = this.commSelect("10100", "선택하세요");
        this.dptCodeList.options = this.commSelect("10000", "선택하세요");
        this.search();
        if(!this.$store.state.isMobile) this.treeSearch();
    },
    computed: {
    },
    methods: {
        treeSearch(){
            let searchparms = {};
            searchparms.stateCd = '10201';

            this.isLoading = true;
            this.$http.post("/api/member/getMemberListView", searchparms,)
                .then((response) => {
                    if(response.data.statusCode != 200){
                        alert('오류가 발생했습니다.');
                        return false;
                    }
                    this.treeData= response.data.data.contents;
                    this.$refs.memberTree.instance.resetAllData(this.treeData, {});

                    let memberTree = this.$refs.memberTree.instance;
                    memberTree.eachAll(
                        function(node, nodeId) {
                            if(memberTree.getNodeData(nodeId).value === "" ) memberTree.open(nodeId);
                        });
                })
                .catch((e) => {
                    console.log(e);
                })
                .finally(() => {
                    this.isLoading = false;
                })
                ;
        },
        onTreeSelect(event) {
            this.$refs.memberTree.instance
                .enableFeature('Selectable')
                .on('select', function(evt) {
                });

            let treeDepth = this.$refs.memberTree.instance.getDepth(event.nodeId);
            if (treeDepth == '1') {
                this.gridSearchParams.dptNo = this.$refs.memberTree.instance.getNodeData(event.nodeId).value;
                this.search("tree");

            } else {
                this.gridSearchParams.dptNo = this.$refs.memberTree.instance.getNodeData(event.nodeId).value;
                this.search("tree");
                // let empNo = this.$refs.memberTree.instance.getNodeData(event.nodeId).value;
                // this.$refs.memInfoViewPop.open(empNo);
            }
        },
        async search(type) {
            this.isLoading = true;
            this.$http.post( "/api/member/getMemberListViewData",this.gridSearchParams,)
                .then((response) => {
                    this.$refs.memberGrid.invoke('resetData', response.data.contents, )
                })
                .catch((e) => {
                    console.log(e);
                })
                .finally(() => {
                    if(type === "tree") {
                        this.gridSearchParams.dptNo = "";
                    }
                    this.isLoading = false;
                });
        },
        treeOpenAll() {
            let memberTree = this.$refs.memberTree.instance;
            memberTree.eachAll(function(node, nodeId) {console.log(nodeId); memberTree.open(nodeId);});
            },
        treeCloseAll() {
            let memberTree = this.$refs.memberTree.instance;
            memberTree.eachAll(function(node, nodeId) {memberTree.close(nodeId);});
        },
        cellClick(ev) {
            const clickValue = this.$refs.memberGrid.invoke('getValue',ev.rowKey, 'empNo');
            const mobileValue = this.$refs.memberGrid.invoke('getValue',ev.rowKey, 'mobilTxt');
            const emailTxtValue = this.$refs.memberGrid.invoke('getValue',ev.rowKey, 'emailTxt');
            if(  this.isNotEmpty(clickValue) && ev.columnName === 'empNm') {
                this.$refs.memInfoViewPop.open(clickValue);
            }

            if(  this.isNotEmpty(mobileValue) && ev.columnName === 'mobilTxt' && this.$store.state.isMobile) {
                location.href = "tel:"+mobileValue;
            }

            if( this.isNotEmpty(emailTxtValue) && ev.columnName === 'emailTxt' ) {
                location.href = "mailto:"+emailTxtValue;
            }

        },
        closeMemberInfoViewPopup (rtnObj) {
        },
        download(){
            this.isLoading = true;
            this.$http.post("/api/member/getMemberListViewDataExcel", this.gridSearchParams,
            { responseType: 'arraybuffer',})
            .then((solve) => {
                if (solve.status === 200) {
                    let fileName = 'memberList.xls';

                    let blob = new Blob([solve.data], {type: solve.headers['content-type']})
                    let link = document.createElement('a');
                    link.href = window.URL.createObjectURL(blob);
                    link.target = '_self';
                    link.download = fileName;
                    link.click();
                } else {
                    throw new Error("주소록 다운로드에 실패하였습니다.");
                }

                return solve;
            })
            .catch((e) => {
                console.log(e);
            })
            .finally(() => {
                this.isLoading = false;
            });
        }
    },
}

</script>
