<template>
  <div class="contents-wrap">
    <loading :active.sync="isLoading" :is-full-page="fullPage"></loading>
    <div class="page-title-box">
      <h1 class="page-title">가동율</h1>
      <ol class="breadcrumb-list" v-if="!this.$store.state.isMobile">
        <li class="breadcrumb">홈</li>
        <li class="breadcrumb">보고자료</li>
        <li class="breadcrumb">가동율</li>
      </ol>
    </div>
    <div class="search-input-box">
      <div class="search-input-row">
        <div class="search-item w100">
          <label for="boardTitle">제목</label>
          <div class="search-value">
            <InputBox
              id="titleTxt"
              type="text"
              inputWidth="100%"
              :value.sync="searchData.titleTxt"
            />
          </div>
        </div>
      </div>
      <div class="search-input-row">
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="boardAuthor">등록자</label>
          <div class="search-value">
            <InputBox
              id="regstaffId"
              type="text"
              :value.sync="searchData.regstaffNm"
            />
          </div>
        </div>
        <div class="search-item" v-if="!this.$store.state.isMobile">
          <label for="regDateFrom">등록일</label>
          <div class="search-value date-range-box">
            <DatePicker ref="fromDate" />
            <span class="tilde-text">~</span>
            <DatePicker ref="toDate" />
          </div>
        </div>
        <div class="search-item confirm-box">
          <button type="button" class="btn" @click="resetSearch">초기화</button>
          <button type="button" class="btn btn-primary" @click="search">
            검색
          </button>
        </div>
      </div>
    </div>
    <div class="grid-box">
      <grid
          class="tui-grid"
          ref="tuiGrid"
          :options="gridProps.options"
          :data="gridProps.data"
          :columns="gridProps.columns"
          @click="onGridClick"
      />
      <div class="per-page-box" v-if="!this.$store.state.isMobile">
        <span class="total-count-text">
          총
          <span class="count">{{ gridProps.data.length }}</span
          >개
        </span>
        <div class="select-box">
          <vSelect
            ref="perPage"
            :options="pageData.data"
            :clearable="false"
            :searchable="false"
            v-model="pageData.selected"
            @input="onPerPageInput"
          ></vSelect>
        </div>
      </div>
    </div>
   
    <OperatingRatioDetailPop
      ref="datailPop"
      :openDetailPop="openDetailPop"
    />
  </div>
</template>
<script>
import { mapActions } from "vuex";
import { Grid } from "@toast-ui/vue-grid";
import vSelect from "vue-select";
import InputBox from "@/components/InputBox";
import DatePicker from "@/components/Datepicker";
import ModalPopup from "@/components/ModalPopup";
import BoardList from "@/views/board/list/BoardList";
import OperatingRatioDetailPop from "@/views/report/pop/OperatingRatioDetailPop";
import { Empty } from "@/mixins/Empty";
import { DateFunc } from "@/mixins/DateFunc";
import Loading from "vue-loading-overlay";

export default {
  name: "Board",
  mixins: [Empty, DateFunc],
  props: ["typeCode"],
  components: {
    grid: Grid,
    vSelect,
    InputBox,
    DatePicker,
    ModalPopup,
    BoardList,
    OperatingRatioDetailPop,
    Loading,
  },
  data() {
    return {
      gridHeader: [
        {
          header: "게시글번호",
          name: "bbsNo",
          width: 0,
          align: "center",
          hidden: true,
        },
        {
          header: "제목",
          name: "titleTxt",
          formatter(data) {
            return `
                <div class="board-title-box">
                  <a class="board-title clickable-text">${data.value}</a>
                  ${
                    Math.floor(
                      (new Date() - new Date(data.row.regDt)) /
                        (1000 * 60 * 60 * 24)
                    ) <= 5
                      ? '<span class="ico-new">새 게시물</span>'
                      : ""
                  }
                </div>
              `;
          },
          sortable: true,
        },
        {
          header: "등록자",
          name: "regstaffNm",
          width: 180,
          align: "center",
          hidden: this.mobileHidden,
          sortable: true,
        },
        {
          header: "등록일",
          name: "regDt",
          width: 180,
          align: "center",
          sortable: true,
        },
        {
          header: "조회수",
          name: "viewCnt",
          width: 120,
          align: "center",
          hidden: this.mobileHidden,
          sortable: true,
        },
      ],
      searchData: {
        titleTxt: "",
        regstaffNm: "",
      },
      pageData: {
        data: [
          { label: "10", value: 10 },
          { label: "20", value: 20 },
          { label: "50", value: 50 },
          { label: "100", value: 100 },
        ],
        selected: "10",
      },
      // 게시판 정보, 목록, 상세
      boardInfo: {
        typCd: "",
        boardNm: "",
      },
      boardList: [],
      isLoading: false,
      fullPage: true,
    };
  },
  created() {
    this.callBoardInfo();
  },
  computed: {
    mobileHidden() {
      return this.$store.getters.isMobile;
    },
    gridProps() {
      return {
        options: {
          rowHeight: 48,
          scrollX: false,
          scrollY: true,
          bodyHeight: 480,
          showDummyRows: false,
          selectionUnit: "row",
          pageOptions: {
            useClient: true,
            perPage: 10,
            visiblePages: 5,
          },
        },
        columns: [...this.gridHeader],
        data: this.boardList,
      };
    },
  },
  watch: {
    mobileHidden(val, oldVal) {},
    // 라우터의 typeCode 변경에 따른 컴포넌트 내용 변경
    typeCode(val, oldVal) {
      this.callBoardInfo();
      this.resetSearch();
    },
    gridProps(val, oldVal) {
      this.$refs.tuiGrid.invoke("resetData", val.data);
    },
  },
  methods: {
    // 상세 팝업
    openDetailPop(bbsNo) {
      this.$refs.datailPop.openDetailPop(bbsNo);
    },
    // 게시판(사내공지, 인사발령, 신규직원소개, 경조사) 정보 조회
    async callBoardInfo(params) {
      this.isLoading = true;

      if (params === void 0)
        params = {
          typCd: this.typeCode,
        };

      const apiUrl = "/api/report/operating-ratio";
      const data = await this.$http
        .post(apiUrl, params)
        .then((solve) => {
          
          if (solve.data.result) {
            this.boardList = solve.data.data;

          } else {
            throw new Error("게시물 목록을 가져오는데 실패하였습니다.");
          }
          return solve;
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    // 그리드 클릭
    onGridClick(event) {
      if (event.targetType !== "columnHeader") {
        if (event.columnName === "titleTxt") {
          const eventRow = this.$refs.tuiGrid.invoke("getRow", event.rowKey);
          
          this.openDetailPop(eventRow.bbsNo);
        }
      }
    },
    onPerPageInput(data) {
      this.$refs.tuiGrid.invoke("setPerPage", data.value);
    },
    // 검색
    search() {
      const param = {
        typCd: this.typeCode,
        titleTxt: this.searchData.titleTxt,
        regstaffNm: this.searchData.regstaffNm,
      };

      const fromDate = this.$refs.fromDate.instance.getDate();
      const toDate = this.$refs.toDate.instance.getDate();
      if (!this.isEmpty(fromDate)) {
        param.fromDate = this.formatDate(fromDate, "-");
      }
      if (!this.isEmpty(toDate)) {
        param.toDate = this.formatDate(toDate, "-");
      }

      this.callBoardInfo(param);
    },
    // 검색조건 초기화
    resetSearch() {
      this.searchData.titleTxt = "";
      this.searchData.regstaffNm = "";
      this.$refs.fromDate.instance.setDate(null);
      this.$refs.toDate.instance.setDate(null);
      //그리드 초기화
      //this.boardList = [];
    },
  },
};
</script>
