import Vue from "vue";
import VueRouter from "vue-router";
import axios from "axios";
import store from '../store'
// import Home from "../views/Home";
import Main from "../views/main/Main";
import Login from "../views/Login";
// import Editor from "../views/Editor";
// import Grid from "../views/Grid";
// import Tree from "../views/Tree";
// import Hello from "../views/Hello";
// import Calendar from "@/views/Calendar";
// import ToastGuide from "../views/ToastGuide";
//import TabulatorGuide from "../views/TabulatorGuide";
import MemberList from "../views/member/MemberList";
import AddressList from "../views/address/AddressList";
import System from "./system";
import MyPage from "./mypage";
import Board from "./board";
import Report from "./report";

Vue.use(VueRouter);

const routes = [
  {
    path: "/main",
    name: "Main",
    component: Main,
  },
  {
    path: "/",
    name: "Login",
    component: Login,
  },
  // {
  //   path: "/editor",
  //   name: "Editor",
  //   component: Editor,
  // },
  // {
  //   path: "/tree",
  //   name: "Tree",
  //   component: Tree,
  // },
  // {
  //   path: "/calendar",
  //   name: "Calendar",
  //   component: Calendar,
  // },
  // {
  //   path: "/hello",
  //   name: "Hello",
  //   component: Hello,
  // },
  // {
  //   path: "/gridGuide",
  //   name: "GridGuide",
  //   component: ToastGuide,
  // },
  // // {
  // //   path: "/gridGuide2",
  // //   name: "GridGuide2",
  // //   component: TabulatorGuide,
  // // },
  {
    path: "/member/member",
    name: "MemberList",
    component: MemberList,
  },
  {
    path: "/address/address",
    name: "AddressList",
    component: AddressList,
  },
  ...MyPage,
  ...Board,
  ...System,
  ...Report,
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach(async (to, from, next) => {

  //중복처리 방지
  if (to.path !== "/") {
    if (
      localStorage.getItem("token") === null &&
      sessionStorage.getItem("refreshtoken") === null
    ) {
      if (from.path !== to.path) router.push({ name: "Login" }).catch(() => { });
    } else {
      axios.post("/api/member/jwt/verify", {}, {})
        .then((response) => {

          if (response.status === 200) {
            if (sessionStorage.getItem("refreshtoken") === null) {
              axios
                .post("/api/member/jwt/refreshReload", {}, {})
                .then((response) => {
                  if (response.status === 200) {
                    sessionStorage.setItem("refreshtoken", response.headers.authorization);
                  } else {
                    alert("로그인을 다시 해주세요");
                    router.push({ name: "Login" })
                  }
                });
            }
          }
        });
    }
  } else {
    if (
      (localStorage.getItem("token") !== null && localStorage.getItem("token") !== "undefined")
      && sessionStorage.getItem("refreshtoken") === null
    ) {
      axios.post("/api/member/jwt/verify", {}, {})
        .then((response) => {
          if (response.status === 200) {
            if (sessionStorage.getItem("refreshtoken") === null) {
              axios
                .post("/api/member/jwt/refreshReload", {}, {})
                .then((response) => {
                  if (response.status === 200) {
                    sessionStorage.setItem("refreshtoken", response.headers.authorization);
                  } else {
                    alert("로그인을 다시 해주세요");
                  }
                });
            }
          }
        });
    }
  }
  next();
});

router.afterEach((to, from) => {
  let menuSize = store.state.menu.userMenu.menu;
  if (menuSize.length <= 0) {
    router.push({ name: "Main" }).catch(() => { });
  }
});

export default router;
