<template lang="html">
  <div>
    <ModalPopup :title="'권한메뉴관리상세'" :visible.sync="modalVisible" width="700px">
      <loading :active.sync="isLoading" :is-full-page="true"/>
      <div class="form-table">
        <table>
        <colgroup>
          <col class="col-form-title">
          <col style="width: 100%;">
          <col class="col-form-title">
          <col style="width: 100%;">
        </colgroup>
        <tbody>
          <tr>
            <th scope="row">상위그룹명</th>
            <td>
              <InputBox type="text" v-model="detail.upmenuNm" :value.sync="detail.upmenuNm" disabled="true" />
            </td>
            <th scope="row">상위메뉴ID</th>
            <td>
              <InputBox type="text" v-model="detail.upmenuId" :value.sync="detail.upmenuId" disabled="true"/>
            </td>
          </tr>

          <tr>
            <th scope="row" class="required">메뉴명</th>
            <td>
              <InputBox type="text" ref="menuNm" v-model="detail.menuNm" :value.sync="detail.menuNm" />
            </td>
            <th scope="row">메뉴ID</th>
            <td>
              <InputBox type="text" v-model="detail.menuId" :value.sync="detail.menuId" disabled="true" />
            </td>
          </tr>
          <tr>
            <th scope="row" class="required">정렬순서</th>
            <td>
              <InputBox type="text" ref="sortSeq" v-model="detail.sortSeq" :value.sync="detail.sortSeq" />
            </td>
            <th scope="row" class="required">사용여부</th>
            <td>
              <vSelect v-model="detail.useYn" :options="useYnList.options" :reduce="label => label.value" :clearable="false" :searchable="false"></vSelect>
            </td>
          </tr>
          <tr>
            <th scope="row">메뉴여부</th>
            <td colspan="3">
              <vSelect v-model="detail.menuYn" :options="useYnList.options" :reduce="label => label.value" :clearable="false" :searchable="false"></vSelect>
            </td>
          </tr>
          <tr>
            <th scope="row" >프로그램 URL</th>
            <td colspan="3">
              <InputBox type="text" v-model="detail.menuurlTxt" :value.sync="detail.menuurlTxt" input-width="80%" />
            </td>
          </tr>
          <tr>
            <th scope="row">비고</th>
            <td colspan="3">
              <textarea style="width:460px; height:70px" v-model="detail.commentTxt"/>
            </td>
        </tr>
        </tbody>
      </table>
      </div>
      <div class="modal-popup-btn-box">
        <button type="button" class="btn btn-primary" @click.prevent.stop="save">신청</button>
        <button type="button" class="btn" @click.prevent.stop="closed">취소</button>
      </div>
    </ModalPopup>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { Check } from "@/mixins/Check";
import { Empty } from "@/mixins/Empty";
import Loading from "vue-loading-overlay";
import 'vue-select/dist/vue-select.css';
import vSelect from 'vue-select'
import InputBox from '@/components/InputBox';
import ModalPopup from "@/components/ModalPopup";

export default {
  name: "MenuDetailPop",
  mixins: [Check,Empty],
  components: {
    InputBox,
    vSelect,
    loading: Loading,
    ModalPopup
  },
  data() {
    return {
      modalVisible: false,
      useYnList: {options:[{label: '선택하세요', value: '' },{label: '사용', value: 'Y' },{label: '미사용', value: 'N'}]},
      detail: { menuId: '', menuNm: '', upmenuId: '', upmenuNm: '',
                sortSeq: '', commentTxt: '', menuimgTxt: '', menuurlTxt: '',
                useYn: '', menuYn: '',        
      },
      isLoading: false,
    };
  },
  computed: {
    // ...mapState(["main"]),
    ...mapState({
      main: (state) => state.main,
    }),
  },
  created() {
  },
  methods: {
    open(info) {
      if( info.type == 'mod' ){
        let params = {};
        params.menuId = info.menuId;
        this.isLoading = true;
        this.$http.post( "/api/system/menu/getMenuDetail",params)
            .then((response) => {
              this.detail = response.data.data[0];
            })
            .catch((e) => {
              console.log(e);
            })
            .finally(() => {
              this.isLoading = false;
            });
      } else {
        this.resetInfo();
        this.detail.upmenuId = info.upmenuId;
        this.detail.upmenuNm = info.upmenuNm;
      }
      this.modalVisible = true;
    },
    closed (obj) {
      this.modalVisible = false;
      this.resetInfo();
      //부모 함수 콜백
      this.$emit('closedMenuDetailPop', obj);
    },
    save(){
      if(!this.validation()){return false;}
      
      let apiUrl = "/api/system/menu/insertMenu";
      if( this.isNotEmpty(this.detail.menuId) ){
        apiUrl = "/api/system/menu/updateMenu";
      }
      this.isLoading = true;
      this.$http.post( apiUrl, this.detail)
          .then((response) => {
            if(response.data.statusCode != 200){
              alert('오류가 발생하였습니다.!!');
              return false;
            } else {
              alert(response.data.message);
              this.closed('success');
            }
          })
          .catch((e) => {
            console.log(e);
          })
          .finally(() => {
            this.isLoading = false;
          });
    },
    validation(){
      if( this.isEmpty(this.detail.menuNm) ){
        alert('메뉴명은 필수값입니다.');
        this.$refs.menuNm.focus();
        return false;
      }

      let bytes = this.maxByteCheck(this.detail.menuNm);
      if( bytes > 50 ){
        this.detail.menuNm = this.detail.menuNm.substring(0, 50);
        alert('메뉴명은 50 바이트 이하로 입력하세요.');
        return false;
      } 

      if( this.isEmpty(this.detail.sortSeq) ){
        alert('정렬순서는 필수값입니다.');
        this.$refs.sortSeq.focus();
        return false;
      }

      if( !Number(this.detail.sortSeq)) {
        alert('정렬순서는 숫자만 입력 하세요.');
        this.$refs.sortSeq.focus();
        return false;
      }

      if( this.isEmpty(this.detail.useYn) ){
        alert('사용여부를 선택해 주세요!!!');
        return false;
      }

      bytes = this.maxByteCheck(this.detail.menuurlTxt);
      if( bytes > 200 ){
        "현재 " + bytes + "바이트 입력하셨습니다.\n 200 바이트 이하로 입력하세요."
        this.detail.menuurlTxt = this.detail.menuurlTxt.substring(0, 200);
        return false;
      }

      bytes = this.maxByteCheck(this.detail.commentTxt);
      if( bytes > 4000 ){
        "현재 " + bytes + "바이트 입력하셨습니다.\n 4000 바이트 이하로 입력하세요."
        this.detail.commentTxt = this.detail.commentTxt.substring(0, 4000);
        return false;
      }

      return true;
    },
    resetInfo(){
      this.detail.menuId = '';
      this.detail.menuNm = '';
      this.detail.menuurlTxt= '';
      this.detail.commentTxt = '';
      this.detail.upmenuId = '';
      this.detail.upmenuNm = '';
      this.detail.sortSeq = '';
      this.detail.useYn = '';
      this.detail.menuYn = '';
    }
  }
}
</script>

<style lang="css" scoped>

</style>
